<template>
  <v-form
    ref="form"
    class="d-flex pa-2 flex-row flex-wrap"
    :style="{ height: 'fit-content' }"
    @submit.prevent="showData"
  >
    <card-row
      :title="'Search'"
      class="red--text text--darken-4"
      :cardColor="'white'"
      :displayed="true"
    >
      <div style="width: 100%">
        <v-row>
          <v-col
            cols="12"
            sm="3"
            md="3"
            v-for="(item, idx) in filterList"
            :key="idx"
          >
            <!-- Date (datepicker) -->
            <v-dialog
              v-if="item.type === 'date'"
              v-model="item.showDatePicker"
              persistent
              max-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="item.value"
                  :label="item.label"
                  readonly
                  v-on="on"
                  :rules="item.rules"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="item.value"
                :max="item.max ? maxDate() : ''"
              >
                <v-spacer></v-spacer>
                <v-btn
                  text
                  color="accent"
                  @click="
                    item.showDatePicker = false;
                    item.value = '';
                  "
                >
                  Cancel
                </v-btn>
                <v-btn text color="accent" @click="item.showDatePicker = false">
                  OK
                </v-btn>
              </v-date-picker>
            </v-dialog>

            <v-dialog
              v-if="item.type === 'dateRange'"
              v-model="item.showDatePicker"
              persistent
              max-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="item.value"
                  :label="item.label"
                  readonly
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                range
                v-model="item.value"
                :max="maxDates(idx)"
                :min="minDates(idx)"
              >
                <v-spacer></v-spacer>
                <v-btn
                  text
                  color="accent"
                  @click="
                    item.showDatePicker = false;
                  "
                >
                  Cancel
                </v-btn>
                <v-btn text color="accent" @click="() => {
                  checkRangeDate(item.value);
                  item.showDatePicker = false
                }">
                  OK
                </v-btn>
              </v-date-picker>
            </v-dialog>

            <!-- Text -->
            <v-text-field
              v-if="item.type === 'text'"
              type="text"
              :label="item.label"
              :required="item.required"
              v-model="item.value"
            ></v-text-field>
            <input-number :item="item" v-if="item.type === 'number'" />

            <!-- Select Normal -->
            <v-select
              v-if="item.type === 'select'"
              v-model="item.value"
              :label="item.label"
              :items="item.item"
              :rules="item.rules"
              item-text="name"
              item-value="value"
            ></v-select>

            <!-- Select Search -->
            <v-autocomplete
              :label="item.label"
              v-model="item.value"
              :items="item.item"
              item-text="name"
              item-value="value"
              :search-input.sync="searchAutocomplete"
              :loading="loading"
              v-if="item.type === 'select search'"
            />

            <!-- Time to Time -->
            <div class="label-time" v-if="item.type === 'time to time'">
              <!-- eslint-disable-next-line vuejs-accessibility/label-has-for -->
              <label for="">{{ item.label }}</label>
              <div class="container-time">
                <vue-timepicker
                  v-model="item.value.start_time"
                  format="HH:mm"
                  hide-clear-button
                  input-width="45%"
                />
                <span class="mx-2 d-flex" style="max-width: 5%; width: 5%">
                  to
                </span>
                <vue-timepicker
                  v-model="item.value.end_time"
                  format="HH:mm"
                  hide-clear-button
                  input-width="45%"
                />
              </div>
            </div>
          </v-col>
        </v-row>
        <v-row class="d-flex align-center justify-left">
          <v-col cols="12">
            <v-btn outlined dark color="primaryred" @click="triggerReset">
              Reset
            </v-btn>
            <v-btn type="submit" color="primary" elevation="0" class="ml-2"
              >show</v-btn
            >
          </v-col>
        </v-row>
      </div>
    </card-row>
  </v-form>
</template>

<script>
import CardRow from '@/components/CardRow.vue';
import { minRangeDate, maxRangeDate } from '@/store/params';
import VueTimepicker from 'vue2-timepicker/dist/VueTimepicker.umd.min';
import 'vue2-timepicker/dist/VueTimepicker.css';
import InputNumber from '../utility/InputNumber.vue';

export default {
  components: { CardRow, VueTimepicker, InputNumber },
  props: {
    search: {
      type: Object,
    },
    loading: {
      type: Boolean,
    },
    filterList: {
      type: Array,
    },
  },
  data() {
    return {
      searchAutocomplete: null,
      today: new Date(),
    };
  },
  methods: {
    checkRangeDate() {
      for (let i = 0; i < this.filterList.length; i += 1) {
        if (this.filterList[i].type === 'dateRange' && this.filterList[i].value.length === 1) {
          [this.filterList[i].value[1]] = this.filterList[i].value;
        }
      }
    },
    maxDates(index) {
      const item = this.filterList[index];
      if (item.value && item.value.length > 0) {
        const tempMaxDate = new Date(item.value[0]);
        const maxDate = new Date(tempMaxDate);
        maxDate.setDate(maxDate.getDate() + maxRangeDate);

        return maxDate > this.today
          ? this.today.toISOString().slice(0, 10)
          : maxDate.toISOString().slice(0, 10);
      }
      return this.today.toISOString().slice(0, 10);
    },
    minDates(index) {
      const item = this.filterList[index];
      if (item.value && item.value.length > 0) {
        const tempMinDate = new Date(item.value[0]);
        const minDate = new Date(tempMinDate);
        minDate.setDate(minDate.getDate() + minRangeDate);
        return minDate.toISOString().slice(0, 10);
      }
      return null;
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    showData() {
      if (this.$refs.form.validate()) {
        this.$emit('showDataParent');
      }
    },
    getMaxDate() {
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      return today.toISOString().slice(0, 10);
    },
    maxDate() {
      const tomorrow = new Date();
      tomorrow.setDate(tomorrow.getDate());
      return tomorrow.toISOString().slice(0, 10);
    },
    triggerReset() {
      this.$emit('resetSearch');
    },
  },
  watch: {
    searchAutocomplete(val) {
      this.$emit('searchAutocomplete', val);
    },
  },
};
</script>

<style>
.label-time {
  color: rgba(0, 0, 0, 0.87);
}
.container-time {
  display: flex;
  align-items: center;
  max-width: 100%;
}
.time-picker input,
.time-picker .dropdown,
.time-picker .select-list {
  width: 100% !important;
}
</style>
