import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';

const ap = {
  namespaced: true,

  state() {
    return {
      image_lokasi: null,
      allMerchants: [],
      totalMerchants: 0,
      allMerchantsHistory: [],
      totalMerchantsHistory: 0,
      reasonList: [],
      loading: false,
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      merchant: null,
      merchantHistory: null,
      totalPage: 0,
      jenis_usaha: [],
      criteria_merchant: [],
      nameMerchant: [],
    };
  },

  mutations: {
    clearErrors(state) {
      state.errors = [];
    },
    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },
    setLoading(state, loading) {
      state.loading = loading;
    },
    setDetailMerchant(state, { payload }) {
      state.merchant = payload;
      state.merchant.image = payload.image_lokasi;
    },
    setMerchants(state, { payload }) {
      state.allMerchants = payload.data.list;
    },
    setTotalMerchants(state, { payload }) {
      state.totalMerchants = parseInt(payload.data.countdata, 10);
      state.totalPage = parseInt(payload.data.totalPage, 10);
    },
    clearMerchants(state) {
      state.allMerchants = [];
      state.totalMerchants = 0;
    },
    setDocLokasi(state, { payload }) {
      state.image_lokasi = payload;
    },
    setJenisUsaha(state, payload) {
      state.jenis_usaha = payload.data.list;
    },
    setCriteriaMerchant(state, payload) {
      state.criteria_merchant = payload.data.list;
    },
    setNameMerchant(state, payload) {
      state.nameMerchant = payload;
    },
  },

  actions: {
    async viewMerchant(ctx, { id }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        inputer_id: id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftartugas/inputer/detail`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setDetailMerchant', {
              payload: response.data.data,
            });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },
    async getData(ctx, {
      page, itemsPerPage, sortBy, sortDesc, search,
    }) {
      ctx.commit('clearMerchants');
      ctx.commit('setLoading', true);
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        sortingBy = `${sortBy[0]} ${sort}`;
      } else {
        sortingBy = `${sort}`;
      }
      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString()
            + page.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftartugas/inputer`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setTotalMerchants', {
              payload: response.data,
            });
            ctx.commit('setMerchants', { payload: response.data });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },
    async uploadData(ctx, { data }) {
      ctx.commit('setLoading', true);
      const formData = new FormData();
      formData.append('file', data);
      formData.append('doc_type', 'file-merchant');
      const config = {
        method: 'post',
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/merchant/document/upload`,
        data: formData,
      };

      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return response.data;
          }
          ctx.commit('setDocLokasi', {
            payload: response.data.data,
          });
          return response.data;
        })
        .catch((err) => {
          console.log(err);
        });
      return result;
    },
    async revisiData(ctx, {
      data, doc, id,
    }) {
      const newData = data;
      newData.document = { image_lokasi: doc };
      const dataUp = {
        ...newData,
        inputer_id: id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      };

      const config = {
        method: 'post',
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftartugas/inputer/revisi`,
        data: dataUp,
      };

      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }

            return false;
          }
          return true;
        })
        .catch((error) => {
          if (error.response.data.code != null) {
            if (error.response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return false;
          }
          return false;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async getCriteriaMerchant(ctx) {
      const data = JSON.stringify({
        version: btoa(
          CryptoJS.SHA256(
            dateHelpers() + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/master/data/merchant-criteria`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }
          } else {
            ctx.commit('setCriteriaMerchant', response.data);
          }
        })
        .catch((err) => {
          if (err.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: err.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (err.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },
    async getJenisUsahaList(ctx, { merchantCriteria }) {
      const data = JSON.stringify({
        merchant_criteria: merchantCriteria,
        version: btoa(
          CryptoJS.SHA256(
            merchantCriteria
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/master/data/business-type`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }
          } else {
            ctx.commit('setJenisUsaha', response.data);
          }
        })
        .catch((err) => {
          if (err.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: err.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (err.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },

    async getJenisUsaha(ctx) {
      const sortBy = 'id ASC';
      const search = {
        mcc: '',
        description: '',
        category_id: '',
        mcc_name: '',
        mdr_on_us: '',
        mdr_off_us: '',
        status: '',
        created_at: null,
        updated_at: null,
      };
      const itemsPerPage = '100';
      const page = '1';
      const data = JSON.stringify({
        search,
        sort_by: sortBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            page.toString()
            + itemsPerPage.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/setting-fee/jenis-usaha/list`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }
          } else {
            ctx.commit('setJenisUsaha', response.data);
          }
        })
        .catch((err) => {
          if (err.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: err.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (err.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },
    async getSearchName(ctx, { value, userId }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        nama: value,
        user_id: userId,
        version: btoa(
          CryptoJS.SHA256(
            userId.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftartugas/inputer/filter-nama`,
        data,
      };

      const result = await axios(config).then((response) => {
        if (response.data.code !== 200) {
          if (localStorage.getItem('language_qram') == null) {
            ctx.commit('setFlashErrorMessage', response.data.message_id);
          } else {
            ctx.commit('setFlashErrorMessage', response.data.message_en);
          }

          if (response.data.code === 401) {
            ctx.dispatch('logout', null, { root: true });
          }

          return false;
        }
        ctx.commit('setNameMerchant', Object.values(response.data.data));
        return true;
      });
      return result;
    },
  },
};

export default ap;
