<template>
  <detail
    :title="'Merchant Detail'"
    :loading="loading"
    :subtitle="'Informasi detail mengenai merchant'"
    :caption="caption"
    :actionButton="actionButton"
  >
    <template v-slot:content>
      <content-data-pic v-if="merchantType === 'Aggregator'" :data="data" />

      <content-data-aggre v-if="merchantType === 'Aggregator'" :data="data" />

      <content-data-merchant
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
        :menu="'tracking'"
      />

      <content-data-doc
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
      />

      <content-data-endpoint
        v-if="data.qr_type === 'QR API'"
        :merchantType="'Merchant'"
        :data="data"
      />

      <content-data-qr :loading="loading" :data="data" :menu="'tracking'" />

      <content-data-keterangan :data="data" />

      <content-data-remark v-if="data != null" :data="data" />
    </template>
  </detail>
</template>

<script>
import Detail from '@/components/detail/Detail.vue';
import ContentDataQr from '@/components/detail/ContentDataQr.vue';
import ContentDataPic from '@/components/detail/ContentDataPic.vue';
import ContentDataAggre from '@/components/detail/ContentDataAggre.vue';
import ContentDataMerchant from '@/components/detail/ContentDataMerchant.vue';
import ContentDataDoc from '@/components/detail/ContentDataDoc.vue';
import ContentDataKeterangan from '@/components/detail/ContentDataKeterangan.vue';
import ContentDataRemark from '@/components/detail/ContentDataRemark.vue';
import ContentDataEndpoint from '@/components/detail/ContentDataEndpoint.vue';

const dateObj = new Date();
const date = `${dateObj.getUTCFullYear()}-${`0${dateObj.getMonth() + 1}`.slice(
  -2,
)}-${`0${dateObj.getDate()}`.slice(-2)}`;

export default {
  components: {
    Detail,
    ContentDataQr,
    ContentDataPic,
    ContentDataAggre,
    ContentDataMerchant,
    ContentDataDoc,
    ContentDataKeterangan,
    ContentDataRemark,
    ContentDataEndpoint,
  },
  beforeMount() {
    this.getDetailData();
  },
  data() {
    return {
      actionButton: {
        buttonPending: false,
        buttonReject: false,
        buttonApprove: false,
      },
      loading: false,
    };
  },
  computed: {
    data() {
      return this.$store.state.trackingPengajuan.merchant;
    },
    caption() {
      let caption = null;
      if (this.data != null) {
        caption = {
          jenis_pengajuan: this.data.jenis_pengajuan,
          created_at: this.data.created_at,
          reference_number: this.data.reference_number,
        };
      }
      return caption;
    },
    merchantType() {
      if (this.data === null) {
        return '';
      }

      return this.data.merchant_type_id === 2 ? 'Aggregator' : 'Merchant';
    },
  },
  methods: {
    async getDetailData() {
      this.loading = true;

      await this.$store
        .dispatch('trackingPengajuan/viewMerchant', {
          id: this.$route.params.id,
          date,
        })
        .then(() => {
          if (this.data == null) {
            this.$router.push('/tracking-pengajuan/list');
          }
        });

      this.loading = false;
    },
  },
};
</script>
