<template>
  <div>
    <filter-component
      :filterList="filterList"
      v-on:showDataParent="showData"
      v-on:resetSearch="resetSearch"
      ref="filterComponentRef"
    />
    <information-bulk v-on:clickedRestart="showData" :data="allData" class="mt-3 mb-5" />
    <table-log
      :expandedItem="false"
      class="mt-5"
      :headers="headers"
      :footerProps="footerProps"
      :options="options"
      :totalData="totalData"
      @update:options="updateOptions"
      :items="items"
      :loading="loading"
    >
      <template #action="{ data }">
        <action-bulk :data="data" />
      </template>
      <template #result="{ data }">
        <result-component :data="data" />
      </template>
    </table-log>
  </div>
</template>

<script>
import { statusAction } from '@/store/params';
import FilterComponent from '../table/FilterComponent.vue';
import TableLog from '../table/TableLog.vue';
import InformationBulk from './InformationBulk.vue';
import ResultComponent from './ResultComponent.vue';
import ActionBulk from './ActionBulk.vue';

export default {
  components: {
    FilterComponent,
    TableLog,
    InformationBulk,
    ResultComponent,
    ActionBulk,
  },
  data() {
    return {
      filterList: [
        {
          label: 'Nama File',
          type: 'text',
          value: '',
        },
        {
          label: 'Created By',
          type: 'select search',
          item: [],
          icon: 'mdi-magnify',
          value: '',
        },
        {
          label: 'Tanggal Upload',
          type: 'date',
          icon: 'mdi-calendar',
          showDatePicker: false,
          max: true,
          value: '',
        },
        {
          label: 'Status Action',
          type: 'select',
          item: statusAction,
          value: '',
        },
      ],
      headers: [
        {
          text: 'No',
          align: 'start',
          width: '10px',
          value: 'no',
        },
        {
          text: 'Nama File',
          align: 'start',
          width: '250px',
          value: 'nama_file',
        },
        {
          text: 'Result',
          align: 'center',
          width: '300px',
          value: 'result',
        },
        {
          text: 'Created By',
          align: 'center',
          width: '150px',
          value: 'created_by',
        },
        {
          text: 'Tanggal Upload',
          align: 'center',
          width: '150px',
          value: 'tanggal_upload',
        },
        {
          text: 'Action',
          align: 'center',
          width: '100px',
          value: 'action',
        },
      ],
      footerProps: {
        itemsPerPageOptions: [10, 20, 50, 100],
      },
      options: {},
    };
  },
  beforeMount() {
    this.getUserMaster();
  },
  watch: {
    userMaster(val) {
      if (val) {
        this.filterList[1].item = val;
      }
    },
  },
  computed: {
    userMaster() {
      const data = this.$store.state.bulkUpload.userMaster.map((obj) => ({
        value: obj.id,
        name: obj.fullname,
        username: obj.username,
      }));
      return data;
    },
    allData() {
      return this.$store.state.bulkUpload.historyBulk;
    },
    items() {
      return this.allData?.list || [];
    },
    totalData() {
      return this.allData?.totalData || 0;
    },
    totalPage() {
      return this.$store.state.bulkUpload.totalPage;
    },
    loading() {
      return this.$store.state.bulkUpload.loading;
    },
  },
  methods: {
    resetSearch() {
      this.filterList.map((obj) => {
        const container = obj;
        container.value = '';

        return container;
      });
      this.$refs.filterComponentRef.resetValidation();
      this.getData();
    },
    updateOptions(val) {
      this.options = val;
      this.getData();
    },
    showData() {
      this.getData();
    },
    getData() {
      const { page, itemsPerPage } = this.options;
      const search = {
        page,
        limit: itemsPerPage,
        search: {
          nama_file: this.filterList[0].value,
          userId: this.filterList[1].value,
          tanggal_upload: this.filterList[2].value,
          status: this.filterList[3].value,
        },
        sort_by: 'id',
        sort_order: 'DESC',
      };
      this.$store
        .dispatch('bulkUpload/fetchDataHistory', {
          search,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },
    async getUserMaster() {
      this.$store.dispatch('bulkUpload/getUserMaster', {
        name: this.filterList[1].value,
      });
    },
  },
};
</script>
