import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';

const ap = {
  namespaced: true,

  state() {
    return {
      allMerchants: [],
      totalMerchants: 0,
      allMerchantsHistory: [],
      totalMerchantsHistory: 0,
      reasonList: [],
      loading: false,
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      merchant: null,
      merchantHistory: null,
      totalPage: 0,
      intervalExport: null,
      exportId: null,
      countDataExport: 0,
      nameMerchant: [],
      parentClassification: [],
    };
  },

  mutations: {
    setMerchants(state, { payload }) {
      state.allMerchants = payload.data.list;
    },

    setTotalMerchants(state, { payload }) {
      state.totalMerchants = parseInt(payload.data.countdata, 10);
      state.totalPage = parseInt(payload.data.totalPage, 10);
    },

    setMerchantsHistory(state, { payload }) {
      state.allMerchantsHistory = payload.data.list;
    },

    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },

    clearMerchants(state) {
      state.allMerchants = [];
      state.totalMerchants = 0;
    },

    clearErrors(state) {
      state.errors = [];
    },

    clearErrorVariable(state, variable) {
      state.errors[variable] = [];
    },

    setLoading(state, loading) {
      state.loading = loading;
    },

    setDetailMerchant(state, { payload }) {
      state.merchant = payload;
    },

    setReasonList(state, { payload }) {
      state.reasonList = payload.data.list;
    },

    clearReasonList(state) {
      state.reasonList = [];
    },

    setNameMerchant(state, payload) {
      state.nameMerchant = payload;
    },
    setParentClassification(state, { payload }) {
      state.parentClassification = payload;
    },
  },

  actions: {
    async getData(ctx, {
      page, itemsPerPage, sortBy, sortDesc, search,
    }) {
      ctx.commit('clearMerchants');
      ctx.commit('setLoading', true);
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        sortingBy = `${sortBy[0]} ${sort}`;
      } else {
        sortingBy = `${sort}`;
      }

      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString()
            + page.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftar_approval/approval/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setTotalMerchants', {
              payload: response.data,
            });
            ctx.commit('setMerchants', { payload: response.data });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },

    async viewMerchant(ctx, { id }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftar_approval/approval/view`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setDetailMerchant', {
              payload: response.data.data,
            });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },

    async getParentClassification(ctx, { id }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        merchant_id: id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftar_approval/approval/classifications`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setParentClassification', {
              payload: response.data.data,
            });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },

    async reasonList(ctx, { type }) {
      ctx.commit('clearReasonList');
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        type,
        version: btoa(
          CryptoJS.SHA256(
            type + dateHelpers() + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftar_approval/approval/list-reasons`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setReasonList', {
              payload: response.data,
            });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },

    async saveReason(ctx, {
      dataForm, type, accountClassification,
    }) {
      if (typeof accountClassification[0] === 'object') {
        accountClassification = parseInt(accountClassification.map((obj) => obj.id), 10);
      }
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        id: dataForm.id,
        account_classification: accountClassification || '',
        id_reason: dataForm.id_reason,
        note: dataForm.note,
        version: btoa(
          CryptoJS.SHA256(
            dataForm.id.toString()
            + dataForm.id_reason.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });

      const route = (typeReason) => {
        switch (typeReason) {
          case 1:
            return 'pending';
          case 2:
            return 'reject';
          default:
            return '';
        }
      };

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftar_approval/approval/${route(type)}`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }

            return false;
          }
          return true;
        })
        .catch((error) => {
          if (error.response.data.code != null) {
            if (error.response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return false;
          }
          return false;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async saveApprove(ctx, { id, accountClassification }) {
      if (typeof accountClassification[0] === 'object') {
        accountClassification = parseInt(accountClassification.map((obj) => obj.id), 10);
      }
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        account_classification: accountClassification,
        id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftar_approval/approval/approve`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }

            return false;
          }
          return true;
        })
        .catch((error) => {
          if (error.response.data.code != null) {
            if (error.response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return false;
          }
          return false;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async bulkApprove(ctx, { id }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        merchant_id: id,
        version: btoa(
          CryptoJS.SHA256(
            dateHelpers() + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftar_approval/approval/approved-bulk`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }

            return false;
          }
          return true;
        })
        .catch((error) => {
          if (error.response.data.code != null) {
            if (error.response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return false;
          }
          return false;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    //  Search Name
    async getSearchName(ctx, { value, userId }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        nama: value,
        user_id: userId,
        version: btoa(
          CryptoJS.SHA256(
            userId.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/daftar_approval/approval/filter-nama`,
        data,
      };

      const result = await axios(config).then((response) => {
        if (response.data.code !== 200) {
          if (localStorage.getItem('language_qram') == null) {
            ctx.commit('setFlashErrorMessage', response.data.message_id);
          } else {
            ctx.commit('setFlashErrorMessage', response.data.message_en);
          }

          if (response.data.code === 401) {
            ctx.dispatch('logout', null, { root: true });
          }

          return false;
        }
        ctx.commit('setNameMerchant', Object.values(response.data.data));
        return true;
      });
      return result;
    },
  },
};

export default ap;
