<template>
  <v-toolbar flat>
    <v-dialog
      v-if="listButton.formDateRange"
      ref="rangeDateDialog"
      v-model="rangeDateDialog"
      :return-value.sync="dateRange"
      persistent
      max-width="290px"
      min-width="290px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
          id="rangeDate"
          prepend-inner-icon="mdi-calendar"
          readonly
          v-bind="attrs"
          v-on="on"
          v-model="dateRangeText"
          :label="listButton.formDateRange.label"
          hide-details="true"
        >
          <template v-slot:append-outer>
            <v-btn large text color="accent" @click="showData">
              <v-icon left> mdi-magnify </v-icon>search
            </v-btn>
          </template>
        </v-text-field>
      </template>
      <v-date-picker
        v-model="dateRange"
        scrollable
        range
        :min="minDate"
        :max="maxDate"
      >
        <v-spacer></v-spacer>
        <v-btn text color="accent" @click="rangeDateDialog = false">
          Cancel
        </v-btn>
        <v-btn
          text
          color="accent"
          @click="
            () => {
              checkRangeDate();
              $refs.rangeDateDialog.save(dateRange);
            }
          "
        >
          OK
        </v-btn>
      </v-date-picker>
    </v-dialog>

    <v-chip
      v-if="listButton.buttonCount && selectedData.length > 0"
      close
      @click:close="clearSelected"
    >
      {{ `${selectedData.length} data(s) selected` }}
    </v-chip>

    <v-spacer></v-spacer>

    <v-btn
      v-if="listButton.buttonCustom"
      outlined
      color="primary"
      :disabled="loading"
      class="ml-2 mb-2"
      @click="save"
    >
      {{ listButton.buttonCustom.label }}
    </v-btn>

    <v-btn
      v-if="listButton.buttonAdd"
      outlined
      color="primary"
      class="ml-2 mb-2"
      @click="addItem"
    >
      <v-icon>mdi-plus</v-icon>
      Add Data
    </v-btn>
    <v-btn
      v-if="listButton.buttonAddCreateMerchant"
      outlined
      color="primary"
      class="ml-2 mb-2"
      @click="addItem"
    >
      <v-icon>mdi-plus</v-icon>
      Add New Merchant
    </v-btn>

    <v-btn
      v-if="listButton.buttonRestart"
      color="primaryred"
      elevation="0"
      class="ml-2 mb-2 white--text"
      @click="getData"
      :loading="loading"
      :disabled="loading"
      :style="{
        cursor: 'pointer',
      }"
      ><v-icon>mdi-restart</v-icon
      ><template v-slot:loader>
        <span class="custom-loader">
          <v-icon light>mdi-cached</v-icon>
        </span>
      </template>
    </v-btn>
    <v-menu
      v-if="listButton.buttonDots"
      offset-y
      rounded="lg"
      transition="slide-y-transition"
      bottom
      left
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          color="accent"
          class="ml-2 mb-2"
          icon
          dark
          v-bind="attrs"
          v-on="on"
        >
          <v-icon light size="20">mdi-dots-vertical</v-icon>
        </v-btn>
      </template>
      <v-list dense>
        <v-list-item link @click="exportItem()">
          <v-list-item-icon>
            <v-icon>mdi-file-excel</v-icon>
          </v-list-item-icon>
          <v-list-item-title>Export to excel</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-toolbar>
</template>

<script>
import { minRangeDate, maxRangeDate } from '@/store/params';
import { getDate } from '@/lib/helpers';

export default {
  props: {
    loading: {
      type: Boolean,
    },
    selectedData: {
      type: Array,
    },
    listButton: {
      type: Object,
    },
  },
  data: () => ({
    today: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .slice(0, 10),
    rangeDateDialog: false,
    dateRange: [],
  }),
  computed: {
    dateRangeText() {
      return this.dateRange.join(' ~ ');
    },
    minDate() {
      if (this.dateRange.length > 0) {
        const tempMinDate = Date.parse(this.dateRange[0]);
        return this.getDate(minRangeDate, tempMinDate);
      }
      return '';
    },
    maxDate() {
      if (this.dateRange.length > 0) {
        const tempMaxDate = Date.parse(this.dateRange[0]);
        const maxDate = this.getDate(maxRangeDate, tempMaxDate);
        return maxDate > this.today ? this.today : maxDate;
      }
      return this.today;
    },
  },
  methods: {
    getDate,
    checkRangeDate() {
      if (this.dateRange.length === 1) {
        this.dateRange.push(this.dateRange[0]);
      }
    },
    showData() {
      this.$emit('showDataParent', this.dateRange);
    },
    getData() {
      this.$emit('getDataParent');
    },
    addItem() {
      this.$emit('addItemParent');
    },
    save() {
      this.$emit('saveParent');
    },
    clearSelected() {
      this.$emit('clearSelectedParent');
    },
    exportItem() {
      this.$emit('exportItemParent');
    },
  },
};
</script>
