<template>
  <div>
    <v-row class="align-center">
      <v-col cols="10" sm="4"> {{ label }} </v-col>
      <v-col cols="2" sm="1"> : </v-col>
      <v-col cols="12" sm="7" v-if="checkStatus">
        <v-checkbox
          v-if="data.task_type === 'Add New Outlet'"
          v-model="checkParent"
          :value="true"
          label="Sesuai dengan Klasifikasi Merchant Utama"
          @change="clickParent"
        />
        <div class="mirror-input">
          <div @keyup.ctrl.enter="openCustomSelect()" @click="openCustomSelect()">
            <p class="mb-0">
              {{
                value.length
                  ? value.length + " Selected"
                  : "Pilih Klasifikasi Merchant"
              }}
            </p>
          </div>
          <div class="popup-option" v-if="openSelect">
            <v-text-field
              label="Search Klasifikasi"
              v-model="search"
              dense
              rounded
              outlined
              hide-details
            >
            </v-text-field>
            <div class="info-selected">
              <p class="ma-2" color="#f3f3f3">
                ( {{ valueSelected() + " Selected Active" }} )
              </p>
            </div>
            <div class="option-select-custom">
              <div v-for="item in defaultItem" :key="item.id">
                <div
                  class="option-item-custom"
                  v-if="
                    item.nama_klasifikasi
                      .toLowerCase()
                      .includes(search.toLowerCase())
                  "
                >
                  <v-checkbox
                    class="mt-0"
                    color="success"
                    :label="item.nama_klasifikasi"
                    :value="item.id"
                    v-model="item.selected"
                    :disabled="disableIndividual(item)"
                    hide-details
                  />
                </div>
              </div>
            </div>
            <div class="button-select-custom">
              <v-btn class="mr-5" @click="cancelSelectCustom">Cancel</v-btn>
              <v-btn color="blue darken-2" @click="saveSelectCustom" dark
                >Save</v-btn
              >
            </div>
          </div>
        </div>
      </v-col>
      <v-col cols="12" sm="7" v-else>
        <chip-list-klasifikasi :value="value" />
      </v-col>
      <v-col v-if="note" cols="12">
        <small>
          <em>
            <b>
              {{ note }}
            </b>
          </em>
        </small>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { areArraysEqual } from '@/lib/helpers';
import ChipListKlasifikasi from './ChipListKlasifikasi.vue';

export default {
  components: { ChipListKlasifikasi },
  props: {
    note: {
      type: String,
    },
    data: {
      type: Object,
    },
    value: { type: [Number, Object, Array] },
    valueId: { type: [Number, Object, Array] },
    label: { type: String },
    items: { type: Array, required: true },
    rule: {
      type: Array,
    },
  },
  data() {
    return {
      checkParent: false,
      search: '',
      itemDefault: [],
      valueItem: [],
      openSelect: false,
    };
  },
  created() {
    this.$store.dispatch('daftarTugas/ap/getParentClassification', {
      id: this.data.id,
    });
  },
  computed: {
    parentClasification() {
      let data = this.$store.state.daftarTugas.ap.parentClassification;
      data = data.account_classification.map((item) => parseInt(item.id, 10));
      return data;
    },
    defaultItem: {
      get() {
        return this.itemDefault;
      },
      set(newVal) {
        this.itemDefault = newVal;
      },
    },
    disableAutocomplete() {
      return this.value.length >= 10;
    },
    checkStatus() {
      const currentPath = window.location.pathname;

      return (
        this.data.status === 'New'
        && !currentPath.includes('tracking-pengajuan')
      );
    },
  },
  watch: {
    items() {
      this.copyItems();
    },
  },
  methods: {
    clickParent(val) {
      if (val) {
        this.$emit('update:value', this.parentClasification);
        setTimeout(() => {
          this.cancelSelectCustom();
        }, 100);
      }
    },
    copyItems() {
      this.itemDefault = this.items.map((obj) => ({
        ...obj,
        selected: this.valueId.includes(obj.id) ? obj.id : false,
      }));
    },
    cancelSelectCustom() {
      this.openSelect = false;
      this.copyItems();
    },
    saveSelectCustom() {
      if (!areArraysEqual(this.parentClasification, this.valueItem)) {
        this.checkParent = false;
      }
      this.$emit('update:value', this.valueItem);
      this.openSelect = false;
      this.$emit('update:items', this.itemDefault);
    },
    openCustomSelect() {
      this.openSelect = !this.openSelect;
    },
    valueSelected() {
      const total = [];
      this.defaultItem.forEach((item) => {
        if (item.selected) {
          total.push(item.selected);
        }
      });

      this.valueItem = total;
      return total.length;
    },
    disableIndividual(item) {
      const selectedCount = this.defaultItem.filter(
        (items) => items.selected,
      ).length;

      return item.selected ? false : selectedCount >= 10;
    },
    searchCustom() {
      if (this.search) {
        this.defaultItem = this.items.filter((item) => item.nama_klasifikasi.toLowerCase()
          .includes(this.search.toLowerCase()));
      } else {
        this.defaultItem = this.items;
      }
    },
    updateSelect(val) {
      this.$emit('update:value', val);
    },
  },
};
</script>

<style scoped>
.mirror-input {
  position: relative;
  border: 1px solid #999;
  border-radius: 4px;
  padding: 10px;
}
.popup-option {
  position: absolute;
  z-index: 1;
  width: 100%;
  top: 55px;
  left: 0;
  height: 385px;
  background: white;
  border-radius: 5px;
  box-shadow: 0 2px 5px 1px rgba(64, 60, 67, 0.16);
  padding: 18px;
}
.option-select-custom {
  height: 220px;
  overflow-y: auto;
  overflow-x: hidden;
  margin-top: 10px;
}
.button-select-custom {
  padding: 15px 0;
}
.option-item-custom {
  height: 45px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 10px;
}
.option-item-custom:hover {
  background: #f3f3f3;
}
</style>
