import Parent from "../views/Parent.vue";

import User from "../views/user/User.vue";

import { setBreadcrumb } from "./index.js";

export const userRoute = {
    path: "/user",
    meta: {
        title: "JAKONE - User",
        appBarTitle: "User",
    },
    component: Parent,
    children: [
        {
            path: "",
            name: "ListUser",
            meta: {
                title: "JAKONE - List User",
                appBarTitle: "List User",
                breadcrumbs: () => {
                    return [
                        setBreadcrumb('user management', '/user', true),
                        setBreadcrumb('list user', '/user'),
                    ]
                }
            },
            component: User,
        },
    ]
}