<template>
  <v-card class="d-flex pa-4 flex-column" outlined>
    <Loading v-if="loading" />
    <div class="px-2 py-4">
      <v-tabs v-model="tab" align-with-title>
        <v-tabs-slider color="none" class="d-none"></v-tabs-slider>

        <v-tab class="pa-2 tab-custom" v-for="item in items" :key="item.tab">
          <div class="slide-menu pa-3">
            {{ item.name }}
          </div>
        </v-tab>
      </v-tabs>
      <v-card class="mt-2 pa-4">
        <v-tabs-items v-model="tab">
          <v-tab-item>
            <upload-merchant v-on:tabMove="tabMove" v-if="tab === 0"/>
          </v-tab-item>
          <v-tab-item>
            <history-bulk v-if="tab === 1" />
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </div>
  </v-card>
</template>

<script>
import UploadMerchant from '@/components/bulkUpload/UploadMerchant.vue';
import HistoryBulk from '@/components/bulkUpload/HistoryBulk.vue';
import Loading from '@/components/Loading.vue';

export default {
  components: {
    UploadMerchant,
    HistoryBulk,
    Loading,
  },
  name: 'bulk-upload-merchant',
  data() {
    return {
      tab: null,
      items: [
        {
          name: 'Bulk Upload Add',
          tab: 1,
        },
        {
          name: 'History Bulk Upload',
          tab: 2,
        },
      ],
    };
  },
  computed: {
    loading() {
      return this.$store.state.bulkUpload.loadingUpload;
    },
  },
  methods: {
    tabMove(val) {
      if (val) {
        this.tab = 1;
      }
    },
  },
};
</script>

<style>
.tab-custom {
  background: #f3f3f6;
  text-transform: capitalize !important;
}
.tab-custom:nth-child(2) {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.tab-custom:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.v-tab--active .slide-menu {
  border-radius: 10px;
  background: white;
  color: #232323;
}
</style>
