<template>
  <v-data-table
    :headers="headers"
    :items="allData"
    :options.sync="optionsTable"
    :server-items-length="totalData"
    :loading="loading"
    loading-text="Loading... Please wait"
    :item-class="rowClass"
    class="d-print-table"
    :footer-props="{
      itemsPerPageOptions: hidePage ? [-1] : [10, 20, 50, 100],
      // showFirstLastPage: true,
    }"
    sort-by="id"
    fixed-header
    :style="{ padding: '10px', height: tableHeight ? tableHeight : '' }"
  >
    <template v-slot:[`body.prepend`]>
      <slot name="filter" />
    </template>

    <template v-slot:top>
      <slot name="top" />
    </template>

    <template v-slot:item="{ item, index }">
      <slot name="item" :item="item" :index="index" />
    </template>

    <template v-slot:no-data>
      <slot name="nodata" />
    </template>
  </v-data-table>
</template>

<script>
export default {
  props: {
    headers: {
      type: Array,
    },
    allData: {
      type: Array,
    },
    options: {
      type: Object,
    },
    totalData: {
      type: [Number, String],
    },
    hidePage: {
      type: Boolean,
    },
    loading: {
      type: Boolean,
    },
    tableHeight: {
      type: [String, Number],
    },
  },
  computed: {
    optionsTable: {
      get() {
        return this.options;
      },
      set(val) {
        this.$emit('update:options', val);
      },
    },
  },
  methods: {
    rowClass() {
      const rowClass = 'max-width-col';
      return rowClass;
    },
  },
};
</script>
