<template>
  <v-text-field
    v-model="item.value"
    type="number"
    :label="item.label"
    @keydown="preventExponentAndSign"
    :rules="item.rules"
  ></v-text-field>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
    },
  },
  methods: {
    preventExponentAndSign(event) {
      const input = event.target;

      // Only validate input if type is not "text"
      if (input.type !== 'text' && input.type !== 'email') {
        const { userAgent } = navigator;

        const keyCode = event.keyCode || event.which;
        const isValid = (keyCode >= 48 && keyCode <= 57) || keyCode === 8;
        const pattern = /^[^A-Za-z0-9\s]+$/;
        const checkSpecial = pattern.test(event.key);

        if (userAgent.indexOf('Firefox') !== -1 && (!isValid || checkSpecial)) {
          event.preventDefault();
        }
        if (
          event.key === 'e'
          || event.key === 'E'
          || event.key === '+'
          || event.key === '-'
          || event.key === ','
          || event.key === '.'
        ) {
          event.preventDefault();
        }
      }
    },
  },
};
</script>
