<template>
  <tr
    :key="index"
    :style="{ cursor: 'pointer' }"
    :class="selectedRow === index ? 'indigo lighten-5' : ''"
    @click="selectRow(index)"
  >
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ (options.page - 1) * options.itemsPerPage + index + 1 }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.role }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-icon small class="mr-2" @click="editItem(item)"> mdi-eye </v-icon>
      <v-icon small @click="actItem(item)"> mdi-delete </v-icon>
    </td>
  </tr>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
    },
    index: {
      type: Number,
    },
    options: {
      type: Object,
    },
    selectedRow: {
      type: [Number, String],
    },
  },
  data: () => ({}),
  methods: {
    selectRow(index) {
      this.$emit('selectRowParent', index);
    },
    editItem(item) {
      this.$emit('editItemParent', item);
    },
    actItem(item) {
      this.$emit('actItemParent', item);
    },
  },
};
</script>
