<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <v-alert
      v-model="flashError"
      dense
      border="left"
      type="error"
      dismissible
      elevation="2"
    >
      {{ flashErrorMessage }}
    </v-alert>

    <table-list
      :headers="headers"
      :allData="allData"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :loading="loading"
      @update:options="updateOptions"
    >
      <template v-slot:filter>
        <filter-list
          :search="search"
          v-on:clearSearchParent="clearSearch"
          v-on:showDataParent="showData"
          :loading="loading"
        />
      </template>

      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
          v-on:addItemParent="addItem"
        />
      </template>

      <template v-slot:item="{ item, index }">
        <data-list
          :item="item"
          :index="index"
          :options="options"
          v-on:actItemParent="actItem"
          :selectedRow="selectedRow"
          v-on:selectRowParent="selectRow"
        />
      </template>

      <template v-slot:nodata>
        <v-btn outlined dark color="primaryred" @click="clearSearch">
          Reset
        </v-btn>
      </template>
    </table-list>

    <form-user
      :loading="loading"
      :dialog="dialog"
      :editedItem="editedItem"
      :editedIndex="editedIndex"
      v-on:closeCancelParent="close"
      :blocked="blocked"
    />

    <confirm-dialog
      :loading="loading"
      :dialog="dialogDelete"
      :title="'Are you sure you want to delete this user'"
      v-on:closeCancelParent="close"
      v-on:confirmParent="confirm"
    />
  </v-card>
</template>

<script>
import FilterList from '@/components/table/FilterUserList.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import TableList from '@/components/table/TableList.vue';
import DataList from '@/components/table/DataUserList.vue';
import FormUser from '@/components/dialog/FormUser.vue';
import ConfirmDialog from '@/components/dialog/ConfirmDialog.vue';

const dateObj = new Date();
const date = `${dateObj.getUTCFullYear()}-${`0${dateObj.getMonth() + 1}`.slice(
  -2,
)}-${`0${dateObj.getDate()}`.slice(-2)}`;

export default {
  components: {
    FilterList,
    ActionButton,
    TableList,
    DataList,
    FormUser,
    ConfirmDialog,
  },
  data: () => ({
    selectedRow: '',
    listButton: {
      buttonRestart: true,
      buttonDots: false,
      buttonAdd: true,
    },
    search: {
      username: '',
      fullname: '',
      role: '',
      created_at: null,
      last_login_at: null,
      status: '',
    },
    searchDefault: {
      username: '',
      fullname: '',
      role: '',
      created_at: null,
      last_login_at: null,
      status: '',
    },
    searchFix: {
      username: '',
      fullname: '',
      role: '',
      created_at: null,
      last_login_at: null,
      status: '',
    },
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'id',
        sortable: false,
        width: '3%',
      },
      {
        text: 'User ID',
        value: 'username',
        width: '13%',
        align: 'center',
      },
      {
        text: 'Fullname',
        value: 'fullname',
        width: '13%',
        align: 'center',
      },
      {
        text: 'Role',
        value: 'role',
        width: '13%',
        align: 'center',
      },
      {
        text: 'Created At',
        value: 'created_at',
        width: '16%',
        align: 'center',
      },
      {
        text: 'Last Login',
        value: 'last_login_at',
        width: '16%',
        align: 'center',
      },
      {
        text: 'Status',
        value: 'status',
        width: '10%',
        align: 'center',
      },
      {
        text: 'Actions',
        value: 'actions',
        sortable: false,
        width: '10%',
      },
    ],
    editedItem: {
      username: '',
      fullname: '',
      email: '',
      mobile_phone: '',
      role: '',
      status: '',
      password: '',
      passwordConfirmation: '',
    },
    defaultItem: {
      username: '',
      fullname: '',
      email: '',
      mobile_phone: '',
      role: '',
      status: '',
      password: '',
      passwordConfirmation: '',
    },
    editedIndex: -1,
    page: '1',
    options: {},
    dialog: false,
    dialogDelete: false,
    isFirstLoad: false,
    blocked: null,
  }),
  beforeMount() {
    this.$store.commit('users/clearUsers');
  },
  computed: {
    flashErrorMessage() {
      return this.$store.state.users.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.users.flashError;
      },
      set() {
        this.$store.commit('users/setFlashErrorMessage', '');
      },
    },
    loading() {
      return this.$store.state.users.loading;
    },
    allData() {
      return this.$store.state.users.allUsers;
    },
    totalData() {
      return this.$store.state.users.totalUsers;
    },
    totalPage() {
      return this.$store.state.users.totalPage;
    },
  },
  methods: {
    selectRow(index) {
      if (this.selectedRow !== index) {
        this.selectedRow = index;
      } else {
        this.selectedRow = '';
      }
    },
    clearSearch() {
      this.search = { ...this.searchDefault };
      this.$nextTick(() => {
        this.$store.commit('users/clearUsers');
        this.showData();
      });
    },

    showData() {
      this.searchFix = { ...this.search };
      this.getData();
    },

    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },

    getData() {
      this.$store.commit('users/setFlashErrorMessage', '');
      const {
        page, itemsPerPage, sortBy, sortDesc,
      } = this.options;

      this.$store
        .dispatch('users/fetchUsers', {
          date,
          page,
          itemsPerPage,
          sortBy,
          sortDesc,
          search: this.searchFix,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },

    resetUniqueValidation() {
      this.$store.commit('users/clearErrors');
    },

    close() {
      this.resetUniqueValidation();
      this.dialog = false;
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },

    addItem() {
      this.dialog = true;
      this.blocked = false;
    },

    actItem(item, type) {
      // eslint-disable-next-line no-param-reassign
      item.data = { fullname: item.fullname, id: item.id };
      this.editedIndex = this.$store.state.users.allUsers.indexOf(item);
      this.editedItem = { ...item };
      if (type === 'edit') {
        if (item.status === '2') this.blocked = true;
        else this.blocked = false;
        this.dialog = true;
      } else {
        this.dialogDelete = true;
      }
    },

    async confirm() {
      await this.$store
        .dispatch('users/deleteUser', {
          date,
          user: this.editedItem,
          index: this.editedItem.id,
        })
        .then((status) => {
          if (status) {
            this.close();
            this.showAlert(
              'Delete user success !',
              'mdi-check-circle',
              'green',
            );
          } else {
            this.showAlert('Error on Delete user !', 'mdi-alert', 'red');
          }
        });
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
