import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';

const ap = {
  namespaced: true,

  state() {
    return {
      allMerchants: [],
      totalMerchants: 0,
      allMerchantsHistory: [],
      totalMerchantsHistory: 0,
      reasonList: [],
      loading: false,
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      merchant: null,
      merchantHistory: null,
      totalPage: 0,
      intervalExport: null,
      exportId: null,
      countDataExport: 0,
      nameMerchant: [],
      requestBy: [],
    };
  },

  mutations: {
    clearErrors(state) {
      state.errors = [];
    },
    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },
    setLoading(state, loading) {
      state.loading = loading;
    },
    setDetailMerchant(state, { payload }) {
      state.merchant = payload;
    },
    setMerchants(state, { payload }) {
      state.allMerchants = payload.data.list;
    },
    setTotalMerchants(state, { payload }) {
      state.totalMerchants = parseInt(payload.data.countdata, 10);
      state.totalPage = parseInt(payload.data.totalPage, 10);
    },
    clearMerchants(state) {
      state.allMerchants = [];
      state.totalMerchants = 0;
    },

    setNameMerchant(state, payload) {
      state.nameMerchant = payload;
    },
    setRequestBy(state, payload) {
      state.requestBy = payload;
    },
  },
  actions: {
    async viewMerchant(ctx, { id }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
              + dateHelpers()
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/history/approval/detail`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setDetailMerchant', {
              payload: response.data.data,
            });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },
    async getData(ctx, {
      page, itemsPerPage, sortBy, sortDesc, search,
    }) {
      ctx.commit('clearMerchants');
      ctx.commit('setLoading', true);
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        sortingBy = `${sortBy[0]} ${sort}`;
      } else {
        sortingBy = `id ${sort}`;
      }
      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        merchant_name: '',
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString()
              + page.toString()
              + dateHelpers()
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/history/approval`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setTotalMerchants', {
              payload: response.data,
            });
            ctx.commit('setMerchants', { payload: response.data });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },

    //  Search Name
    async getSearchName(ctx, { value }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        name: value,
        version: btoa(
          CryptoJS.SHA256(
            value.toString()
              + dateHelpers()
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/history/approval/filter-name`,
        data,
      };

      const result = await axios(config).then((response) => {
        if (response.data.code !== 200) {
          if (localStorage.getItem('language_qram') == null) {
            ctx.commit('setFlashErrorMessage', response.data.message_id);
          } else {
            ctx.commit('setFlashErrorMessage', response.data.message_en);
          }

          if (response.data.code === 401) {
            ctx.dispatch('logout', null, { root: true });
          }

          return false;
        }
        ctx.commit('setNameMerchant', Object.values(response.data.data));
        return true;
      });
      return result;
    },

    async getRequestBy(ctx, { value }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        request_by: value,
        version: btoa(
          CryptoJS.SHA256(
            value.toString()
              + dateHelpers()
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/history/approval/filter-request-by`,
        data,
      };

      const result = await axios(config).then((response) => {
        if (response.data.code !== 200) {
          if (localStorage.getItem('language_qram') == null) {
            ctx.commit('setFlashErrorMessage', response.data.message_id);
          } else {
            ctx.commit('setFlashErrorMessage', response.data.message_en);
          }

          if (response.data.code === 401) {
            ctx.dispatch('logout', null, { root: true });
          }

          return false;
        }
        ctx.commit('setRequestBy', response.data.data);
        return true;
      });
      return result;
    },
  },
};

export default ap;
