<template>
  <div>
    <v-card
      class="mb-12"
      height="20px"
      :style="{ 'box-shadow': '0 0 black', 'margin-top': '20px' }"
    >
      <div class="d-flex pa-5 flex-column align-center justify-center">
        <h3>Dokumen</h3>
      </div>
    </v-card>
    <div>
      <v-row>
        <v-col cols="10" sm="4"> Foto Lokasi Usaha </v-col>
        <v-col cols="2" sm="1"> : </v-col>
        <v-col cols="12" sm="7">
          <v-file-input
            v-model="images"
            label="File input"
            variant="filled"
            prepend-icon="mdi-camera"
            :clearable="false"
            clear-icon=""
            :rules="fileRule"
            @change="upload_image"
            :error-messages="error"
            :accept="accept"
          ></v-file-input>
          <v-card v-if="data.image || images" @click="popUpImage" elevation="2">
            <v-img
              class="grey lighten-2"
              style="height: 300px;"
              :src="data.image || preview"
              contain
            ></v-img>
          </v-card>
          <slot name="content" />
        </v-col>
      </v-row>
    </div>

    <image-dialog
      :dialog="dialog"
      :caution="data.image || preview"
      v-on:closeCancelParent="close"
    />
  </div>
</template>

<script>
import ImageDialog from '@/components/dialog/ImageDialog.vue';

export default {
  props: {
    fileRule: {
      type: Array,
    },
    data: {
      type: Object,
      default: null,
    },
    accept: {
      type: String,
      default: null,
    },
  },
  components: { ImageDialog },
  data: () => ({
    error: null,
    dialog: false,
    images: null,
    preview: null,
  }),
  watch: {
    'data.image': function (val) {
      this.blobImage(val);
    },
  },
  mounted() {
    if (this.data.image) {
      this.blobImage(this.data.image);
    }
  },
  methods: {
    blobImage(val) {
      const blob = this.dataURItoBlob(val);
      const file = new File([blob], 'image.png', { type: 'image/png' });
      this.images = file;
    },
    dataURItoBlob(dataURI) {
      const byteString = atob(dataURI.split(',')[1]);
      const mimeString = dataURI
        .split(',')[0]
        .split(':')[1]
        .split(';')[0];
      const ab = new ArrayBuffer(byteString.length);
      const ia = new Uint8Array(ab);
      for (let i = 0; i < byteString.length; i += 1) {
        ia[i] = byteString.charCodeAt(i);
      }
      return new Blob([ab], { type: mimeString });
    },
    popUpImage() {
      this.dialog = true;
    },
    close() {
      this.dialog = false;
    },
    upload_image(val) {
      if (val === null) {
        this.preview = null;
        this.images = null;
      } else {
        this.preview = URL.createObjectURL(this.images);
        this.data.image = this.preview;
      }
      this.$emit('update:value', this.images);
      this.$store
        .dispatch('daftarInputer/ap/uploadData', {
          data: this.images,
        })
        .then((res) => {
          if (res.code === 302) {
            this.error = res.data.message;
            this.$emit('errorFromImages', {
              status: false,
              message: res.data.message,
            });
          }
          if (res.code === 200) {
            this.$emit('errorFromImages', { status: true, message: '' });
            this.error = null;
          }
        });
    },
  },
};
</script>
