<template>
  <detail
    :title="merchantType + ' Detail'"
    :loading="loading"
    :subtitle="'Informasi detail mengenai ' + merchantType"
    :caption="caption"
    :actionButton="actionButton"
  >
    <template v-slot:content>
      <content-data-merchant
        :merchantType="merchantType"
        :data="data"
        :menu="'client'"
      />

      <content-data-doc
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
      />

      <content-data-endpoint
        v-if="data.qr_type === 'QR API'"
        :merchantType="'Merchant'"
        :data="data"
      />

      <content-data-qr :loading="loading" :data="data" :menu="'client'" />
    </template>
  </detail>
</template>

<script>
import Detail from '@/components/detail/Detail.vue';
import ContentDataQr from '@/components/detail/ContentDataQr.vue';
import ContentDataMerchant from '@/components/detail/ContentDataMerchant.vue';
import ContentDataDoc from '@/components/detail/ContentDataDoc.vue';
import ContentDataEndpoint from '@/components/detail/ContentDataEndpoint.vue';

export default {
  components: {
    Detail,
    ContentDataQr,
    ContentDataMerchant,
    ContentDataDoc,
    ContentDataEndpoint,
  },
  beforeMount() {
    this.getDetailData();
  },
  data() {
    return {
      actionButton: {
        buttonPending: false,
        buttonReject: false,
        buttonApprove: false,
      },
      loading: false,
    };
  },
  computed: {
    data() {
      return this.$store.state.client.merchant.merchant;
    },
    caption() {
      return this.data == null ? '' : this.data.jenis_pengajuan;
    },
    merchantTypeId() {
      if (this.data != null) {
        if (this.data.merchant_type_id != null) {
          return this.data.merchant_type_id;
        }
        return '1';
      }
      return '1';
    },
    merchantType() {
      // eslint-disable-next-line eqeqeq
      return this.merchantTypeId == '1' || this.merchantTypeId == '4'
        ? 'Merchant'
        : 'Sub Merchant';
    },
  },
  methods: {
    async getDetailData() {
      this.loading = true;

      await this.$store
        .dispatch('client/merchant/viewMerchant', {
          id: this.$route.params.id,
        })
        .then(() => {
          if (this.data == null) {
            // eslint-disable-next-line no-throw-literal
            throw 'data not found!';
          }
        })
        .catch(() => {
          this.$router.back();
        });

      this.loading = false;
    },
  },
};
</script>
