<template>
  <v-app :style="{ 'background-color': 'white' }" id="root">
    <div
      v-if="!this.$store.state.authenticated || forceChangePasswordStatus"
      class="login-container"
      id="login-container"
    >
      <v-alert
        v-if="this.$store.state.isExpired"
        dense
        border="left"
        type="warning"
      >
        Your session has <strong>expired</strong>
      </v-alert>
      <v-snackbar
        v-model="snackbar"
        :timeout="snackbarTimeout || 6000"
        absolute
        top
        right
        :color="snackbarColor"
      >
        <v-icon dark>{{ snackbarIcon }}</v-icon>
        <span>&nbsp;{{ snackbarMessage }}</span>
        <template v-slot:action="{ attrs }">
          <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
            Close
          </v-btn>
        </template>
      </v-snackbar>

      <v-main class="d-flex align-center flex-wrap">
        <v-row dense>
          <v-col
            class="d-flex align-center flex-wrap pa-0"
            color="grey lighten-2"
            cols="12"
            lg="6"
          >
            <v-img
              class="d-flex mb-auto white--text"
              src="./assets/background.svg"
            >
              <v-container>
                <v-row>
                  <v-col cols="3" class="ml-5">
                    <v-img
                      class="d-flex mb-auto white--text"
                      src="./assets/logo.png"
                    />
                  </v-col>
                </v-row>
              </v-container>
            </v-img>
          </v-col>
          <v-col
            class="d-flex align-center flex-wrap"
            color="grey lighten-2"
            cols="12"
            lg="6"
          >
            <forgot-password
              v-if="
                this.$route.name === 'ForgotPassword' ||
                this.$route.name === 'ValidateForgotPassword'
              "
            />
            <change-password
              v-else-if="
                this.$store.state.authenticated &&
                (this.$route.name === 'ChangePassword' ||
                  forceChangePasswordStatus)
              "
            />
            <login v-else />
          </v-col>
        </v-row>
      </v-main>
    </div>
    <div v-else>
      <v-navigation-drawer
        v-model="drawer"
        app
        color="primaryred"
        class="d-print-none"
        dark
      >
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title class="title" color="secondaryyred">
              <v-img :style="{ width: '60%' }" src="./assets/logo.png"> </v-img>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list dense nav>
          <v-subheader>Menu</v-subheader>
          <v-progress-linear
            v-if="isMenuLoading"
            :active="isMenuLoading"
            :indeterminate="isMenuLoading"
            color="secondaryyred"
            class="mb-2 mt-2"
          ></v-progress-linear>
          <v-list-item-group color="secondaryyred">
            <sub-menu
              v-for="(item, i) in menus"
              :key="i"
              :item="item"
              :root="root"
            ></sub-menu>
          </v-list-item-group>
        </v-list>
      </v-navigation-drawer>

      <v-app-bar app flat light class="d-print-none">
        <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>

        <v-toolbar-title>
          {{ $route.meta.appBarTitle }}
        </v-toolbar-title>

        <v-spacer></v-spacer>
        <v-menu offset-y rounded="lg" transition="slide-y-transition">
          <template v-slot:activator="{ on, attrs }">
            <div
              class="d-flex flex-row align-center"
              :style="{ cursor: 'pointer' }"
              v-bind="attrs"
              v-on="on"
            >
              <v-avatar size="30" :style="{ 'background-color': '#717171' }">
                <v-icon dark size="20"> mdi-account </v-icon>
              </v-avatar>
              <v-toolbar-title :style="{ 'font-size': '13px', margin: '10px' }">
                Hallo {{ user == null ? "" : user.username }}, Selamat datang !
              </v-toolbar-title>
              <v-icon light size="20">mdi-menu-down</v-icon>
            </div>
          </template>
          <v-list dense>
            <v-list-item link to="/profile">
              <v-list-item-icon>
                <v-icon>mdi-account-circle</v-icon>
              </v-list-item-icon>
              <v-list-item-title>Profile</v-list-item-title>
            </v-list-item>
            <v-list-item link to="/logout">
              <v-list-item-icon>
                <v-icon>mdi-logout</v-icon>
              </v-list-item-icon>
              <v-list-item-title>Logout</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-app-bar>

      <v-main>
        <v-overlay :value="logoutLoading">
          <v-progress-circular indeterminate size="44"></v-progress-circular>
        </v-overlay>
        <v-snackbar
          v-model="snackbar"
          :timeout="snackbarTimeout || 3000"
          absolute
          top
          right
          :color="snackbarColor"
        >
          <v-icon dark>{{ snackbarIcon }}</v-icon>
          <span>&nbsp;{{ snackbarMessage }}</span>
          <template v-slot:action="{ attrs }">
            <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
              Close
            </v-btn>
          </template>
        </v-snackbar>
        <div
          id="breadcrumb-container"
          class="pa-0 ml-4 mr-2"
          v-if="breadcrumbs.length > 0"
        >
          <v-breadcrumbs :items="breadcrumbs">
            <template v-slot:divider>
              <v-icon>mdi-chevron-right</v-icon>
            </template>
          </v-breadcrumbs>
        </div>
        <router-view
          :style="{
            'border-radius': '0',
            top: '0',
            left: '0',
            width: '100%',
            height: '100%',
            'background-color': 'white',
            'border-width': '0',
          }"
          id="view-container"
        ></router-view>
      </v-main>
      <v-expand-transition>
        <v-card
          v-show="absoluteBar"
          :style="barStyle"
          class="mx-auto pl-5 pr-5 pt-2 pb-2"
          :width="minimized ? '120px' : '350px'"
          elevation="9"
          @dragend="dropBar"
          draggable="true"
        >
          <v-card-text class="d-flex flex-row">
            <v-row style="width: 80%" class="justify-space-between">
              <h4 v-if="!minimized">{{ absoluteBarText }}</h4>
              <div style="align-items: ">
                <v-icon @click="minimized = true"> mdi-window-minimize </v-icon>
                <v-icon @click="minimized = false">
                  mdi-window-maximize
                </v-icon>
                <v-icon @click="absoluteBar = !absoluteBar">
                  mdi-close-circle
                </v-icon>
              </div>
            </v-row>
          </v-card-text>
        </v-card>
      </v-expand-transition>
    </div>
  </v-app>
</template>

<script>
import Login from './components/Login.vue';
import ForgotPassword from './components/ForgotPassword.vue';
import ChangePassword from './components/ChangePassword.vue';
import SubMenu from './components/SubMenu.vue';

export default {
  components: {
    Login,
    ForgotPassword,
    ChangePassword,
    SubMenu,
  },
  beforeMount() {
    if (this.$store.state.authenticated && this.menus.length === 0) {
      this.$store.dispatch('renderMenu');
    }
  },
  mounted() {
    try {
      // eslint-disable-next-line no-restricted-globals, no-throw-literal
      if (top.location.hostname !== self.location.hostname) throw 1;
    } catch (e) {
      document.querySelector('#html-root').remove();
    }
    if (localStorage.getItem('logged_in_qram') != null) {
      if (
        JSON.parse(localStorage.getItem('logged_in_qram')).user == null
        || JSON.parse(localStorage.getItem('logged_in_qram')).token == null
      ) {
        this.$store.dispatch('logout', null, { root: true });
      } else {
        this.$store.commit(
          'setUser',
          JSON.parse(localStorage.getItem('logged_in_qram')).user,
          { root: true },
        );
      }
    }
  },
  computed: {
    forceChangePasswordStatus() {
      const forceChangePassword = this.$store.state.user != null
        ? this.$store.state.user.force_password_change
        : false;
      return forceChangePassword;
    },
    menus() {
      return this.$store.state.menus;
    },
    isMenuLoading() {
      return this.$store.state.isMenuLoading;
    },
    logoutLoading() {
      return this.$store.state.logoutLoading;
    },
    snackbar: {
      get() {
        return this.$store.state.snackbar;
      },
      set(val) {
        this.$store.commit('setSnackbar', {
          status: val,
          color: '',
          message: '',
          icon: '',
        });
      },
    },
    snackbarColor() {
      return this.$store.state.snackbarColor;
    },
    snackbarMessage() {
      return this.$store.state.snackbarMessage;
    },
    snackbarIcon() {
      return this.$store.state.snackbarIcon;
    },
    snackbarTimeout() {
      return this.$store.state.snackbarTimeout;
    },
    absoluteBar: {
      get() {
        return this.$store.state.absoluteBar;
      },
      set(val) {
        this.$store.commit('setInsertProgressGlobal', {
          absoluteBar: val,
          absoluteBarText: '',
        });
      },
    },
    absoluteBarText() {
      return this.$store.state.absoluteBarText;
    },
    minimized: {
      get() {
        return this.$store.state.minimized;
      },
      set(val) {
        this.$store.commit('setMinimized', val);
      },
    },
    user() {
      return this.$store.state.user;
    },
    breadcrumbs() {
      return this.$store.state.breadcrumbs;
    },
  },
  data() {
    return {
      root: true,
      drawer: null,
      right: null,
      rightPos: 0,
      barStyle: {
        'z-index': '5',
        position: 'fixed',
        bottom: 0,
        right: 0,
        cursor: 'pointer',
      },
    };
  },
  methods: {
    dropBar(x) {
      this.barStyle.right = `${
        document.getElementById('view-container').offsetWidth - x.screenX
      }px`;
    },
  },
};
</script>
<style>
@import '/assets/css/global.css';
</style>
