<template>
  <div>
    <v-card
      class="mb-12"
      height="20px"
      :style="{ 'box-shadow': '0 0 black', 'margin-top': '20px' }"
    >
      <div class="d-flex pa-5 flex-column align-center justify-center">
        <h3>Info {{ merchantType }}</h3>
      </div>
    </v-card>
    <div :style="{ padding: '20px 50px 20px 50px' }">
      <data-detail-row
        v-if="showMenu == 1"
        :oldValueData="false"
        :data="data"
        :label="'MPAN'"
        :valueData="data.mpan"
      />
      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'Tipe ' + merchantType"
        :valueData="
          accountType == 'Corporate'
            ? 'Badan Usaha'
            : data.account_type.name
            ? data.account_type.name
            : data.account_type
        "
      />
      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'Jenis QR'"
        :valueData="data.qr_type.name || data.qr_type"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Nomor Rekening'"
        :valueData="data.account_number"
      />
      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'Nama Perusahaan'"
        :valueData="masking(data.company_name)"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Nama ' + merchantType"
        :valueData="data.merchant_name"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Nama Pemilik Usaha'"
        :valueData="data.owner_name"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Nomor KTP Pemilik Usaha'"
        :valueData="text(data.ktp_no)"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="
          accountType == 'Corporate'
            ? 'Nomor NPWP Perusahaan'
            : 'Nomor NPWP Pemilik Usaha'
        "
        :valueData="text(data.npwp_no)"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Email'"
        :valueData="data.email"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'No Handphone Pemilik Usaha'"
        :valueData="data.mobile_phone"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'No Handphone ' + merchantType"
        :valueData="text(data.merchant_mobile_phone)"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="
          accountType == 'Corporate'
            ? 'No Telepon Perusahaan'
            : 'No Telepon ' + merchantType
        "
        :valueData="merchantPhone == ' ' ? '( not set )' : merchantPhone"
      />
      <!-- beda data tracing dan client -->
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Kriteria ' + merchantType"
        :valueData="data.merchant_criteria.name || data.merchant_criteria"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Jenis Usaha'"
        :valueData="data.mcc_name || data.mcc.name"
      />
      <data-detail-row
        v-if="showMenu == 2 || showMenu == 3"
        :oldValueData="false"
        :data="data"
        :label="'Kode MCC'"
        :valueData="data.mcc_id || data.mcc.id"
      />
      <!-- ============================= -->
      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'MDR On-Us'"
        :valueData="data.mdr_on_us + '%'"
      />
      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'MDR Off-Us'"
        :valueData="data.mdr_off_us + '%'"
      />

      <draw-google-map :valueData="data.gmaps" :label="'Lokasi Alamat'" />

      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Alamat ' + merchantType"
        :valueData="data.address"
      />

      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Kota, Kecamatan, Kode Pos'"
        :valueData="data.area"
      />
      <select-search
        :oldValueData="false"
        :data="data"
        :items="itemsKlasifikasi"
        :label="'Klasifikasi Merchant'"
        :value="data.account_classification"
        :valueId="valueAccountClassification(data.account_classification)"
        @update:value="(newValue) => (data.account_classification = newValue)"
        @update:items="(newValue) => (itemsKlasifikasi = newValue)"
        :note="noteMessage"
      />
      <data-detail-row
        v-if="showMenu == 2"
        :oldValueData="false"
        :data="data"
        :label="'Referral ID'"
        :valueData="data.referal_code"
      />
    </div>
  </div>
</template>

<script>
import DataDetailRow from '@/components/detail/DataDetailRow.vue';
import DrawGoogleMap from '@/components/detail/DrawGoogleMap.vue';
import { text } from '@/lib/helpers';
import { maskingWord } from '@/store/params';
import SelectSearch from '@/components/detail/SelectSearchDetailRow.vue';

export default {
  props: {
    data: {
      type: Object,
    },
    merchantType: {
      type: String,
    },
    menu: {
      type: String,
    },
  },
  components: { DataDetailRow, DrawGoogleMap, SelectSearch },
  data() {
    return {
      center: { lat: -7.797068, lng: 110.370529, label: 'KENDAL' },
      dataKlasifikasi: [],
    };
  },
  watch: {
    'allData.list': function () {
      this.dataKlasifikasi = this.allData?.list
        .filter((obj) => obj.id !== 1)
        .map((obj) => ({ nama_klasifikasi: obj.nama_klasifikasi, id: obj.id }))
        .sort((a, b) => a.nama_klasifikasi.localeCompare(b.nama_klasifikasi));
    },
  },
  computed: {
    noteMessage() {
      const currentPath = window.location.pathname;
      if (this.data.status === 'New' && !currentPath.includes('tracking-pengajuan')) {
        return '*Klasifikasi merchant yang dapat dipilih maksimal adalah 10 data, lakukan un-checklist apabila melakukan pergantian data.';
      }
      return '*Klasifikasi Merchant berwarna merah adalah Merchant tidak aktif, berwarna biru menunjukkan Merchant Klasifikasi aktif';
    },
    allData() {
      return this.$store.state.klasifikasi.items;
    },
    itemsKlasifikasi: {
      get() {
        return this.dataKlasifikasi;
      },
      set(newVal) {
        this.dataKlasifikasi = newVal;
      },
    },
    showMenu() {
      let show;
      switch (this.menu) {
        case 'client':
          show = 1;
          break;
        case 'tracking':
          show = 2;
          break;
        case 'approval':
          show = 3;
          break;
        default:
          show = 0;
      }

      return show;
    },
    accountType() {
      return this.data != null ? this.data.account_type : null;
    },
    merchantPhone() {
      const area = this.data.phone_area_code !== '' && this.data.phone_area_code != null
        ? `(${this.data.phone_area_code})`
        : '';
      const phone = this.data.phone != null ? this.data.phone : '';
      return `${area} ${phone}`;
    },
  },
  beforeMount() {
    const search = {
      page: 1,
      limit: 100,
      search: {
        created_date_start: '',
        created_date_end: '',
        name: '',
        status: 'ACTIVE',
      },
    };
    this.$store
      .dispatch('klasifikasi/fetchData', {
        search,
      });
  },
  methods: {
    valueAccountClassification(val) {
      if (typeof val[0] === 'object') {
        const resultArray = val.map((item) => parseInt(item.id, 10));
        return resultArray;
      }
      return val;
    },
    masking(val) {
      return maskingWord(val, 3);
    },
    text,
  },
};
</script>
