<template>
  <div class="d-flex flex-column align-center justify-center">
    <div
      class="d-flex flex-column align-center justify-center"
      style="height:500px"
    >
      <v-card width="350px" outlined elevation="7" rounded>
        <div class="triangle-down"></div>

        <v-card-title class="d-flex flex-column align-center justify-center">
          <span class="headline" style="z-index:1; color:white"
            >My Profile</span
          >
          <v-avatar color="primaryred" size="100" class="mt-4">
            <v-icon dark size="60">
              mdi-account
            </v-icon>
          </v-avatar>
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <table aria-describedby="detail-user" style="width: 100%">
                  <th />
                  <tr>
                    <td class="td1">Full Name</td>
                    <td class="td2">:</td>
                    <td class="td3">
                      {{ user != null ? user.fullname : '' }}
                    </td>
                  </tr>
                  <tr>
                    <td class="td1">Username</td>
                    <td class="td2">:</td>
                    <td class="td3">
                      {{ user != null ? user.username : '' }}
                    </td>
                  </tr>
                  <tr>
                    <td class="td1">Email</td>
                    <td class="td2">:</td>
                    <td class="td3">
                      {{ user != null ? user.email : '' }}
                    </td>
                  </tr>
                  <tr>
                    <td class="td1">Role</td>
                    <td class="td2">:</td>
                    <td class="td3">
                      {{ user != null ? user.role : '' }}
                    </td>
                  </tr>
                  <tr>
                    <td class="td1">Last Login</td>
                    <td class="td2">:</td>
                    <td class="td3">
                      {{
                        user != null
                          ? user.last_login_at != undefined
                            ? user.last_login_at
                            : ''
                          : ''
                      }}
                    </td>
                  </tr>
                </table>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    user() {
      return this.$store.state.user;
    }
  }
};
</script>
<style scoped>
.td1 {
  width: 35%;
  text-align: left;
  vertical-align: top;
}
.td2 {
  width: 15%;
  text-align: center;
  vertical-align: top;
}
.td3 {
  width: 50%;
  text-align: left;
  vertical-align: top;
}
.triangle-down {
  position: absolute;
  /* background-color: grey; */
  width: 0px;
  height: 0px;
  border-left: 174px solid transparent;
  border-right: 174px solid transparent;
  border-top: 125px solid grey;
}
</style>
