<template>
  <v-card style="min-height: 80vh;" class="py-5 px-10">
    <h1 class="text-center primaryred--text">
      Syarat dan Ketentuan Menjadi JakOne Merchant
    </h1>
    <v-card-text>
      <v-scroll-x-transition>
        <v-card class="mb-3" :style="{ height: boxHeight }">
          <v-card-text style="height: 100%;overflow: auto;">
            <div
              class="text--primary"
              v-html="terms_conditions.terms_conditions"
            ></div>
          </v-card-text>
        </v-card>
      </v-scroll-x-transition>
      <v-form ref="form" @submit.prevent="next">
        <v-checkbox
          v-model="data.agree"
          @change="$emit('update:value', data.agree)"
          :rules="[(v) => !!v || 'This field is required']"
          label="Calon Merchant dengan ini menyatakan telah membaca, memahami dan menyetujui 
  seluruh Syarat dan Ketentuan "
        ></v-checkbox>
        <Button :buttonClick="buttonClick" v-on:backParent="backParent" />
      </v-form>
    </v-card-text>
  </v-card>
</template>

<script>
import Button from "./ButtonClick.vue";

export default {
  props: {
    data: {
      type: Object,
    },
  },
  mounted() {
    this.setBoxHeight();
    window.addEventListener("resize", this.setBoxHeight);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.setBoxHeight);
  },

  components: {
    Button,
  },
  data() {
    return {
      buttonClick: {
        buttonBack: true,
        buttonLanjut: true,
      },
      boxHeight: "300px",
    };
  },
  computed: {
    terms_conditions() {
      return this.$store.state.client.addMerchant.syaratKetentuan;
    },
    loading() {
      return this.$store.state.client.addMerchant.ap.loading;
    },
  },
  methods: {
    backParent() {
      this.$emit("backParent");
    },
    setBoxHeight() {
      this.boxHeight = window.innerHeight - 200 + "px";
    },
    next() {
      if (this.$refs.form.validate()) {
        this.$emit("nextParent");
      }
    },
  },
};
</script>
