<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div>
    <v-dialog v-model="dialog" width="500" persistent>
      <v-card>
        <v-card-title class="primaryred--text">
          {{ formTitle }}
        </v-card-title>
        <v-card-text>
          <v-form>
            <v-container>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.mcc"
                    label="Kode MCC"
                    :error-messages="mccCodeErrors"
                    @change="$v.editedItem.mcc.$touch()"
                    @blur="$v.editedItem.mcc.$touch()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.description"
                    label="Deskripsi MCC"
                    :error-messages="descriptionErrors"
                    @change="$v.editedItem.description.$touch()"
                    @blur="$v.editedItem.description.$touch()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-select
                    v-model="editedItem.category_id"
                    :error-messages="mccCategoryErrors"
                    @change="$v.editedItem.category_id.$touch()"
                    @blur="$v.editedItem.category_id.$touch()"
                    :items="allCategory"
                    label="Category"
                    item-text="text"
                    item-value="value"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.mcc_name"
                    :error-messages="jenisUsahaErrors"
                    @change="$v.editedItem.mcc_name.$touch()"
                    @blur="$v.editedItem.mcc_name.$touch()"
                    label="Jenis Usaha"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.mdr_on_us"
                    :error-messages="mdrOnUsErrors"
                    @change="$v.editedItem.mdr_on_us.$touch()"
                    @blur="$v.editedItem.mdr_on_us.$touch()"
                    label="MDR On Us"
                    suffix="%"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.mdr_off_us"
                    :error-messages="mdrOffUsErrors"
                    @change="$v.editedItem.mdr_off_us.$touch()"
                    @blur="$v.editedItem.mdr_off_us.$touch()"
                    label="MDR Off Us"
                    suffix="%"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-select
                    v-model="editedItem.status"
                    :error-messages="statusErrors"
                    @change="$v.editedItem.status.$touch()"
                    @blur="$v.editedItem.status.$touch()"
                    :items="listMccMdrStatus"
                    label="Status"
                    item-text="text"
                    item-value="value_number"
                  ></v-select>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primaryred" outlined @click="cancelDialog">
            Cancel
          </v-btn>
          <v-btn
            color="primary"
            outlined
            @click="save"
            :disabled="loading"
            :loading="loading"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { listMccMdrStatus, categoryMccMdr } from '@/store/params';
import { validationMixin } from 'vuelidate';
import {
  required,
  maxLength,
  helpers,
  numeric,
} from 'vuelidate/lib/validators';

const numericDecimal = helpers.regex(
  'numeric',
  /^100$|^(0|[1-9]\d)?(\.\d{1,2})?$/,
);

export default {
  props: ['loading', 'editedItem', 'dialog', 'editedIndex'],
  mixins: [validationMixin],
  validations: {
    editedItem: {
      mcc: {
        isUnique() {
          return (
            this.errorValidation == null
            || this.errorValidation.mcc == null
            || this.errorValidation.mcc.length < 1
          );
        },

        required,
        numeric,
        maxLength: maxLength(4),
      },
      description: { required },
      category_id: { required },
      mcc_name: { required },
      mdr_on_us: {
        required,
        numericDecimal,
        minVal() {
          return this.editedItem.mdr_on_us >= process.env.VUE_APP_MIN_MDR;
        },
        maxVal() {
          return this.editedItem.mdr_on_us <= process.env.VUE_APP_MAX_MDR;
        },
      },
      mdr_off_us: {
        required,
        numericDecimal,
        minVal() {
          return this.editedItem.mdr_off_us >= process.env.VUE_APP_MIN_MDR;
        },
        maxVal() {
          return this.editedItem.mdr_off_us <= process.env.VUE_APP_MAX_MDR;
        },
      },
      status: { required },
    },
  },
  data: () => ({
    listMccMdrStatus,
    allCategory: categoryMccMdr,
  }),
  computed: {
    formTitle() {
      return this.editedIndex !== -1 ? 'Edit MCC & MDR' : 'Add MCC & MDR';
    },
    flashError: {
      get() {
        return this.$store.state.settingFee.mccmdr.flashError;
      },
      set() {
        this.$store.commit('settingFee/mccmdr/setFlashErrorMessage', '');
      },
    },
    errorValidation() {
      return this.$store.state.settingFee.mccmdr.errors;
    },
    mccCodeErrors() {
      const errors = [];
      if (!this.$v.editedItem.mcc.$dirty) return errors;
      if (!this.$v.editedItem.mcc.required) {
        errors.push('Kode MCC is required');
      } else if (!this.$v.editedItem.mcc.numeric) {
        errors.push('Kode MCC must be numeric only');
      } else if (!this.$v.editedItem.mcc.maxLength) {
        errors.push('Kode MCC must be less than 5 digits');
      }

      if (!this.$v.editedItem.mcc.isUnique) {
        if (this.errorValidation.mcc != null) {
          this.errorValidation.mcc.forEach((item) => {
            errors.push(item);
          });
        }
      }
      return errors;
    },
    descriptionErrors() {
      const errors = [];
      if (!this.$v.editedItem.description.$dirty) return errors;
      if (!this.$v.editedItem.description.required) {
        errors.push('Deskripsi is required');
      }
      return errors;
    },
    mccCategoryErrors() {
      const errors = [];
      if (!this.$v.editedItem.category_id.$dirty) return errors;
      if (!this.$v.editedItem.category_id.required) {
        errors.push('Kategori MCC is required');
      }
      return errors;
    },
    jenisUsahaErrors() {
      const errors = [];
      if (!this.$v.editedItem.mcc_name.$dirty) return errors;
      if (!this.$v.editedItem.mcc_name.required) {
        errors.push('Jenis Usaha is required');
      }
      return errors;
    },
    mdrOnUsErrors() {
      const errors = [];
      if (!this.$v.editedItem.mdr_on_us.$dirty) return errors;
      if (!this.$v.editedItem.mdr_on_us.minVal) {
        errors.push(`Minimal value is ${process.env.VUE_APP_MIN_MDR}!`);
      } else if (!this.$v.editedItem.mdr_on_us.maxVal) {
        errors.push(`Maximal value is ${process.env.VUE_APP_MAX_MDR}!`);
      } else if (!this.$v.editedItem.mdr_on_us.required) {
        errors.push('MDR On Us is required');
      } else if (!this.$v.editedItem.mdr_on_us.numericDecimal) {
        errors.push('MDR On Us must be numeric');
      }
      return errors;
    },
    mdrOffUsErrors() {
      const errors = [];
      if (!this.$v.editedItem.mdr_off_us.$dirty) return errors;
      if (!this.$v.editedItem.mdr_off_us.minVal) {
        errors.push(`Minimal value is ${process.env.VUE_APP_MIN_MDR}!`);
      } else if (!this.$v.editedItem.mdr_off_us.maxVal) {
        errors.push(`Maximal value is ${process.env.VUE_APP_MAX_MDR}!`);
      } else if (!this.$v.editedItem.mdr_off_us.required) {
        errors.push('MDR Off Us is required');
      } else if (!this.$v.editedItem.mdr_off_us.numericDecimal) {
        errors.push('MDR Off Us must be numeric');
      }

      return errors;
    },
    statusErrors() {
      const errors = [];
      if (!this.$v.editedItem.status.$dirty) return errors;
      if (!this.$v.editedItem.status.required) {
        errors.push('Status is required');
      }
      return errors;
    },
  },
  watch: {
    dialog(val) {
      if (!val) this.cancelDialog();
    },

    'editedItem.mcc': {
      handler(val) {
        if (this.errorValidation != null) {
          if (
            this.errorValidation.mcc != null
            && val !== this.errorValidation.mcc
          ) {
            this.$store.commit('settingFee/mccmdr/clearErrorVariable', 'mcc');
          }
        }
      },
    },
  },
  methods: {
    cancelDialog() {
      this.resetUniqueValidation();
      this.$emit('closeCancelParent');
    },
    resetUniqueValidation() {
      this.$store.commit('settingFee/mccmdr/clearErrors');
    },
    async save() {
      this.$store.commit('settingFee/mccmdr/setFlashErrorMessage', '');
      this.resetUniqueValidation();
      this.$v.$touch();
      if (!this.$v.$invalid) {
        const data = this.editedItem;
        if (this.editedIndex > -1) {
          await this.$store
            .dispatch('settingFee/mccmdr/updateData', {
              body: data,
              index: this.editedIndex,
            })
            .then((status) => {
              if (status) {
                this.cancelDialog();
                this.showAlert(
                  'Update MDR & MCC success !',
                  'mdi-check-circle',
                  'green',
                );
              } else {
                this.showAlert(
                  'Error on update MDR & MCC !',
                  'mdi-alert',
                  'red',
                );
              }
            });
        } else {
          await this.$store
            .dispatch('settingFee/mccmdr/createData', {
              value: this.editedItem,
            })
            .then((status) => {
              if (status) {
                this.cancelDialog();
                this.showAlert(
                  'Add MDR & MCC success !',
                  'mdi-check-circle',
                  'green',
                );
              } else {
                this.showAlert(
                  'Error on create MDR & MCC !',
                  'mdi-alert',
                  'red',
                );
              }
            });
        }
      }
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
