import Parent from "../views/Parent.vue";

// import view RBAC
import Role from "../views/rbac/Role.vue";
import RoleDetail from "../views/rbac/RoleDetail.vue";

import { setBreadcrumb } from "./index.js";

export const rbacRoute = {
    path: "/rbac",
    meta: {
        title: "JAKONE - RBAC",
        appBarTitle: "RBAC",
    },
    component: Parent,
    children: [
        {
            path: "/rbac-role",
            name: "Role",
            meta: {
                title: "JAKONE - Role",
                appBarTitle: "Role",
                breadcrumbs: () => {
                    return [
                        setBreadcrumb('user management', '/rbac-role', true),
                        setBreadcrumb('user role', '/rbac-role'),
                    ]
                }
            },
            component: Role,
        },
        {
            path: "/rbac-role/:id/view",
            meta: {
                title: "JAKONE - View Detail Role",
                appBarTitle: "View Detail Role",
                breadcrumbs: (params) => {
                    const id = params.id
                    return [
                        setBreadcrumb('user management', '/rbac-role', true),
                        setBreadcrumb('user role', '/rbac-role'),
                        setBreadcrumb(id.replaceAll('-',' '), `/rbac-role/${id}/view`, true),
                    ]
                }
            },
            component: RoleDetail,
            name: "RoleDetail",
        },
    ]
}