<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div>
    <v-dialog v-model="dialog" width="500" persistent>
      <v-card>
        <v-card-title class="primaryred--text">
          {{ formTitle }}
        </v-card-title>
        <v-card-text>
          <v-form>
            <v-container>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    v-model="editedItem.role"
                    label="Role Name"
                    :error-messages="roleErrors"
                    @change="$v.editedItem.role.$touch()"
                    @blur="$v.editedItem.role.$touch()"
                  ></v-text-field>
                </v-col>
                <v-col v-if="editedIndex === -1" cols="12" sm="12" md="12">
                  <v-text-field
                    v-model="editedItem.description"
                    label="Description"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primaryred" outlined @click="cancelDialog">
            Cancel
          </v-btn>
          <v-btn
            color="primary"
            outlined
            @click="save"
            :disabled="loading"
            :loading="loading"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import { required } from 'vuelidate/lib/validators';

export default {
  props: ['loading', 'editedItem', 'dialog', 'editedIndex'],
  mixins: [validationMixin],
  validations: {
    editedItem: {
      role: {
        required,
        isUnique() {
          if (this.errorValidation != null) {
            if (
              this.errorValidation.role == null
              || this.errorValidation.role.length < 1
            ) {
              return true;
            }
          } return false;
        },
      },
    },
  },
  data: () => ({}),
  computed: {
    formTitle() {
      return this.editedIndex !== -1 ? 'Edit Role' : 'Add Role';
    },
    flashError: {
      get() {
        return this.$store.state.rbac.role.flashError;
      },
      set() {
        this.$store.commit('rbac/role/setFlashErrorMessage', '');
      },
    },
    errorValidation() {
      return this.$store.state.rbac.role.errors;
    },
    roleErrors() {
      const errors = [];
      if (!this.$v.editedItem.role.$dirty) {
        return errors;
      }
      if (!this.$v.editedItem.role.required) errors.push('Rolename is required.');
      if (!this.$v.editedItem.role.isUnique) {
        if (this.errorValidation != null && this.errorValidation.role != null) {
          this.errorValidation.role.forEach((item) => {
            errors.push(item);
          });
        }
      }
      return errors;
    },
  },
  watch: {
    dialog(val) {
      if (!val) this.cancelDialog();
    },
    'editedItem.role': {
      handler(val) {
        if (this.errorValidation != null) {
          if (
            this.errorValidation.role != null
            && val !== this.errorValidation.role
          ) {
            this.$store.commit('rbac/role/clearErrorVariable', 'role');
          }
        }
      },
    },
  },
  methods: {
    cancelDialog() {
      this.resetUniqueValidation();
      this.$emit('closeCancelParent');
    },
    resetUniqueValidation() {
      this.$store.commit('rbac/role/clearErrors');
    },
    async save() {
      this.$store.commit('rbac/role/setFlashErrorMessage', '');
      this.resetUniqueValidation();
      this.$v.$touch();
      if (!this.$v.$invalid) {
        if (this.editedIndex > -1) {
          await this.$store
            .dispatch('rbac/role/updateRole', {
              nameRole: this.editedItem.role,
              description: this.editedItem.description,
              index: this.editedIndex,
            })
            .then((status) => {
              if (status) {
                this.cancelDialog();
                this.showAlert(
                  'Update role success !',
                  'mdi-check-circle',
                  'green',
                );
              } else {
                this.showAlert('Error on update role !', 'mdi-alert', 'red');
              }
            });
        } else {
          await this.$store
            .dispatch('rbac/role/createRole', {
              nameRole: this.editedItem.role,
              description: this.editedItem.description,
            })
            .then((status) => {
              if (status) {
                this.cancelDialog();
                this.showAlert(
                  'Add role success !',
                  'mdi-check-circle',
                  'green',
                );
              } else {
                this.showAlert('Error on create role !', 'mdi-alert', 'red');
              }
            });
        }
      }
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
