<template>
  <v-form ref="bulkUpload" class="d-flex" style="min-height: 550px" @submit.prevent="submitUpload">
    <v-container class="my-auto">
      <v-row class="flex-column" justify="center" align="center">
        <h2 class="ma-2 mb-10 text-center">Bulk Upload - Add</h2>
        <v-row
          class="flex-column my-4"
          justify="center"
          align="center"
          style="min-width: 85%"
        >
          <v-row align="center" class="ma-1" style="width: 60%">
            <file-input-bulk
              label="Data Merchant (.xlsx)"
              placeholder="Select your files"
              :rule="[
                (v) => !!v || 'File Merchant is required for upload',
                (v) =>
                  (v &&
                    v.type ===
                      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') ||
                  'Format File salah, File harus berformat .xlsx',
              ]"
              v-model="fileXLSX"
            />
          </v-row>
          <button-upload v-on:downloadTemplate="downloadTemplate" />
        </v-row>
        <note-upload />
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import NoteUpload from './NoteUpload.vue';
import ButtonUpload from './ButtonUpload.vue';
import FileInputBulk from './FileInputBulk.vue';

export default {
  components: { NoteUpload, ButtonUpload, FileInputBulk },
  data() {
    return {
      fileXLSX: null,
    };
  },
  methods: {
    downloadTemplate() {
      this.$store.dispatch('bulkUpload/downloadTemplate');
    },
    async submitUpload() {
      if (!this.$refs.bulkUpload.validate()) {
        return;
      }
      try {
        this.$store.dispatch('bulkUpload/uploadBulk', { file: this.fileXLSX }).then((res) => {
          if (res) {
            this.$emit('tabMove', res);
          }
        });
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

<style>
.w-100 {
  width: 100%;
}
</style>
