export const required = (value, label) => !!value || `${label || 'This field'} is required.`;
export const minLength = (value, min) => (value && value.length >= min) || `Min ${min} characters`;
export const maxLength = (value, max) => (value && value.length <= max) || `Max ${max} characters`;
export const maxLengthPhone = (value, { label, max }) => (value && value.length <= max && /^\d{1,15}$/.test(value)) || `${label || 'This field'} Maximum ${max} digit only and only Numbers.`;
export const pengkinianData = (value) => !!value || 'Mohon lakukan pengkinian data rekening anda ke Bank DKI cabang terdekat';
export const imageOnly = (value) => console.log(value.type.startsWith('image/'));
export const sizeImage = (value, max) => value.size <= max * 1000000 || `Foto Lokasi Usaha ukuran file Max ${max}mb`;
