<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <v-alert
      v-model="flashError"
      dense
      border="left"
      type="error"
      dismissible
      elevation="2"
    >
      {{ flashErrorMessage }}
    </v-alert>

    <table-list
      :headers="headers"
      :allData="allData"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :loading="loading"
      @update:options="updateOptions"
    >
      <template v-slot:filter>
        <filter-list
          :search="search"
          v-on:clearSearchParent="clearSearch"
          v-on:showDataParent="showData"
          :loading="loading"
          @update-prop="updatePropValue"
          :selectAllData="selectAllData"
          v-on:selectAllParent="selectAll"
        />
      </template>

      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
          v-on:saveParent="save"
          v-on:clearSelectedParent="clearSelected"
          :selectedData="selectedData"
        />
      </template>

      <template v-slot:item="{ item, index }">
        <data-list
          :item="item"
          :index="index"
          :options="options"
          v-on:viewItemParent="viewItem"
          :selectedData="selectedData"
          :selectedRow="selectedRow"
          v-on:selectRowParent="selectRow"
          v-on:changeSelectedDataParent="changeSelectedData"
        />
      </template>

      <template v-slot:nodata>
        <v-btn outlined dark color="primaryred" @click="clearSearch">
          Reset
        </v-btn>
      </template>
    </table-list>

    <confirm-dialog
      :loading="loading"
      :dialog="dialogApprove"
      :title="'Apakah anda yakin ingin melanjutkan proses ?'"
      v-on:closeCancelParent="close"
      v-on:confirmParent="confirm"
    />
  </v-card>
</template>

<script>
import FilterList from '@/components/table/FilterApprovalList.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import TableList from '@/components/table/TableList.vue';
import DataList from '@/components/table/DataApprovalList.vue';
import ConfirmDialog from '@/components/dialog/ConfirmDialog.vue';
import { maxSelectedApproval } from '@/store/params';

export default {
  components: {
    FilterList,
    ActionButton,
    TableList,
    DataList,
    ConfirmDialog,
  },
  data: () => ({
    selectedRow: '',
    listButton: {
      buttonRestart: true,
      buttonDots: false,
      buttonCount: true,
      buttonCustom: {
        label: 'Bulk Approve',
      },
    },
    search: {
      account_type: '',
      merchant_name: '',
      mid: '',
      address: '',
      reference_number: '',
      created_date: null,
      task_type: '',
      status: '',
      last_update: null,
      account_classification: '',
      qr_type: '',
    },
    searchDefault: {
      merchant_name: '',
      mid: '',
      account_type: '',
      address: '',
      reference_number: '',
      created_date: null,
      task_type: '',
      status: '',
      last_update: null,
      account_classification: '',
      qr_type: '',
    },
    searchFix: {
      merchant_name: '',
      mid: '',
      account_type: '',
      address: '',
      reference_number: '',
      created_date: null,
      task_type: '',
      status: '',
      last_update: null,
      account_classification: '',
      qr_type: '',
    },
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'id',
        sortable: false,
        width: '1%',
      },
      {
        text: '#',
        align: 'center',
        value: '#',
        sortable: false,
        width: '1%',
      },
      {
        text: 'Nama',
        value: 'merchant_name',
        align: 'center',
        sortable: false,
        width: '15%',
      },
      {
        text: 'Type Merchant',
        value: 'merchant_type',
        align: 'center',
        sortable: false,
        width: '15%',
      },
      {
        text: 'Jenis QR',
        value: 'qr_type',
        width: '10%',
        sortable: false,
      },
      {
        text: 'Alamat Lengkap',
        value: 'address',
        width: '15%',
        align: 'start',
        sortable: false,
      },
      {
        text: 'Klasifikasi Merchant',
        value: 'account_classification',
        width: '10%',
        align: 'start',
        sortable: false,
      },
      {
        text: 'Nomor Pengajuan',
        value: 'reference_number',
        width: '10%',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Submit Date',
        value: 'created_at',
        width: '10%',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Jenis Pengajuan',
        value: 'task_type',
        width: '8%',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Status',
        value: 'status',
        width: '5%',
        align: 'center',
        sortable: false,
      },
      { text: 'Actions', value: 'actions', sortable: false },
    ],
    editedItem: {
      merchant_name: '',
    },
    defaultItem: {
      merchant_name: '',
    },
    editedIndex: -1,
    page: '1',
    options: {},
    dialog: false,
    dialogApprove: false,
    isFirstLoad: false,
    selectedData: [],
    selectAllData: false,
    maxSelected: maxSelectedApproval,
  }),
  beforeMount() {
    this.$store.commit('daftarTugas/ap/clearMerchants');
  },
  computed: {
    flashErrorMessage() {
      return this.$store.state.daftarTugas.ap.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.daftarTugas.ap.flashError;
      },
      set() {
        this.$store.commit('daftarTugas/ap/setFlashErrorMessage', '');
      },
    },
    loading() {
      return this.$store.state.daftarTugas.ap.loading;
    },
    allData() {
      return this.$store.state.daftarTugas.ap.allMerchants;
    },
    totalData() {
      return this.$store.state.daftarTugas.ap.totalMerchants;
    },
    totalPage() {
      return this.$store.state.daftarTugas.ap.totalPage;
    },
  },
  watch: {
    selectedData: {
      handler() {
        if (this.selectedData.length > this.maxSelected) {
          this.showAlert(
            `Limit selected data is ${this.maxSelected} !`,
            'mdi-alert',
            'red',
          );
          this.selectedData.splice(
            this.maxSelected,
            this.selectedData.length - 1,
          );
        }
        this.checkAllSelected();
      },
    },
  },
  methods: {
    updatePropValue() {
      this.search.merchant_name = '';
    },
    selectRow(index) {
      if (this.selectedRow !== index) {
        this.selectedRow = index;
      } else {
        this.selectedRow = '';
      }
    },
    clearSearch() {
      this.search = { ...this.searchDefault };
      this.$nextTick(() => {
        this.$store.commit('daftarTugas/ap/clearMerchants');
        this.showData();
      });
    },

    showData() {
      this.searchFix = JSON.parse(JSON.stringify(this.search));
      this.getData();
    },

    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },

    getData() {
      this.$store.commit('daftarTugas/ap/setFlashErrorMessage', '');
      const {
        page, itemsPerPage, sortBy, sortDesc,
      } = this.options;

      this.$store
        .dispatch('daftarTugas/ap/getData', {
          page,
          itemsPerPage,
          sortBy,
          sortDesc,
          search: this.searchFix,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        })
        .finally(() => {
          this.checkAllSelected();
        });
    },

    resetUniqueValidation() {
      this.$store.commit('daftarTugas/ap/clearErrors');
    },

    close() {
      this.resetUniqueValidation();
      this.dialog = false;
      this.dialogApprove = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },

    async save() {
      if (this.selectedData.length < 1) {
        this.dialogApprove = false;
        this.showAlert('Data must be selected!', 'mdi-alert', 'red');
      } else {
        this.dialogApprove = true;
      }
    },

    async confirm() {
      this.$store
        .dispatch('daftarTugas/ap/bulkApprove', {
          id: this.selectedData,
        })
        .then((res) => {
          if (res !== false) {
            this.clearSelected();
            this.getData();
            this.showAlert('Approve success !', 'mdi-check-circle', 'green');
          }
        })
        .finally(() => {
          this.dialogApprove = false;
        });
    },

    viewItem(item) {
      this.$router.push(`/daftar-approval/view/${item.id}`);
    },

    // ============================================ function bulk update
    clearSelected() {
      this.selectedData = [];
      this.selectAllData = false;
    },

    changeSelectedData(val) {
      this.selectedData = val;
    },

    checkAllSelected() {
      const allIds = this.allData.map((val) => val.id);
      const allSelected = allIds.filter(
        (val) => !this.selectedData.includes(val),
      );
      if (
        allSelected.length === 0
        && this.selectedData.length > 0
        && this.allData.length > 0
      ) {
        this.selectAllData = true;
      } else {
        this.selectAllData = false;
      }
    },
    selectAll(status) {
      this.$nextTick(() => {
        const allIds = this.allData.map((val) => val.id);
        const filtered = allIds.filter(
          (val) => !this.selectedData.includes(val),
        );
        if (status) {
          this.selectedData = this.selectedData.concat(filtered);
        } else {
          this.selectedData = this.selectedData.filter(
            (val) => !allIds.includes(val),
          );
        }
      });
    },
    // ============================================ end of function bulk update

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
