<template>
  <v-card style="min-height: 80vh" class="py-5 px-10">
    <div class="d-flex flex-column justify-center" style="height: 100%">
      <h1 class="text-center primaryred--text">Merchant Registration</h1>
      <v-form ref="form" @submit.prevent="next">
        <v-row justify="center">
          <v-col cols="12">
            <v-row>
              <v-col cols="12" class="progress-step center">
                <ol class="d-flex justify-center">
                  <li
                    :class="step == 1 ? 'progress-active' : 'progress-inactive'"
                  >
                    <span class="label">
                      <h5>Info Oultet</h5>
                    </span>
                  </li>
                  <li
                    :class="step != 1 ? 'progress-active' : 'progress-inactive'"
                  >
                    <span class="bar"></span>
                    <span class="label">
                      <h5>Konfirmasi</h5>
                    </span>
                  </li>
                </ol>
              </v-col>
            </v-row>
          </v-col>
          <v-col cols="12">
            <RegisterMerchantEdit
              v-if="step == 1"
              v-on:nextParent="nextSubmit"
              :merchantType="'Merchant'"
              :data="dataRekening"
              :dataBaru="dataBaru"
              :firstData="data"
              :isEmailVerif="isEmailVerif"
              :menu="'approval'"
              @setLoading="setLoading"
              v-on:backParent="back"
            />
            <OverviewMerchant
              v-if="step == 2"
              v-on:backParent="prevStep"
              v-on:submitParent="submit"
              :menu="'approval'"
              :data="dataOverview"
              :merchantType="'Merchant'"
            />
          </v-col>
        </v-row>
      </v-form>
    </div>
  </v-card>
</template>

<script>
import RegisterMerchantEdit from '@/components/detail/RegisterMerchantEdit.vue';
import OverviewMerchant from '@/components/detail/OverviewMerchantEdit.vue';

export default {
  components: {
    RegisterMerchantEdit,
    OverviewMerchant,
  },
  props: {
    data: {
      type: Object,
    },
  },
  data() {
    return {
      isEmailVerif: false,
      dataOverview: null,
      idPendaftaran: null,
      dataUser: {
        merchant_name: '',
        merchant_criteria: null,
        area: null,
        account_number: '',
        company_name: '',
        ktp_no: '',
        npwp_no: '',
        email: '',
        mobile_phone: '',
        merchant_mobile_phone: '',
        phone: '',
        owner_name: '',
        mcc: null,
        mdr_on_us: '',
        mdr_off_us: '',
        address: '',
        document: null,
        qr_type: null,
        gmaps: {
          lat: 0,
          lng: 0,
        },
        ip_address: '',
        endpoint: '',
      },
      merchantName: '',
      merchantType: '',
      email: '',
      errors: {},
      step: 1,
    };
  },
  computed: {
    dataRekening: {
      get() {
        return this.$store.state.client.addMerchant.dataOnboarding;
      },
      set(newVal) {
        this.$store.commit('client/addMerchant/setDataOnboarding', newVal);
      },
    },
    requiredRule() {
      return (value) => !!value || 'This field is required';
    },
    emailRule() {
      return (value) => /.+@.+\..+/.test(value) || 'Please enter a valid email address';
    },
    numberRule() {
      return (value) => /^\d+$/.test(value) || 'Please enter a valid number';
    },
  },

  methods: {
    dataBaru(val) {
      this.data = val;
    },
    setLoading(val) {
      this.$emit('setLoading', val);
    },
    nextStep() {
      if (this.$refs.form.validate()) {
        // Validation passes, move to next step
        this.step += 1;
      }
    },
    prevStep(data) {
      console.log('prev', data);
      this.dataRekening = {
        ...data,
        image_lokasi: data.document,
        version: '',
      };
      this.isEmailVerif = true;
      if (this.step !== 1) {
        this.step -= 1;
      }
    },

    back() {
      this.$emit('backParent');
    },
    nextSubmit(val) {
      this.idPendaftaran = val;
      this.$store
        .dispatch('client/addMerchant/OverviewMerchant', {
          id: val,
        })
        .then((response) => {
          this.step += 1;
          this.dataOverview = response.data;
        });
    },
    submit() {
      this.$store
        .dispatch('client/addMerchant/ConfirmMerchant', {
          id: this.idPendaftaran,
        })
        .then((response) => {
          this.$emit('submitParent', response);
        });
    },
  },
};
</script>

<style>
body {
  counter-reset: list-counter;
}
/* ADD MERCHANT */
.progress-step > ol > li.progress-active:before {
  background: #990000;
  color: white;
}
.progress-step > ol > li:before {
  counter-increment: list-counter;
  content: counter(list-counter);
  width: 65px;
  height: 65px;
  background: #938686;
  font-size: 33px;
  line-height: 1.75;
  color: #fff;
  border: 5px solid rgb(255, 255, 255);
  border-radius: 50%;
  display: inline-block;
  text-align: center;
}

.progress-step > ol > li.progress-active:after {
  background: #990000;
}

.progress-step > ol > li:after {
  content: "";
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  color: rgb(255, 255, 255);
  border-radius: 50%;
  bottom: -1px;
  background: #938686;
  z-index: -1;
}

.progress-step > ol > li:first-child {
  margin-left: 0;
}

.progress-step > ol > li {
  display: inline-block;
  position: relative;
  list-style-type: none;
  margin-left: 60px;
}

.progress-step > ol > li > span.bar {
  background: #bdb1b1;
  height: 2px;
  width: 60px;
  position: absolute;
  top: 33px;
  left: -60px;
}

/* END ADD MERCHANT */
.no-shadow {
  box-shadow: none !important;
  background-color: transparent;
  border: none !important;
}
.step-divider {
  position: relative;
  height: 2px;
  margin: 0 16px;
  background-color: #ccc;
}

.step-divider.active {
  background-color: green;
}

.step-divider.done {
  background-color: #4caf50;
}

.step-divider.active:before,
.step-divider.done:before {
  border-color: green;
}

.step-divider.done:before {
  background-color: #4caf50;
}
.v-stepper__step {
  padding: 0 !important;
}
.step-divider,
.v-application--is-ltr .v-stepper__step__step {
  margin: 0 !important;
}
.theme--light.v-stepper
  .v-stepper__step:not(.v-stepper__step--active):not(
    .v-stepper__step--complete
  ):not(.v-stepper__step--error)
  .v-stepper__step__step {
  background: #455a64 !important;
}
.v-stepper__step--active span {
  background-color: red !important;
}
.v-stepper__step__step {
  height: 40px !important;
  width: 40px !important;
}
</style>
