import axios from "axios";
import CryptoJS from "crypto-js";
import { btoa, atob, dateHelpers } from "@/lib/helpers";

export const inqueryMDR = {
  namespaced: true,

  state() {
    return {
      errors: [],
      flashErrorMessage: "",
      flashError: false,
      loading: false,
      inqueryMDR: null,
      totalPage: 0,
    };
  },

  mutations: {
    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload != "" ? true : false;
    },
    setLoading(state, loading) {
      state.loading = loading;
    },
    setInqueryMDR(state, { payload }) {
      state.inqueryMDR = payload;
    },
    setTotalInquery(state, { payload }) {
      console.log("total", parseInt(payload.totalPage));
      state.totalPage = parseInt(payload.totalPage);
    },
    setClearInquery(state) {
      state.inqueryMDR = [];
    },
  },

  actions: {
    async fetchInqueryMDR(ctx, { search }) {
      ctx.commit("setLoading", true);

      const data = {
        ...search,
        version: btoa(
          CryptoJS.SHA256(
            search.limit.toString() +
            search.page.toString() +
            dateHelpers() +
            atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
          ).toString()
        ),
      };

      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/log/mdr/index",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };
      await axios(config)
        .then(function (response) {
          console.log("inires", response);
          if (response.data.code != "200") {
            if (localStorage.getItem("language_qram") == null) {
              ctx.commit("setFlashErrorMessage", response.data.message_id);
            } else {
              ctx.commit("setFlashErrorMessage", response.data.message_en);
            }

            if (response.data.code == "401") {
              ctx.dispatch("logout", null, { root: true });
            }
          } else {
            ctx.commit("setTotalInquery", { payload: response.data.data });
            ctx.commit("setInqueryMDR", { payload: response.data.data });
          }
        })
        .catch(function (error) {
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit("setLoading", false);
        });
    },
  },
};
