<!-- eslint-disable vue/no-mutating-props -->
<template>
  <tr>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.merchant_name"
        type="text"
        label="Merchant Name"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.bill_num"
        type="text"
        label="Bill Number"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.account_number"
        type="number"
        label="Nomor Rekening"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.status"
        :items="listTrxReportStatus"
        label="Status"
        item-text="text"
        item-value="value"
        @change="showData"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.mid"
        type="text"
        label="MID"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.mpan"
        type="text"
        label="MPAN"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.rrn"
        label="Reff ID"
        type="text"
        @change="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.qr_method"
        :items="qrMethod"
        label="QR Method"
        item-text="text"
        item-value="value"
        @change="showData"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.qr_type"
        :items="qrType"
        label="QR Type"
        item-text="text"
        item-value="value"
        @change="showData"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.trx_type"
        :items="listTrxType"
        label="Transaction Type"
        item-text="text"
        item-value="value"
        @change="showData"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-autocomplete
        clearable
        v-model="search.issuer_code"
        :items="itemsIssuer"
        item-text="issuer_name"
        item-value="issuer_code"
        label="Issuer Name"
        :loading="loadingIssuer"
        :search-input.sync="searchIssuer"
        @change="showData"
        hide-selected
        hide-details="true"
        dense
      ></v-autocomplete>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.customer_pan"
