      <v-select
        v-model="search.payment_type"
        :items="listPaymentType"
        label="Payment Type"
        item-text="text"
        item-value="value"
        @change="showData"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
  </tr>
</template>

<script>
import {
  qrMethod,
  qrType,
  listTrxType,
  sof,
  listPaymentType,
  listTrxReportStatus,
  listSettlementStatus,
} from '@/store/params';

export default {
  props: {
    search: {
      type: Object,
    },
    loading: {
      type: Boolean,
      default: true,
    },
  },
  data: () => ({
    qrMethod,
    qrType,
    listTrxType,
    listSof: sof,
    listPaymentType,
    listTrxReportStatus,
    listSettlementStatus,
    searchIssuer: '',
  }),
  async mounted() {
    this.getIssuer();
  },
  computed: {
    itemsIssuer() {
      return this.$store.state.report.merchantTransaction.allIssuer;
    },
    loadingIssuer() {
      return this.$store.state.report.merchantTransaction.loadingIssuer;
    },
  },
  watch: {},
  methods: {
    getIssuer() {
      this.$store.dispatch('report/merchantTransaction/getIssuer', {
      });
    },
    clearSearch() {
      this.$emit('clearSearchParent');
    },
    showData() {
      this.$emit('showDataParent');
    },
  },
};
</script>
