<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <filter-component
      :filterList="filterList"
      v-on:showDataParent="showData"
      v-on:resetSearch="resetSearch"
      ref="filterComponentRef"
    />
    <table-log
      :expandedItem="true"
      class="mt-5"
      :headers="headers"
      :items="items"
      :options="options"
      :totalData="totalData"
      @update:options="updateOptions"
      :loading="loading"
      :footerProps="footerProps"
    >
      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
        />
      </template>

      <template #no-data>
        <button-reset v-on:resetSearch="resetSearch"/>
      </template>

      <template #expanded-item="props">
        <ExpandedItemsLog :data="props"/>
      </template>
    </table-log>
  </v-card>
</template>

<script>
import FilterComponent from '@/components/table/FilterComponent.vue';
import TableLog from '@/components/table/TableLog.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import ExpandedItemsLog from '@/components/utility/ExpandedItemsLog.vue';
import ButtonReset from '../../components/button/ButtonReset.vue';

export default {
  components: {
    FilterComponent,
    TableLog,
    ActionButton,
    ExpandedItemsLog,
    ButtonReset,
  },
  data() {
    return {
      listButton: {
        buttonRestart: true,
      },
      filterList: [
        {
          label: 'Transaction Date',
          type: 'date',
          icon: 'mdi-calendar',
          showDatePicker: false,
          max: true,
          value: '',
          rules: [(v) => !!v || 'This Field is required'],
        },
        {
          label: 'Transaction Time',
          type: 'time to time',
          value: {
            start_time: {
              HH: '00',
              mm: '00',
            },
            end_time: {
              HH: '23',
              mm: '59',
            },
          },
        },
        {
          label: 'MPAN',
          type: 'number',
          icon: 'mdi-magnify',
          value: '',
        },
        {
          label: 'Merchant Criteria',
          type: 'select',
          item: [],
          value: '',
        },
      ],
      headers: [
        {
          text: 'No', align: 'center', width: '1%', value: 'no',
        },
        {
          text: 'MPAN', align: 'center', width: '15%', value: 'mpan',
        },
        {
          text: 'Merchant Criteria',
          align: 'center',
          width: '15%',
          value: 'merchant_criteria',
        },
        {
          text: 'Brief Activity',
          align: 'center',
          width: '65%',
          value: 'brief_activity',
        },
      ],
      options: {},
      isFirstLoad: true,
      footerProps: {
        itemsPerPageOptions: [10, 20, 50, 100],
      },
    };
  },
  watch: {
    criteria_merchant(val) {
      if (val) {
        this.filterList[3].item = val;
      }
    },
  },
  computed: {
    criteria_merchant() {
      const data = this.$store.state.daftarInputer.ap.criteria_merchant.map(
        (obj) => ({ value: obj.id, name: obj.name }),
      );
      return data;
    },
    allData() {
      return this.$store.state.log.inqueryMDR.inqueryMDR;
    },
    items() {
      return this.allData?.list || [];
    },
    totalData() {
      return this.allData?.totalData || 0;
    },
    totalPage() {
      return this.$store.state.log.inqueryMDR.totalPage;
    },
    loading() {
      return this.$store.state.log.inqueryMDR.loading;
    },
  },
  beforeMount() {
    this.$store.commit('log/inqueryMDR/setClearInquery');
    this.getCriteriaMerchant();
  },
  methods: {
    resetSearch() {
      const value = {
        start_time: {
          HH: '00',
          mm: '00',
        },
        end_time: {
          HH: '23',
          mm: '59',
        },
      };
      this.filterList.map((obj) => {
        const container = obj;
        if (obj.type !== 'time to time') {
          container.value = '';
        } else {
          container.value = { ...value };
        }
        return container;
      });
      this.$refs.filterComponentRef.resetValidation();
      this.$store.commit('log/inqueryMDR/setClearInquery');
    },
    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },
    showData() {
      this.getData();
    },
    getData() {
      const { page, itemsPerPage } = this.options;
      const search = {
        page,
        limit: itemsPerPage,
        search: {
          trx_date: this.filterList[0].value,
          trx_time_start:
            `${(this.filterList[1].value.start_time.HH || '00').padStart(2, '0')
            }:${
              (this.filterList[1].value.start_time.mm || '00').padStart(2, '0')}`,
          trx_time_end:
            `${(this.filterList[1].value.end_time.HH || '00').padStart(2, '0')
            }:${
              (this.filterList[1].value.end_time.mm || '00').padStart(2, '0')}`,
          mpan: this.filterList[2].value,
          merchant_criteria: this.filterList[3].value,
        },
        sort_by: 'datetime',
        sort_order: 'DESC',
      };

      this.$store
        .dispatch('log/inqueryMDR/fetchInqueryMDR', {
          search,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0 || !this.totalPage) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },
    async getCriteriaMerchant() {
      return this.$store.dispatch('daftarInputer/ap/getCriteriaMerchant');
    },
  },
};
</script>
