<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <filter-component
      :filterList="filterList"
      v-on:showDataParent="showData"
      v-on:resetSearch="resetSearch"
      ref="filterComponentRef"
    />

    <table-log
      :expandedItem="true"
      class="mt-5"
      :headers="headers"
      :items="items"
      :options="options"
      :totalData="totalData"
      @update:options="updateOptions"
      :loading="loading"
      :footerProps="footerProps"
    >
      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
        />
      </template>

      <template #no-data>
        <button-reset v-on:resetSearch="resetSearch"/>
      </template>

      <template #expanded-item="props">
        <expanded-items-log :data="props"/>
      </template>
    </table-log>
  </v-card>
</template>

<script>
import FilterComponent from '@/components/table/FilterComponent.vue';
import TableLog from '@/components/table/TableLog.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import ButtonReset from '@/components/button/ButtonReset.vue';
import ExpandedItemsLog from '@/components/utility/ExpandedItemsLog.vue';

const dateObj = new Date();
const date = `${dateObj.getUTCFullYear()
}-${
  (`0${dateObj.getMonth() + 1}`).slice(-2)
}-${
  (`0${dateObj.getDate()}`).slice(-2)}`;

export default {
  components: {
    FilterComponent,
    TableLog,
    ActionButton,
    ButtonReset,
    ExpandedItemsLog,
  },
  data() {
    return {
      listButton: {
        buttonRestart: true,
      },
      filterList: [
        {
          label: 'Transaction Date',
          type: 'date',
          icon: 'mdi-calendar',
          showDatePicker: false,
          max: true,
          value: '',
          rules: [(v) => !!v || 'This Field is required'],
        },
        {
          label: 'Transaction Time',
          type: 'time to time',
          value: {
            start_time: {
              HH: '00',
              mm: '00',
            },
            end_time: {
              HH: '23',
              mm: '59',
            },
          },
        },
        {
          label: 'Request ID',
          type: 'text',
          icon: 'mdi-magnify',
          value: '',
        },
        {
          label: 'Merchant ID',
          type: 'number',
          icon: 'mdi-magnify',
          value: '',
        },
        {
          label: 'Bill Number',
          type: 'text',
          icon: 'mdi-magnify',
          value: '',
        },
      ],
      headers: [
        {
          text: 'No', align: 'center', width: '1%', value: 'no',
        },
        {
          text: 'Merchant ID',
          align: 'center',
          width: '15%',
          value: 'merchant_id',
        },
        {
          text: 'Bill Number',
          align: 'center',
          width: '15%',
          value: 'bill_number',
        },
        {
          text: 'Request ID',
          align: 'center',
          width: '15%',
          value: 'request_id',
        },
        {
          text: 'Transaction Date',
          align: 'center',
          width: '15%',
          value: 'transaction_date',
        },
        {
          text: 'Transaction Time',
          align: 'center',
          width: '15%',
          value: 'transaction_time',
        },
        {
          text: 'Message',
          align: 'center',
          width: '15%',
          value: 'message',
        },
        {
          text: 'IP Address',
          align: 'center',
          width: '15%',
          value: 'ip_address',
        },
        {
          text: 'Elapsed Time',
          align: 'center',
          width: '15%',
          value: 'elapsed_time',
        },
        {
          text: 'Brief Activity',
          align: 'center',
          width: '15%',
          value: 'brief_activity',
        },
      ],
      options: {},
      isFirstLoad: true,
      footerProps: {
        itemsPerPageOptions: [10, 20, 50, 100],
      },
    };
  },
  beforeMount() {
    this.$store.commit('log/statusBill/setClearBill');
  },
  watch: {
    issuerName(val) {
      if (val) {
        this.filterList[5].item = val;
      }
    },
  },
  computed: {
    issuerName() {
      const data = this.$store.state.log.statusRNN.issuerName.map((obj) => ({
        value: obj.bank_nns,
        name: obj.bank_name,
      }));
      return data;
    },
    allData() {
      return this.$store.state.log.statusBill.statusBill;
    },
    items() {
      return this.allData?.list || [];
    },
    totalData() {
      return this.allData?.totalData || 0;
    },
    totalPage() {
      return this.$store.state.log.statusBill.totalPage;
    },
    loading() {
      return this.$store.state.log.statusBill.loading;
    },
  },
  methods: {
    resetSearch() {
      const value = {
        start_time: {
          HH: '00',
          mm: '00',
        },
        end_time: {
          HH: '23',
          mm: '59',
        },
      };
      this.filterList.map((obj) => {
        const container = obj;
        if (obj.type !== 'time to time') {
          container.value = '';
        } else {
          container.value = { ...value };
        }

        return container;
      });
      this.$refs.filterComponentRef.resetValidation();
      this.$store.commit('log/statusBill/setClearBill');
    },
    showData() {
      this.getData();
    },
    async getIssuerName() {
      return this.$store.dispatch('log/statusRNN/getIssuer', {
        date,
        bank_name: this.filterList[5].value,
      });
    },
    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },
    getData() {
      const { page, itemsPerPage } = this.options;
      const search = {
        page,
        limit: itemsPerPage,
        search: {
          trx_date: this.filterList[0].value,
          trx_time_start:
            `${(this.filterList[1].value.start_time.HH || '00').padStart(2, '0')
            }:${
              (this.filterList[1].value.start_time.mm || '00').padStart(2, '0')}`,
          trx_time_end:
            `${(this.filterList[1].value.end_time.HH || '00').padStart(2, '0')
            }:${
              (this.filterList[1].value.end_time.mm || '00').padStart(2, '0')}`,
          merchant_id: this.filterList[3].value,
          bill_number: this.filterList[4].value,
          request_id: this.filterList[2].value,
        },
        sort_by: 'datetime',
        sort_order: 'DESC',
      };

      this.$store
        .dispatch('log/statusBill/fetchData', {
          date,
          search,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },
  },
};
</script>
