<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div>
    <v-dialog v-model="dialog" width="500" persistent>
      <v-card>
        <v-card-title class="primaryred--text text-center">
          {{ formTitle }}
        </v-card-title>
        <v-card-text>
          <v-form ref="userAct" @submit.prevent="save">
            <v-container>
              <v-row>
                <v-col v-if="errorEdit" cols="12">
                  <v-card color="#f9f9f9">
                    <div class="pa-2 d-flex align-center text--primary">
                      <div class="mr-4">
                        <v-icon large color="#b71c1c">
                          mdi-alert-circle-outline
                        </v-icon>
                      </div>
                      <div>
                        <p
                          class="d-flex align-center mb-0"
                          style="color: #b71c1c"
                        >
                          {{ messageError }}
                        </p>
                      </div>
                    </div>
                  </v-card>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    filled
                    v-model="editedItem.nama_klasifikasi"
                    label="Nama Klasifikasi"
                    :error-messages="namaError"
                    @change="$v.editedItem.nama_klasifikasi.$touch()"
                    @blur="$v.editedItem.nama_klasifikasi.$touch()"
                    :counter="50"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-textarea
                    v-model="editedItem.deskripsi"
                    label="Deskripsi"
                    filled
                    :error-messages="deskripsiError"
                    @change="$v.editedItem.deskripsi.$touch()"
                    @blur="$v.editedItem.deskripsi.$touch()"
                    :counter="255"
                  ></v-textarea>
                </v-col>
                <v-col cols="12" sm="12" md="12" v-if="editedIndex !== -1">
                  <v-select
                    v-model="editedItem.is_active"
                    :error-messages="statusError"
                    @change="$v.editedItem.is_active.$touch()"
                    @blur="$v.editedItem.is_active.$touch()"
                    :items="statusActive"
                    label="Status"
                    item-text="name"
                    item-value="value"
                    clearable
                  ></v-select>
                </v-col>
              </v-row>
            </v-container>
            <v-divider></v-divider>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primaryred" outlined @click="cancelDialog">
                Cancel
              </v-btn>
              <v-btn color="primary" type="submit" outlined :loading="loading">
                Save
              </v-btn>
            </v-card-actions>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import { required } from 'vuelidate/lib/validators';
import { statusActive } from '@/store/params';

export default {
  props: {
    loading: {
      type: Boolean,
    },
    editedItem: {
      type: Object,
    },
    dialog: {
      type: Boolean,
    },
    editedIndex: {
      type: Number,
    },
  },
  mixins: [validationMixin],
  validations: {
    editedItem: {
      nama_klasifikasi: {
        required,
        alphabet(value) {
          const pattern = /^[a-zA-Z0-9 ]+$/;
          return !!pattern.test(value);
        },
        maxChar(value) {
          return value.length <= 50;
        },
      },
      deskripsi: {
        required,
        maxChar(value) {
          return value.length <= 255;
        },
      },
      is_active: { required },
    },
  },
  data: () => ({
    statusActive,
    statusItem: [],
    errorEdit: false,
    messageError: '',
  }),
  computed: {
    formTitle() {
      return this.editedIndex !== -1 ? 'Edit Klasifikasi' : 'Add Klasifikasi';
    },
    flashError: {
      get() {
        return this.$store.state.klasifikasi.flashError;
      },
      set() {
        this.$store.commit('klasifikasi/setFlashErrorMessage', '');
      },
    },
    namaError() {
      const errors = [];
      if (!this.$v.editedItem.nama_klasifikasi.$dirty) return errors;
      if (!this.$v.editedItem.nama_klasifikasi.required) {
        errors.push('Nama Klasifikasi is required');
      }
      if (!this.$v.editedItem.nama_klasifikasi.alphabet) {
        errors.push('Format Nama Klasifikasi tidak sesuai (A-Z, a-z, 0-9)');
      }
      if (!this.$v.editedItem.nama_klasifikasi.maxChar) {
        errors.push('Nama Klasifikasi must be less than 50 digits');
      }
      return errors;
    },
    deskripsiError() {
      const errors = [];
      if (!this.$v.editedItem.deskripsi.$dirty) return errors;
      if (!this.$v.editedItem.deskripsi.required) {
        errors.push('Deskripsi is required');
      }
      if (!this.$v.editedItem.deskripsi.maxChar) {
        errors.push('Deskripsi must be less than 255 digits');
      }
      return errors;
    },
    statusError() {
      const errors = [];
      if (!this.$v.editedItem.is_active.$dirty) return errors;
      if (!this.$v.editedItem.is_active.required) {
        errors.push('Status is required');
      }
      return errors;
    },
  },
  watch: {
    dialog(val) {
      if (!val) this.cancelDialog();
    },
  },
  methods: {
    cancelDialog() {
      this.resetUniqueValidation();
      this.$emit('closeCancelParent');
      this.errorEdit = false;
      this.$v.$reset();
    },
    resetUniqueValidation() {
      this.$store.commit('klasifikasi/clearErrors');
    },
    async save() {
      this.$store.commit('klasifikasi/setFlashErrorMessage', '');
      this.resetUniqueValidation();
      this.$v.$touch();

      if (this.$v.$invalid) {
        return;
      }

      const data = {
        id: this.editedItem.id || '',
        name: this.editedItem.nama_klasifikasi,
        description: this.editedItem.deskripsi,
        is_active: this.editedItem.is_active,
      };

      try {
        const status = await this.saveKlasifikasi(data);

        this.handleSaveStatus(status);
      } catch (error) {
        this.showAlert('Error on Add Klasifikasi !', 'mdi-alert', 'red');
      }
    },

    async saveKlasifikasi(data) {
      const result = await this.$store.dispatch('klasifikasi/addKlasifikasi', {
        data,
        edit: this.editedIndex !== -1,
      });

      return result;
    },

    handleSaveStatus(status) {
      this.messageError = '';
      window.scrollTo(0, 0);

      if (status === true) {
        this.handleSuccess();
      } else if (status === false) {
        this.showAlert('Error on Add Klasifikasi !', 'mdi-alert', 'red');
      } else if (status?.code === 422) {
        this.handleValidationErrors(status.data);
      } else if (status?.code === 304) {
        this.errorEdit = true;
        this.messageError = status.data.message;
      }
    },

    handleSuccess() {
      this.cancelDialog();
      this.showAlert(
        this.editedIndex !== -1
          ? 'Data Klasifikasi Berhasil Diubah!'
          : 'Data Klasifikasi Berhasil Ditambahkan!',
        'mdi-check-circle',
        'green',
      );
      this.$emit('closeSuccess');
    },

    handleValidationErrors(data) {
      this.errorEdit = true;

      if (data.name && data.name.length > 0) {
        this.messageError += data.name.join(' & ');
      }

      if (data.description && data.description.length > 0) {
        if (this.messageError.length > 0) {
          this.messageError += ' & ';
        }
        this.messageError += data.description.join(' & ');
      }
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
