<template>
  <v-btn
    :disabled="disabled"
    :color="color"
    :dark="dark"
    @click="$emit('clickButton')"
    :type="type"
  >
    <v-icon v-if="positionIcon === 'left'" left :color="iconColor">
      {{ icon }}
    </v-icon>
    {{ label }}
    <v-icon v-if="positionIcon === 'right'" right :color="iconColor">
      {{ icon }}
    </v-icon>
  </v-btn>
</template>

<script>
export default {
  props: {
    label: {
      type: String,
    },
    color: {
      type: String,
    },
    type: {
      type: String,
    },
    icon: {
      type: String,
    },
    iconColor: {
      type: String,
    },
    positionIcon: {
      type: String,
      default: 'left',
    },
    dark: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
};
</script>
