<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div>
    <v-dialog v-model="dialog" width="500" persistent>
      <v-card>
        <v-card-title class="primaryred--text">
          Detail Merchant Report
        </v-card-title>
        <v-card-text>
          <v-form>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <table
                    aria-describedby="transaktion-detail"
                    style="width: 100%"
                  >
                    <th />
                    <tr>
                      <td>Merchant Name</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.merchant_name }}
                      </td>
                    </tr>
                    <tr>
                      <td>Nomor Rekening</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.account_number }}
                      </td>
                    </tr>
                    <tr>
                      <td>Bill Number</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.bill_num }}
                      </td>
                    </tr>
                    <tr>
                      <td>Transaction Date</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.trx_datetime }}
                      </td>
                    </tr>
                    <tr>
                      <td>Transaction Status</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.status }}
                      </td>
                    </tr>
                    <tr>
                      <td>MID</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.mid }}
                      </td>
                    </tr>
                    <tr>
                      <td>Merchant PAN</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.mpan }}
                      </td>
                    </tr>
                    <tr>
                      <td>Reff ID</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.rrn }}
                      </td>
                    </tr>
                    <tr>
                      <td>QR Method</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.qr_method }}
                      </td>
                    </tr>
                    <tr>
                      <td>QR Type</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.qr_type }}
                      </td>
                    </tr>
                    <tr>
                      <td>Transaction Type</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.trx_type }}
                      </td>
                    </tr>
                    <tr>
                      <td>Issuer Name</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.issuer_name }}
                      </td>
                    </tr>
                    <tr>
                      <td>Customer PAN</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.customer_pan }}
                      </td>
                    </tr>
                    <tr>
                      <td>Customer Name</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.customer_name }}
                      </td>
                    </tr>
                    <tr>
                      <td>Payment Type</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.payment_type }}
                      </td>
                    </tr>
                    <tr>
                      <td>Acquirer Name</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.payment_channel }}
                      </td>
                    </tr>
                    <tr>
                      <td>Total Amount</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.amount }}
                      </td>
                    </tr>
                    <tr>
                      <td>Fee</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.amount_fee }}
                      </td>
                    </tr>
                    <tr>
                      <td>MDR</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.amount_mdr }}
                      </td>
                    </tr>
                    <tr>
                      <td>Net Amount</td>
                      <td>:</td>
                      <td>
                        {{ editedItem.net_amount }}
                      </td>
                    </tr>
                  </table>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primaryred" outlined @click="cancelDialog">
            Cancel
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  props: {
    loading: {
      type: Boolean,
    },
    editedItem: {
      type: Object,
    },
    dialog: {
      type: Boolean,
    },
  },
  watch: {
    dialog(val) {
      if (!val) this.cancelDialog();
    },
  },
  methods: {
    cancelDialog() {
      this.$emit('closeCancelParent');
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
