<template>
<div>
  <v-card class="mb-12" height="20px" :style="{ 'box-shadow': '0 0 black', 'margin-top': '20px' }">
    <div
      class="d-flex pa-5 flex-column align-center justify-center"
    >
      <h3>Data Aggregator</h3>
    </div>
  </v-card>
  <div :style="{ padding: '20px 50px 20px 50px' }">
    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'Nama Perusahaan'"
      :valueData = "data.company_name"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'Nama Pemilik Usaha'"
      :valueData = "data.owner_name"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'Nomor Rekening Perusahaan'"
      :valueData = "data.account_number"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'Nomor KTP Pemilik Usaha'"
      :valueData = "data.ktp_no"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'Nomor NPWP Perusahaan'"
      :valueData = "data.npwp_no"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'Nomor Telepon'"
      :valueData = "merchantPhone == ' ' ? '( not set )' : merchantPhone"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'Email'"
      :valueData = "data.email"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'Alamat Perusahaan'"
      :valueData = "data.address"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'Kota, Kecamatan, Kode Pos'"
      :valueData = "data.kabupaten_kota + ', ' + data.kecamatan + ', ' + data.kode_pos"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'ID Approver 1'"
      :valueData = "data.approver_1"
    />

    <data-detail-row 
      :oldValueData = "false"
      :data = "data"
      :label = "'ID Approver 2'"
      :valueData = "data.approver_2"
    />
  </div>
</div>
</template>

<script>
import DataDetailRow from '@/components/detail/DataDetailRow.vue'
export default {
  props: ["data"],
  components: { DataDetailRow },
  computed: {
		merchantPhone() {
			let area =
				this.data.phone_area_code != "" &&
				this.data.phone_area_code != null
					? "(" + this.data.phone_area_code + ")"
					: "";
			let phone = this.data.phone != null ? this.data.phone : "";
			return area + " " + phone;
		},
  }
}
</script>
