import Parent from "../views/Parent.vue";

import ApList from "../views/daftar-tugas/ApList.vue";
import ApDetail from "../views/daftar-tugas/ApDetail.vue";

import { setBreadcrumb } from "./index.js";


export const daftarTugasRoute = {
  path: "/daftar-approval",
  name: "DaftarApproval",
  meta: {
      title: "JAKONE - Daftar Approval",
      appBarTitle: "Daftar Approval",
  },
  component: Parent,
  children: [
    {
      path: "/daftar-approval",
      meta: {
          title: "JAKONE - Daftar Approval",
          appBarTitle: "Daftar Approval",
          breadcrumbs: () => { 
              return [
                  setBreadcrumb('Daftar Approval', '/daftar-approval', true),
              ]
          }
      },
      component: ApList,
      name: "DaftarApproval",
    },
    {
      path: "/daftar-approval/view/:id(\\d+)",
      meta: {
          title: "JAKONE - View Detail",
          appBarTitle: "View Detail",
          breadcrumbs: (params) => { 
                const id = params.id 
                return [
                  setBreadcrumb('Daftar Approval', '/daftar-approval'),
                  setBreadcrumb(`detail`, `/daftar-approval/view`, true),
                  setBreadcrumb(`${id}`, `/daftar-approval/view/${id}`),
                ]
          }
      },
      component: ApDetail,
      name: "Approver1ViewDetail",
    },
  ]
}