/* eslint-disable no-shadow */
import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';

const role = {
  namespaced: true,

  state() {
    return {
      allRoles: [],
      totalRoles: 0,
      loading: false,
      loadingActiveRole: false,
      roleCache: {},
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      allActiveRole: [],
      requestActiveRole: null,
      totalPage: 0,
      detail: null,
    };
  },

  mutations: {
    setRoles(state, { payload }) {
      state.allRoles = payload.data;
    },

    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },

    clearRoles(state) {
      state.allRoles = [];
      state.totalRoles = 0;
    },

    setDetailData(state, payload) {
      state.detail = payload;
    },

    clearErrors(state) {
      state.errors = [];
    },

    setErrors(state, payload) {
      state.errors = payload;
    },

    setTotalRoles(state, { payload }) {
      state.totalRoles = parseInt(payload.data.length, 10);
    },

    setLoading(state, loading) {
      state.loading = loading;
    },

    setLoadingActiveRole(state, loadingActiveRole) {
      state.loadingActiveRole = loadingActiveRole;
    },

    removeRole(state, { nameRole }) {
      state.totalRoles -= 1;
      state.allRoles = state.allRoles.filter((value) => value.role !== nameRole);
    },
    clearErrorVariable(state, variable) {
      state.errors[variable] = [];
    },

    pushRole(state, { payload }) {
      state.allRoles.push(payload);
      state.totalRoles += 1;
    },

    editRole(state, { payload, index }) {
      Object.assign(state.allRoles[index], payload);
    },

    setActiveRoles(state, { payload }) {
      state.allActiveRole = payload.data;
    },
  },

  actions: {
    async fetchRoles(ctx, { date, search }) {
      ctx.commit('clearRoles');
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        user_id: '',
        search,
        version: btoa(
          CryptoJS.SHA256(
            `${date}${atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))}`,
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url: `${process.env.VUE_APP_API_BASE_URL
        }/index.php?r=api/v1/rbac/role/list`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setTotalRoles', {
              payload: response.data,
            });
            ctx.commit('setRoles', { payload: response.data });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },

    async fetchActiveRoles(ctx) {
      ctx.commit('clearRoles');
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        user_id: '',
        search: {
          role_type: '',
        },
        version: btoa(
          CryptoJS.SHA256(
            `${dateHelpers()}${atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))}`,
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url: `${process.env.VUE_APP_API_BASE_URL
        }/index.php?r=api/v1/rbac/role/list`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setActiveRoles', {
              payload: response.data,
            });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },

    async createRole(ctx, { nameRole, description }) {
      console.log(role);
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        role: nameRole,
        description,
        version: btoa(
          CryptoJS.SHA256(
            nameRole
            + description
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url: `${process.env.VUE_APP_API_BASE_URL
        }/index.php?r=api/v1/rbac/role/add`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const results = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            ctx.commit('setErrors', response.data.data);
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }
            return false;
          }
          ctx.commit('pushRole', {
            payload: { nameRole },
          });
          return true;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return false;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return results;
    },

    async updateRole(ctx, {
      nameRole, description, index,
    }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        id: index,
        role: nameRole,
        description,
        version: btoa(
          CryptoJS.SHA256(
            index.toString()
            + nameRole.toString()
            + description
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url: `${process.env.VUE_APP_API_BASE_URL
        }/index.php?r=api/v1/rbac/role/update`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const results = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            ctx.commit('setErrors', response.data.data);
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }
            return false;
          }
          const updatedRole = response.data.data;
          ctx.commit('editRole', {
            payload: updatedRole,
            index,
          });
          return true;
        })
        .catch(() => false)
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return results;
    },

    async deleteRole(ctx, { date, nameRole }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        role: nameRole,
        version: btoa(
          CryptoJS.SHA256(
            nameRole.toString()
            + date
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url: `${process.env.VUE_APP_API_BASE_URL
        }/index.php?r=api/v1/rbac/role/delete`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const results = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }
            return false;
          }
          ctx.commit('removeRole', {
            nameRole,
          });
          return true;
        })
        .catch(() => {
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return results;
    },
  },
};

export default role;
