<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <Loading v-if="loading" />

    <!-- Syarat & Ketentuan -->
    <SyaratKetentuan
      v-if="step == 1"
      :data="dataUser"
      v-on:nextParent="nextParent"
      v-on:backParent="backParent"
      @update:value="(newVal) => (dataUser.agree = newVal)"
    ></SyaratKetentuan>

    <!-- Jenis QR -->
    <JenisQr
      v-if="step == 2"
      :data="dataUser"
      v-on:nextParent="nextParent"
      v-on:backParent="backParent"
      @update:value="(newVal) => (dataUser.qr_type = newVal)"
    ></JenisQr>

    <!-- Input No Rekening -->
    <InputNomorRekening
      v-if="step == 3"
      @setLoading="setLoading"
      :data="dataUser"
      v-on:nextParent="nextParent"
      v-on:backParent="backParent"
      @update:value="(newVal) => (dataUser.account_number = newVal)"
    ></InputNomorRekening>

    <!-- Merchant Registration -->
    <MerchantRegister
      v-if="step == 4"
      :data="dataUser"
      @setLoading="setLoading"
      v-on:nextParent="nextParent"
      v-on:backParent="backParent"
      v-on:submitParent="submitParent"
    >
    </MerchantRegister>
    <ConfirmPage v-if="step == 5" :data="dataUser" />
  </v-card>
</template>

<script>
import Loading from "../../../components/Loading.vue";
import SyaratKetentuan from "@/components/merchant/SyaratKetentuan.vue";
import JenisQr from "@/components/merchant/JenisQr.vue";
import InputNomorRekening from "@/components/merchant/InputNomorRekening.vue";
import MerchantRegister from "@/components/merchant/MerchantRegister.vue";
import ConfirmPage from "@/components/merchant/ConfirmPage.vue";

const dateObj = new Date();
const date =
  dateObj.getUTCFullYear() +
  "-" +
  ("0" + (dateObj.getMonth() + 1)).slice(-2) +
  "-" +
  ("0" + dateObj.getDate()).slice(-2);

export default {
  components: {
    SyaratKetentuan,
    JenisQr,
    InputNomorRekening,
    MerchantRegister,
    ConfirmPage,
    Loading,
  },
  beforeMount() {
    this.getSyaratKetentuan();
  },
  data() {
    return {
      loading: false,
      step: 1,
    };
  },
  computed: {
    dataUser: {
      get() {
        return this.$store.state.client.addMerchant.dataOnboarding;
      },
      set(newVal) {
        this.$store.commit("client/addMerchant/setDataOnboarding", newVal);
      },
    },
  },
  methods: {
    setLoading(val) {
      this.loading = val;
    },
    getSyaratKetentuan() {
      this.$store.dispatch("client/addMerchant/getSyaratKetentuan", {
        date,
      });
    },
    nextParent(val, type) {
      if (val) {
        this.dataUser = {
          ...this.data,
          ...val,
        };
      }
      if (type === "norek") {
        this.dataUser = {
          ...this.dataUser,
          address: "",
          area: null,
          checkbox_merchant_mobile_phone: false,
          document: null,
          image: "",
          image_lokasi: null,
          mcc: null,
          mdr_off_us: null,
          mdr_on_us: null,
          merchant_criteria: "",
          phone: "",
          version: "",
        };
      }
      this.step++;
    },
    submitParent(val) {
      if (val) {
        this.dataUser.reference_number = val;
      }
      this.step++;
    },
    backParent() {
      if (this.step !== 1) {
        this.step--;
      } else {
        this.$router.push("/client/merchant");
      }
    },
  },
};
</script>
