<template>
  <div>
    <v-dialog v-model="dialog" width="500" persistent>
      <v-card>
        <v-card-title class="primaryred--text">
          Alasan {{ formTitle }}
        </v-card-title>
        <v-card-text>
          <v-form>
            <v-container>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-select
                    v-model="editedItem.id_reason"
                    :error-messages="idReasonErrors"
                    @change="$v.editedItem.id_reason.$touch()"
                    @blur="$v.editedItem.id_reason.$touch()"
                    :items="reasonList"
                    label="Pilih Alasan"
                    item-text="reason_desc"
                    item-value="id"
                    :loading="loading"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-textarea
                    :label="'Input alasan ' + formTitle"
                    variant="outlined"
                    v-model="editedItem.note"
                  ></v-textarea>
                </v-col>
                <v-col v-if="warning" cols="12" sm="12" md="12">
                  <v-alert dense outlined type="error">
                    {{ message }}
                  </v-alert>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primaryred" outlined @click="cancelDialog">
            Cancel
          </v-btn>
          <v-btn
            color="primary"
            outlined
            @click="save"
            :disabled="loading"
            :loading="loading"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import { required } from 'vuelidate/lib/validators';

export default {
  props: {
    message: {
      type: String,
    },
    dialog: {
      type: Boolean,
    },
    editedItem: {
      type: Object,
    },
    editedIndex: {
      type: Number,
    },
    reasonType: {
      type: [String, Number],
    },
    data: {
      type: Object,
    },
  },
  mixins: [validationMixin],
  validations: {
    editedItem: {
      id_reason: {
        required,
      },
    },
  },
  data: () => ({}),
  computed: {
    loading() {
      return this.$store.state.daftarTugas.ap.loading;
    },
    formTitle() {
      return this.reasonType === 1 ? 'Pending' : 'Reject';
    },
    flashError: {
      get() {
        return this.$store.state.daftarTugas.ap.flashError;
      },
      set() {
        this.$store.commit('daftarTugas/ap/setFlashErrorMessage', '');
      },
    },
    reasonList() {
      return this.$store.state.daftarTugas.ap.reasonList;
    },
    errorValidation() {
      return this.$store.state.daftarTugas.ap.errors;
    },
    idReasonErrors() {
      const errors = [];
      if (!this.$v.editedItem.id_reason.$dirty) {
        return errors;
      }
      if (!this.$v.editedItem.id_reason.required) {
        errors.push('Reason is required.');
      }
      return errors;
    },
    warning() {
      return !!this.editedItem.id_reason && this.editedItem.id_reason < 5;
    },
  },
  watch: {
    dialog(val) {
      if (!val) this.cancelDialog();
    },
    reasonType(val) {
      this.getReasonList(val);
    },
  },
  methods: {
    getReasonList(reasonType) {
      this.$store.dispatch('daftarTugas/ap/reasonList', {
        type: reasonType,
      });
    },
    cancelDialog() {
      this.resetUniqueValidation();
      this.$emit('closeCancelParent');
    },
    resetUniqueValidation() {
      this.$store.commit('daftarTugas/ap/clearErrors');
    },
    async save() {
      this.$store.commit('daftarTugas/ap/setFlashErrorMessage', '');
      this.resetUniqueValidation();
      this.$v.$touch();
      if (!this.$v.$invalid) {
        const data = this.editedItem;
        await this.$store
          .dispatch('daftarTugas/ap/saveReason', {
            accountClassification: this.data.account_classification,
            dataForm: data,
            type: this.reasonType,
            index: this.editedIndex,
          })
          .then((status) => {
            if (status) {
              this.cancelDialog();
              this.showAlert(
                'Update reason success !',
                'mdi-check-circle',
                'green',
              );
              this.$router.push('/daftar-approval');
            } else {
              this.showAlert('Error on update reason !', 'mdi-alert', 'red');
            }
          });
      }
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
