import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';

const addMerchant = {
  namespaced: true,

  state() {
    return {
      dataOnboarding: {
        account_number: '',
        qr_type: null,
        agree: false,
        terms_conditions: null,
        reference_number: null,
        gmaps: {
          lat: 0,
          lng: 0,
        },
        merchant_mobile_phone: '',
        mobile_phone: '',
      },
      loading: false,
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      syaratKetentuan: null,
      dataAccountNumber: null,
      otp: null,
      idPendaftaran: null,
    };
  },

  mutations: {
    setDefaultOnBoarding(state) {
      state.dataOnboarding = {
        account_number: '',
        qr_type: null,
        agree: false,
        terms_conditions: null,
        reference_number: null,
        gmaps: {
          lat: 0,
          lng: 0,
        },
        merchant_mobile_phone: '',
        mobile_phone: '',
      };
    },
    setDataOnboarding(state, payload) {
      state.dataOnboarding = {
        ...state.dataOnboarding,
        ...payload,
      };
    },
    setLoading(state, loading) {
      state.loading = loading;
    },

    clearErrors(state) {
      state.errors = [];
    },

    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },

    setErrors(state, payload) {
      state.errors = payload;
    },

    setSyaratKetentuan(state, payload) {
      state.syaratKetentuan = payload;
    },
    setDataAccountNumber(state, payload) {
      state.dataAccountNumber = payload;
    },
    setOtp(state, payload) {
      state.otp = payload;
    },

    setIdRegistration(state, payload) {
      state.idPendaftaran = payload;
    },
  },

  actions: {
    async getSyaratKetentuan(ctx, { date }) {
      const data = JSON.stringify({
        version: btoa(
          CryptoJS.SHA256(
            date + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/master/data/terms-conditions`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setSyaratKetentuan', response.data.data);
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },
    async getOtpCode(ctx, { email, userId }) {
      const data = JSON.stringify({
        email,
        merchant_id: userId,
        version: btoa(
          CryptoJS.SHA256(
            userId.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/onboarding/otp/send`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return response.data;
          }
          return response.data;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return error;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },
    async confirmOtp(ctx, { email, userId, otp }) {
      const data = JSON.stringify({
        email,
        merchant_id: userId,
        otp,
        version: btoa(
          CryptoJS.SHA256(
            userId.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/onboarding/otp/validate`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return response.data;
          }
          return response.data;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return error;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },
    async submitOnboarding(ctx, { data }) {
      const newData = {
        ...data,
        version: btoa(
          CryptoJS.SHA256(
            data.qr_type.toString()
            + data.merchant_name.toString()
            + data.merchant_criteria.toString()
            + data.area.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      };
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/corporate/onboarding/submit`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data: JSON.stringify(newData),
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return response.data;
          }
          ctx.commit('setIdRegistration', response.data.data);
          return response.data;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return error;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async OverviewMerchant(ctx, { id }) {
      const data = JSON.stringify({
        id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/corporate/onboarding/overview`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return response.data;
          }
          ctx.commit('setIdRegistration', response.data.data);
          return response.data;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return error;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },
    async ConfirmMerchant(ctx, { id }) {
      const data = JSON.stringify({
        id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/corporate/onboarding/confirm`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return response.data;
          }
          ctx.commit('setIdRegistration', response.data.data);
          return response.data;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return error;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async getCorporateAccountNumber(ctx, { accountNumber }) {
      ctx.commit('setLoading', true);

      const data = JSON.stringify({
        account_number: accountNumber,
        version: btoa(
          CryptoJS.SHA256(
            accountNumber
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/corporate/onboarding/account-number-data`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return response.data;
          }
          ctx.commit('setDataAccountNumber', response.data.data);
          return response.data.data;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async getMDR(ctx, { mddId, merchantCriteria }) {
      ctx.commit('setLoading', true);

      const data = JSON.stringify({
        mddId,
        merchantCriteria,
        version: btoa(
          CryptoJS.SHA256(
            merchantCriteria
            + mddId
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/master/data/mdr`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return response.data;
          }
          ctx.commit('setDataAccountNumber', response.data.data);
          return response.data.data;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },
  },
};

export default addMerchant;
