<template>
  <div>
    <v-row>
      <v-col cols="10" sm="4">{{ label }}</v-col>
      <v-col cols="2" sm="1">:</v-col>
      <v-col cols="12" sm="7">
        <gmap-map
          :center="valueData"
          :zoom="10"
          style="width:100%;  height: 300px;"
        >
          <gmap-marker :position="valueData"></gmap-marker>
        </gmap-map>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  props: {
    label: {
      type: String,
    },
    valueData: {
      type: Object,
    },
  },
  name: "DrawGoogleMap",
  data() {
    return {
      center: {
        lat: -6.2,
        lng: 106.816666,
      },
      locations: [],
      currentLocation: null,
    };
  },

  methods: {
    setPlace(loc) {
      this.currentLocation = loc;
    },
    setLocationLatLng: function() {
      navigator.geolocation.getCurrentPosition((geolocation) => {
        this.center = {
          lat: geolocation.coords.latitude,
          lng: geolocation.coords.longitude,
        };
      });

      this.locations = [
        {
          lat: -7.797068,
          lng: 110.370529,
          label: "Yogyakarta",
        },
        {
          lat: -6.2,
          lng: 106.816666,
          label: "Jakarta",
        },
      ];
    },
  },
};
</script>
