<template>
  <tr
    :key="index"
    :style="{ cursor: 'pointer' }"
    :class="selectedRow === index ? 'indigo lighten-5' : ''"
    @click="selectRow(index)"
  >
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ (options.page - 1) * options.itemsPerPage + index + 1 }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.nmid }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.mid }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.qr_type }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.status }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-btn
        small
        disabled
        v-if="item != null && item.qr_type.toLowerCase() != 'qr sticker'"
      >
        <v-icon class="mr-2"> mdi-printer</v-icon>Print QR
      </v-btn>
      <a
        style="text-decoration: none"
        :download="qrFilename"
        :href="qrisFile"
        v-else
      >
        <v-btn dark small :loading="loadingDocument">
          <v-icon class="mr-2"> mdi-printer</v-icon>Print QR</v-btn
        ></a
      >
    </td>
  </tr>
</template>

<script>
import { dateHelpers } from '@/lib/helpers';

export default {
  props: {
    item: {
      type: Object,
    },
    index: {
      type: Number,
    },
    options: {
      type: Object,
    },
    selectedRow: {
      type: Number,
    },
  },
  data: () => ({
    loadingDocument: false,
    qrisFile: null,
  }),
  mounted() {
    this.getQr();
  },
  computed: {
    qrFilename() {
      return `QR-Merchant-${dateHelpers()}-${this.$route.params.id}`;
    },
  },
  methods: {
    selectRow(index) {
      this.$emit('selectRowParent', index);
    },
    getQr() {
      this.loadingDocument = true;
      this.$store
        .dispatch('client/merchant/viewDocument', {
          id: this.$route.params.id,
          docType: '4',
          type: 'current',
          download: '0',
        })
        .then((response) => {
          if (response != null) {
            if (
              response.replace('data:image/jpg;base64,', '') === ''
              || response.replace('data:image/png;base64,', '') === ''
            ) {
              this.qrisFile = null;
            } else {
              this.qrisFile = response;
            }
          }
        })
        .finally(() => {
          this.loadingDocument = false;
        });
    },
  },
};
</script>
