      </div>
      <div class="col-md-6">
        <h6 class="text-start text-subtitle-2">: {{ data.total_data }}</h6>
      </div>
      <div class="col-md-5">
        <h6 class="green--text text--darken-3 text-start text-subtitle-2">
          Sukses
        </h6>
      </div>
      <div class="col-md-6">
        <h6 class="green--text text--darken-3 text-start text-subtitle-2">
          : {{ data.sukses }}
        </h6>
      </div>
      <div class="col-md-5">
        <h6 class="red--text text--darken-3 text-start text-subtitle-2">
