        </h6>
      </div>
      <div class="col-md-6">
        <h6 class="red--text text--darken-3 text-start text-subtitle-2">
          : {{ data.gagal }}
        </h6>
      </div>
    </v-row>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
    },
  },
};
</script>

