<!-- eslint-disable vue/no-mutating-props -->
<template>
  <v-dialog v-model="dialog" width="500" persistent>
    <v-card>
      <v-card-title v-if="title" class="title justify-center">
        {{ title }}
      </v-card-title>
      <v-card-text v-if="caution">
        {{ caution }}
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn dark color="primaryred" outlined @click="cancelDialog">
          Cancel
        </v-btn>
        <v-btn
          color="primary"
          outlined
          @click="confirm"
          :disabled="loading"
          :loading="loading"
        >
          Ok
        </v-btn>
        <v-spacer></v-spacer>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    dialog: {
      type: Boolean,
    },
    title: {
      type: [String, Number],
    },
    caution: {
      type: [String, Number],
    },
    loading: {
      type: Boolean,
    },
  },
  methods: {
    cancelDialog() {
      this.$emit('closeCancelParent');
    },
    confirm() {
      this.$emit('confirmParent');
    },
  },
};
</script>
