<template>
  <div>
    <v-row class="align-center">
      <v-col cols="10" sm="4"> {{ label }} </v-col>
      <v-col cols="2" sm="1"> : </v-col>
      <v-col cols="12" sm="7">
        <v-text-field
          :label="label"
          :type="type"
          :rules="rule || [v => !!v || `${label} is required`]"
          :value="value"
          @input="updateInput($event)"
          @keydown="preventExponentAndSign"
          :disabled="disabled"
        >
        </v-text-field>
        <slot name="content"></slot>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: 'text',
    },
    rule: Array,
    label: String,
    value: { type: [String, Number, Object], default: '' },
    disabled: {
      type: Boolean,
      default: false,
    },
    required: Boolean,
  },
  methods: {
    preventExponentAndSign(event) {
      const input = event.target;

      // Only validate input if type is not "text"
      if (input.type !== 'text' && input.type !== 'email') {
        const { userAgent } = navigator;

        const keyCode = event.keyCode || event.which;
        const isValid = (keyCode >= 48 && keyCode <= 57) || keyCode === 8;
        const pattern = /^[^A-Za-z0-9\s]+$/;
        const checkSpecial = pattern.test(event.key);

        if (userAgent.indexOf('Firefox') !== -1 && (!isValid || checkSpecial)) {
          event.preventDefault();
        }
        if (
          event.key === 'e'
          || event.key === 'E'
          || event.key === '+'
          || event.key === '-'
          || event.key === ','
          || event.key === '.'
        ) {
          event.preventDefault();
        }
      }
    },
    updateInput(val) {
      this.$emit('update:value', val);
    },
  },
};
</script>
