<template>
  <div
    class="d-flex pa-2 flex-column"
    :style="{
      'margin-bottom': '40px',
      width: '80%',
      'min-height': '550px',
      height: 'min-content',
    }"
  >
    <div class="d-flex pa-5 flex-column">
      <h3>{{ title }}</h3>
      <p>{{ subtitle }}</p>

      <p v-if="caption">
        <v-chip
          v-if="caption.jenis_pengajuan"
          class="mr-2"
          outlined
          small
          color="primary"
        >
          {{ caption.jenis_pengajuan }}
        </v-chip>
        <v-chip
          v-if="caption.created_at"
          class="mr-2"
          outlined
          small
          color="primaryred"
        >
          <v-icon small left>
            mdi-calendar
          </v-icon>
          {{ caption.created_at }}
        </v-chip>
        <v-chip
          v-if="caption.reference_number"
          class="mr-2"
          outlined
          small
          color="primaryred"
        >
          Referrence No :
          {{ caption.reference_number }}
        </v-chip>
      </p>
    </div>

    <div v-if="loading" class="d-flex pa-5 flex-column">
      <v-progress-linear
        top
        v-if="loading"
        :active="loading"
        :indeterminate="loading"
        color="red lighten-2"
      ></v-progress-linear>
    </div>

    <v-card
      v-if="!loading"
      class="d-flex pa-2 flex-column"
      :style="{ margin: '0px 20px 40px 20px' }"
      elevation="2"
    >
      <slot name="content" />

      <div :style="{ padding: '20px 50px 20px 50px' }" v-if="!loading">
        <v-row v-if="!actionButton.hideButton">
          <v-col cols="4">
            <v-btn
              small
              color="primaryred"
              elevation="0"
              dark
              :style="{ margin: '2px' }"
              @click="back"
            >
              <v-icon dark left> mdi-arrow-left </v-icon>
              Back
            </v-btn>
          </v-col>

          <v-col cols="8" align="end">
            <v-btn
              v-if="actionButton.buttonPending"
              small
              color="yellow darken-3"
              elevation="0"
              outlined
              :style="{ margin: '2px' }"
              @click="pending"
            >
              <v-icon dark left> mdi-restore </v-icon>
              Pending
            </v-btn>

            <v-btn
              v-if="actionButton.buttonReject"
              small
              color="primaryred"
              elevation="0"
              outlined
              :style="{ margin: '2px' }"
              @click="reject"
            >
              <v-icon dark left> mdi-close </v-icon>
              Reject
            </v-btn>

            <v-btn
              v-if="actionButton.buttonApprove"
              small
              color="green darken-1"
              elevation="0"
              outlined
              :style="{ margin: '2px' }"
              @click="approve"
            >
              <v-icon dark left> mdi-check </v-icon>
              Approve
            </v-btn>

            <v-btn
              v-if="actionButton.buttonSave"
              small
              color="primaryred"
              elevation="0"
              outlined
              :style="{ margin: '2px' }"
              @click="save"
            >
              Save
            </v-btn>
            <v-btn
              v-if="actionButton.buttonRevisi"
              small
              color="primaryred"
              elevation="0"
              dark
              :style="{ margin: '2px' }"
              @click="revisi"
            >
              Revisi
            </v-btn>
          </v-col>
        </v-row>
      </div>
    </v-card>
  </div>
</template>

<script>
export default {
  props: ["loading", "title", "subtitle", "caption", "actionButton"],
  methods: {
    back() {
      this.$router.back();
    },
    pending() {
      this.$emit("pendingParent");
    },
    reject() {
      this.$emit("rejectParent");
    },
    approve() {
      this.$emit("approveParent");
    },
    save() {
      this.$emit("saveParent");
    },
    revisi() {
      this.$emit("revisiParent");
    },
  },
};
</script>
