<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <v-alert
      v-model="flashError"
      dense
      border="left"
      type="error"
      dismissible
      elevation="2"
    >
      {{ flashErrorMessage }}
    </v-alert>

    <table-list
      :headers="headers"
      :allData="allData"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :loading="loading"
      @update:options="updateOptions"
    >
      <template v-slot:filter>
        <filter-list
          :search="search"
          v-on:clearSearchParent="clearSearch"
          v-on:showDataParent="showData"
          :loading="loading"
        />
      </template>

      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:showDataParent="filterData"
          v-on:getDataParent="getData"
          v-on:exportItemParent="exportItem"
        />
      </template>

      <template v-slot:item="{ item, index }">
        <data-list
          :item="item"
          :index="index"
          :options="options"
          v-on:viewItemParent="viewItem"
          :selectedRow="selectedRow"
          v-on:selectRowParent="selectRow"
        />
      </template>

      <template v-slot:nodata>
        <v-btn outlined dark color="primaryred" @click="clearSearch">
          Reset
        </v-btn>
      </template>
    </table-list>

    <dialog-detail
      :loading="loading"
      :dialog="dialog"
      :editedItem="editedItem"
      :editedIndex="editedIndex"
      v-on:closeCancelParent="close"
    />
  </v-card>
</template>

<script>
import FilterList from '@/components/table/FilterReportMerchantList.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import TableList from '@/components/table/TableList.vue';
import DataList from '@/components/table/DataReportMerchantList.vue';
import DialogDetail from '@/components/dialog/DetailReportMerchant.vue';

export default {
  components: {
    FilterList, ActionButton, TableList, DataList, DialogDetail,
  },
  data: () => ({
    selectedRow: '',
    listButton: {
      buttonRestart: true,
      buttonDots: true,
      formDateRange: {
        label: 'Tanggal Transaksi',
      },
    },
    filter: {
      trx_datetime: [],
    },
    search: {
      bill_num: '',
      merchant_name: '',
      mid: '',
      mpan: '',
      rrn: '',
      qr_method: '',
      qr_type: '',
      trx_type: '',
      issuer_code: '',
      customer_pan: '',
      payment_method: '',
      customer_name: '',
      payment_type: '',
      payment_channel: '',
      trx_datetime: [],
      status: '',
      settlement_status: '',
      account_number: '',
    },
    searchDefault: {
      bill_num: '',
      merchant_name: '',
      mid: '',
      mpan: '',
      rrn: '',
      qr_method: '',
      qr_type: '',
      trx_type: '',
      issuer_code: '',
      customer_pan: '',
      payment_method: '',
      customer_name: '',
      payment_type: '',
      payment_channel: '',
      trx_datetime: [],
      status: '',
      settlement_status: '',
      account_number: '',
    },
    searchFix: {
      bill_num: '',
      merchant_name: '',
      mid: '',
      mpan: '',
      rrn: '',
      qr_method: '',
      qr_type: '',
      trx_type: '',
      issuer_code: '',
      customer_pan: '',
      payment_method: '',
      customer_name: '',
      payment_type: '',
      payment_channel: '',
      trx_datetime: [],
      status: '',
      settlement_status: '',
      account_number: '',
    },
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'trx_id',
        sortable: false,
      },
      {
        text: 'Merchant Name',
        value: 'merchant_name',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Bill Number',
        value: 'bill_num',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Nomor Rekening',
        value: 'account_number',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Transaction Date',
        value: 'trx_datetime',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Transaction Status',
        value: 'status',
        width: '100',
        sortable: false,
        align: 'center',
      },
      {
        text: 'MID',
        value: 'mid',
        width: '150',
        sortable: false,
        align: 'center',
      },
      {
        text: 'Merchant PAN',
        value: 'mpan',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Reff ID',
        value: 'rrn',
        width: '150',
        sortable: false,
        align: 'center',
      },
      {
        text: 'QR Method',
        value: 'qr_method',
        width: '100',
        align: 'center',
        sortable: false,
      },
      {
        text: 'QR Type',
        value: 'qr_type',
        width: '100',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Transaction Type',
        value: 'trx_type',
        width: '100',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Issuer Name',
        value: 'issuer_name',
        width: '100',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Customer PAN',
        value: 'customer_pan',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Customer Name',
        value: 'customer_name',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Payment Type',
        value: 'payment_type',
        width: '100',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Acquirer Name',
        value: 'payment_channel',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Amount',
        value: 'amount',
        width: '150',
        sortable: false,
        align: 'center',
      },
      {
        text: 'MDR',
        value: 'amount_mdr',
        width: '150',
        sortable: false,
        align: 'center',
      },
      {
        text: 'Net Amount',
        value: 'amount_net',
        width: '150',
        sortable: false,
        align: 'center',
      },

      { text: 'Action', value: 'actions', sortable: false },
    ],
    editedItem: {
      bill_num: '',
      merchant_name: '',
      mid: '',
      mpan: '',
      rrn: '',
      qr_method: '',
      qr_type: '',
      trx_type: '',
      customer_pan: '',
      payment_method: '',
      payment_type: '',
      payment_channel: '',
      amount: '',
      currency: '',
      amount_mdr: '',
      net_amount: '',
      trx_datetime: '',
      status: '',
      settlement_status: '',
    },
    editedIndex: -1,
    page: '1',
    options: {},
    dialog: false,
    isFirstLoad: true,
  }),
  beforeMount() {
    this.$store.commit('report/merchantTransaction/clearData');
  },
  computed: {
    flashErrorMessage() {
      return this.$store.state.report.merchantTransaction.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.report.merchantTransaction.flashError;
      },
      set() {
        this.$store.commit(
          'report/merchantTransaction/setFlashErrorMessage',
          '',
        );
      },
    },
    loading() {
      return this.$store.state.report.merchantTransaction.loading;
    },
    allData() {
      return this.$store.state.report.merchantTransaction.allData;
    },
    totalData() {
      return this.$store.state.report.merchantTransaction.totalData;
    },
    totalPage() {
      return this.$store.state.report.merchantTransaction.totalPage;
    },
    filterAllow() {
      if (this.search.trx_datetime.length < 1) {
        return true;
      }
      return false;
    },
  },
  methods: {
    selectRow(index) {
      if (this.selectedRow !== index) {
        this.selectedRow = index;
      } else {
        this.selectedRow = '';
      }
    },
    clearSearch() {
      this.search = { ...this.searchDefault };
      this.$nextTick(() => {
        this.$store.commit('report/merchantTransaction/clearData');
        this.showData();
      });
    },

    filterData(val) {
      val.sort();
      this.filter.trx_datetime = val;
      this.showData();
    },

    showData() {
      this.search.trx_datetime = this.filter.trx_datetime;
      this.searchFix = { ...this.search };
      this.getData();
    },

    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },

    getData() {
      if (this.filterAllow) {
        this.$vuetify.goTo(0);
        this.showAlert('tanggal transaksi is required !', 'mdi-alert', 'red');
        this.$store.commit('report/merchantTransaction/clearData');
      } else {
        this.$store.commit(
          'report/merchantTransaction/setFlashErrorMessage',
          '',
        );
        const {
          page, itemsPerPage, sortBy, sortDesc,
        } = this.options;

        this.$store
          .dispatch('report/merchantTransaction/getData', {
            page,
            itemsPerPage,
            sortBy,
            sortDesc,
            search: this.searchFix,
          })
          .then(() => {
            if (this.totalPage < page) {
              if (this.totalPage === 0) {
                this.options.page = 1;
              } else {
                this.options.page = this.totalPage;
              }
            }
          });
      }
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },

    viewItem(item) {
      const newItem = item;
      newItem.data = { id: item.id };
      this.editedIndex = this.$store.state.report.merchantTransaction.allData.indexOf(
        newItem,
      );
      this.editedItem = { ...newItem };
      this.dialog = true;
    },

    exportItem() {
      if (this.filterAllow) {
        this.$vuetify.goTo(0);
        this.showAlert('tanggal transaksi is required !', 'mdi-alert', 'red');
        this.$store.commit('report/merchantTransaction/clearData');
      } else if (this.totalData === 0) {
        this.$vuetify.goTo(0);
        this.showAlert('data tidak ditemukan !', 'mdi-alert', 'red');
        this.$store.commit('report/merchantTransaction/clearData');
      } else {
        const {
          page, itemsPerPage, sortBy, sortDesc,
        } = this.options;
        this.$store.dispatch('report/merchantTransaction/exportList', {
          page,
          itemsPerPage,
          sortBy,
          sortDesc,
          search: this.search,
        });
      }
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
