<template>
  <div>
    <v-row class="align-center">
      <v-col cols="10" sm="4"> {{ label }} </v-col>
      <v-col cols="2" sm="1"> : </v-col>
      <v-col cols="12" sm="7">
        <v-select
          :items="items"
          item-text="name"
          item-value="id"
          :value="value"
          :label="label"
          :rules="rule || [(v) => !!v || `${label} is required`]"
          @input="updateSelect($event)"
          return-object
        >
        </v-select>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: [
        Number,
        Object,
        Array,
        String,
      ],
    },
    label: { type: String },
    items: { type: Array },
    rule: {
      type: Array,
    },
  },
  methods: {
    updateSelect(val) {
      this.$emit('update:value', val);
    },
  },
};
</script>
