<template>
  <v-dialog content-class="otp-popup" v-model="dialog" width="400" persistent>
    <v-form ref="form">
      <v-card>
        <h5 v-if="title" class="pa-5 pb-0 title text-center">
          Inputkan kode OTP yang dikirimkan
        </h5>
        <h5 v-if="title" class="pb-5 title text-center">
          ke alamat email
        </h5>
        <v-card-text class="pt-3 row justify-center">
          <v-row justify="center" style="max-width: 350px;">
            <v-col cols="2" v-for="(digit, index) in otpDigits" :key="index">
              <v-text-field
                v-model="otpCode[index]"
                :maxlength="1"
                :rules="otpRules"
                outlined
                type="number"
                dense
                class="otp-input"
                :ref="`otpField${index}`"
                @input="nextInput(index)"
                @keydown="handleKeyDown(index, $event)"
                @paste="handlePaste"
              ></v-text-field>
            </v-col>
            <span id="eror-otp" style="color: #b71c1c;"></span>
          </v-row>
          <div class="text-center">
            <span
              >Belum menerima Kode OTP ?
              <button
                type="button"
                @click="resendOtp"
                :style="countdown == 0 ? 'text-decoration: underline' : ''"
                >{{ countdown > 0 ? formattedTime : "Kirim Ulang" }}</button
              ></span
            >
          </div>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            dark
            type="button"
            color="primaryred"
            outlined
            @click="closePopup"
          >
            Back
          </v-btn>
          <v-btn type="submit" color="primaryred" dark @click="confirm">
            Send
          </v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>

<script>
export default {
  props: {
    dialog: {
      type: Boolean,
    },
    title: {
      type: String,
    },
    countdown: {
      type: Number,
    },
  },
  data() {
    return {
      otpCode: Array(6).fill(''),
      otpDigits: [1, 2, 3, 4, 5, 6],
      otpRules: [
        (v) => !!v || '',
        (v) => (v && v.length === 1 && /^\d+$/.test(v)) || 'Invalid OTP',
      ],
      otp: null,
    };
  },
  watch: {
    dialog(val) {
      if (val) this.otpCode = Array(6).fill('');
      this.resetValidation();
    },
  },
  computed: {
    formattedTime() {
      const minutes = Math.floor(this.countdown / 60);
      const seconds = this.countdown % 60;
      return `${minutes < 10 ? '0' : ''}${minutes}:${
        seconds < 10 ? '0' : ''
      }${seconds}`;
    },
  },

  methods: {
    handlePaste(e) {
      e.preventDefault();
    },

    nextInput(index) {
      if (this.otpCode[index].length === 1 && index < this.otpCode.length - 1) {
        this.$refs[`otpField${index + 1}`][0].focus();
      }
    },
    handleKeyDown(index, event) {
      const input = event.target;

      if (input.type !== 'text' && input.type !== 'email') {
        this.handleNonTextAndNonEmailInput(event);
      }

      if (this.otpCode[index].length === 1 && event.key !== 'Backspace') {
        event.preventDefault();
      }

      if (event.key === 'Backspace' && this.otpCode[index] === '') {
        this.focusPreviousInput(index);
      }
    },

    handleNonTextAndNonEmailInput(event) {
      const { userAgent } = navigator;
      const keyCode = event.keyCode || event.which;
      const isValid = (keyCode >= 48 && keyCode <= 57) || keyCode === 8;
      const pattern = /^[^A-Za-z0-9\s]+/;
      const checkSpecial = pattern.test(event.key);

      if (userAgent.indexOf('Firefox') !== -1 && (!isValid || checkSpecial)) {
        event.preventDefault();
      }

      if (['e', 'E', '+', '-', ',', '.'].includes(event.key)) {
        event.preventDefault();
      }
    },

    focusPreviousInput(index) {
      if (index > 0) {
        this.$refs[`otpField${index - 1}`][0].focus();
      }
    },

    resendOtp() {
      if (this.countdown === 0) {
        this.otpCode = Array(6).fill('');
        this.$emit('resendOTP');
      }
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    closePopup() {
      this.resetValidation();
      this.otpCode = Array(6).fill('');
      this.$emit('closePopup');
    },
    confirm(e) {
      e.preventDefault();

      if (this.$refs.form.validate()) {
        this.$emit('confirmParent', this.otpCode.join(''));
      }
    },
  },
};
</script>

<style>
.otp-input input {
  text-align: center;
}
.v-application .title {
  font-size: 1.1rem !important;
}
</style>
