<template>
  <tr
    :key="index"
    :style="{ cursor: 'pointer' }"
    :class="selectedRow === index ? 'indigo lighten-5' : ''"
    @click="selectRow(index)"
  >
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ (options.page - 1) * options.itemsPerPage + index + 1 }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.mcc }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.category_name }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.mcc_name }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.mdr_on_us }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.mdr_off_us }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.created_at }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.updated_at }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-chip
        class="ma-2"
        :color="item.status == 1 ? 'green' : 'red'"
        text-color="white"
        x-small
      >
        {{ textStatus(item.status) }}
      </v-chip>
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-icon small class="mr-2" @click="editItem(item)">
        mdi-pencil
      </v-icon>
    </td>
  </tr>
</template>

<script>
import { listMccMdrStatus } from '@/store/params';

export default {
  props: {
    item: {
      type: Object,
    },
    index: {
      type: Number,
    },
    options: {
      type: Object,
    },
    selectedRow: {
      type: [Number, String],
    },
  },
  data: () => ({
    listMccMdrStatus,
  }),
  methods: {
    selectRow(index) {
      this.$emit('selectRowParent', index);
    },
    textStatus(status) {
      const index = this.listMccMdrStatus.find((val) => val.value === status);
      return index.text;
    },
    editItem(item) {
      this.$emit('editItemParent', item);
    },
    actItem(item) {
      this.$emit('actItemParent', item);
    },
  },
};
</script>
