import Parent from "../views/Parent.vue";

// import Bulk Upload Merchant
import bulkUpload from "@/views/bulk-upload-merchant/index.vue";

import { setBreadcrumb } from "./index.js";

export const bulkUploadMerchant = {
  path: "/bulk-upload-merchant",
  name: "Bulk Upload Merchant",
  meta: {
    title: "JAKONE - Bulk Upload Merchant",
    appBarTitle: "Bulk Upload Merchant",
  },
  component: Parent,
  children: [
    {
      path: "/",
      meta: {
        title: "JAKONE - Bulk Upload Merchant",
        appBarTitle: "Bulk Upload Merchant",
        breadcrumbs: () => {
          return [
            setBreadcrumb("Dashboard", "/", true),
            setBreadcrumb("Bulk Upload Merchant", "/Bulk Upload Merchant"),
          ];
        },
      },
      component: bulkUpload,
      name: "Bulk Upload Merchant",
    },
  ],
};
