<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <v-alert
      v-model="flashError"
      dense
      border="left"
      type="error"
      dismissible
      elevation="2"
    >
      {{ flashErrorMessage }}
    </v-alert>
    <table-list
      :headers="headers"
      :allData="allData"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :loading="loading"
      @update:options="updateOptions"
    >
      <template v-slot:filter>
        <filter-list
          :search="search"
          v-on:clearSearchParent="clearSearch"
          v-on:showDataParent="showData"
          :loading="loading"
          v-on:update-props="onUpdateProps()"
        />
      </template>

      <template v-slot:item="{ item, index }">
        <data-list
          :item="item"
          :index="index"
          :options="options"
          v-on:viewItemParent="viewItem"
        />
      </template>
    </table-list>
  </v-card>
</template>

<script>
import TableList from '@/components/table/TableList.vue';
import FilterList from '@/components/table/FilterInputerList.vue';
import DataList from '@/components/table/DataInputerList.vue';

export default {
  components: { TableList, FilterList, DataList },
  data: () => ({
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'id',
        sortable: false,
      },
      {
        text: 'Nama',
        value: 'merchant_name',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Type Merchant',
        value: 'merchant_type',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Jenis QR',
        width: '100',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Alamat Lengkap',
        value: 'address',
        width: '250',
        align: 'start',
        sortable: false,
      },
      {
        text: 'Klasifikasi Merchant',
        value: 'account_classification',
        width: '250',
        align: 'start',
        sortable: false,
      },
      {
        text: 'Nomor Pengajuan',
        value: 'reference_number',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Request Date',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Last Update',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Jenis Pengajuan',
        value: 'task_type',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Status',
        value: 'status',
        width: '100',
        align: 'center',
        sortable: false,
      },
      { text: 'Actions', value: 'actions', sortable: false },
    ],
    options: {},
    search: {
      merchant_name: '',
      account_type: '',
      qr_type: '',
      address: '',
      reference_number: '',
      created_at: '',
      updated_at: '',
      task_type: '',
      account_classification: '',
    },
    searchFix: {
      merchant_name: '',
      account_type: '',
      qr_type: '',
      address: '',
      reference_number: '',
      created_at: '',
      updated_at: '',
      task_type: '',
      account_classification: '',
    },
  }),
  beforeMount() {
    this.$store.commit('daftarInputer/ap/clearMerchants');
  },
  computed: {
    flashErrorMessage() {
      return this.$store.state.daftarInputer.ap.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.daftarInputer.ap.flashError;
      },
      set() {
        this.$store.commit('daftarInputer/ap/setFlashErrorMessage', '');
      },
    },
    loading() {
      return this.$store.state.daftarInputer.ap.loading;
    },
    allData() {
      return this.$store.state.daftarInputer.ap.allMerchants;
    },
    totalData() {
      return this.$store.state.daftarInputer.ap.totalMerchants;
    },
    totalPage() {
      return this.$store.state.daftarInputer.ap.totalPage;
    },
  },
  methods: {
    onUpdateProps() {
      this.search.merchant_name = '';
    },
    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },
    showData() {
      this.searchFix = JSON.parse(JSON.stringify(this.search));
      this.getData();
    },
    getData() {
      this.$store.commit('daftarInputer/ap/setFlashErrorMessage', '');
      const {
        page, itemsPerPage, sortBy, sortDesc,
      } = this.options;

      this.$store
        .dispatch('daftarInputer/ap/getData', {
          page,
          itemsPerPage,
          sortBy,
          sortDesc,
          search: this.searchFix,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },
    clearSearch() {
      this.search = { ...this.searchDefault };
      this.$nextTick(() => {
        this.$store.commit('daftarInputer/ap/clearMerchants');
        this.showData();
      });
    },
    viewItem(item) {
      this.$router.push(`/daftar-inputer/view/${item.id}`);
    },
  },
};
</script>
