import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';
import { intervalExport } from '@/store/params';

const users = {
  namespaced: true,

  state() {
    return {
      allUsers: [],
      totalUsers: 0,
      loading: false,
      loadingActiveUser: false,
      userCache: {},
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      allActiveUser: [],
      requestActiveUser: null,
      allActiveUserInputer: [],
      totalPage: 0,
      intervalExport: null,
      exportId: null,
      countDataExport: 0,
    };
  },

  mutations: {
    setUsers(state, { payload }) {
      state.allUsers = payload.data.list;
    },

    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },

    setCancelationUser(state, payload) {
      state.requestActiveUser = payload;
    },

    clearUsers(state) {
      state.allUsers = [];
      state.totalUsers = 0;
    },

    clearErrors(state) {
      state.errors = [];
    },

    setErrors(state, payload) {
      state.errors = payload;
    },

    setTotalUsers(state, { payload }) {
      state.totalUsers = parseInt(payload.data.countdata, 10);
      state.totalPage = parseInt(payload.data.totalPage, 10);
    },

    setLoading(state, loading) {
      state.loading = loading;
    },

    setLoadingActiveUser(state, loadingActiveUser) {
      state.loadingActiveUser = loadingActiveUser;
    },

    removeUser(state, { index }) {
      state.totalUsers -= 1;
      state.allUsers = state.allUsers.filter((value) => value.id !== index);
    },
    clearErrorVariable(state, variable) {
      state.errors[variable] = [];
    },
    addUser(state) {
      state.totalUsers += 1;
    },

    pushUser(state, { payload }) {
      state.allUsers.push(payload);
    },

    editUser(state, { payload, index }) {
      Object.assign(state.allUsers[index], payload);
    },

    setActiveUser(state, payload) {
      state.allActiveUser = payload;
    },

    setActiveUserInputer(state, payload) {
      state.allActiveUserInputer = payload;
    },

    setIntervalExport(state, payload) {
      state.intervalExport = payload;
    },

    setExportData(state, payload) {
      state.exportId = payload.export_id;
      state.countDataExport = payload.count_data;
    },

    setStateVariable(state, { payload, variable }) {
      state[variable] = payload;
    },
  },

  actions: {
    async fetchUsers(
      ctx,
      {
        date, page, itemsPerPage, sortBy, sortDesc, search,
      },
    ) {
      ctx.commit('clearUsers');
      ctx.commit('setLoading', true);
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        sortingBy = `${sortBy[0]} ${sort}`;
      } else {
        sortingBy = `id ${sort}`;
      }
      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString()
              + page.toString()
              + date
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/user/user/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setTotalUsers', {
              payload: response.data,
            });
            ctx.commit('setUsers', { payload: response.data });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },

    async createUser(
      ctx,
      {
        user, encryptedPassword, passwordConfirmation,
      },
    ) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        fullname: user.fullname,
        username: user.username,
        email: user.email,
        mobile_phone: user.mobile_phone,
        role: user.role,
        status: user.status,
        password: encryptedPassword,
        confirm_password: passwordConfirmation,
        version: btoa(
          CryptoJS.SHA256(
            user.fullname
              + user.username
              + user.email
              + user.mobile_phone
              + user.role
              + user.password
              + dateHelpers()
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/user/user/create`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const results = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            ctx.commit('setErrors', response.data.data);
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }
            return false;
          }
          const newUser = response.data.data;
          ctx.commit('pushUser', {
            payload: newUser,
          });
          ctx.commit('addUser');
          return true;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return false;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return results;
    },

    async updateUser(ctx, {
      user, index, password,
    }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        id: user.id.toString(),
        fullname: user.fullname,
        username: user.username,
        email: user.email,
        mobile_phone: user.mobile_phone,
        role: user.role,
        status: user.status,
        password,
        version: btoa(
          CryptoJS.SHA256(
            user.id.toString()
              + user.fullname
              + user.username
              + user.email
              + user.mobile_phone
              + user.role
              + dateHelpers()
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/user/user/update`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const results = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            ctx.commit('setErrors', response.data.data);
            if (response.data.code === 313) {
              return response.data.code;
            }
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }
            return false;
          }
          const updatedUser = response.data.data;
          ctx.commit('editUser', {
            payload: updatedUser,
            index,
          });
          return true;
        })
        .catch(() => false)
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return results;
    },

    async deleteUser(ctx, { date, user, index }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        id: user.id.toString(),
        version: btoa(
          CryptoJS.SHA256(
            user.id.toString()
              + date
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/user/user/delete`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const results = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }
            return false;
          }
          // const removedUser = response.data.data;
          ctx.commit('removeUser', {
            index,
          });
          return true;
        })
        .catch(() => {})
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return results;
    },

    async getActiveUser(ctx, {
      userID, inputer, all,
    }) {
      if (ctx.state.requestActiveUser) ctx.state.requestActiveUser.cancel();

      const axiosSource = axios.CancelToken.source();
      ctx.commit('setCancelationUser', {
        cancel: axiosSource.cancel,
        msg: 'Loading...',
      });

      ctx.commit('setLoadingActiveUser', true);

      const data = JSON.stringify({
        search: { userID },
        sort_by: 'userID asc',
        limit: '50',
        page: '1',
        inputer,
        all,
        version: btoa(
          CryptoJS.SHA256(
            `${'50'
              + '1'}${
              dateHelpers()
            }${atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))}`,
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/user/user/list-role`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
        cancelToken: axiosSource.token,
      };
      const doGetOut = () => ctx.dispatch('logout', null, { root: true });
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) doGetOut();
          } else if (inputer) {
            ctx.commit('setActiveUserInputer', response.data.data.list);
          } else {
            ctx.commit('setActiveUser', response.data.data.list);
          }
        })
        .catch((err) => {
          if (err.response !== undefined && err.response.data.code !== null) {
            if (err.response.data.code === 401) doGetOut();
          }
        })
        .finally(() => {
          ctx.commit('setLoadingActiveUser', false);
        });
    },

    async exportList(
      ctx,
      {
        date, page, itemsPerPage, sortBy, sortDesc, search,
      },
    ) {
      if (ctx.rootState.isExport === true) {
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Export in progress !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        return;
      }
      ctx.commit('setLoading', true);
      ctx.commit('setIsExport', true, { root: true });
      ctx.commit('setStateVariable', {
        payload: 0,
        variable: 'countSameExport',
      });
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        const q = sortBy[0] === 'name' ? 'merchant_name' : sortBy[0];
        sortingBy = `${q} ${sort}`;
      } else {
        sortingBy = `id ${sort}`;
      }
      const controller = () => 'export-user-list';

      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString()
              + page.toString()
              + date
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/export/${
            controller()
          }/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const failed = () => {
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Export Failed !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        ctx.commit(
          'setInsertProgressGlobal',
          {
            absoluteBar: false,
            absoluteBarText: '',
          },
          { root: true },
        );
        ctx.commit('setIsExport', false, { root: true });
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            failed();
          } else {
            if (response.data.data.message != null) {
              ctx.commit(
                'setSnackbar',
                {
                  status: true,
                  message: response.data.data.message,
                  icon: 'mdi-alert',
                  color: 'red',
                },
                { root: true },
              );
              ctx.commit('setIsExport', false, { root: true });
              return;
            }
            ctx.commit('setExportData', response.data.data);
            const interval = setInterval(() => {
              ctx.dispatch(
                'exportStatus',
                {
                  date,
                  exportId: ctx.state.exportId,
                  countDataExport: ctx.state.countDataExport,
                  controller: controller(),
                },
                { root: true },
              );
            }, intervalExport);
            ctx.commit('setIntervalExport', interval, { root: true });
            ctx.commit(
              'setInsertProgressGlobal',
              {
                absoluteBar: true,
                absoluteBarText: 'Start to export data..',
              },
              { root: true },
            );
          }
        })
        .catch((error) => {
          failed();
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },
  },
};

export default users;
