<template>
  <v-card style="min-height: 80vh;" class="py-5 px-10">
    <div class="d-flex flex-column justify-center" style="height: 100%;">
      <h1 class="text-center primaryred--text">
        Silahkan input nomor rekening tabungan giro!
      </h1>
      <v-form ref="form" @submit.prevent="next">
        <v-row justify="center">
          <v-col cols="4">
            <v-text-field
              type="number"
              v-model="data.account_number"
              @update="$emit('update:value', $event)"
              :rules="[v => !!v || 'This field is required']"
              label="Masukkan Nomor Rekening"
              @keydown="preventExponentAndSign"
            ></v-text-field>
          </v-col>
        </v-row>
        <Button :buttonClick="buttonClick" v-on:backParent="back" />
      </v-form>
    </div>
  </v-card>
</template>

<script>
import Button from './ButtonClick.vue';

export default {
  props: {
    data: {
      type: Object,
    },
  },
  components: {
    Button,
  },
  data() {
    return {
      buttonClick: {
        buttonBack: true,
        buttonLanjut: true,
      },
    };
  },
  methods: {
    preventExponentAndSign(event) {
      const input = event.target;

      if (input.type !== 'text') {
        const { userAgent } = navigator;

        const keyCode = event.keyCode || event.which;
        const isValid = (keyCode >= 48 && keyCode <= 57) || keyCode === 8;
        const pattern = /^[^A-Za-z0-9\s]+$/;
        const checkSpecial = pattern.test(event.key);

        if (userAgent.indexOf('Firefox') !== -1 && (!isValid || checkSpecial)) {
          event.preventDefault();
        }
        if (
          event.key === 'e'
          || event.key === 'E'
          || event.key === '+'
          || event.key === '-'
          || event.key === ','
          || event.key === '.'
        ) {
          event.preventDefault();
        }
      }
    },
    back() {
      this.$emit('backParent');
    },
    next() {
      if (this.$refs.form.validate()) {
        this.$emit('setLoading', true);
        this.$store
          .dispatch('client/addMerchant/getCorporateAccountNumber', {
            accountNumber: this.data.account_number,
          })
          .then((res) => {
            if (res.code === 302) {
              this.showAlert(res.data, 'mdi-alert', 'red');
            } else {
              this.$emit('nextParent', res, 'norek');
            }
            this.$emit('setLoading', false);
          })
          .catch(() => {
            this.$emit('setLoading', false);
            this.showAlert('Error Please Retry or Refresh', 'mdi-alert', 'red');
          })
          .finally(() => {
            this.$emit('setLoading', false);
          });
      }
    },
    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
          timeout: 10000,
        },
        { root: true },
      );
    },
  },
};
</script>
