<template>
  <tr
    :key="index"
    :style="{ cursor: 'pointer' }"
    :class="selectedRow === index ? 'indigo lighten-5' : ''"
    @click="selectRow(index)"
  >
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ (options.page - 1) * options.itemsPerPage + index + 1 }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.merchant_name }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.mid }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.nmid }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.address }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <div class="d-flex">
        <div
          class="d-flex elipsis"
          style="
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            max-width: 20ch;
          "
        >
          <p
            class="mr-1"
            :style="items.status === '0' ? 'color: red' : ''"
            v-for="(items, index) in item.account_classification"
            :key="items.id"
          >
            {{ items.nama_klasifikasi }}
            {{
              item.account_classification &&
              item.account_classification.length !== index + 1
                ? ","
                : ""
            }}
          </p>
        </div>
        {{ elipsis(item.account_classification) }}
      </div>
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.merchant_criteria }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.mobile_phone }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.qr_type }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.created_at }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.deactivated_at }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-chip
        class="ma-2"
        :color="item.status == 'active' ? 'green' : 'red'"
        text-color="white"
        x-small
      >
        {{ item.status }}
      </v-chip>
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-icon
        v-if="merchantType == 'outlet'"
        small
        class="mr-2"
        @click="viewItem(item)"
      >
        mdi-eye
      </v-icon>

      <v-menu
        v-if="merchantType == 'merchant'"
        offset-y
        rounded="lg"
        transition="slide-y-transition"
        bottom
        left
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            color="primaryred"
            class="mb-2"
            icon
            dark
            v-bind="attrs"
            v-on="on"
          >
            <v-icon light size="20">mdi-dots-horizontal</v-icon>
          </v-btn>
        </template>
        <v-list dense>
          <v-list-item link :to="`/client/merchant/view/${item.id}`">
            <v-list-item-icon>
              <v-icon>mdi-eye</v-icon>
            </v-list-item-icon>
            <v-list-item-title>View Merchant</v-list-item-title>
          </v-list-item>
          <v-list-item link @click="listItem(item)">
            <v-list-item-icon>
              <v-icon>mdi-format-list-bulleted</v-icon>
            </v-list-item-icon>
            <v-list-item-title>List Outlet</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </td>
  </tr>
</template>

<script>
import { elipsis } from '../../store/params';

export default {
  props: {
    item: {
      type: Object,
    },
    index: {
      type: Number,
    },
    options: {
      type: Object,
    },
    selectedData: {
      type: Array,
    },
    selectedRow: {
      type: [Number, String],
    },
    merchantType: {
      type: String,
    },
  },
  data: () => ({}),
  methods: {
    elipsis,
    selectRow(index) {
      this.$emit('selectRowParent', index);
    },
    listItem(item) {
      this.$emit('listItemParent', item);
    },
    viewItem(item) {
      this.$router.push({ path: `/client/merchant/view/${item.id}` });
    },
  },
};
</script>
