<!-- eslint-disable vue/no-mutating-props -->
<template>
  <tr>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.inputer_name"
        type="text"
        label="Inputer Name"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.merchant_name"
        type="text"
        label="Nama Merchant"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.reference_number"
        type="text"
        label="Reference No"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.mid"
        type="text"
        label="MID"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.task_type"
        :items="listTaskType"
        label="Jenis Pengajuan"
        item-text="text"
        item-value="value"
        @change="showData"
        @input="search.task_type = $event ? $event : ''"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.qr_type"
        :items="listQrType"
        label="Jenis QR"
        item-text="text"
        item-value="value"
        @change="showData"
        @input="search.qr_type = $event ? $event : ''"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-autocomplete
        clearable
        label="Pilih Klasifikasi"
        v-model="search.account_classification"
        :items="itemsKlasifikasi"
        item-text="nama_klasifikasi"
        item-value="id"
      />
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-menu
        ref="dateMenuLastUpdate"
        v-model="dateMenuLastUpdate"
        :close-on-content-click="false"
        transition="scale-transition"
        offset-y
        max-width="290px"
        min-width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="dateFormattedLastUpdate"
            label="Last Update"
            v-bind="attrs"
            @blur="search.updated_at = parseDate(dateFormattedLastUpdate)"
            v-on="on"
            hide-details="true"
            dense
            clearable
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="search.updated_at"
          no-title
          scrollable
          @input="dateMenuLastUpdate = false"
        >
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dateMenuLastUpdate = false">
            Cancel
          </v-btn>
        </v-date-picker>
      </v-menu>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.status"
        :items="listStatus"
        label="Status"
        item-text="text"
        item-value="value"
        @change="showData"
        @input="search.status = $event ? $event : ''"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
  </tr>
</template>

<script>
import {
  allQrType,
  listTrackingPengajuanStatus,
  delayServiceHit,
  taskType,
} from '@/store/params';

export default {
  props: {
    search: {
      type: Object,
    },
    loading: {
      type: Boolean,
    },
  },
  data: () => ({
    listTaskType: taskType,
    listQrType: allQrType,
    listStatus: listTrackingPengajuanStatus,
    rangeDateDialog: false,
    searchActiveUser: '',
    dateFormattedLastUpdate: '',
    dateMenuLastUpdate: false,
  }),
  computed: {
    loadingActiveUser() {
      return this.$store.state.users.loadingActiveUser;
    },
    itemsActiveUser() {
      return this.$store.state.users.allActiveUser;
    },
    allData() {
      return this.$store.state.klasifikasi.items;
    },
    itemsKlasifikasi() {
      return this.allData?.list || [];
    },
  },
  beforeMount() {
    const search = {
      page: 1,
      limit: 100,
      search: {
        created_date_start: '',
        created_date_end: '',
        name: '',
        status: 'ALL',
      },
    };
    this.$store
      .dispatch('klasifikasi/fetchData', {
        search,
      });
  },
  watch: {
    'search.account_classification': function (val) {
      if (!val) {
        this.search.account_classification = '';
      }
      this.showData();
    },
    'search.updated_at': {
      handler() {
        this.dateFormattedLastUpdate = this.formatDate(this.search.updated_at);
        this.showData();
      },
    },
    searchActiveUser: {
      handler() {
        clearTimeout(this.delayHitService);
        this.delayHitService = setTimeout(
          () => {
            this.getActiveUser();
          },
          delayServiceHit,
        );
      },
    },
  },
  methods: {
    formatDate(newDate) {
      if (!newDate) return null;

      const [year, month, day] = newDate.split('-');
      return `${day}/${month}/${year}`;
    },
    parseDate(newDate) {
      if (!newDate) return null;

      const [day, month, year] = newDate.split('/');
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`;
    },
    getActiveUser() {
      this.$store.dispatch('users/getActiveUser', {
        userID: this.searchActiveUser,
      });
    },
    clearSearch() {
      this.$emit('clearSearchParent');
    },
    showData() {
      this.$emit('showDataParent');
    },
  },
};
</script>
