<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div>
    <v-dialog v-model="dialog" width="500" persistent>
      <v-card>
        <v-card-title class="primaryred--text">
          {{ formTitle }}
        </v-card-title>
        <v-card-text>
          <v-form>
            <v-container>
              <v-row style="align-items: center">
                <v-col cols="10" sm="4"> Kode Bank </v-col>
                <v-col cols="2" sm="1"> : </v-col>
                <v-col cols="12" sm="7">
                  <v-text-field
                    id="bank_code"
                    ref="bank_code"
                    v-model="editedItem.bank_code"
                    @change="$v.editedItem.bank_code.$touch()"
                    @blur="$v.editedItem.bank_code.$touch()"
                    :error-messages="bankCodeErrors"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row style="align-items: center">
                <v-col cols="10" sm="4"> Kode NNS </v-col>
                <v-col cols="2" sm="1"> : </v-col>
                <v-col cols="12" sm="7">
                  <v-text-field
                    id="bank_nns"
                    ref="bank_nns"
                    v-model="editedItem.bank_nns"
                    @change="$v.editedItem.bank_nns.$touch()"
                    @blur="$v.editedItem.bank_nns.$touch()"
                    :error-messages="bankNnsErrors"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row style="align-items: center">
                <v-col cols="10" sm="4"> Nama PJSP </v-col>
                <v-col cols="2" sm="1"> : </v-col>
                <v-col cols="12" sm="7">
                  <v-text-field
                    id="bank_name"
                    ref="bank_name"
                    v-model="editedItem.bank_name"
                    @change="$v.editedItem.bank_name.$touch()"
                    @blur="$v.editedItem.bank_name.$touch()"
                    :error-messages="bankNameErrors"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row style="align-items: center">
                <v-col cols="10" sm="4"> Brand Name </v-col>
                <v-col cols="2" sm="1"> : </v-col>
                <v-col cols="12" sm="7">
                  <v-text-field
                    id="brand_name"
                    ref="brand_name"
                    v-model="editedItem.brand_name"
                    @change="$v.editedItem.brand_name.$touch()"
                    @blur="$v.editedItem.brand_name.$touch()"
                    :error-messages="brandNameErrors"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primaryred" outlined @click="cancelDialog">
            Cancel
          </v-btn>
          <v-btn
            color="primary"
            outlined
            @click="save"
            :disabled="loading"
            :loading="loading"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import {
  required,
  maxLength,
  minLength,
  numeric,
} from 'vuelidate/lib/validators';

export default {
  props: {
    loading: {
      type: Boolean,
    },
    editedItem: {
      type: Object,
    },
    dialog: {
      type: Boolean,
    },
    editedIndex: {
      type: Number,
    },
  },
  mixins: [validationMixin],
  validations: {
    editedItem: {
      bank_code: {
        required,
        numeric,
        maxLength: maxLength(4),
        minLength: minLength(3),
        isUnique() {
          if (this.errorValidation != null) {
            return (
              !this.errorValidation
              || !this.errorValidation.bank_code
              || this.errorValidation.bank_code.length < 1
            );
          }
          return false;
        },
      },
      bank_nns: {
        required,
        numeric,
        maxLength: maxLength(8),
        minLength() {
          if (this.editedItem.bank_nns.length > 0) {
            return this.editedItem.bank_nns.length === 8;
          }
          return true;
        },
        isUnique() {
          if (this.errorValidation != null) {
            return (
              this.errorValidation.bank_nns === null
              || this.errorValidation.bank_nns.length < 1
            );
          }
          return false;
        },
      },
      bank_name: {
        required,
      },
      brand_name: {
        required,
      },
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex !== -1 ? 'Edit NNS' : 'Add NNS';
    },
    flashError: {
      get() {
        return this.$store.state.nnsManagement.flashError;
      },
      set() {
        this.$store.commit('nnsManagement/setFlashErrorMessage', '');
      },
    },
    errorValidation() {
      return this.$store.state.nnsManagement.errors;
    },
    bankCodeErrors() {
      const errors = [];
      if (!this.$v.editedItem.bank_code.$dirty) return errors;
      if (!this.$v.editedItem.bank_code.numeric) {
        errors.push('Must be numeric');
      } else if (!this.$v.editedItem.bank_code.required) {
        errors.push('Bank Code is required');
      } else if (!this.$v.editedItem.bank_code.maxLength) {
        errors.push("Bank Code can't be more than 4 digits");
      } else if (!this.$v.editedItem.bank_code.minLength) {
        errors.push("Bank Code can't be less than 3 digits");
      }

      if (!this.$v.editedItem.bank_code.isUnique) {
        if (
          this.errorValidation != null
          && this.errorValidation.bank_code != null
        ) {
          this.errorValidation.bank_code.forEach((item) => {
            errors.push(item);
          });
        }
      }
      return errors;
    },
    bankNnsErrors() {
      const errors = [];
      if (!this.$v.editedItem.bank_nns.$dirty) return errors;
      if (!this.$v.editedItem.bank_nns.numeric) {
        errors.push('Must be numeric');
      } else if (!this.$v.editedItem.bank_nns.required) {
        errors.push('Bank NNS is required');
      } else if (!this.$v.editedItem.bank_nns.maxLength) {
        errors.push('Bank NNS must be 8 digits');
      } else if (!this.$v.editedItem.bank_nns.minLength) {
        errors.push('Bank NNS must be 8 digits');
      }

      if (!this.$v.editedItem.bank_nns.isUnique) {
        if (
          this.errorValidation != null
          && this.errorValidation.bank_nns != null
        ) {
          this.errorValidation.bank_nns.forEach((item) => {
            errors.push(item);
          });
        }
      }
      return errors;
    },
    bankNameErrors() {
      const errors = [];
      if (!this.$v.editedItem.bank_name.$dirty) return errors;
      if (!this.$v.editedItem.bank_name.required) {
        errors.push('Bank Name is required');
      }
      return errors;
    },
    brandNameErrors() {
      const errors = [];
      if (!this.$v.editedItem.brand_name.$dirty) return errors;
      if (!this.$v.editedItem.brand_name.required) {
        errors.push('Brand Name is required');
      }
      return errors;
    },
  },
  watch: {
    dialog(val) {
      if (!val) this.cancelDialog();
    },

    'editedItem.bank_code': {
      handler(val) {
        if (this.errorValidation != null) {
          if (
            this.errorValidation.bank_code != null
            && val !== this.errorValidation.bank_code
          ) {
            this.$store.commit('nnsManagement/clearErrorVariable', 'bank_code');
          }
        }
      },
    },
    'editedItem.bank_nns': {
      handler(val) {
        if (this.errorValidation != null) {
          if (
            this.errorValidation.bank_nns != null
            && val !== this.errorValidation.bank_nns
          ) {
            this.$store.commit('nnsManagement/clearErrorVariable', 'bank_nns');
          }
        }
      },
    },
    'editedItem.bank_name': {
      handler(val) {
        if (this.errorValidation != null) {
          if (
            this.errorValidation.bank_name != null
            && val !== this.errorValidation.bank_name
          ) {
            this.$store.commit('nnsManagement/clearErrorVariable', 'bank_name');
          }
        }
      },
    },
    'editedItem.brand_name': {
      handler(val) {
        if (this.errorValidation != null) {
          if (
            this.errorValidation.brand_name != null
            && val !== this.errorValidation.brand_name
          ) {
            this.$store.commit(
              'nnsManagement/clearErrorVariable',
              'brand_name',
            );
          }
        }
      },
    },
  },
  methods: {
    cancelDialog() {
      this.resetUniqueValidation();
      this.$emit('closeCancelParent');
    },
    resetUniqueValidation() {
      this.$store.commit('nnsManagement/clearErrors');
    },
    async save() {
      this.$store.commit('nnsManagement/setFlashErrorMessage', '');
      this.resetUniqueValidation();
      this.$v.$touch();
      if (!this.$v.$invalid) {
        const data = this.editedItem;
        if (this.editedIndex > -1) {
          await this.$store
            .dispatch('nnsManagement/updateNns', {
              dataForm: data,
              index: this.editedIndex,
            })
            .then((status) => {
              if (status) {
                this.cancelDialog();
                this.showAlert(
                  'Update NNS success !',
                  'mdi-check-circle',
                  'green',
                );
              } else {
                this.showAlert('Error on update NNS !', 'mdi-alert', 'red');
              }
            });
        } else {
          await this.$store
            .dispatch('nnsManagement/addNns', {
              dataForm: data,
            })
            .then((status) => {
              if (status) {
                this.cancelDialog();
                this.showAlert(
                  'Add NNS success !',
                  'mdi-check-circle',
                  'green',
                );
              } else {
                this.showAlert('Error on create NNS !', 'mdi-alert', 'red');
              }
            });
        }
      }
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
