<template>
  <v-chip
    color="success"
    outlined
    v-if="data.persentase === 100 && data.gagal === 0"
  >
    <v-icon left> mdi-check-circle-outline </v-icon>
    Completed</v-chip
  >
  <button-component
    v-else
    label="Download"
    icon="mdi-download"
    type="button"
    color="red text--white cursor-pointer"
    iconColor="dark"
    :disabled="data.persentase !== 100 ? true : false"
    :dark="data.persentase !== 100 ? false : true"
    v-on:clickButton="downloadFile(data)"
  />
</template>

<script>
import ButtonComponent from '@/components/button/ButtonComponent.vue';

export default {
  props: {
    data: {
      type: Object,
    },
  },
  components: {
    ButtonComponent,
  },
  methods: {
    downloadFile(data) {
      this.$store.dispatch('bulkUpload/downloadFailed', {
        historyId: data.history_id,
      });
    },
  },
};
</script>
