import Parent from "../views/Parent.vue";

// import client merchant
import InqueryMDR from "../views/log/InqueryMDR.vue";
import StatusRRN from "../views/log/StatusRRN";
import StatusBill from "../views/log/StatusBillNumber";
import TransactionNotif from "../views/log/TransactionNotif";
import JakOneMobile from "../views/log/JakOneMobile";

import { setBreadcrumb } from "./index.js";

export const log = {
  path: "/log",
  name: "Log",
  meta: {
    title: "JAKONE - Log",
    appBarTitle: "Log",
  },
  component: Parent,
  children: [
    {
      path: "/log/inquiry-mdr",
      meta: {
        title: "JAKONE - Inquiry MDR",
        appBarTitle: "Log Inquiry MDR",
        breadcrumbs: () => {
          return [
            setBreadcrumb("Log", "/log", true),
            setBreadcrumb("Inquiry MDR", "/log/inquiry-mdr"),
          ];
        },
      },
      component: InqueryMDR,
      name: "Inquiry MDR",
    },
    {
      path: "/log/status-rrn",
      meta: {
        title: "JAKONE - Log Check Status by RRN",
        appBarTitle: "Log Check Status by RRN",
        breadcrumbs: () => {
          return [
            setBreadcrumb("Log", "/log", true),
            setBreadcrumb("Check Status by RRN", "/log/status-rrn"),
          ];
        },
      },
      component: StatusRRN,
      name: "Status by RRN",
    },
    {
      path: "/log/transaction-notification",
      meta: {
        title: "JAKONE - Log Transaction Notification",
        appBarTitle: "Log Transaction Notification",
        breadcrumbs: () => {
          return [
            setBreadcrumb("Log", "/log", true),
            setBreadcrumb(
              "Transaction Notification",
              "/log/transaction-notification"
            ),
          ];
        },
      },
      component: TransactionNotif,
      name: "Log Transaction Notification",
    },
    {
      path: "/log/status-bill",
      meta: {
        title: "JAKONE - Log Check Status by Bill Number",
        appBarTitle: "Log Check Status by Bill Number",
        breadcrumbs: () => {
          return [
            setBreadcrumb("Log", "/log", true),
            setBreadcrumb("Check Status by Bill Number", "/log/status-bill"),
          ];
        },
      },
      component: StatusBill,
      name: "Log Check Status by Bill Number",
    },
    {
      path: "/log/notif-jo-mobile",
      meta: {
        title: "JAKONE - Log Notification JakOne Mobile",
        appBarTitle: "Log Notification JakOne Mobile",
        breadcrumbs: () => {
          return [
            setBreadcrumb("Log", "/log", true),
            setBreadcrumb(
              "Log Notification JakOne Mobile",
              "/log/notif-j-mobile"
            ),
          ];
        },
      },
      component: JakOneMobile,
      name: "Log Notification JakOne Mobile",
    },
  ],
};
