import axios from 'axios';
import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';

const klasifikasi = {
  namespaced: true,

  state() {
    return {
      items: null,
      totalPage: 0,
      flashErrorMessage: '',
      flashError: false,
      loading: false,
      errors: [],
    };
  },

  mutations: {
    setErrors(state, payload) {
      state.errors = payload;
    },
    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },
    setLoading(state, loading) {
      state.loading = loading;
    },
    setData(state, { payload }) {
      state.items = payload;
    },
    setTotalData(state, { payload }) {
      state.totalPage = parseInt(payload.totalPage, 10);
    },
    setClearData(state) {
      state.items = [];
    },
    clearErrors(state) {
      state.errors = [];
    },
  },

  actions: {
    async fetchData(ctx, { search }) {
      ctx.commit('setLoading', true);

      const data = {
        ...search,
        version: btoa(
          CryptoJS.SHA256(
            search.page.toString()
               + search.limit.toString()
                + dateHelpers()
                + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      };

      const config = {
        method: 'post',
        url:
            `${process.env.VUE_APP_API_BASE_URL
            }/index.php?r=api/v1/account/classification/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setTotalData', { payload: response.data.data });
            ctx.commit('setData', { payload: response.data.data });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },
    async addKlasifikasi(ctx, {
      data, edit,
    }) {
      ctx.commit('setLoading', true);
      const datas = {
        ...data,
        version: btoa(
          CryptoJS.SHA256(
            data.name
            + data.description
            + data.is_active
              + dateHelpers()
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      };
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/account/classification/${edit ? 'update' : 'create'}`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data: datas,
      };
      const results = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            ctx.commit('setErrors', response.data.data);
            if (response.data.code === 305) {
              return response.data.code;
            }
            if (response.data.code === 304 || response.data.code === 422) {
              return response.data;
            }
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }
            return false;
          }
          return true;
        })
        .catch(() => false)
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return results;
    },

  },
};
export default klasifikasi;
