<template>
  <div>
    <v-row>
      <v-col cols="10" sm="4"> {{ label }} </v-col>
      <v-col cols="2" sm="1"> : </v-col>
      <v-col cols="12" sm="7">
        <v-card @click="popUpImage" elevation="2">
          <v-img
            class="grey lighten-2"
            style="height: 300px;"
            :src="data.image"
            contain
          ></v-img>
        </v-card>
      </v-col>
    </v-row>
    <image-dialog
      :loading="loading"
      :dialog="dialog"
      :caution="data.image"
      v-on:closeCancelParent="close"
    />
  </div>
</template>

<script>
import ImageDialog from "@/components/dialog/ImageDialog.vue";
export default {
  props: {
    data: {
      type: Object,
    },
    label: {
      type: String,
    },
  },
  components: { ImageDialog },
  data: () => ({
    dialog: false,
  }),
  methods: {
    popUpImage() {
      this.dialog = true;
    },
    close() {
      this.dialog = false;
    },
  },
};
</script>
