<template>
  <tr
    :key="index"
    :style="{ cursor: 'pointer' }"
    :class="selectedRow === index ? 'indigo lighten-5' : ''"
    @click="selectRow(index)"
  >
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ (options.page - 1) * options.itemsPerPage + index + 1 }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.username }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.fullname }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.role }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.created_at }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.last_login_at }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-chip
        class="ma-2"
        :color="item.status === '1' ? 'green' : item.status === '0' ? 'red' : 'orange'"
        text-color="white"
        small
      >
        {{ item.status_label }}
      </v-chip>
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-icon small class="mr-2" @click="actItem(item, 'edit')">
        mdi-pencil
      </v-icon>
      <v-icon small @click="actItem(item, 'delete')"> mdi-delete </v-icon>
    </td>
  </tr>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
    },
    index: {
      type: Number,
    },
    options: {
      type: Object,
    },
    selectedRow: {
      type: [Number, String],
    },
  },
  data: () => ({}),
  methods: {
    selectRow(index) {
      this.$emit('selectRowParent', index);
    },
    actItem(item, type) {
      this.$emit('actItemParent', item, type);
    },
  },
};
</script>
