export const maskingWord = (inputString, length) => {
  if (!inputString) {
    return '( not set )';
  }
  const words = inputString.split(' ');

  const maskedWords = words.map((word) => {
    if (word.length <= length) {
      return word;
    }
    const firstThreeChars = word.substring(0, length);
    const remainingChars = '*'.repeat(word.length - length);
    return `${firstThreeChars}${remainingChars}`;
  });
  const maskedString = maskedWords.join(' ');

  return maskedString;
};

export const generatePassword = () => {
  const specialChars = '~!@#$%^&*()';
  const uppercaseChars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
  const lowerChars = 'abcdefhgijklmnopqrstuvwxyz';
  const numberChars = '0123456789';

  const allChars = specialChars + uppercaseChars + numberChars + lowerChars;

  let password = '';

  password += specialChars[Math.floor(Math.random() * specialChars.length)];
  password += lowerChars[Math.floor(Math.random() * lowerChars.length)];
  password += uppercaseChars[Math.floor(Math.random() * uppercaseChars.length)];
  password += numberChars[Math.floor(Math.random() * numberChars.length)];

  const remainingLength = 4;
  for (let i = 0; i < remainingLength; i += 1) {
    password += allChars[Math.floor(Math.random() * allChars.length)];
  }

  return password;
};

