export const qrByMerchantType = (type) => {
  let individu = [];
  let corporate = [];
  switch (type) {
    case '1':
      individu.push({ value: '1', text: 'QR Sticker' });
      corporate.push({ value: '1', text: 'QR Sticker' });
      break;
    case '2':
      break;
    case '3':
      break;
    case '4':
      individu.push({ value: '1', text: 'QR Sticker' });
      corporate.push({ value: '1', text: 'QR Sticker' });
      break;
    case '5':
      individu = allQrType;
      corporate = allQrType;
      break;
    case '6':
      individu.push({ value: '1', text: 'QR Sticker' });
      corporate.push({ value: '1', text: 'QR Sticker' });
      break;
    case '7':
      break;
    default:
      return qr_type;
  }

  return { individu, corporate };
};

export const qrIdToArray = (type) => {
  switch (type) {
    case '1':
      return ['1'];
    case '2':
      return ['2'];
    case '3':
      return ['3'];
    case '4':
      return ['1', '2'];
    case '5':
      return ['1', '2', '3'];
    case '6':
      return ['1', '3'];
    case '7':
      return ['2', '3'];
    default:
      return [];
  }
};

export const qrArrayToId = (arr) => {
  if (arr.length === 1) {
    return arr[0];
  }
  const sorted = arr.sort();
  if (isEqual(['1', '2'], sorted)) {
    return '4';
  } if (isEqual(['1', '2', '3'], sorted)) {
    return '5';
  } if (isEqual(['1', '3'], sorted)) {
    return '6';
  } if (isEqual(['2', '3'], sorted)) {
    return '7';
  }

  return '';
};

