export const elipsis = (val) => {
  if (val) {
    let character = 0;
    for (let i = 0; i < val.length; i += 1) {
      character += val[i].nama_klasifikasi.length;
    }
    if (character > 20) {
      return '...';
    }
  }
  return '';
};
