<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12">
        <v-card
          v-if="!validAccessKey"
          shaped
          class="elevation-0"
          :style="{ 'border-bottom-right-radius': '0px' }"
        >
          <v-toolbar flat>
            <v-toolbar-title>Lupa Password</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn icon large v-on="on">
                  <v-icon>mdi-domain</v-icon>
                </v-btn>
              </template>
              <span>JAKONE</span>
            </v-tooltip>
          </v-toolbar>
          <v-card-title
            v-if="loadingValidate"
            class="d-flex justify-center align-center"
            >Validating . . .</v-card-title
          >
          <v-card-text style="padding-bottom: 4px" v-if="!loadingValidate">
            <div class="grey--text text-justify">
              Untuk melakukan reset password silahkan masukkan Email Anda. Kami
              akan mengirimkan link reset password ke email yang Anda input
            </div>
            <v-form ref="form" lazy-validation v-on:submit.prevent="">
              <v-text-field
                :hint="hint"
                persistent-hint
                label="Email"
                name="email"
                prepend-icon="mdi-email"
                type="text"
                v-model="email"
                v-on:keyup.enter="reset"
                :error-messages="emailErrors"
                @change="$v.email.$touch()"
                @blur="$v.email.$touch()"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions
            v-if="!loadingValidate"
            style="
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            padding: 8px 16px 16px 16px;
                        "
          >
            <v-btn
              color="primaryred"
              @click="back"
              :disabled="loading"
              style="color: white"
              >back</v-btn
            >
            <v-btn
              color="blue-grey darken-2"
              style="color: white"
              @click="reset"
              :disabled="loading || timer > 1"
              :loading="loading"
              >Send</v-btn
            >
          </v-card-actions>
        </v-card>

        <v-card
          v-if="validAccessKey"
          shaped
          class="elevation-24"
          :style="{ 'border-bottom-right-radius': '0px' }"
        >
          <v-toolbar flat color="blue-grey darken-2" dark>
            <v-toolbar-title v-if="!passChanged">Lupa Password</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn icon large v-on="on">
                  <v-icon>mdi-domain</v-icon>
                </v-btn>
              </template>
              <span>JAKONE</span>
            </v-tooltip>
          </v-toolbar>
          <v-card-title style="padding-bottom: 0px" v-if="!passChanged"
            >Input Password Baru</v-card-title
          >
          <v-card-text v-if="passChanged"
            >Password berhasil diubah, silahkan klik link berikut untuk login ke
            portal admin. <a @click="back" @keydown.enter="back">klik disini</a></v-card-text
          >

          <v-card-text style="padding-bottom: 8px" v-if="!passChanged">
            <div class="grey--text">
              Silahkan masukkan pasword baru anda.
            </div>
            <v-form ref="form" lazy-validation>
              <v-text-field
                id="password"
                label="Password"
                name="password"
                prepend-icon="mdi-lock"
                v-model="password"
                v-on:keyup.enter="changePassword"
                :error-messages="passwordErrors"
                @change="$v.password.$touch()"
                @blur="$v.password.$touch()"
                @input="$v.password.$touch()"
                :append-icon="passwordFieldType ? 'mdi-eye-off' : 'mdi-eye'"
                @click:append="() => (passwordFieldType = !passwordFieldType)"
                :type="passwordFieldType ? 'password' : 'text'"
              ></v-text-field>
              <v-text-field
                id="passwordConfirmation"
                label="Password Confirmation"
                name="passwordConfirmation"
                prepend-icon="mdi-lock-check"
                v-model="passwordConfirmation"
                v-on:keyup.enter="changePassword"
                :error-messages="passwordConfirmationErrors"
                @change="$v.passwordConfirmation.$touch()"
                @blur="$v.passwordConfirmation.$touch()"
                @input="$v.passwordConfirmation.$touch()"
                :append-icon="
                  passwordConfirmFieldType ? 'mdi-eye-off' : 'mdi-eye'
                "
                @click:append="
                  () => (passwordConfirmFieldType = !passwordConfirmFieldType)
                "
                :type="passwordConfirmFieldType ? 'password' : 'text'"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions
            v-if="!passChanged"
            style="
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            padding: 4px 16px 16px 16px;
                        "
          >
            <v-btn
              color="primaryred"
              @click="back"
              :disabled="loading"
              style="color: white"
              >back</v-btn
            >
            <v-btn
              color="blue-grey darken-2"
              @click="changePassword"
              :loading="loading"
              style="color: white"
              >Reset</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { validationMixin } from 'vuelidate';
import { sameAs, email } from 'vuelidate/lib/validators';
import { resendEmail, extraString3 } from '@/store/params';
import CustomEncryption from '@/lib/customEncryption';

export default {
  name: 'ForgotPassword',
  props: {
    source: String,
  },
  mixins: [validationMixin],
  validations: {
    email: {
      required(value) {
        if (this.validAccessKey === false) {
          return value !== '';
        }
        return true;
      },
      email,
    },
    password: {
      required(value) {
        if (this.validAccessKey === true) {
          return value !== '';
        }
        return true;
      },
      minLength(value) {
        if (this.validAccessKey === true) {
          return value.length >= 8;
        }
        return true;
      },
      upperCase(value) {
        if (this.validAccessKey === true) {
          const pattern = /[A-Z]/;
          return !!pattern.test(value);
        }
        return true;
      },
      lowerCase(value) {
        if (this.validAccessKey === true) {
          const pattern = /[a-z]/;
          return !!pattern.test(value);
        }
        return true;
      },
      specialChar(value) {
        if (this.validAccessKey === true) {
          const pattern = /[!-/:-@[-`{-~]/;
          return !!pattern.test(value);
        }
        return true;
      },
      numeric(value) {
        if (this.validAccessKey === true) {
          const pattern = /\d/;
          return !!pattern.test(value);
        }
        return true;
      },
    },
    passwordConfirmation: {
      required(value) {
        if (this.validAccessKey === true) {
          return value !== '';
        }
        return true;
      },
      sameAsPassword: sameAs('password'),
    },
  },
  beforeCreate() {
    if (this.$store.state.authenticated) {
      this.$router.push({ name: 'Home' });
    }
  },
  mounted() {
    if (this.$route.name === 'ValidateForgotPassword') {
      if (
        this.$route.params.accessKey !== ''
        && this.$route.params.accessKey != null
      ) {
        this.checkAccessKey();
      }
    }
  },
  data() {
    return {
      loadingValidate: false,
      passwordFieldType: true,
      passwordConfirmFieldType: true,
      email: '',
      password: '',
      passwordConfirmation: '',
      validAccessKey: false,
      timer: 0,
      passChanged: false,
    };
  },
  computed: {
    loading() {
      return this.$store.state.loading;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) {
        return errors;
      }

      if (!this.$v.email.required) {
        errors.push('Email is required');
      }

      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) {
        return errors;
      }

      if (!this.$v.password.required) {
        errors.push('Password is required');
      }

      if (!this.$v.password.lowerCase) {
        errors.push('Password must have a lowercase character');
      }

      if (!this.$v.password.upperCase) {
        errors.push('Password must have an uppercase character');
      }

      if (!this.$v.password.numeric) {
        errors.push('Password must have a numeric character');
      }

      if (!this.$v.password.specialChar) {
        errors.push('Password must have a special character');
      }

      if (!this.$v.password.minLength) {
        errors.push('Password must be at least 8 characters long');
      }

      return errors;
    },
    passwordConfirmationErrors() {
      const errors = [];
      if (!this.$v.passwordConfirmation.$dirty) {
        return errors;
      }

      if (!this.$v.passwordConfirmation.required) {
        errors.push('Password Confirmation is required');
      }

      if (!this.$v.passwordConfirmation.sameAsPassword) {
        errors.push('Passwords must be identical');
      }
      return errors;
    },
    hint() {
      const second = (`0${(this.timer % 60).toString()}`).slice(-2);
      const minute = (`0${Math.floor(this.timer / 60).toString()}`).slice(-2);
      return this.timer === 0
        ? ''
        : `Kirim ulang reset email dalam ${minute}:${second}`;
    },
  },

  methods: {
    async reset() {
      this.$v.$touch();

      if (this.isValidToReset()) {
        const extras = this.decryptExtras();
        const response = await this.requestAuthKey(extras);

        if (response !== false && response != null) {
          await this.forgotPassword(extras);
        }
      }
    },

    isValidToReset() {
      return !this.$v.$invalid && !this.loading && this.timer < 1;
    },

    decryptExtras() {
      const speEncrypt = new CustomEncryption();
      return speEncrypt.decryption({
        enc: process.env.VUE_APP_STRING_EXTRA2,
        masterkey: extraString3,
      });
    },

    async requestAuthKey(extras) {
      const speEncrypt = new CustomEncryption();
      const encryptedRequest = speEncrypt.encrypt('', this.email, extras);
      return this.$store.dispatch('requestAuthKey', { encrypted_request: encryptedRequest });
    },

    async forgotPassword(extras) {
      const speEncrypt = new CustomEncryption();
      const encryptedEmail = speEncrypt.encrypt(speEncrypt.encryption(this.email, true), '', extras);
      const accessKey = speEncrypt.encrypt(localStorage.getItem('fp_qram_key'), '', extras);

      const res = await this.$store.dispatch('forgotPassword', {
        email: this.email,
        encrypted_email: encryptedEmail,
        access_key: accessKey,
      });

      if (res !== false && res != null) {
        this.startTimer();
      }

      this.$store.commit('setLoading', false);
    },

    startTimer() {
      this.timer = resendEmail == null ? 300 : resendEmail;
      this.interval = setInterval(() => {
        this.timer -= 1;
        if (this.timer === 0) {
          clearInterval(this.interval);
        }
      }, 1000);
    },

    async checkAccessKey() {
      this.loadingValidate = true;
      const speEncrypt = new CustomEncryption();
      const extras = speEncrypt.decryption({
        enc: process.env.VUE_APP_STRING_EXTRA2,
        masterkey: extraString3,
      });
      let accessKey = this.$route.params.accessKey.replace('-', '+');
      accessKey = accessKey.replace('_', '/');
      let idEncrypted = this.$route.params.idEncrypted.replace('-', '+');
      idEncrypted = idEncrypted.replace('_', '/');
      const authkey = speEncrypt.decrypt(atob(accessKey), true, true, extras);
      if (authkey === false) {
        this.$router.push('/login');
      } else {
        await this.$store
          .dispatch('validateForgotPassword', {
            access_key: speEncrypt.encrypt(authkey, '', extras),
            id: speEncrypt.decrypt(atob(idEncrypted), false, false, extras),
            id_encrypted: speEncrypt.encrypt(
              speEncrypt.decrypt(atob(idEncrypted), false, false, extras),
              '',
              extras,
            ),
          })
          .then((res) => {
            if (res) {
              this.validAccessKey = true;
            } else {
              this.$router.push('/login');
            }
          });
      }
      this.loadingValidate = false;
    },

    async changePassword() {
      this.$v.$touch();
      if (!this.$v.$invalid && !this.loading) {
        let idEncrypted = this.$route.params.idEncrypted.replace('-', '+');
        idEncrypted = idEncrypted.replace('_', '/');
        const speEncrypt = new CustomEncryption();
        let accessKey = this.$route.params.accessKey.replace('-', '+');
        accessKey = accessKey.replace('_', '/');
        const extras = speEncrypt.decryption({
          enc: process.env.VUE_APP_STRING_EXTRA2,
          masterkey: extraString3,
        });
        const authkey = speEncrypt.decrypt(atob(accessKey), false, false, extras);
        this.$store
          .dispatch('changePassword', {
            password: this.password,
            passwordConfirmation: this.passwordConfirmation,
            encryptedPassword: speEncrypt.encrypt(
              speEncrypt.encryption(this.password, true),
              '',
              extras,
            ),
            encryptedPasswordConfirmation: speEncrypt.encrypt(
              speEncrypt.encryption(this.passwordConfirmation, true),
              '',
              extras,
            ),
            accessKey: speEncrypt.encrypt(authkey, '', extras),
            id: speEncrypt.decrypt(atob(idEncrypted), false, false, extras),
            idEncrypted: speEncrypt.encrypt(
              speEncrypt.decrypt(atob(idEncrypted), false, false, extras),
              '',
              extras,
            ),
          })
          .then((res) => {
            if (res !== false) {
              this.passChanged = true;
              localStorage.removeItem('fp_qram_key');
            }
          });
      }
    },

    back() {
      this.$router.push('/login');
    },
  },
};
</script>

<style scoped>
.border-top {
  border-radius: 24px 0px 0px 0px;
}
.header-logo {
  align-self: flex-start;
  position: absolute;
  left: 0;
  top: 0;
}
</style>
