<template>
  <div>
    <v-card
      class="mb-12"
      height="20px"
      :style="{ 'box-shadow': '0 0 black', 'margin-top': '20px' }"
    >
      <div class="d-flex pa-5 flex-column align-center justify-center">
        <h3>Info {{ merchantType }}</h3>
      </div>
    </v-card>
    <v-form
      @submit.prevent="submitForm"
      :style="{ padding: '20px 50px 20px 50px' }"
    >
      <data-detail-row
        v-if="showMenu == 1"
        :oldValueData="false"
        :data="data"
        :label="'MPAN'"
        :valueData="data.mpan"
      />
      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'Jenis QR'"
        :valueData="data.qr_type.name || data.qr_type"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Nomor Rekening'"
        :valueData="data.account_number"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Nama ' + merchantType"
        :valueData="data.merchant_name"
      />

      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'Nama Perusahaan'"
        :valueData="masking(data.company_name)"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Nama Pemilik Usaha'"
        :valueData="data.owner_name"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Nomor KTP Pemilik Usaha'"
        :valueData="text(data.ktp_no)"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="
          accountType == 'Corporate'
            ? 'Nomor NPWP Perusahaan'
            : 'Nomor NPWP Pemilik Usaha'
        "
        :valueData="text(data.npwp_no)"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Email'"
        :valueData="data.email"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'No Handphone Pemilik Usaha'"
        :valueData="data.mobile_phone"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'No Handphone ' + merchantType"
        :valueData="text(data.merchant_mobile_phone)"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'No Telepon Perusahaan'"
        :valueData="merchantPhone == ' ' ? '( not set )' : merchantPhone"
      />
      <!-- beda data tracing dan client -->
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Kriteria ' + merchantType"
        :valueData="data.merchant_criteria.name || data.merchant_criteria"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Jenis Usaha'"
        :valueData="data.mcc_name || data.mcc.name"
      />
      <!-- ============================= -->
      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'MDR On-Us'"
        :valueData="data.mdr_on_us + '%'"
      />
      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'MDR Off-Us'"
        :valueData="data.mdr_off_us + '%'"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Alamat ' + merchantType"
        :valueData="data.address"
      />

      <draw-google-map :valueData="data.gmaps" :label="'Lokasi Alamat'" />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Kota, Kecamatan, Kode Pos'"
        :valueData="data.area"
      />

      <LokasiUsaha label="Foto Lokasi Usaha" :data="data" />

      <content-data-endpoint
        v-if="data.qr_type.id == 2"
        :merchantType="'Merchant'"
        :data="data"
      />

      <v-row>
        <v-col cols="4">
          <v-btn
            small
            color="primaryred"
            elevation="0"
            dark
            :style="{ margin: '2px' }"
            @click="back"
          >
            <v-icon dark left> mdi-arrow-left </v-icon>
            Back
          </v-btn>
        </v-col>
        <v-col cols="8" align="end">
          <v-btn
            small
            color="primaryred"
            elevation="0"
            outlined
            :style="{ margin: '2px' }"
            type="submit"
          >
            Submit
          </v-btn>
        </v-col>
      </v-row>
    </v-form>
  </div>
</template>

<script>
import DataDetailRow from '@/components/detail/DataDetailRow.vue';
import DrawGoogleMap from '@/components/detail/DrawGoogleMap.vue';
import LokasiUsaha from '@/components/detail/LokasiPreview.vue';
import { text } from '@/lib/helpers';
import { maskingWord } from '@/store/params';
import ContentDataEndpoint from '@/components/detail/ContentDataEndpoint.vue';

export default {
  props: {
    merchantType: {
      type: String,
    },
    menu: {
      type: String,
    },
    data: {
      type: [Object, Array],
    },
  },
  components: {
    DataDetailRow,
    DrawGoogleMap,
    LokasiUsaha,
    ContentDataEndpoint,
  },
  data() {
    return {
      center: { lat: -7.797068, lng: 110.370529, label: 'KENDAL' },
    };
  },
  computed: {
    showMenu() {
      let show;
      switch (this.menu) {
        case 'client':
          show = 1;
          break;
        case 'tracking':
          show = 2;
          break;
        case 'approval':
          show = 3;
          break;
        default:
          show = 0;
      }

      return show;
    },
    accountType() {
      return this.data != null ? this.data.account_type : null;
    },
    merchantPhone() {
      const area = this.data.phone_area_code !== '' && this.data.phone_area_code != null
        ? `(${this.data.phone_area_code})`
        : '';
      const phone = this.data.phone != null ? this.data.phone : '';
      return `${area} ${phone}`;
    },
  },
  methods: {
    masking(val) {
      return maskingWord(val, 3);
    },
    submitForm() {
      this.$emit('submitParent');
    },

    back() {
      this.$emit('backParent', this.data);
    },
    text,
  },
};
</script>
