import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';

const mccmdr = {
  namespaced: true,

  state() {
    return {
      allData: [],
      totalData: 0,
      loading: false,
      cacheData: {},
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      allCategory: [],
      totalPage: 0,
    };
  },

  mutations: {
    setData(state, { payload }) {
      state.allData = payload.data.list;
    },

    setCategory(state, data) {
      state.allCategory = data;
    },

    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },

    clearData(state) {
      state.allData = [];
      state.totalData = 0;
    },

    clearErrors(state) {
      state.errors = [];
    },

    setErrors(state, payload) {
      state.errors = payload;
    },

    setTotalData(state, { payload }) {
      state.totalData = parseInt(payload.data.countdata, 10);
      state.totalPage = parseInt(payload.data.totalPage, 10);
    },

    setLoading(state, loading) {
      state.loading = loading;
    },

    removeSingleData(state, { index }) {
      state.totalData -= 1;
      state.allData = state.allData.filter((value) => value.mcc !== index);
    },

    addData(state) {
      state.totalData += 1;
    },

    pushData(state, { payload }) {
      state.allData.push(payload);
    },

    editData(state, { payload, index }) {
      Object.assign(state.allData[index], payload);
    },
    clearErrorVariable(state, variable) {
      state.errors[variable] = [];
    },
  },

  actions: {
    fetchAllCategory(ctx) {
      ctx.commit('setCategory', [
        {
          id: '1',
          name: 'Reguler',
        },
        {
          id: '2',
          name: 'Khusus',
        },
      ]);
    },

    async getData(
      ctx,
      {
        date, page, itemsPerPage, sortBy, sortDesc, search,
      },
    ) {
      ctx.commit('clearData');
      ctx.commit('setLoading', true);
      const sort = !sortDesc[0] ? 'ASC' : 'DESC';
      const sortingBy = `${sortBy[0] ? sortBy[0] : 'mcc'} ${sort}`;

      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            page.toString()
                        + itemsPerPage.toString()
                        + date
                        + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
                    `${process.env.VUE_APP_API_BASE_URL
                    }/index.php?r=api/v1/setting-fee/jenis-usaha/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const doGetOut = () => ctx.dispatch('logout', null, { root: true });

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }

            if (response.data.code === 401) doGetOut();
          } else {
            ctx.commit('setTotalData', { payload: response.data });
            ctx.commit('setData', { payload: response.data });
          }
        })
        .catch((err) => {
          if (err.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: err.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (err.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },

    async createData(ctx, { value }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        mcc: value.mcc,
        description: value.description,
        category_id: value.category_id,
        mcc_name: value.mcc_name,
        mdr_on_us: value.mdr_on_us,
        mdr_off_us: value.mdr_off_us,
        status: value.status,
        version: btoa(
          CryptoJS.SHA256(
            value.mcc
                        + value.description
                        + value.category_id
                        + value.mcc_name
                        + value.mdr_on_us
                        + value.mdr_off_us
                        + value.status
                        + dateHelpers()
                        + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
                    `${process.env.VUE_APP_API_BASE_URL
                    }/index.php?r=api/v1/setting-fee/jenis-usaha/create`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            ctx.commit('setErrors', response.data.data);
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }
            return false;
          }
          const newData = response.data.data;
          ctx.commit('pushData', {
            payload: newData,
          });
          ctx.commit('addData');
          return true;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return false;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async updateData(ctx, { body, index }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        id: body.id,
        mcc: body.mcc,
        description: body.description,
        category_id: body.category_id,
        mcc_name: body.mcc_name,
        mdr_on_us: body.mdr_on_us,
        mdr_off_us: body.mdr_off_us,
        status: body.status,
        version: btoa(
          CryptoJS.SHA256(
            body.id
                        + body.mcc
                        + body.description
                        + body.category_id
                        + body.mcc_name
                        + body.mdr_on_us
                        + body.mdr_off_us
                        + body.status
                        + dateHelpers()
                        + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
                    `${process.env.VUE_APP_API_BASE_URL
                    }/index.php?r=api/v1/setting-fee/jenis-usaha/update`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            ctx.commit('setErrors', response.data.data);
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }
            return false;
          }
          const updatedData = response.data.data.data;
          ctx.commit('editData', {
            payload: updatedData,
            index,
          });
          return true;
        })
        .catch(() => false)
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async deleteData(ctx, { date, body, index }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        mcc: body.mcc.toString(),
        version: btoa(
          CryptoJS.SHA256(
            body.mcc.toString()
                        + date
                        + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
                    `${process.env.VUE_APP_API_BASE_URL
                    }/index.php?r=api/v1/setting-fee/jenis-usaha/deactivated`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }
            return false;
          }
          const updatedData = response.data.data.data;
          ctx.commit('editData', {
            payload: updatedData,
            index,
          });
          return true;
        })
        .catch(() => false)
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },
  },
};

export default mccmdr;
