<template>
  <div>
    <v-card
      class="mb-12"
      height="20px"
      :style="{ 'box-shadow': '0 0 black', 'margin-top': '20px' }"
    >
      <div class="d-flex pa-5 flex-column align-center justify-center">
        <h3>Info {{ merchantType }}</h3>
      </div>
    </v-card>
    <v-form
      ref="form"
      @submit.prevent="submitForm"
      :style="{ padding: '20px 50px 20px 50px' }"
    >
      <input-data-row
        v-if="showMenu == 1"
        :oldValueData="false"
        :data="data"
        :label="'MPAN'"
        v-model="data.mpan"
      />
      <data-detail-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'Tipe ' + merchantType"
        :valueData="
          accountType == 'Corporate'
            ? 'Badan Usaha'
            : data.account_type.name
            ? data.account_type.name
            : data.account_type
        "
      />
      <select-data-row
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :items="list_qr_type"
        :label="'Jenis QR'"
        :value="data.qr_type"
        @update:value="(newValue) => (data.qr_type = newValue)"
      />
      <input-data-row
        type="number"
        :oldValueData="false"
        :data="data"
        :label="'Nomor Rekening'"
        :value="data.account_number"
        @update:value="(newValue) => (data.account_number = newValue)"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Nama Perusahaan'"
        :valueData="masking(data.company_name)"
      />
      <input-data-row
        :oldValueData="false"
        :data="data"
        :label="'Nama ' + merchantType"
        :value="data.merchant_name"
        :rule="[validation.pengkinianData]"
        @update:value="(newValue) => (data.merchant_name = newValue)"
      />
      <input-data-row
        :oldValueData="false"
        :data="data"
        :label="'Nama Pemilik Usaha'"
        :value="data.owner_name"
        :rule="[validation.pengkinianData]"
        @update:value="(newValue) => (data.owner_name = newValue)"
        :disabled="true"
      />
      <input-data-row
        :oldValueData="false"
        :data="data"
        :label="'Nomor KTP Pemilik Usaha'"
        :value="data.ktp_no"
        :rule="[validation.pengkinianData]"
        type="number"
        @update:value="(newValue) => (data.ktp_no = newValue)"
        :disabled="true"
      />
      <input-data-row
        :oldValueData="false"
        :data="data"
        :label="
          accountType == 'Corporate'
            ? 'Nomor NPWP Perusahaan'
            : 'Nomor NPWP Pemilik Usaha'
        "
        :rule="[validation.pengkinianData]"
        :value="data.npwp_no"
        type="number"
        @update:value="(newValue) => (data.npwp_no = newValue)"
        :disabled="true"
      />
      <data-detail-row
        :oldValueData="false"
        :data="data"
        :label="'Email'"
        :rule="[validation.pengkinianData]"
        :valueData="data.email"
      />
      <input-data-row
        type="number"
        :oldValueData="false"
        :data="data"
        :label="'No Handphone Pemilik Usaha'"
        :value="data.mobile_phone"
        :rule="[
          (v) => validation.required(v, 'No Handphone Pemilik Usaha'),
          (v) => validation.required(v, 'No Handphone Pemilik Usaha', 15),
        ]"
        @update:value="(newValue) => (data.mobile_phone = newValue)"
      />
      <input-data-row
        type="number"
        :oldValueData="false"
        :data="data"
        :label="'No Handphone ' + merchantType"
        :value="data.merchant_mobile_phone"
        :rule="[
          (v) => validation.required(v, `No Handphone + ${merchantType}`),
          (v) => validation.required(v, `No Handphone + ${merchantType}`, 15),
        ]"
        @update:value="(newValue) => (data.merchant_mobile_phone = newValue)"
      />
      <input-data-row
        type="number"
        :oldValueData="false"
        :data="data"
        :label="
          accountType == 'Corporate'
            ? 'No Telepon Perusahaan'
            : 'No Telepon ' + merchantType
        "
        :value="data.phone"
        :rule="[
          (v) => validation.required(v, 'No Telepon'),
          (v) => validation.required(v, 'No Telepon', 15),
        ]"
        @update:value="(newValue) => (data.phone = newValue)"
      />

      <select-data-row
        :data="data"
        :items="criteria_merchant"
        :label="'Kriteria Merchant'"
        :value="data.merchant_criteria"
        @update:value="(newValue) => (data.merchant_criteria = newValue)"
      />
      <select-data-row
        v-if="data.merchant_criteria"
        :data="data"
        :items="jenis_usaha"
        :label="'Jenis Usaha'"
        :value="data.mcc"
        @update:value="(newValue) => (data.mcc = newValue)"
      />
      <input-data-row
        :disabled="true"
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'MDR On-Us'"
        :value="data.mdr_on_us"
      />
      <input-data-row
        :disabled="true"
        v-if="showMenu"
        :oldValueData="false"
        :data="data"
        :label="'MDR Off-Us'"
        :value="data.mdr_off_us"
      />

      <vue-select-search
        :data="data"
        label="text"
        :value="data.area"
        @update:value="areaChange"
        :searchable="true"
        :error-messages="errorMessage.area"
        :placeholder="'Kota, Kecamatan, Kode Pos'"
      >
        <template v-slot:content>
          <p
            :class="errorMessage.area.length > 0 ? 'error--text' : ''"
            v-if="errorMessage.area.length > 0"
            style="color: #00ff00; margin-bottom: 5px"
          >
            {{ errorMessage.area[0] }}
          </p>
        </template>
      </vue-select-search>

      <draw-google-map
        :edit="true"
        :value="data.gmaps"
        :kodeposLocate="kodeposLocate"
        :label="'Lokasi Alamat'"
        @update:value="(newValue) => (data.gmaps = newValue)"
      />

      <input-data-row
        :oldValueData="false"
        :data="data"
        :label="'Alamat ' + merchantType"
        :value="data.address"
        @update:value="(newValue) => (data.address = newValue)"
      />
      <select-search
        :oldValueData="false"
        :data="data"
        :items="itemsKlasifikasi"
        :label="'Klasifikasi Merchant'"
        :value="data.account_classification"
        @update:value="(newValue) => (data.account_classification = newValue)"
        :note="noteMessage"
      />
      <input-data-row
        v-if="showMenu == 2"
        :oldValueData="false"
        :data="data"
        :label="'Referral ID'"
        :value="data.referal_code"
        @update:value="(newValue) => (data.referal_code = newValue)"
      />
      <content-data-doc
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
        :fileRule="[
          (v) => validation.required(v, 'Foto Lokasi Usaha'),
          validation.imageOnly,
          (v) => validation.sizeImage(v, 15),
        ]"
        accept="image/*"
        @errorFromImages="handleImage"
        subtitle="Mohon upload foto diri Anda didepan lokasi usaha anda"
        @update:value="updateImage"
      >
        <template v-slot:content>
          <span>
            <small>
              *Hanya file png, jpg, jpeg yang diizinkan untuk File Foto & Max
              Size 15mb
            </small>
          </span>
        </template>
      </content-data-doc>

      <content-data-endpoint
        v-if="data.qr_type.id == 2"
        :merchantType="'Merchant'"
        :data="data"
      />

      <content-data-remark
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
        @update:value="(newValue) => (data.remark = newValue)"
      >
      </content-data-remark>
      <v-row>
        <v-col cols="4">
          <v-btn
            small
            color="primaryred"
            elevation="0"
            dark
            :style="{ margin: '2px' }"
            @click="back"
          >
            <v-icon dark left> mdi-arrow-left </v-icon>
            Back
          </v-btn>
        </v-col>
        <v-col cols="8" align="end">
          <v-btn
            small
            color="primaryred"
            elevation="0"
            outlined
            :style="{ margin: '2px' }"
            type="submit"
          >
            Save
          </v-btn>
        </v-col>
      </v-row>
    </v-form>
  </div>
</template>

<script>
import {
  listMerchantEdit,
  listQrEdit,
  listMerchantCriteriaEdit,
  maskingWord,
} from '@/store/params';
import InputDataRow from '@/components/detail/InputDataDetailRow.vue';
import SelectDataRow from '@/components/detail/SelectDataDetailRow.vue';
import DrawGoogleMap from '@/components/detail/EditGoogleMap.vue';
import { text } from '@/lib/helpers';
import * as ValidationHelpers from '@/lib/ValidationHelpers';
import ContentDataDoc from '@/components/detail/ContentDataDocEdit.vue';
import ContentDataRemark from '@/components/detail/ContentDataRemarkEdit.vue';
import ContentDataEndpoint from '@/components/detail/ContentDataEndpointEdit.vue';
import DataDetailRow from '@/components/detail/DataDetailRow.vue';
import VueSelectSearch from '@/components/detail/VueSelectSearch.vue';
import SelectSearch from '@/components/detail/SelectSearchDetailRow.vue';

export default {
  props: {
    data: {
      type: Object,
    },
    merchantType: {
      type: String,
    },
    menu: {
      type: String,
    },
  },
  components: {
    InputDataRow,
    DrawGoogleMap,
    SelectDataRow,
    ContentDataDoc,
    ContentDataEndpoint,
    ContentDataRemark,
    DataDetailRow,
    VueSelectSearch,
    SelectSearch,
  },
  beforeMount() {
    this.getCriteriaMerchant();
    const search = {
      page: 1,
      limit: 100,
      search: {
        created_date_start: '',
        created_date_end: '',
        name: '',
        status: 'ACTIVE',
      },
    };
    this.$store.dispatch('klasifikasi/fetchData', {
      search,
    });
  },
  data() {
    return {
      imageValid: {
        status: true,
      },
      kodeposLocate: null,
      errorMessage: {
        area: [],
      },
      listMerchantCriteriaEdit,
      listMerchantEdit,
      list_qr_type: listQrEdit,
    };
  },
  computed: {
    validation() {
      return ValidationHelpers;
    },
    noteMessage() {
      if (this.data.status === 'New') {
        return '*Klasifikasi merchant yang dapat dipilih maksimal adalah 10 data, lakukan un-checklist apabila melakukan pergantian data.';
      }
      return '*Klasifikasi Merchant berwarna merah adalah Merchant tidak aktif, berwarna biru menunjukkan Merchant Klasifikasi aktif';
    },
    allData() {
      return this.$store.state.klasifikasi.items;
    },
    itemsKlasifikasi() {
      const data = this.allData?.list
        .filter((obj) => obj.id !== 1)
        .map((obj) => ({ name: obj.nama_klasifikasi, id: obj.id }));
      return data || [];
    },
    criteria_merchant() {
      const data = this.$store.state.daftarInputer.ap.criteria_merchant.map(
        (obj) => ({ id: obj.id, name: obj.name }),
      );
      return data;
    },
    jenis_usaha() {
      const array = this.$store.state.daftarInputer.ap.jenis_usaha.map(
        (obj) => ({ id: obj.mcc, name: obj.mcc_name, mcc_id: obj.id }),
      );
      return array;
    },
    showMenu() {
      let show;
      switch (this.menu) {
        case 'client':
          show = 1;
          break;
        case 'tracking':
          show = 2;
          break;
        case 'approval':
          show = 3;
          break;
        default:
          show = 0;
      }

      return show;
    },
    accountType() {
      return this.data != null ? this.data.account_type : null;
    },
    merchantPhone() {
      const area = this.data.phone_area_code !== '' && this.data.phone_area_code != null
        ? `(${this.data.phone_area_code})`
        : '';
      const phone = this.data.phone != null ? this.data.phone : '';
      return `${area} ${phone}`;
    },
  },
  watch: {
    data: {
      immediate: true,
      handler(newData, oldData) {
        if (newData !== oldData) {
          this.getJenisUsaha(this.data.merchant_criteria.id);
        }
      },
    },
    'data.merchant_criteria': function (val) {
      this.data.mcc = '';
      this.$store.dispatch('daftarInputer/ap/getJenisUsahaList', {
        merchantCriteria: val.id,
      });
    },
    'data.mcc': function (val) {
      this.$store
        .dispatch('client/addMerchant/getMDR', {
          mccId: val.mcc_id,
          merchantCriteria: this.data.merchant_criteria.id,
        })
        .then((res) => {
          if (res) {
            this.data.mdr_off_us = res.mdr_off_us;
            this.data.mdr_on_us = res.mdr_on_us;
          }
        });
    },
  },
  methods: {
    masking(val) {
      return maskingWord(val, 3);
    },
    handleImage(data) {
      this.imageValid = data;
    },
    areaChange(val) {
      this.data.area = val;
      const kodePos = val.text.split(', ')[4];
      const request = {
        query: kodePos,
        fields: ['name', 'geometry'],
      };
      this.kodeposLocate = request;
    },
    async getJenisUsaha(id) {
      return this.$store.dispatch('daftarInputer/ap/getJenisUsahaList', {
        merchantCriteria: id,
      });
    },
    async getCriteriaMerchant() {
      return this.$store.dispatch('daftarInputer/ap/getCriteriaMerchant');
    },
    updateImage(val) {
      this.data.image_lokasi = URL.createObjectURL(val);
    },
    text,
    back() {
      this.$router.back();
    },
    submitForm() {
      let newData;
      if (this.data.area) {
        newData = {
          ...this.data,
          qr_type: this.data.qr_type.id,
          account_type: this.data.account_type.id,
          mcc_description: this.data.mcc.name,
          mcc: this.data.mcc.id,
          merchant_criteria: this.data.merchant_criteria.id,
          latitude: this.data.gmaps.lat,
          longitude: this.data.gmaps.lng,
          postal_code: this.data.kode_pos,
          sub_district_name: this.data.kelurahan,
          province_name: this.data.provinsi,
          city_name: this.data.kabupaten_kota,
          urban_name: this.data.kecamatan,
          area: this.data.area.text || this.data.area,
        };
        this.errorMessage.area = [];
      } else {
        this.errorMessage.area.push('This field is Required');
      }
      if (
        this.$refs.form.validate()
        && this.data.area
        && this.imageValid.status
      ) {
        this.$store
          .dispatch('daftarInputer/ap/revisiData', {
            data: newData,
            id: this.$route.params.id,
            doc: this.$store.state.daftarInputer.ap.image_lokasi,
          })
          .then((res) => {
            if (res) {
              this.showAlert('Revisi success !', 'mdi-check-circle', 'green');
              this.$router.push('/daftar-inputer');
            } else {
              this.showAlert('Error !', 'mdi-alert', 'red');
            }
          });
      } else {
        const invalidInput = document.querySelector('.error--text');
        if (invalidInput) {
          const scrollOptions = {
            behavior: 'smooth',
            block: 'center',
            inline: 'nearest',
          };

          invalidInput.focus();
          invalidInput.scrollIntoView(scrollOptions);
        }
      }
    },
    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
