<template>
  <div>
    <v-row>
      <v-col cols="10" sm="4">{{ label }}</v-col>
      <v-col cols="2" sm="1">:</v-col>
      <v-col cols="12" sm="7">
        <gmap-map
          ref="map"
          :center="value.lat ? value : center"
          :zoom="10"
          style="padding-bottom: 20px;width:100%;  height: 300px;"
          @click="handleMapClick"
        >
          <gmap-marker
            :position="value.lat ? value : center"
            :clickable="true"
            @click="panToMarker"
          ></gmap-marker>
        </gmap-map>
        <small>*Click the location for Pinned</small>
        <slot name="content"></slot>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { gmapApi } from 'vue2-google-maps';

export default {
  props: {
    kodeposLocate: {
      type: Object,
      default() {
        return {};
      }
    },
    label: {
      type: String
    },
    value: {
      type: Object,
      default() {
        return { lat: 0, lng: 0 };
      }
    }
  },
  computed: {
    google: gmapApi
  },
  name: 'DrawGoogleMap',
  data() {
    return {
      // Center The map Default
      center: {
        lat: -6.2,
        lng: 106.816666
      },
      locations: [],
      currentLocation: null
    };
  },
  watch: {
    kodeposLocate(val) {
      const map = this.$refs.map.$mapObject;
      const placeService = new this.google.maps.places.PlacesService(map);
      placeService.findPlaceFromQuery(val, (results, status) => {
        if (status === 'OK' || results) {
          const newObj = {
            latLng: results[0].geometry.location
          };
          this.handleMapClick(newObj);
        }
      });
    }
  },
  mounted() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        position => {
          this.center = {
            lat: position.coords.latitude,
            lng: position.coords.longitude
          };
        },
        error => {
          console.error(error);
        }
      );
    } else {
      console.error('Geolocation is not supported by this browser.');
    }
  },
  methods: {
    // PanTo Marker
    panToMarker() {
      this.$refs.mapRef.panTo(this.value);
      this.$refs.mapRef.setZoom(18);
    },
    setPlace(loc) {
      this.currentLocation = loc;
    },

    // Update Value to parent
    handleMapClick(e) {
      console.log('click', e);
      this.$emit('update:value', { lat: e.latLng.lat(), lng: e.latLng.lng() });
    },

    //Moves the marker to click position on the map
    setLocationLatLng: function() {
      navigator.geolocation.getCurrentPosition(geolocation => {
        this.center = {
          lat: geolocation.coords.latitude,
          lng: geolocation.coords.longitude
        };
      });
    }
  }
};
</script>
