import Parent from "../views/Parent.vue";

import ApList from "../views/daftar-inputer/ApList.vue";
import ApDetail from "../views/daftar-inputer/ApDetail.vue";
import ApEdit from "../views/daftar-inputer/ApEdit.vue";

import { setBreadcrumb } from "./index.js";

export const daftarInputerRoute = {
  path: "/daftar-inputer",
  name: "DaftarInputer",
  meta: {
    title: "JAKONE - Daftar Approval",
    appBarTitle: "Daftar Approval",
  },
  component: Parent,
  children: [
    {
      path: "/daftar-inputer",
      meta: {
        title: "JAKONE - Daftar Inputer",
        appBarTitle: "Daftar Tugas Inputer",
        breadcrumbs: () => {
          return [
            setBreadcrumb("Daftar Tugas Inputer", "/daftar-inputer", true),
          ];
        },
      },
      component: ApList,
      name: "DaftarInputer",
    },
    {
      path: "/daftar-inputer/view/:id(\\d+)",
      meta: {
        title: "JAKONE - Daftar Inputer",
        appBarTitle: "Daftar Inputer",
        breadcrumbs: (params) => {
          const id = params.id;
          return [
            setBreadcrumb("Daftar Inputer", "/daftar-inputer"),
            setBreadcrumb(`detail`, `/daftar-inputer/view`, true),
            setBreadcrumb(`${id}`, `/daftar-inputer/view/${id}`),
          ];
        },
      },
      component: ApDetail,
      name: "Inputer1ViewDetail",
    },
    {
      path: "/daftar-inputer/view/:id(\\d+)/edit",
      meta: {
        title: "JAKONE - Daftar Inputer",
        appBarTitle: "Revisi Inputer",
        breadcrumbs: (params) => {
          const id = params.id;
          return [
            setBreadcrumb("Daftar Inputer", "/daftar-inputer"),
            setBreadcrumb(`detail`, `/daftar-inputer/view`, true),
            setBreadcrumb(`${id}`, `/daftar-inputer/view/${id}`, true),
            setBreadcrumb("Revisi", `/daftar-inputer/view/${id}/edit`),
          ];
        },
      },
      component: ApEdit,
      name: "Inputer1ViewDetail",
    },
  ],
};
