import Parent from "../views/Parent.vue";

import ApList from "../views/history-inputer/ApList.vue";
import ApDetail from "../views/history-inputer/ApDetail.vue";

import { setBreadcrumb } from "./index.js";

export const historyInputerRoute = {
  path: "/history-inputer",
  name: "HistoryInputer",
  meta: {
    title: "JAKONE - History Approval",
    appBarTitle: "History Approval",
  },
  component: Parent,
  children: [
    {
      path: "/history-inputer",
      meta: {
        title: "JAKONE - History Inputer",
        appBarTitle: "Daftar History Inputer",
        breadcrumbs: () => {
          return [
            setBreadcrumb("Daftar History Inputer", "/history-inputer", true),
          ];
        },
      },
      component: ApList,
      name: "HistoryInputer",
    },
    {
      path: "/history-inputer/view/:id(\\d+)",
      meta: {
        title: "JAKONE - View Detail",
        appBarTitle: "View Detail",
        breadcrumbs: (params) => {
          const id = params.id;
          return [
            setBreadcrumb("History Inputer", "/history-inputer"),
            setBreadcrumb(`detail`, `/history-inputer/view`, true),
            setBreadcrumb(`${id}`, `/history-inputer/view/${id}`),
          ];
        },
      },
      component: ApDetail,
      name: "History Inputer Detail",
    },
  ],
};
