export const keyTest = () => 'ABC';

export function isEmpty(data) {
  return data == null || data === '' || data === undefined;
}

export const dateHelpers = (dates) => {
  const dateObj = dates || new Date();
  const year = dateObj.getUTCFullYear();
  const month = `0${dateObj.getMonth() + 1}`.slice(-2);
  const day = `0${dateObj.getDate()}`.slice(-2);
  const date = `${year}-${month}-${day}`;
  return date;
};

export function text(data) {
  if (data == null || data === '' || data === undefined) {
    return '( not set )';
  }
  return data;
}

export function getTommorowDate(days) {
  const dateObj = new Date();
  const tomorrow = new Date();
  tomorrow.setDate(dateObj.getDate() + days);
  const dateTommorrow = dateHelpers(tomorrow);
  return dateTommorrow;
}

export function getDate(days, date) {
  const dateObj = new Date(date);
  const newDate = new Date(date);
  newDate.setDate(dateObj.getDate() + days);
  const dateTommorrow = dateHelpers(newDate);
  return dateTommorrow;
}

export const atob = (str) => Buffer.from(str, 'base64').toString('binary');

export const btoa = (str) => Buffer.from(str, 'binary').toString('base64');

export const areArraysEqual = (defaultArray, anotherArray) => {
  if (defaultArray.length !== anotherArray.length) {
    return false;
  }

  for (let i = 0; i < defaultArray.length; i += 1) {
    if (defaultArray[i] !== anotherArray[i]) {
      return false;
    }
  }

  return true;
};

export const scrollToInvalidInput = () => {
  const invalidInput = document.querySelector('.error--text');
  if (invalidInput) {
    const scrollOptions = {
      behavior: 'smooth',
      block: 'center',
      inline: 'nearest',
    };
    setTimeout(() => {
      invalidInput.focus();
      invalidInput.scrollIntoView(scrollOptions);
    }, 1000);
  }
};
