import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';

const bulkUpload = {

  namespaced: true,

  state() {
    return {
      loading: false,
      loadingUpload: false,
      flashErrorMessage: '',
      flashError: false,
      userMaster: [],
      totalPage: null,
      historyBulk: {},
    };
  },

  mutations: {
    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },
    setLoading(state, loading) {
      state.loading = loading;
    },
    setLoadingUpload(state, loading) {
      state.loadingUpload = loading;
    },
    setUserMaster(state, payload) {
      state.userMaster = payload.data;
    },
    setHistoryBulk(state, { payload }) {
      state.historyBulk = payload;
    },
    setTotalHistory(state, { payload }) {
      state.totalPage = parseInt(payload.totalPage, 10);
    },
    setClearHistory(state) {
      state.historyBulk = {};
    },
  },

  actions: {
    async downloadTemplate(ctx) {
      ctx.commit('setLoading', true);

      const data = {
        version: btoa(
          CryptoJS.SHA256(
            dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      };

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/merchant/bulk/download-template`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config).then((response) => {
        if (response.data.code !== 200) {
          if (localStorage.getItem('language_qram') == null) {
            ctx.commit('setFlashErrorMessage', response.data.message_id);
          } else {
            ctx.commit('setFlashErrorMessage', response.data.message_en);
          }

          if (response.data.code === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        } else {
          ctx.dispatch('downloadFile', { url: response.data.data.link_download });
        }
      });
    },
    downloadFile(ctx, { url }) {
      const link = document.createElement('a');
      link.href = url;
      link.target = '_blank';
      link.style.display = 'none';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    async getUserMaster(ctx, { name }) {
      const data = JSON.stringify({
        q: name,
        version: btoa(
          CryptoJS.SHA256(
            name
            + dateHelpers() + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/master/user/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }
          } else {
            ctx.commit('setUserMaster', response.data);
          }
        })
        .catch((err) => {
          if (err.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: err.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (err.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });

      ctx.commit('setLoading', false);
    },
    async fetchDataHistory(ctx, { search }) {
      ctx.commit('setLoading', true);
      const data = {
        ...search,
        version: btoa(
          CryptoJS.SHA256(
            search.limit.toString()
            + search.page.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      };

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/merchant/bulk/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setTotalHistory', { payload: response.data.data });
            ctx.commit('setHistoryBulk', { payload: response.data.data });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },
    async downloadFailed(ctx, { historyId }) {
      ctx.commit('setLoading', true);
      const data = {
        history_id: historyId,
        version: btoa(
          CryptoJS.SHA256(
            dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      };

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/merchant/bulk/download-failed`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            window.open(response.data.data.link_download);
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },
    async uploadBulk(ctx, { file }) {
      ctx.commit('setLoading', true);
      ctx.commit('setLoadingUpload', true);

      const formData = new FormData();
      formData.append('file', file);

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/merchant/bulk/upload-bulk`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data: formData,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            if (response.data.code === 302) {
              ctx.commit(
                'setSnackbar',
                {
                  status: true,
                  message: response.data.data,
                  icon: 'mdi-alert',
                  color: 'red',
                  timeout: 10000,
                },
                { root: true },
              );
              return false;
            }
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: 'Proses Upload File Gagal',
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
            return false;
          }
          ctx.commit(
            'setSnackbar',
            {
              status: true,
              message: 'Sukses Upload File',
              icon: 'mdi-check-circle-outline',
              color: 'success',
            },
            { root: true },
          );
          return true;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          } else {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: 'Terjadi Error Silahkan Hubungi CS/Admin',
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
          ctx.commit('setLoadingUpload', false);
        });
      return result;
    },

  },

  modules: {},
};

export default bulkUpload;
