<template>
  <v-file-input
    :label="label"
    :placeholder="placeholder"
    :accept="validate.file_bulk_merchant.type.join()"
    outlined
    small-chips
    persistent-placeholder
    prepend-inner-icon="mdi-paperclip"
    prepend-icon=""
    :rules="rule"
    v-model="file"
    @change="onChangeFileXSLX"
  ></v-file-input>
</template>

<script>
import { validations } from '@/store/params';

export default {
  props: {
    placeholder: {
      type: String,
    },
    label: {
      type: String,
    },
    rule: {
      type: Array,
    },
    value: {
      type: File,
    },
  },
  validations: {
    fileBulkMerchant: {
      required() {
        if (this.fileBulkMerchantFocus > 1) {
          return this.fileBulkMerchant != null;
        }
        return true;
      },
      fileType() {
        if (this.fileBulkMerchant != null) {
          return !!validations.file_bulk_merchant.type.includes(
            this.fileBulkMerchant.type.toLowerCase(),
          );
        }
        return true;
      },
    },
  },
  data() {
    return {
      validate: validations,
      fileBulkMerchant: null,
      fileBulkMerchantFocus: 0,
      file: this.value,
    };
  },
  methods: {
    onChangeFileXSLX(file) {
      this.file = file;
      this.$emit('input', this.file);
    },
  },
  watch: {
    value(newFile) {
      this.file = newFile;
    },
  },
};
</script>
