<!-- eslint-disable vue/no-mutating-props -->
<template>
  <tr>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        label="User ID"
        v-model="search.username"
        type="text"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        label="Fullname"
        v-model="search.fullname"
        type="text"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        label="Role"
        v-model="search.role"
        :items="itemsRole"
        item-text="role"
        item-value="role"
        @change="showData"
        clearable
        hide-details="true"
        dense
        @input="search.role = $event ? $event : ''"
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-menu
        ref="dateMenu"
        v-model="dateMenu"
        :close-on-content-click="false"
        transition="scale-transition"
        offset-y
        max-width="290px"
        min-width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            label="Created At"
            v-model="dateFormatted"
            v-bind="attrs"
            @blur="search.created_at = parseDate(dateFormatted)"
            v-on="on"
            hide-details="true"
            dense
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="search.created_at"
          no-title
          scrollable
          @input="dateMenu = false"
        >
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dateMenu = false"> Cancel </v-btn>
        </v-date-picker>
      </v-menu>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-menu
        ref="dateMenuLastLogin"
        v-model="dateMenuLastLogin"
        :close-on-content-click="false"
        transition="scale-transition"
        offset-y
        max-width="290px"
        min-width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            label="Last Login"
            v-model="dateFormattedLastLogin"
            v-bind="attrs"
            @blur="search.last_login_at = parseDate(dateFormattedLastLogin)"
            v-on="on"
            hide-details="true"
            dense
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="search.last_login_at"
          no-title
          scrollable
          @input="dateMenuLastLogin = false"
        >
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dateMenuLastLogin = false">
            Cancel
          </v-btn>
        </v-date-picker>
      </v-menu>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        label="Status"
        v-model="search.status"
        :items="listStatusUser"
        item-text="text"
        item-value="value"
        @change="showData"
        @input="search.status = $event ? $event : ''"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
  </tr>
</template>

<script>
import { listStatusUser } from '@/store/params';

export default {
  props: {
    search: {
      type: Object,
      required: true,
    },
    loading: {
      type: Boolean,
    },
  },
  data: () => ({
    dateFormatted: '',
    dateFormattedLastLogin: '',
    dateMenu: false,
    dateMenuLastLogin: false,
    listStatusUser,
  }),
  async mounted() {
    this.getRoles();
  },
  computed: {
    itemsRole() {
      return this.$store.state.rbac.role.allActiveRole;
    },
  },
  watch: {
    'search.created_at': {
      handler() {
        this.dateFormatted = this.formatDate(this.search.created_at);
        this.showData();
      },
    },
    'search.last_login_at': {
      handler() {
        this.dateFormattedLastLogin = this.formatDate(
          this.search.last_login_at,
        );
        this.showData();
      },
    },
  },
  methods: {
    formatDate(valueDate) {
      if (!valueDate) return null;

      const [year, month, day] = valueDate.split('-');
      return `${day}/${month}/${year}`;
    },
    parseDate(valueDate) {
      if (!valueDate) return null;

      const [day, month, year] = valueDate.split('/');
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`;
    },
    getRoles() {
      this.$store.dispatch('rbac/role/fetchActiveRoles');
    },
    clearSearch() {
      this.$emit('clearSearchParent');
    },
    showData() {
      this.$emit('showDataParent');
    },
  },
};
</script>
