<template>
  <tr
    :key="index"
    :style="{ cursor: 'pointer' }"
    :class="selectedRow === index ? 'indigo lighten-5' : ''"
    @click="selectRow(index)"
  >
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ (options.page - 1) * options.itemsPerPage + index + 1 }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.merchant_name }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.bill_num }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.account_number }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.trx_datetime }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-chip
        class="ma-2"
        :color="item.status == 'success' ? 'green' : 'red'"
        text-color="white"
        x-small
      >
        {{ item.status }}
      </v-chip>
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.mid }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.mpan }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.rrn }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.qr_method }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.qr_type }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.trx_type }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.issuer_name }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.customer_pan }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.customer_name }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.payment_type }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.payment_channel }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.amount }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.amount_mdr }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.amount_net }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-icon small class="mr-2" @click="viewItem(item)"> mdi-eye </v-icon>
    </td>
  </tr>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
    },
    index: {
      type: Number,
    },
    options: {
      type: Object,
    },
    selectedRow: {
      type: Number,
    },
  },
  data: () => ({}),
  methods: {
    selectRow(index) {
      this.$emit('selectRowParent', index);
    },
    viewItem(item) {
      this.$emit('viewItemParent', item);
    },
  },
};
</script>
