<template>
  <v-data-table
    :headers="headers"
    :items="dataItems"
    :options.sync="optionsTable"
    :server-items-length="totalData"
    item-key="no"
    class="elevation-1 table-log"
    density="compact"
    :show-expand="expandedItem"
    single-expand
    :loading="loading"
    :footer-props="footerProps"
    loading-text="Loading... Please wait"
  >
    <template v-slot:[`item.no`]="{ item }">
      {{ (options.page - 1) * options.itemsPerPage + item.no }}
    </template>

    <template v-slot:top>
      <slot name="top" />
    </template>

    <template v-slot:[`item.brief_activity`]="{ item }">
      <div
        class="mb-0 no-wrap-text d-flex align-center"
        :class="
          item.brief_activity.response_message !== 'Success'
            ? 'response-failed'
            : ''
        "
      >
        <div v-if="item.brief_activity.ip_address" class="d-flex align-center">
          IP Address
          <v-chip class="bg-green-chip">
            {{ item.brief_activity.ip_address }}
          </v-chip>
        </div>
        <div class="d-flex align-center">
          Response Code
          <v-chip class="bg-green-chip">
            {{ item.brief_activity.response_code }}
          </v-chip>
        </div>
        <div class="d-flex align-center">
          Start Time
          <v-chip class="bg-green-chip">
            {{ item.brief_activity.start_time }}
          </v-chip>
        </div>
        <div class="d-flex align-center">
          End Time
          <v-chip class="bg-green-chip">
            {{ item.brief_activity.end_time }}
          </v-chip>
        </div>
        <div class="d-flex align-center">
          Elapsed Time
          <v-chip class="bg-green-chip">
            {{ item.brief_activity.elapsed_time }}
          </v-chip>
        </div>
      </div>
    </template>

    <template v-slot:[`item.description`]="{ item }">
      <slot name="description" :data="item" />
    </template>

    <template v-slot:[`item.brief_activitys`]="{ item }">
      <slot name="brief_activitys" :data="item" />
    </template>

    <template v-slot:expanded-item="{ item }">
      <slot name="expanded-item" :data="item" />
    </template>

    <template v-slot:no-data>
      <slot name="no-data" />
    </template>
    <template v-slot:[`item.result`]="{ item }">
      <slot name="result" :data="item"/>
    </template>
    <template v-slot:[`item.action`]="{ item }">
      <slot name="action" :data="item"/>
    </template>
    <template v-slot:[`item.is_editable`]="{ item }">
      <slot name="is_editable" :data="item"/>
    </template>
    <template v-slot:[`item.status`]="{ item }">
      <slot name="status" :data="item" />
    </template>
    <template v-slot:[`item.is_active`]="{ item }">
      <slot name="is_active" :data="item" />
    </template>
  </v-data-table>
</template>

<script>
export default {
  props: {
    headers: {
      type: Array,
    },
    items: {
      type: Array,
    },
    options: {
      type: Object,
    },
    loading: {
      type: Boolean,
    },
    totalData: {
      type: Number,
    },
    footerProps: {
      type: Object,
    },
    expandedItem: {
      type: Boolean,
    },
  },
  computed: {
    optionsTable: {
      get() {
        return this.options;
      },
      set(val) {
        this.$emit('update:options', val);
      },
    },
    dataItems() {
      return this.items.map((item, index) => ({ ...item, no: index + 1 }));
    },
  },
};
</script>

<style>
.bg-green-chip {
  background-color: rgb(4 179 4) !important;
  color: white !important;
  margin: 0 10px;
}
.response-failed .bg-green-chip {
  background-color: red !important;
}
.table-log.v-data-table tbody tr.v-data-table__expanded__content {
  box-shadow: none !important;
}
.card-log {
  border: 1px solid red;
  padding: 10px;
}
.no-wrap-text {
  text-wrap: nowrap;
}
</style>
