<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <filter-component
      :filterList="filterList"
      v-on:showDataParent="showData"
      v-on:resetSearch="resetSearch"
      ref="filterComponentRef"
    />

    <table-log
      class="mt-5"
      :headers="headers"
      :items="items"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :footerProps="footerProps"
      @update:options="updateOptions"
      :loading="loading"
    >
      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
          v-on:saveParent="addKlasifikasi"
        />
      </template>
      <template #no-data>
        <button-reset v-on:resetSearch="resetSearch" />
      </template>
      <template #is_editable="{ data }">
        <v-icon v-if="data.is_editable" small class="mr-2" @click="actItem(data, 'edit')">
          mdi-pencil
        </v-icon>
      </template>
      <template #status="{ data }">
        <v-chip :color="data.status ? 'green' : 'red'" text-color="white">
          {{ data.status ? "Active" : "Inactive" }}
        </v-chip>
      </template>
      <template #is_active="{ data }">
        <v-chip :color="data.is_active ? 'green' : 'red'" text-color="white">
          {{ data.is_active ? "Active" : "Inactive" }}
        </v-chip>
      </template>
    </table-log>

    <form-klasifikasi
      :dialog="dialog"
      :editedItem="editedItem"
      v-on:closeCancelParent="close"
      :editedIndex="editedIndex"
      v-on:closeSuccess="getData"
    />
  </v-card>
</template>

<script>
import FilterComponent from '@/components/table/FilterComponent.vue';
import TableLog from '@/components/table/TableLog.vue';
import { statusKlasifikasi } from '@/store/params';
import ActionButton from '@/components/table/ActionButton.vue';
import ButtonReset from '../../components/button/ButtonReset.vue';
import FormKlasifikasi from '../../components/dialog/FormKlasifikasi.vue';

export default {
  components: {
    FilterComponent,
    TableLog,
    ButtonReset,
    ActionButton,
    FormKlasifikasi,
  },
  data() {
    return {
      editedIndex: -1,
      dialog: false,
      editedItem: {
        nama_klasifikasi: '',
        deskripsi: '',
        is_active: 1,
      },
      defaultItem: {
        nama_klasifikasi: '',
        deskripsi: '',
        is_active: 1,
      },
      listButton: {
        buttonRestart: true,
        buttonCustom: {
          label: '+ Add Klasifikasi',
        },
      },
      footerProps: {
        itemsPerPageOptions: [10, 20, 50, 100],
      },
      filterList: [
        {
          label: 'Nama Klasifikasi',
          type: 'text',
          value: '',
        },
        {
          label: 'Created Date',
          type: 'dateRange',
          icon: 'mdi-calendar',
          showDatePicker: false,
          max: true,
          value: [],
        },
        {
          label: 'Status',
          type: 'select',
          item: statusKlasifikasi,
          value: '',
        },
      ],
      headers: [
        {
          text: 'No',
          align: 'center',
          width: '1%',
          value: 'no',
        },
        {
          text: 'Nomor Klasifikasi',
          align: 'center',
          width: '15%',
          value: 'nomor_klasifikasi',
        },
        {
          text: 'Nama Klasifikasi',
          align: 'center',
          width: '15%',
          value: 'nama_klasifikasi',
        },
        {
          text: 'Deskripsi',
          align: 'center',
          width: '15%',
          value: 'deskripsi',
        },
        {
          text: 'Created Date',
          align: 'center',
          width: '15%',
          value: 'created_date',
        },
        {
          text: 'Last Updated',
          align: 'center',
          width: '15%',
          value: 'updated_date',
        },
        {
          text: 'Status',
          align: 'center',
          width: '10%',
          value: 'is_active',
        },
        {
          text: 'Action',
          align: 'center',
          width: '10%',
          value: 'is_editable',
        },
      ],
      options: {},
    };
  },
  computed: {
    status() {
      return statusKlasifikasi;
    },
    flashErrorMessage() {
      return this.$store.state.klasifikasi.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.klasifikasi.flashError;
      },
      set() {
        this.$store.commit('klasifikasi/setFlashErrorMessage', '');
      },
    },
    loading() {
      return this.$store.state.klasifikasi.loading;
    },
    allData() {
      return this.$store.state.klasifikasi.items;
    },
    items() {
      return this.allData?.list || [];
    },
    totalData() {
      return this.allData?.totalData || 0;
    },
    totalPage() {
      return this.$store.state.klasifikasi.totalPage;
    },
  },
  watch: {
    filterList: {
      handler(newItems) {
        newItems.forEach((item, idx) => {
          if (item.value[0] && item.value[1] && item.value[0] > item.value[1] && item.type === 'dateRange') {
            const [startDate, endDate] = item.value;
            this.filterList[idx].value = [endDate, startDate];
          }
        });
      },
      deep: true,
    },
  },
  methods: {
    actItem(item) {
      const newItem = item;
      newItem.data = { name: newItem.nama_klasifikasi, description: newItem.deskripsi };
      this.editedIndex = newItem.id;
      this.editedItem = { ...newItem };
      this.dialog = true;
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },
    addKlasifikasi() {
      this.dialog = true;
    },
    resetSearch() {
      this.$refs.filterComponentRef.resetValidation();
      this.$store.commit('klasifikasi/setClearData');
      this.filterList.map((obj) => {
        const container = obj;
        if (obj.type !== 'dateRange') {
          container.value = '';
        } else {
          container.value = [];
        }
        return container;
      });
      this.getData();
    },
    updateOptions(val) {
      this.options = val;
      this.getData();
    },
    showData() {
      this.getData();
    },
    getData() {
      const { page, itemsPerPage } = this.options;
      const search = {
        page,
        limit: itemsPerPage,
        search: {
          created_date_start: this.filterList[1].value[0] || '',
          created_date_end: this.filterList[1].value[1] || '',
          name: this.filterList[0].value,
          status: this.filterList[2].value,
        },
        sort_by: 'datetime',
        sort_order: 'DESC',
      };

      this.$store
        .dispatch('klasifikasi/fetchData', {
          search,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0 || !this.totalPage) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },
  },
};
</script>
