<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <v-alert
      v-model="flashError"
      dense
      border="left"
      type="error"
      dismissible
      elevation="2"
    >
      {{ flashErrorMessage }}
    </v-alert>

    <table-list
      :headers="headers"
      :allData="allData"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :hidePage="true"
      :loading="loading"
      @update:options="updateOptions"
    >
      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
          v-on:addItemParent="addItem"
        />
      </template>

      <template v-slot:item="{ item, index }">
        <data-list
          :item="item"
          :index="index"
          :options="options"
          v-on:editItemParent="editItem"
          v-on:actItemParent="actItem"
          :selectedRow="selectedRow"
          v-on:selectRowParent="selectRow"
        />
      </template>

      <template v-slot:nodata>
        <v-btn outlined dark color="primaryred" @click="clearSearch">
          Reset
        </v-btn>
      </template>
    </table-list>

    <form-role
      :loading="loading"
      :dialog="dialog"
      :editedItem="editedItem"
      :editedIndex="editedIndex"
      v-on:closeCancelParent="close"
    />

    <confirm-dialog
      :loading="loading"
      :dialog="dialogDelete"
      :title="'Are you sure you want to delete this item'"
      v-on:closeCancelParent="close"
      v-on:confirmParent="confirm"
    />
  </v-card>
</template>

<script>
import ActionButton from '@/components/table/ActionButton.vue';
import TableList from '@/components/table/TableList.vue';
import DataList from '@/components/table/DataRoleList.vue';
import FormRole from '@/components/dialog/FormRole.vue';
import ConfirmDialog from '@/components/dialog/ConfirmDialog.vue';

const dateObj = new Date();
const date = `${dateObj.getUTCFullYear()}-${`0${dateObj.getMonth() + 1}`.slice(
  -2,
)}-${`0${dateObj.getDate()}`.slice(-2)}`;

export default {
  components: {
    ActionButton,
    TableList,
    DataList,
    FormRole,
    ConfirmDialog,
  },
  data: () => ({
    selectedRow: '',
    listButton: {
      buttonRestart: true,
      buttonDots: false,
      buttonAdd: true,
    },
    search: {
      role: '',
      description: '',
    },
    searchDefault: {
      role: '',
      description: '',
    },
    searchFix: {
      role: '',
      description: '',
    },
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'id',
        sortable: false,
        width: '50',
      },
      {
        text: 'Role',
        value: 'role',
        align: 'start',
        sortable: false,
        width: '150',
      },
      {
        text: 'Actions',
        width: '150',
        value: 'actions',
        sortable: false,
      },
    ],
    editedItem: {
      role: '',
      description: '',
    },
    defaultItem: {
      role: '',
      description: '',
    },
    editedIndex: -1,
    page: '1',
    options: {},
    dialog: false,
    dialogDelete: false,
    isFirstLoad: false,
  }),
  beforeMount() {
    this.$store.commit('rbac/role/clearRoles');
  },
  computed: {
    flashErrorMessage() {
      return this.$store.state.rbac.role.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.rbac.role.flashError;
      },
      set() {
        this.$store.commit('rbac/role/setFlashErrorMessage', '');
      },
    },
    loading() {
      return this.$store.state.rbac.role.loading;
    },
    allData() {
      return this.$store.state.rbac.role.allRoles;
    },
    totalData() {
      return this.$store.state.rbac.role.totalRoles;
    },
    totalPage() {
      return this.$store.state.rbac.role.totalPage;
    },
  },
  methods: {
    selectRow(index) {
      if (this.selectedRow !== index) {
        this.selectedRow = index;
      } else {
        this.selectedRow = '';
      }
    },
    clearSearch() {
      this.search = { ...this.searchDefault };
      this.$nextTick(() => {
        this.$store.commit('rbac/role/clearRoles');
        this.showData();
      });
    },

    showData() {
      this.searchFix = { ...this.search };
      this.getData();
    },

    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },

    getData() {
      this.$store.commit('rbac/role/setFlashErrorMessage', '');
      const {
        page, itemsPerPage, sortBy, sortDesc,
      } = this.options;

      this.$store
        .dispatch('rbac/role/fetchRoles', {
          date,
          page,
          itemsPerPage,
          sortBy,
          sortDesc,
          search: this.searchFix,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },

    resetUniqueValidation() {
      this.$store.commit('rbac/role/clearErrors');
    },

    close() {
      this.resetUniqueValidation();
      this.dialog = false;
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },

    addItem() {
      this.dialog = true;
    },

    editItem(item) {
      const id = item.role.replace(' ', '-');
      this.$router.push(`/rbac-role/${id}/view`);
      this.$store.commit('rbac/role/setDetailData', item);
    },

    actItem(item) {
      const newItem = item;
      newItem.data = { id: newItem.id };
      this.editedIndex = this.$store.state.rbac.role.allRoles.indexOf(newItem);
      this.editedItem = { ...newItem };
      this.dialogDelete = true;
    },

    async confirm() {
      await this.$store
        .dispatch('rbac/role/deleteRole', {
          date,
          nameRole: this.editedItem.role,
          index: this.editedIndex,
        })
        .then((status) => {
          if (status) {
            this.close();
            this.showAlert(
              'Delete role success !',
              'mdi-check-circle',
              'green',
            );
          } else {
            this.showAlert('Error on Delete role !', 'mdi-alert', 'red');
          }
        });
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
