<template>
  <div class="d-flex" :style="{ padding: '10px' }">
    <v-dialog v-model="dialog" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text>
          Applying change
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-card
      class="d-flex pa-2 flex-column"
      :style="{ 'margin-bottom': '40px', width: '100%', height: 'unset' }"
    >
      <div
        :style="{ padding: '25px', paddingBottom:'10px', fontSize: 'small' }"
      >
        <v-row class="align-center">
          <v-col sm="2">Role Name</v-col>
          <v-col sm="1" style="text-align:center">:</v-col>
          <v-col sm="9">{{ role }}</v-col>
        </v-row>
      </div>
      <div
        class="d-flex pa-2 flex-wrap"
        :style="{ width: '100%', height: '500px' }"
      >
        <div class="d-flex pa-2 flex-column" :style="{ flex: '5', width: 'inherit' }">
          <v-text-field
            :style="{ flex: '1' }"
            v-model="searchAvailable"
            label="Search for available"
          ></v-text-field>
          <select
            v-model="selectedLeft"
            multiple
            class="d-flex pa-2 flex-column"
            :style="{ overflow: 'scroll', flex: '9' }"
            :aria-label="itemsLeft.length !== 0 ? 'Permission' : ''"
          >
            <optgroup
              label="Permission"
              v-if="itemsLeft.length != 0"
            >
              <option v-for="(item, i) in itemsLeft" :key="i">
                {{ item.permission }}
              </option>
            </optgroup>
          </select>
        </div>
        <div
          class="d-flex pa-2 flex-column justify-center align-center"
          :style="{ flex: '1' }"
        >
          <v-btn
            color="primary"
            class="mb-2"
            @click="getPermission"
            :disabled="dialog || loading"
            outlined
            ><v-icon>mdi-refresh</v-icon></v-btn
          >
          <v-btn
            color="primaryred"
            style="color: white"
            class="mb-2"
            @click="toLeft"
            :disabled="dialog || loading"
            outlined
            ><v-icon>mdi-chevron-double-left</v-icon></v-btn
          >
          <v-btn
            color="success"
            class="mb-2"
            @click="toRight"
            :disabled="dialog || loading"
            outlined
            ><v-icon>mdi-chevron-double-right</v-icon></v-btn
          >
        </div>
        <div class="d-flex pa-2 flex-column" :style="{ flex: '5', width: 'inherit' }">
          <v-text-field
            :style="{ flex: '1' }"
            v-model="searchAssigned"
            label="Search for assigned"
          ></v-text-field>
          <select
            v-model="selectedRight"
            multiple
            class="d-flex pa-2 flex-column"
            :style="{ overflow: 'scroll', flex: '9' }"
            :aria-label="itemsRight.length !== 0 ? 'Permission' : ''"
          >
            <optgroup
              label="Permission"
              v-if="itemsRight.length != 0"
            >
              <option v-for="(item, i) in itemsRight" :key="i">
                {{ item.permission }}
              </option>
            </optgroup>
          </select>
        </div>
      </div>
    </v-card>
  </div>
</template>

<script>
const dateObj = new Date();
const date = `${dateObj.getUTCFullYear()
}-${
  (`0${dateObj.getMonth() + 1}`).slice(-2)
}-${
  (`0${dateObj.getDate()}`).slice(-2)}`;

export default {
  data: () => ({
    dialog: false,
    searchAvailable: '',
    searchAssigned: '',
    benched: 0,
    selectedLeft: [],
    selectedRight: [],
  }),
  mounted() {
    this.getPermission();
  },
  computed: {
    itemsLeft: {
      get() {
        const right = [];
        this.$store.state.rbac.permission.assignedPermissions.forEach(
          (data) => {
            right.push(data.permission);
          },
        );
        return this.$store.state.rbac.permission.allPermissions.filter(
          (x) => !right.includes(x.permission)
            && x.permission
              .toLowerCase()
              .search(this.searchAvailable.toLowerCase()) !== -1,
        );
      },
      set() {},
    },
    itemsRight: {
      get() {
        return this.$store.state.rbac.permission.assignedPermissions.filter(
          (x) => x.permission
            .toLowerCase()
            .search(this.searchAssigned.toLowerCase()) !== -1,
        );
      },
      set() {},
    },
    loading() {
      return this.$store.state.rbac.permission.loading;
    },
    role() {
      const role = this.$route.params.id.replace('-', ' ');
      return role;
    },
  },
  methods: {
    getPermission() {
      this.$store.dispatch('rbac/permission/fetchPermissions', {
        date,
        role: this.role,
      });

      this.$store.dispatch('rbac/permission/fetchPermissions', {
        date,
        role: '',
      });
    },
    toRight() {
      if (this.selectedLeft.length > 0) {
        this.dialog = true;
        this.$store
          .dispatch('rbac/assignment/assign', {
            date,
            parent: this.role,
            child: this.selectedLeft,
          })
          .then((res) => {
            if (res !== false) {
              this.getPermission();
            }
          })
          .finally(() => {
            this.dialog = false;
          });

        this.selectedLeft = [];
        this.selectedRight = [];
      }
    },
    toLeft() {
      if (this.selectedRight.length > 0) {
        this.dialog = true;
        this.$store
          .dispatch('rbac/assignment/revoke', {
            date,
            parent: this.role,
            child: this.selectedRight,
          })
          .then((res) => {
            if (res !== false) {
              this.getPermission();
            }
          })
          .finally(() => {
            this.dialog = false;
          });

        this.selectedLeft = [];
        this.selectedRight = [];
      }
    },
  },
  beforeDestroy() {
    this.$store.commit('rbac/role/setDetailData', null);
  },
};
</script>
