import Parent from "../views/Parent.vue";

// import view setting Fee
import MccMdr from "../views/setting-fee/MccMdr.vue";

import { setBreadcrumb } from "./index.js";

export const settingFeeRoute = {
    path: "/setting-fee",
    name: "SettingFee",
    children: [
        {
            path: "/setting-fee/mccmdr",
            meta: {
                title: "JAKONE - MCC MDR",
                appBarTitle: "MCC MDR",
                breadcrumbs: () => {
                    return [
                        setBreadcrumb('setting parameter bank', '/setting-fee', true),
                        setBreadcrumb('MCC MDR', '/setting-fee/mccmdr'),
                    ]
                }
            },
            component: MccMdr,
        },
    ],
    meta: {
        title: "JAKONE - Setting Fee",
        appBarTitle: "Setting Fee",
    },
    component: Parent,
}