import Vue from 'vue';
import VueRouter from 'vue-router';
import { appTitle } from '../store/params';

// ======================================== auth page
import Login from '../components/Login.vue';
import ForgotPassword from '../components/ForgotPassword.vue';
import Logout from '../components/Logout.vue';
import ChangePassword from '../components/ChangePassword.vue';

import store from '../store/index';

import Parent from '../views/Parent.vue';

// ======================================== import all route module

// dashbard
import WelcomePage from '../views/WelcomePage.vue';
// client list
import { clientRoute } from './client';
// tracking pengajuan
import { trackingPengajuanRoute } from './trackingPengajuan';
// settiing fee
import { settingFeeRoute } from './settingFee';
// nns
import { nnsRoute } from './nns';
// user
import { userRoute } from './user';
// rbac
import { rbacRoute } from './rbac';
// report
import { reportRoute } from './report';
// daftar tugas
import { daftarTugasRoute } from './daftarTugas';
// daftar inputer
import { daftarInputerRoute } from './daftarInputer';
// history inputer
import { historyInputerRoute } from './historyInputer';
// history Approval
import historyApprovalRoute from './historyApproval';
// Log
import { log } from './log';
// Bulk Upload Merchant
import { bulkUploadMerchant } from './bulkUpload';
import klasifikasi from './klasifikasi';

// import other
import Profile from '../views/Profile.vue';
// import other
import NotFound from '../views/NotFound.vue';
// encrypt extra2
import EncryptPk from '../components/EncryptPk.vue';

Vue.use(VueRouter);

export const setBreadcrumb = (text = '', link = '', disabled = false) => ({
  text,
  disabled,
  to: link,
  link: true,
  exact: true,
});

const routes = [
  {
    path: '/',
    meta: {
      title: 'JAKONE - Dashboard',
      appBarTitle: 'Dashboard',
    },
    component: Parent,
    children: [
      {
        path: '',
        name: 'Dashboard',
        meta: {
          title: 'JAKONE - Dashboard',
          appBarTitle: 'Dashboard',
        },
        component: WelcomePage,
      },
    ],
  },
  {
    path: '/login',
    name: 'Login',
    meta: {
      title: 'JAKONE - Login',
      appBarTitle: 'Login',
    },
    component: Login,
  },
  {
    path: '/forgot-password',
    name: 'ForgotPassword',
    meta: {
      title: 'JAKONE - Forgot Password',
      appBarTitle: 'Forgot Password',
    },
    component: ForgotPassword,
  },
  {
    path: '/validate-forgot-password/:accessKey/activate/:idEncrypted',
    name: 'ValidateForgotPassword',
    meta: {
      title: 'JAKONE - Validate Forgot Password',
      appBarTitle: 'Validate Forgot Password',
    },
    component: ForgotPassword,
  },
  {
    path: '/logout',
    name: 'Logout',
    meta: {
      title: 'JAKONE - Logout',
      appBarTitle: 'Logout',
    },
    component: Logout,
  },
  {
    path: '/profile',
    name: 'Profile',
    meta: {
      title: 'JAKONE - User Profile',
      appBarTitle: 'User Profile',
    },
    component: Profile,
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash]) for this route
    // which is lazy-loaded when the route is visited.
    meta: {
      title: 'JAKONE - About',
      appBarTitle: 'About',
    },
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
  },
  {
    path: '/change-password',
    name: 'ChangePassword',
    meta: {
      title: 'JAKONE - Change Password',
      appBarTitle: 'Change Password',
    },
    component: ChangePassword,
  },
  {
    path: '*',
    meta: {
      title: 'JAKONE',
      appBarTitle: '',
    },
    component: NotFound,
  },
  {
    path: '/check-extras2',
    name: 'Extras2',
    meta: {
      title: 'JAKONE - Extras2',
      appBarTitle: 'Extras2',
    },
    component: EncryptPk,
  },
  clientRoute,
  trackingPengajuanRoute,
  settingFeeRoute,
  nnsRoute,
  userRoute,
  rbacRoute,
  reportRoute,
  daftarTugasRoute,
  daftarInputerRoute,
  historyInputerRoute,
  historyApprovalRoute,
  log,
  bulkUploadMerchant,
  klasifikasi,
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.name !== 'Logout') {
    store.dispatch('checkAuth', {
      data: localStorage.getItem('logged_in_qram'),
      key: localStorage.getItem('qram_key'),
      // expired : store.state.expired,
    });
    next();
  } else {
    store.dispatch('logout');
    next({ name: 'Login' });
  }
});

const DEFAULT_TITLE = appTitle == null ? 'JAKONE' : appTitle;
router.afterEach((to) => {
  Vue.nextTick(() => {
    store.commit('setBreadcrumbs', {
      data:
        to.meta.breadcrumbs !== undefined ? to.meta.breadcrumbs(to.params) : [],
      // data: []
    });
    document.title = to.meta.title || DEFAULT_TITLE;
  });
});

export default router;
