<template>
  <v-row justify="center">
    <v-col cols="3">
      <v-row justify="space-around">
        <v-btn
          v-if="buttonClick.buttonBack"
          small
          type="button"
          density="compact"
          variant="outlined"
          color="grey-lighten-3"
          :style="{ margin: '2px' }"
          @click="back"
        >
          Kembali
        </v-btn>
        <v-btn
          v-if="buttonClick.buttonLanjut"
          small
          type="submit"
          density="compact"
          color="primaryred"
          elevation="0"
          dark
          :style="{ margin: '2px' }"
        >
          Lanjut
        </v-btn>
        <v-btn
          v-if="buttonClick.buttonConfirm"
          small
          type="submit"
          density="compact"
          color="primaryred"
          elevation="0"
          dark
          @click="confirm"
          :style="{ margin: '2px' }"
        >
          Confirm
        </v-btn>
      </v-row>
    </v-col>
  </v-row>
</template>

<script>
export default {
  props: {
    buttonClick: {
      type: Object,
    },
  },
  methods: {
    back() {
      this.$emit('backParent');
    },
    confirm() {
      this.$emit('confirmParent');
    },
  },
};
</script>
