<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div>
    <v-dialog v-model="dialog" width="500" persistent>
      <v-card>
        <v-card-title class="primaryred--text">
          {{ formTitle }}
        </v-card-title>
        <v-card-text>
          <v-form ref="userAct">
            <v-container>
              <v-row>
                <v-col v-if="errorEdit" cols="12">
                  <v-card color="#f9f9f9">
                    <div class="pa-4 d-flex align-center text--primary">
                      <div class="mr-4">
                        <v-icon large color="#b71c1c">
                          mdi-alert-circle-outline
                        </v-icon>
                      </div>
                      <div>
                        <p
                          class="d-flex align-center mb-0"
                          style="color: #b71c1c"
                        >
                          Pengguna yang diblokir telah dibuka oleh IT Security,
                          silahkan klik tombol cancel untuk kembali ke halaman
                          list user
                        </p>
                      </div>
                    </div>
                  </v-card>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    v-model="editedItem.fullname"
                    label="Fullname"
                    :error-messages="fullnameErrors"
                    @change="$v.editedItem.fullname.$touch()"
                    @blur="$v.editedItem.fullname.$touch()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.username"
                    label="User ID"
                    :error-messages="usernameErrors"
                    @change="$v.editedItem.username.$touch()"
                    @blur="$v.editedItem.username.$touch()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.email"
                    :error-messages="emailErrors"
                    @change="$v.editedItem.email.$touch()"
                    @blur="$v.editedItem.email.$touch()"
                    label="Email"
                  ></v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                  md="6"
                  v-if="editedIndex < 0 && editedIndex != -2"
                >
                  <v-text-field
                    v-model="editedItem.password"
                    :error-messages="passwordErrors"
                    @change="$v.editedItem.password.$touch()"
                    @blur="$v.editedItem.password.$touch()"
                    @input="$v.editedItem.password.$touch()"
                    label="Password"
                    :append-icon="passwordFieldType ? 'mdi-eye-off' : 'mdi-eye'"
                    @click:append="
                      () => (passwordFieldType = !passwordFieldType)
                    "
                    :type="passwordFieldType ? 'password' : 'text'"
                  ></v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                  md="6"
                  v-if="editedIndex < 0 && editedIndex != -2"
                >
                  <v-text-field
                    v-model="editedItem.passwordConfirmation"
                    :error-messages="passwordConfirmationErrors"
                    @change="$v.editedItem.passwordConfirmation.$touch()"
                    @blur="$v.editedItem.passwordConfirmation.$touch()"
                    @input="$v.editedItem.passwordConfirmation.$touch()"
                    label="Password Confirmation"
                    :append-icon="
                      passwordConfirmFieldType ? 'mdi-eye-off' : 'mdi-eye'
                    "
                    @click:append="
                      () =>
                        (passwordConfirmFieldType = !passwordConfirmFieldType)
                    "
                    :type="passwordConfirmFieldType ? 'password' : 'text'"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    type="number"
                    v-model="editedItem.mobile_phone"
                    :error-messages="mobilePhoneErrors"
                    @change="$v.editedItem.mobile_phone.$touch()"
                    @blur="$v.editedItem.mobile_phone.$touch()"
                    label="Mobile Phone"
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" md="6">
                  <v-select
                    v-model="editedItem.role"
                    :error-messages="roleErrors"
                    @change="$v.editedItem.role.$touch()"
                    @blur="$v.editedItem.role.$touch()"
                    :items="itemsRole"
                    label="role"
                    item-text="role"
                    item-value="role"
                    clearable
                  ></v-select>
                </v-col>

                <v-col cols="12" sm="12" md="12">
                  <v-select
                    v-model="editedItem.status"
                    :error-messages="statusErrors"
                    @change="$v.editedItem.status.$touch()"
                    @blur="$v.editedItem.status.$touch()"
                    :items="statusItem"
                    label="status"
                    item-text="text"
                    item-value="value"
                    clearable
                  ></v-select>
                </v-col>
                <v-col cols="12" v-if="showGenerate">
                  <v-row class="m-0">
                    <v-col cols="6">
                      <v-text-field
                        readonly
                        type="text"
                        v-model="generatedPassword"
                        label="Password"
                      >
                        <v-icon
                          v-if="generatedPassword"
                          slot="append"
                          @click="copyToClipboard"
                          color="blue"
                          style="cursor: pointer"
                        >
                          mdi-content-copy
                        </v-icon>
                      </v-text-field>
                      <small
                        v-if="generatedPassword && !copied"
                        class="error--text"
                      >
                        Mohon salin Password terlebih dahulu!
                      </small>
                    </v-col>
                    <v-col cols="6">
                      <v-btn
                        color="blue"
                        outlined
                        :disabled="generatedPassword ? true : false"
                        light
                        @click="passwordGenerate"
                      >
                        Generate Password
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primaryred" outlined @click="cancelDialog">
            Cancel
          </v-btn>
          <v-btn
            color="primary"
            outlined
            @click="save"
            :disabled="disabledSave()"
            :loading="loading"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import {
  required,
  email,
  numeric,
  minLength,
  sameAs,
} from 'vuelidate/lib/validators';

import { listStatusUser, extraString3, generatePassword } from '@/store/params';
import CustomEncryption from '@/lib/customEncryption';
import Copy from 'clipboard-copy';

export default {
  props: {
    loading: {
      type: Boolean,
    },
    editedItem: {
      type: Object,
    },
    dialog: {
      type: Boolean,
    },
    editedIndex: {
      type: Number,
    },
    blocked: {
      type: Boolean,
    },
  },
  mixins: [validationMixin],
  validations: {
    editedItem: {
      fullname: { required },
      email: {
        required,
        email,
        isUnique() {
          if (this.errorValidation != null) {
            return (
              this.errorValidation.email === null
              || this.errorValidation.email.length < 1
            );
          }
          return false;
        },
      },
      mobile_phone: { required, numeric },
      role: { required },
      status: { required },
      password: {
        required(value) {
          if (this.editedIndex < 0) {
            return value !== '';
          }
          return true;
        },
        minLength: minLength(8),
        upperCase(value) {
          if (this.editedIndex < 0) {
            const pattern = /[A-Z]/;
            return !!pattern.test(value);
          }
          return true;
        },
        lowerCase(value) {
          if (this.editedIndex < 0) {
            const pattern = /[a-z]/;
            return !!pattern.test(value);
          }
          return true;
        },
        specialChar(value) {
          if (this.editedIndex < 0) {
            const pattern = /[\x21-\x2F\x3A-\x40\x5B-\x60\x7B-\x7E]/;
            return !!pattern.test(value);
          }
          return true;
        },
        numeric(value) {
          if (this.editedIndex < 0) {
            const pattern = /\d/;
            return !!pattern.test(value);
          }
          return true;
        },
      },
      passwordConfirmation: {
        required(value) {
          if (this.editedIndex < 0) {
            return value !== '';
          }
          return true;
        },
        sameAsPassword: sameAs('password'),
      },
      username: {
        required,
        whitespace(value) {
          const pattern = /\s/;
          return !pattern.test(value);
        },
        isUnique() {
          if (this.errorValidation != null) {
            return (
              this.errorValidation.username == null
              || this.errorValidation.username.length < 1
            );
          }
          return false;
        },
      },
    },
  },
  data: () => ({
    listStatusUser,
    statusItem: [],
    passwordFieldType: true,
    passwordConfirmFieldType: true,
    showGenerate: false,
    generatedPassword: '',
    copied: false,
    errorEdit: false,
  }),
  async mounted() {
    this.getRoles();
  },
  computed: {
    editedStatus() {
      return this.editedItem.status;
    },
    formTitle() {
      return this.editedIndex !== -1 ? 'Edit User' : 'Add User';
    },
    flashError: {
      get() {
        return this.$store.state.users.flashError;
      },
      set() {
        this.$store.commit('users/setFlashErrorMessage', '');
      },
    },
    errorValidation() {
      return this.$store.state.users.errors;
    },
    itemsRole() {
      return this.$store.state.rbac.role.allActiveRole;
    },
    usernameErrors() {
      const errors = [];
      if (!this.$v.editedItem.username.$dirty) {
        return errors;
      }
      if (!this.$v.editedItem.username.required) {
        errors.push('Username is required.');
      } else if (!this.$v.editedItem.username.whitespace) {
        errors.push("Can't contain whitespace.");
      }
      if (!this.$v.editedItem.username.isUnique) {
        if (
          this.errorValidation != null
          && this.errorValidation.username != null
        ) {
          this.errorValidation.username.forEach((item) => {
            errors.push(item);
          });
        }
      }
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.editedItem.email.$dirty) return errors;
      if (!this.$v.editedItem.email.email) {
        errors.push('Must be a valid email.');
      } else if (!this.$v.editedItem.email.required) {
        errors.push('Email is required.');
      }

      if (!this.$v.editedItem.email.isUnique) {
        if (
          this.errorValidation != null
          && this.errorValidation.email != null
        ) {
          this.errorValidation.email.forEach((item) => {
            errors.push(item);
          });
        }
      }
      return errors;
    },
    fullnameErrors() {
      const errors = [];
      if (!this.$v.editedItem.fullname.$dirty) return errors;
      if (!this.$v.editedItem.fullname.required) {
        errors.push('Fullname is required');
      }
      return errors;
    },
    roleErrors() {
      const errors = [];
      if (!this.$v.editedItem.role.$dirty) return errors;
      if (!this.$v.editedItem.role.required) {
        errors.push('Role is required');
      }
      return errors;
    },
    statusErrors() {
      const errors = [];
      if (!this.$v.editedItem.status.$dirty) return errors;
      if (!this.$v.editedItem.status.required) {
        errors.push('Status is required');
      }
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.editedItem.password.$dirty) return errors;
      if (!this.$v.editedItem.password.required) {
        errors.push('Password is required.');
      } else if (!this.$v.editedItem.password.lowerCase) {
        errors.push('Password must have a lowercase character.');
      } else if (!this.$v.editedItem.password.upperCase) {
        errors.push('Password must have an uppercase character.');
      } else if (!this.$v.editedItem.password.numeric) {
        errors.push('Password must have a numeric character.');
      } else if (!this.$v.editedItem.password.specialChar) {
        errors.push('Password must have a special character.');
      } else if (!this.$v.editedItem.password.minLength) {
        errors.push('Password must be at least 8 characters long.');
      }
      return errors;
    },
    passwordConfirmationErrors() {
      const errors = [];
      if (!this.$v.editedItem.passwordConfirmation.$dirty) return errors;
      if (!this.$v.editedItem.passwordConfirmation.required) {
        errors.push('Password Confirmation is required.');
      } else if (!this.$v.editedItem.passwordConfirmation.sameAsPassword) {
        errors.push('Passwords must be identical.');
      }

      return errors;
    },
    mobilePhoneErrors() {
      const errors = [];
      if (!this.$v.editedItem.mobile_phone.$dirty) return errors;
      if (!this.$v.editedItem.mobile_phone.numeric) {
        errors.push('Mobile Phone must be numeric.');
      } else if (!this.$v.editedItem.mobile_phone.required) {
        errors.push('Mobile Phone is required.');
      }

      return errors;
    },
  },
  watch: {
    editedStatus(val) {
      if (this.blocked && val === '1') {
        this.showGenerate = true;
      } else {
        this.showGenerate = false;
        this.generatedPassword = '';
        this.copied = false;
      }
    },
    blocked(val) {
      this.statusItem = this.listStatusUser
        .map((obj, idx) => {
          if (
            (val && this.listStatusUser[idx].value !== '0')
            || (!val && this.listStatusUser[idx].value !== '2')
          ) {
            return obj;
          }
          return null; // Add this line to return null for items that don't meet the conditions
        })
        .filter((obj) => obj !== null);
    },
    dialog(val) {
      if (!val) this.cancelDialog();
    },
    'editedItem.email': {
      handler(val) {
        if (this.errorValidation != null) {
          if (
            this.errorValidation.email != null
            && val !== this.errorValidation.email
          ) {
            this.$store.commit('users/clearErrorVariable', 'email');
          }
        }
      },
    },

    'editedItem.username': {
      handler(val) {
        if (this.errorValidation != null) {
          if (
            this.errorValidation.username != null
            && val !== this.errorValidation.username
          ) {
            this.$store.commit('users/clearErrorVariable', 'username');
          }
        }
      },
    },
  },
  methods: {
    disabledSave() {
      if (this.blocked) {
        return !this.copied;
      }
      return this.loading;
    },

    copyToClipboard() {
      if (this.generatedPassword) {
        Copy(this.generatedPassword);
        this.copied = true;
      }
    },

    passwordGenerate() {
      if (!this.generatedPassword) {
        this.generatedPassword = generatePassword();
      }
    },

    cancelDialog() {
      this.resetUniqueValidation();
      this.$emit('closeCancelParent');
      this.errorEdit = false;
    },
    resetUniqueValidation() {
      this.$store.commit('users/clearErrors');
    },
    getRoles() {
      this.$store.dispatch('rbac/role/fetchActiveRoles');
    },
    async save() {
      this.$store.commit('users/setFlashErrorMessage', '');
      this.resetUniqueValidation();
      this.$v.$touch();

      if (this.$v.$invalid) {
        return;
      }

      const speEncrypt = new CustomEncryption();
      const extras = speEncrypt.decryption({
        enc: process.env.VUE_APP_STRING_EXTRA2,
        masterkey: extraString3,
      });

      const user = this.editedItem;

      if (this.editedIndex > -1) {
        await this.updateUser(user);
      } else {
        await this.createUser(user, extras);
      }
    },

    async updateUser(user) {
      const status = await this.$store.dispatch('users/updateUser', {
        user,
        password: this.generatedPassword,
        index: this.editedIndex,
      });

      if (status === 313) {
        this.errorEdit = true;
      } else if (status) {
        this.cancelDialog();
        this.showAlert('Update user success !', 'mdi-check-circle', 'green');
      } else {
        this.showAlert('Error on update user !', 'mdi-alert', 'red');
      }
    },

    async createUser(user, extras) {
      const speEncrypt = new CustomEncryption();
      const encryptedPassword = speEncrypt.encrypt(
        speEncrypt.encryption(this.editedItem.password),
        '',
        extras,
      );
      const passwordConfirmation = speEncrypt.encrypt(
        speEncrypt.encryption(this.editedItem.passwordConfirmation),
        '',
        extras,
      );

      const status = await this.$store.dispatch('users/createUser', {
        user,
        encryptedPassword,
        passwordConfirmation,
      });

      if (status) {
        this.cancelDialog();
        this.showAlert('Add user success !', 'mdi-check-circle', 'green');
      } else {
        this.showAlert('Error on create user !', 'mdi-alert', 'red');
      }
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
