<template>
  <div>
    <v-card
      class="mb-12"
      height="20px"
      :style="{ 'box-shadow': '0 0 black', 'margin-top': '20px' }"
    >
      <div class="d-flex pa-5 flex-column align-center justify-center">
        <h3>Data QR</h3>
      </div>
    </v-card>
    <div :style="{ padding: '20px 50px 20px 50px' }" v-if="!loading">
      <data-detail-row
        v-if="showMenu == 2"
        :oldValueData="false"
        :data="data"
        :label="'NMID'"
        :valueData="data.nmid"
      />
      <v-row
        v-if="showMenu == 2 && merchantType === 'Aggregator'"
        style="align-items: center"
        v-show="
          data != null && (data.id_qr_type == '4' || data.id_qr_type == '5')
        "
      >
        <v-col cols="10" sm="4"></v-col>
        <v-col cols="2" sm="1"> </v-col>
        <v-col cols="12" sm="7">
          <v-radio-group
            class="ma-0"
            v-model="notifSticker"
            column
            disabled
            readonly
            dense
          >
            <template v-slot:label>
              <div>
                Apakah anda membutuhkan <strong>notif transaksi</strong> via API
                untuk merchant sticker anda?
              </div>
            </template>
            <v-radio label="Ya" value="1"></v-radio>
            <v-radio label="Tidak" value="0"></v-radio>
          </v-radio-group>
        </v-col>
      </v-row>

      <table-list
        v-if="showMenu == 1"
        class="elevation-1"
        :headers="headers"
        :allData="allData"
        :options="options"
        :totalData="totalData"
        :loading="loadingQr"
        @update:options="updateOptions"
      >
        <template v-slot:item="{ item, index }">
          <data-list
            :item="item"
            :index="index"
            :options="options"
            :selectedRow="selectedRow"
            v-on:selectRowParent="selectRow"
          />
        </template>
      </table-list>
    </div>
  </div>
</template>

<script>
import TableList from '@/components/table/TableList.vue';
import DataList from '@/components/table/DataQrList.vue';
import DataDetailRow from '@/components/detail/DataDetailRow.vue';

export default {
  props: {
    data: {
      type: Object
    },
    loading: {
      type: Boolean
    },
    menu: {
      type: String
    }
  },
  components: { TableList, DataList, DataDetailRow },
  data: () => ({
    selectedRow: '',
    search: {},
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'id',
        sortable: false,
      },
      {
        text: 'NMID',
        sortable: false,
        value: 'nmid',
      },
      {
        text: 'MID',
        sortable: false,
        value: 'mid',
      },
      {
        text: 'QR Type',
        sortable: false,
        value: 'qr_type',
      },
      {
        text: 'Status',
        sortable: false,
        value: 'status',
      },
      {
        text: 'Actions',
        value: 'actions',
        sortable: false,
      },
    ],
    page: '1',
    options: {},
    dialog: false,
    isFirstLoad: false,
    loadingQr: false,
    allData: [],
    totalData: 10,
    // totalPage: 1,
  }),
  computed: {
    showMenu() {
      let show;
      switch (this.menu) {
        case 'client':
          show = 1;
          break;
        case 'tracking':
          show = 2;
          break;
        default:
          show = 0;
      }

      return show;
    },
    merchantType() {
      let result = '';
      if (this.data != null) {
        if (this.data.merchant_type_id === 2) {
          result = 'Aggregator';
        } else {
          result = 'Merchant';
        }
      }
      return result;
    },
    notifSticker() {
      try {
        return this.data == null
          ? ''
          : this.data.notif_sticker_status.toString();
      } catch {
        return '';
      }
    },
  },
  methods: {
    selectRow(index) {
      if (this.selectedRow !== index) {
        this.selectedRow = index;
      } else {
        this.selectedRow = '';
      }
    },

    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },

    getData() {
      this.loadingQr = true;
      const {
        page, itemsPerPage, sortBy, sortDesc,
      } = this.options;

      this.$store
        .dispatch('client/merchant/getQRList', {
          id: this.$route.params.id,
          page,
          itemsPerPage,
          sortBy,
          sortDesc,
          search: this.search,
        })
        .then((response) => {
          if (response != null) {
            this.allData = response.list;
            this.totalData = parseInt(response.countdata, 10);
          }
        })
        .finally(() => {
          this.loadingQr = false;
        });
    },
  },
};
</script>
