<template>
  <v-card style="min-height: 80vh;" class="py-5 px-10">
    <div class="d-flex flex-column justify-center" style="height: 100%;">
      <h1 class="text-center primaryred--text">
        Jenis QR
      </h1>
      <v-form ref="form" @submit.prevent="next">
        <v-row justify="center">
          <v-col cols="4">
            <p class="text-center">Pilih Jenis QR yang ingin digunakan</p>
            <v-select
              :items="items"
              item-text="name"
              item-value="id"
              :value="data.qr_type"
              label="Jenis QR"
              @change="$emit('update:value', $event)"
              return-object
              :rules="[(v) => !!v || 'This field is required']"
            >
            </v-select>
          </v-col>
        </v-row>
        <Button v-on:backParent="back" :buttonClick="buttonClick" />
      </v-form>
    </div>
  </v-card>
</template>

<script>
import Button from "./ButtonClick.vue";
import { list_qr_edit } from "@/store/params.js";

export default {
  props: {
    data: {
      type: Object,
    },
  },
  components: {
    Button,
  },
  data() {
    return {
      buttonClick: {
        buttonBack: true,
        buttonLanjut: true,
      },
      items: list_qr_edit,
    };
  },
  methods: {
    back() {
      this.$emit("backParent");
    },
    next() {
      if (this.$refs.form.validate()) {
        this.$emit("nextParent");
      }
    },
  },
};
</script>
