import Parent from '../views/Parent.vue';

import ApList from '../views/history-approval/ApList.vue';
import ApDetail from '../views/history-approval/ApDetail.vue';

import { setBreadcrumb } from './index';

const historyApprovalRoute = {
  path: '/history-approval',
  name: 'Historyapproval',
  meta: {
    title: 'JAKONE - History Approval',
    appBarTitle: 'History Approval',
  },
  component: Parent,
  children: [
    {
      path: '/history-approval',
      meta: {
        title: 'JAKONE - History approval',
        appBarTitle: 'Daftar History approval',
        breadcrumbs: () => [
          setBreadcrumb('Daftar History approval', '/history-approval', true),
        ],
      },
      component: ApList,
      name: 'Historyapproval',
    },
    {
      path: '/history-approval/view/:id(\\d+)',
      meta: {
        title: 'JAKONE - View Detail',
        appBarTitle: 'View Detail',
        breadcrumbs: (params) => {
          const { id } = params;
          return [
            setBreadcrumb('History approval', '/history-approval'),
            setBreadcrumb('detail', '/history-approval/view', true),
            setBreadcrumb(`${id}`, `/history-approval/view/${id}`),
          ];
        },
      },
      component: ApDetail,
      name: 'History approval Detail',
    },
  ],
};

export default historyApprovalRoute;
