<template>
  <tr
    :key="index"
    :style="{ cursor: 'pointer' }"
    :class="selectedRow === index ? 'indigo lighten-5' : ''"
    @click="selectRow(index)"
  >
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ (options.page - 1) * options.itemsPerPage + index + 1 }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.merchant_name }}
    </td>
    <td
      v-if="page == 'history'"
      class="d-block d-sm-table-cell d-print-table-cell"
    >
      {{ item.nmid || item.mid }}
    </td>
    <td class="d-block d-sm-table-cell text-center d-print-table-cell">
      <v-chip
        :class="item.account_type.split(' ').join('_')"
        text-color="white"
      >
        {{ item.account_type }}
      </v-chip>
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.qr_type }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.address }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <div class="d-flex">
        <div
          class="d-flex elipsis"
          style="
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            max-width: 20ch;
          "
        >
          <p
            class="mr-1"
            :style="items.status === '0' ? 'color: red' : ''"
            v-for="(items, index) in item.account_classification"
            :key="items.id"
          >
            {{ items.nama_klasifikasi }}
            {{ item.account_classification.length !== index + 1 ? "," : "" }}
          </p>
        </div>
        {{ elipsis(item.account_classification) }}
      </div>
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.reference_number }}
    </td>
    <td v-if="!page" class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.created_at }}
    </td>
    <td v-if="page" class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.request_at }}
    </td>
    <td v-if="!page" class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.updated_at }}
    </td>
    <td v-if="page" class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.last_update }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.task_type }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      {{ item.status }}
    </td>
    <td class="d-block d-sm-table-cell d-print-table-cell">
      <v-icon small class="mr-2" @click="viewItem(item)"> mdi-eye </v-icon>
    </td>
  </tr>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
    },
    index: {
      type: Number,
    },
    options: {
      type: Object,
    },
    selectedData: {
      type: Array,
    },
    selectedRow: {
      type: [Number, String],
    },
    page: {
      type: String,
    },
  },
  computed: {
    selectedDataRow: {
      get() {
        return this.selectedData;
      },
      set(val) {
        this.$emit('changeSelectedDataParent', val);
      },
    },
  },
  methods: {
    elipsis(val) {
      let character = 0;
      for (let i = 0; i < val.length; i += 1) {
        character += val[i].nama_klasifikasi.length;
      }
      if (character > 20) {
        return '...';
      }
      return '';
    },
    selectRow(index) {
      this.$emit('selectRowParent', index);
    },
    viewItem(item) {
      this.$emit('viewItemParent', item);
    },
  },
};
</script>
