<template>
  <div>
    <v-list-item v-if="item.child == null" :to="item.link" router>
      <v-list-item-icon>
        <v-icon>{{ item.icon }}</v-icon>
      </v-list-item-icon>
      <v-list-item-content>
        <v-list-item-title>{{ item.title }}</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
    <v-list-group
      v-else
      :value="false"
      no-action
      :sub-group="!root"
      :prepend-icon="renderIcon"
      color="red lighten-3"
    >
      <template v-slot:activator>
        <v-list-item-content>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item-content>
      </template>

      <sub-menu
        v-for="(item, i) in item.child"
        :key="i"
        :item="item"
        class="pl-3"
      ></sub-menu>
    </v-list-group>
  </div>
</template>

<script>
export default {
  name: 'sub-menu',
  props: {
    item: {
      type: Object,
    },
    root: {
      type: Boolean,
    },
  },
  computed: {
    renderIcon() {
      return !this.root ? 'mdi-menu-down' : this.item.icon;
    },
  },
};
</script>
