<template>
  <v-card style="min-height: 80vh;" class="py-5 px-10">
    <div class="d-flex flex-column justify-center" style="height: 100%;">
      <h1 class="text-center primaryred--text">
        Nomor Pendaftaran JakOne Merchant
      </h1>
      <p class="text-center mb-0">Terimakasih !</p>
      <p class="text-center">
        Data Merchant sudah berhasil di submit. Kami akan memproses pendaftaran
        Anda.
      </p>
      <p class="text-center">
        No Pendaftaran: {{ data.reference_number.data.reference_number }}
      </p>
      <div>
        <ButtonClick :buttonClick="buttonClick" v-on:confirmParent="confirm" />
      </div>
    </div>
  </v-card>
</template>

<script>
import ButtonClick from "./ButtonClick.vue";
export default {
  props: {
    data: {
      type: Object,
    },
  },
  components: {
    ButtonClick,
  },
  data() {
    return {
      buttonClick: {
        buttonBack: false,
        buttonLanjut: false,
        buttonConfirm: true,
      },
    };
  },
  methods: {
    confirm() {
      this.showAlert(
        "Onboarding Merchant success !",
        "mdi-check-circle",
        "green"
      );
      this.$router.push("/client/merchant");
    },
    showAlert(message, icon, color) {
      this.$store.commit(
        "setSnackbar",
        {
          status: true,
          message: message,
          icon: icon,
          color: color,
        },
        { root: true }
      );
    },
  },
};
</script>
