import CryptoJS from 'crypto-js';
import { btoa, atob } from '@/lib/helpers';
import axios from 'axios';
import { intervalExport } from '@/store/params';

const trackingPengajuan = {
  namespaced: true,

  state() {
    return {
      allMerchants: [],
      totalMerchants: 0,
      loading: false,
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      merchant: null,
      totalPage: 0,
      intervalExport: null,
      exportId: null,
      countDataExport: 0,
    };
  },

  mutations: {
    setMerchants(state, { payload }) {
      state.allMerchants = payload.data.list;
    },

    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },

    clearMerchants(state) {
      state.allMerchants = [];
      state.totalMerchants = 0;
    },

    clearErrors(state) {
      state.errors = [];
    },

    setErrors(state, payload) {
      state.errors = payload;
    },

    setTotalMerchants(state, { payload }) {
      state.totalMerchants = parseInt(payload.data.countdata, 10);
      state.totalPage = parseInt(payload.data.totalPage, 10);
    },

    setLoading(state, loading) {
      state.loading = loading;
    },

    setDetailMerchant(state, { payload }) {
      state.merchant = payload;
    },

    setIntervalExport(state, payload) {
      state.intervalExport = payload;
    },

    setExportData(state, payload) {
      state.exportId = payload.export_id;
      state.countDataExport = payload.count_data;
    },

    setStateVariable(state, { payload, variable }) {
      state[variable] = payload;
    },
  },

  actions: {
    async fetchMerchants(
      ctx,
      {
        date, page, itemsPerPage, sortBy, sortDesc, search,
      },
    ) {
      ctx.commit('clearMerchants');
      ctx.commit('setLoading', true);
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        sortingBy = `${sortBy[0]} ${sort}`;
      } else {
        sortingBy = `id ${sort}`;
      }

      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString()
              + page.toString()
              + date
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/tracking/trackingpengajuan/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setTotalMerchants', {
              payload: response.data,
            });
            ctx.commit('setMerchants', { payload: response.data });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },

    async viewMerchant(ctx, { date, id }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
              + date
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/tracking/trackingpengajuan/view`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setDetailMerchant', {
              payload: response.data.data,
            });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },

    async viewDocument(ctx, {
      date, id, docType, type, download,
    }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        account_id: id,
        type,
        doc_type: docType,
        download,
        version: btoa(
          CryptoJS.SHA256(
            type.toString()
              + docType.toString()
              + id.toString()
              + date
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/document/document/view`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return null;
          }
          return response.data.data.base64_image;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return null;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async exportList(
      ctx,
      {
        date, page, itemsPerPage, sortBy, sortDesc, search,
      },
    ) {
      if (ctx.rootState.isExport === true) {
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Export in progress !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        return;
      }
      ctx.commit('setLoading', true);
      ctx.commit('setIsExport', true, { root: true });
      ctx.commit('setStateVariable', {
        payload: 0,
        variable: 'countSameExport',
      });
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        sortingBy = `${sortBy[0]} ${sort}`;
      } else {
        sortingBy = `id ${sort}`;
      }
      const controller = () => 'export-tracking-pengajuan-merchant';

      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString()
              + page.toString()
              + date
              + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/export/${
            controller()
          }/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const failed = () => {
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Export Failed !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        ctx.commit(
          'setInsertProgressGlobal',
          {
            absoluteBar: false,
            absoluteBarText: '',
          },
          { root: true },
        );
        ctx.commit('setIsExport', false, { root: true });
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            failed();
          } else {
            if (response.data.data.message != null) {
              ctx.commit(
                'setSnackbar',
                {
                  status: true,
                  message: response.data.data.message,
                  icon: 'mdi-alert',
                  color: 'red',
                },
                { root: true },
              );
              ctx.commit('setIsExport', false, { root: true });
              return;
            }
            ctx.commit('setExportData', response.data.data);
            const interval = setInterval(() => {
              ctx.dispatch(
                'exportStatus',
                {
                  date,
                  exportId: ctx.state.exportId,
                  countDataExport: ctx.state.countDataExport,
                  controller: controller(),
                },
                { root: true },
              );
            }, intervalExport);
            ctx.commit('setIntervalExport', interval, { root: true });
            ctx.commit(
              'setInsertProgressGlobal',
              {
                absoluteBar: true,
                absoluteBarText: 'Start to export data..',
              },
              { root: true },
            );
          }
        })
        .catch((error) => {
          failed();
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },
  },
};

export default trackingPengajuan;
