<template>
  <div>
    <v-row class="align-start">
      <v-col cols="10" sm="4"> {{ label }} </v-col>
      <v-col cols="2" sm="1"> : </v-col>
      <v-col cols="12" sm="7">
        <v-textarea
          :label="label"
          :rules="rule || [(v) => !!v || `${label} is required`]"
          :value="value"
          @input="updateInput($event)"
        >
        </v-textarea>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  props: {
    rule: Array,
    label: String,
    value: { type: [String, Number, Object], default: "" },
    required: Boolean,
  },
  methods: {
    updateInput(val) {
      this.$emit("update:value", val);
    },
  },
};
</script>
