<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <v-alert
      v-model="flashError"
      dense
      border="left"
      type="error"
      dismissible
      elevation="2"
    >
      {{ flashErrorMessage }}
    </v-alert>

    <table-list
      :headers="headers"
      :allData="allData"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :loading="loading"
      @update:options="updateOptions"
    >
      <template v-slot:filter>
        <filter-list
          :search="search"
          v-on:clearSearchParent="clearSearch"
          v-on:showDataParent="showData"
          :loading="loading"
        />
      </template>

      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
          v-on:addItemParent="addItem"
        />
      </template>

      <template v-slot:item="{ item, index }">
        <data-list
          :item="item"
          :index="index"
          :options="options"
          v-on:editItemParent="editItem"
          :selectedRow="selectedRow"
          v-on:selectRowParent="selectRow"
        />
      </template>

      <template v-slot:nodata>
        <v-btn outlined dark color="primaryred" @click="clearSearch">
          Reset
        </v-btn>
      </template>
    </table-list>

    <form-nns
      :loading="loading"
      :dialog="dialog"
      :editedItem="editedItem"
      :editedIndex="editedIndex"
      v-on:closeCancelParent="close"
    />
  </v-card>
</template>

<script>
import FilterList from '@/components/table/FilterNnsList.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import TableList from '@/components/table/TableList.vue';
import DataList from '@/components/table/DataNnsList.vue';
import FormNns from '@/components/dialog/FormNns.vue';

const dateObj = new Date();
const date = `${dateObj.getUTCFullYear()}-${`0${dateObj.getMonth() + 1}`.slice(
  -2,
)}-${`0${dateObj.getDate()}`.slice(-2)}`;

export default {
  components: {
    FilterList,
    ActionButton,
    TableList,
    DataList,
    FormNns,
  },
  data: () => ({
    selectedRow: '',
    listButton: {
      buttonRestart: true,
      buttonDots: false,
      buttonAdd: true,
    },
    search: {
      bank_code: '',
      bank_nns: '',
      bank_name: '',
      brand_name: '',
      created_at: null,
    },
    searchDefault: {
      bank_code: '',
      bank_nns: '',
      bank_name: '',
      brand_name: '',
      created_at: null,
    },
    searchFix: {
      bank_code: '',
      bank_nns: '',
      bank_name: '',
      brand_name: '',
      created_at: null,
    },
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'id',
        width: '1%',
        sortable: false,
      },
      {
        text: 'Kode Bank',
        value: 'bank_code',
        width: '14%',
        align: 'center',
      },
      {
        text: 'Kode NNS',
        value: 'bank_nns',
        width: '14%',
        align: 'center',
      },
      {
        text: 'Nama PJSP',
        value: 'bank_name',
        width: '14%',
        align: 'center',
      },
      {
        text: 'Brand Name',
        value: 'brand_name',
        width: '14%',
        align: 'center',
      },
      {
        text: 'Created Date',
        value: 'created_at',
        width: '14%',
        align: 'center',
      },
      {
        text: 'Actions',
        value: 'actions',
        width: '14%',
        sortable: false,
      },
    ],
    editedItem: {
      bank_code: '',
      bank_nns: '',
      bank_name: '',
      brand_name: '',
    },
    defaultItem: {
      bank_code: '',
      bank_nns: '',
      bank_name: '',
      brand_name: '',
    },
    editedIndex: -1,
    page: '1',
    options: {},
    dialog: false,
    dialogDelete: false,
    isFirstLoad: false,
  }),
  beforeMount() {
    this.$store.commit('nnsManagement/clearData');
  },
  computed: {
    flashErrorMessage() {
      return this.$store.state.nnsManagement.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.nnsManagement.flashError;
      },
      set() {
        this.$store.commit('nnsManagement/setFlashErrorMessage', '');
      },
    },
    loading() {
      return this.$store.state.nnsManagement.loading;
    },
    allData() {
      return this.$store.state.nnsManagement.allData;
    },
    totalData() {
      return this.$store.state.nnsManagement.totalData;
    },
    totalPage() {
      return this.$store.state.nnsManagement.totalPage;
    },
  },
  methods: {
    selectRow(index) {
      if (this.selectedRow !== index) {
        this.selectedRow = index;
      } else {
        this.selectedRow = '';
      }
    },
    clearSearch() {
      this.search = { ...this.searchDefault };
      this.$nextTick(() => {
        this.$store.commit('nnsManagement/clearData');
        this.showData();
      });
    },

    showData() {
      this.searchFix = { ...this.search };
      this.getData();
    },

    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },

    getData() {
      this.$store.commit('nnsManagement/setFlashErrorMessage', '');
      const {
        page, itemsPerPage, sortBy, sortDesc,
      } = this.options;

      this.$store
        .dispatch('nnsManagement/getData', {
          date,
          page,
          itemsPerPage,
          sortBy,
          sortDesc,
          search: this.searchFix,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },

    resetUniqueValidation() {
      this.$store.commit('nnsManagement/clearErrors');
    },

    close() {
      this.resetUniqueValidation();
      this.dialog = false;
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },

    addItem() {
      this.dialog = true;
    },

    editItem(item) {
      const newItem = item;
      newItem.data = { id: newItem.id };
      this.editedIndex = this.$store.state.nnsManagement.allData.indexOf(newItem);
      this.editedItem = { ...newItem };
      this.dialog = true;
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
