<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <v-alert
      v-model="flashError"
      dense
      border="left"
      type="error"
      dismissible
      elevation="2"
    >
      {{ flashErrorMessage }}
    </v-alert>

    <filter-list
      :search="search"
      v-on:clearSearchParent="clearSearch"
      v-on:showDataParent="showData"
      :loading="loading"
      v-on:update-created="onUpdateCreated"
      v-on:update-deactivated="onUpdateDeactivated"
    />

    <table-list
      :headers="headers"
      :allData="allData"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :loading="loading"
      @update:options="updateOptions"
    >
      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
          v-on:addItemParent="addNewMerchant"
        />
      </template>

      <template v-slot:item="{ item, index }">
        <data-list
          :item="item"
          :index="index"
          :options="options"
          v-on:listItemParent="listItem"
          :merchantType="'merchant'"
          :selectedRow="selectedRow"
          v-on:selectRowParent="selectRow"
        />
      </template>

      <template v-slot:nodata>
        <v-btn outlined dark color="primaryred" @click="clearSearch">
          Reset
        </v-btn>
      </template>
    </table-list>
  </v-card>
</template>

<script>
import FilterList from '@/components/table/FilterMerchantList.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import TableList from '@/components/table/TableList.vue';
import DataList from '@/components/table/DataMerchantList.vue';
import { isEmpty } from '@/lib/helpers';

export default {
  components: {
    FilterList, ActionButton, TableList, DataList,
  },
  data: () => ({
    selectedRow: '',
    listButton: {
      buttonRestart: true,
      buttonDots: false,
      buttonAddCreateMerchant: true,
    },
    search: {
      merchant_name: '',
      mid: '',
      nmid: '',
      mpan: '',
      qr_type: '',
      deactivated_at: [],
      mobile_phone: '',
      merchant_criteria: '',
      address: '',
      status: '',
      created_at: [],
      account_classification: '',
    },
    searchDefault: {
      merchant_name: '',
      mid: '',
      nmid: '',
      mpan: '',
      qr_type: '',
      deactivated_at: [],
      mobile_phone: '',
      merchant_criteria: '',
      address: '',
      status: '',
      created_at: [],
      account_classification: '',
    },
    searchFix: {
      merchant_name: '',
      mid: '',
      nmid: '',
      mpan: '',
      qr_type: '',
      deactivated_at: [],
      mobile_phone: '',
      merchant_criteria: '',
      address: '',
      status: '',
      created_at: [],
      account_classification: '',
    },
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'id',
        width: '10',
        sortable: false,
      },
      {
        text: 'Nama Merchant',
        value: 'merchant_name',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'MID',
        value: 'mid',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'NMID',
        value: 'nmid',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Alamat Lengkap',
        value: 'address',
        width: '250',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Klasifikasi Merchant',
        value: 'account_classification',
        width: '250',
        align: 'start',
        sortable: false,
      },
      {
        text: 'Kriteria Merchant',
        value: 'merchant_criteria',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'No. Handphone',
        value: 'mobile_phone',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Jenis QR',
        value: 'qr_type',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Tanggal Active',
        value: 'created_at',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Tanggal Inactive',
        value: 'deactivated_at',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Status',
        value: 'status',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Action',
        value: 'actions',
        align: 'center',
        width: '50',
        sortable: false,
      },
    ],
    page: '1',
    options: {},
    dialog: false,
    isFirstLoad: true,
  }),
  beforeMount() {
    this.$store.commit('client/merchant/clearMerchants');
  },
  computed: {
    flashErrorMessage() {
      return this.$store.state.client.merchant.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.client.merchant.flashError;
      },
      set() {
        this.$store.commit('client/merchant/setFlashErrorMessage', '');
      },
    },
    loading() {
      return this.$store.state.client.merchant.loading;
    },
    allData() {
      return this.$store.state.client.merchant.allMerchants;
    },
    totalData() {
      return this.$store.state.client.merchant.totalMerchants;
    },
    totalPage() {
      return this.$store.state.client.merchant.totalPage;
    },
    filterAllow() {
      if (
        isEmpty(this.search.merchant_name)
        && isEmpty(this.search.nmid)
        && isEmpty(this.search.mpan)
        && isEmpty(this.search.mobile_phone)
        && isEmpty(this.search.mid)
        && isEmpty(this.search.merchant_criteria)
        && isEmpty(this.search.qr_type)
        && isEmpty(this.search.address)
        // isEmpty(this.search.settlement_type) &&
        && isEmpty(this.search.status)
        && this.search.created_at.length < 1
        && this.search.deactivated_at.length < 1
        && this.search.account_classification.length < 1
      ) {
        return true;
      }
      return false;
    },
  },
  methods: {
    onUpdateDeactivated(val) {
      this.search.deactivated_at.push(val);
    },
    onUpdateCreated(val) {
      this.search.created_at.push(val);
    },
    addNewMerchant() {
      this.$store.commit('client/addMerchant/setDefaultOnBoarding');
      this.$router.push('/client/merchant/add');
    },
    selectRow(index) {
      if (this.selectedRow !== index) {
        this.selectedRow = index;
      } else {
        this.selectedRow = '';
      }
    },
    clearSearch() {
      this.search = { ...this.searchDefault };
      this.$nextTick(() => {
        this.$store.commit('client/merchant/clearMerchants');
      });
    },

    showData() {
      this.searchFix = JSON.parse(JSON.stringify(this.search));
      this.getData();
    },

    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },

    getData() {
      if (this.filterAllow) {
        this.$vuetify.goTo(0);
        this.showAlert(
          'one on the field need to be filled !',
          'mdi-alert',
          'red',
        );
        this.$store.commit('client/merchant/clearMerchants');
      } else {
        this.searchFix = {
          ...this.searchDefault,
          ...this.search,
        };

        this.searchFix.created_at.sort();
        this.searchFix.deactivated_at.sort();

        this.$store.commit('client/merchant/setFlashErrorMessage', '');
        const {
          page, itemsPerPage, sortBy, sortDesc,
        } = this.options;

        this.$store
          .dispatch('client/merchant/fetchMerchants', {
            page,
            itemsPerPage,
            sortBy,
            sortDesc,
            search: this.searchFix,
            searchDefault: this.searchDefault,
            merchantId: null,
            action: 'merchant',
          })
          .then(() => {
            if (this.totalPage < page) {
              if (this.totalPage === 0) {
                this.options.page = 1;
              } else {
                this.options.page = this.totalPage;
              }
            }
          });
      }
    },

    listItem(item) {
      this.$router.push({
        path: `/client/merchant/${item.id}/list-outlet`,
      });
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
