import Parent from "../views/Parent.vue";

// import report
import MerchantTransaction from "../views/report/MerchantTransaction.vue";

import { setBreadcrumb } from "./index.js";

export const reportRoute = {
    path: "/report",
    name: "Report",
    children: [
        {
            path: "/report/merchant-transaction",
            meta: {
                title: "JAKONE - Merchant Transaction Report",
                appBarTitle: "Merchant Transaction Report",
                breadcrumbs: () => {
                    return [
                        setBreadcrumb('report', '/report', true),
                        setBreadcrumb('merchant transaction report', '/report/merchant-transaction'),
                    ]
                }
            },
            component: MerchantTransaction,
            name: "MerchantTransaction",
        },
    ],
    meta: {
        title: "JAKONE - Report",
        appBarTitle: "Report",
    },
    component: Parent,
}