<template>
  <detail
    :title="'Merchant Detail'"
    :loading="loading"
    :subtitle="'Informasi detail mengenai merchant'"
    :caption="caption"
    v-on:revisiParent="revisi"
    :actionButton="actionButton"
  >
    <template v-slot:content>
      <content-data-merchant
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
        :menu="'approval'"
      />

      <content-data-doc
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
      />

      <content-data-endpoint
        v-if="data.qr_type === 'QR API'"
        :merchantType="'Merchant'"
        :data="data"
      />

      <content-data-remark v-if="data != null" :data="data" />

      <confirm-dialog
        :loading="loadingAll"
        :dialog="dialogApprove"
        :title="'Apakah anda yakin ingin melanjutkan proses ?'"
        v-on:closeCancelParent="close"
        v-on:confirmParent="confirm"
      />
    </template>
  </detail>
</template>

<script>
import Detail from '@/components/detail/Detail.vue';
import ConfirmDialog from '@/components/dialog/ConfirmDialog.vue';
import ContentDataMerchant from '@/components/detail/ContentDataMerchant.vue';
import ContentDataDoc from '@/components/detail/ContentDataDoc.vue';
import ContentDataRemark from '@/components/detail/ContentDataRemark.vue';
import ContentDataEndpoint from '@/components/detail/ContentDataEndpoint.vue';

export default {
  components: {
    Detail,
    ConfirmDialog,
    ContentDataMerchant,
    ContentDataDoc,
    ContentDataRemark,
    ContentDataEndpoint,
  },
  beforeMount() {
    this.getDetailData();
  },
  data() {
    return {
      dialog: false,
      dialogApprove: false,
      actionButton: {
        buttonPending: false,
        buttonReject: false,
        buttonApprove: false,
        buttonSave: false,
        buttonRevisi: false,
      },
      loading: false,
      reasonType: null,
    };
  },
  computed: {
    data() {
      return this.$store.state.historyApproval.ap.merchant;
    },
    loadingAll() {
      return this.$store.state.historyApproval.ap.loading;
    },
    caption() {
      let caption = null;
      if (this.data != null) {
        caption = {
          jenis_pengajuan: this.data.task_type,
          created_at: this.data.created_at,
          reference_number: this.data.reference_number,
        };
      }
      return caption;
    },
    merchantType() {
      if (this.data) {
        return this.data.merchant_type_id === 2 ? 'Aggregator' : 'Merchant';
      }
      return '';
    },
  },
  methods: {
    revisi() {
      this.$router.push(`/histoy-inputer/view/${this.$route.params.id}/edit`);
    },
    async getDetailData() {
      this.loading = true;
      await this.$store
        .dispatch('historyApproval/ap/viewMerchant', {
          id: this.$route.params.id,
        })
        .then(() => {
          if (this.data == null) {
            this.$router.push('/history-approval');
          }
        });
      this.loading = false;
    },
    pending() {
      this.reasonType = 1;
      this.dialog = true;
    },
    reject() {
      this.reasonType = 2;
      this.dialog = true;
    },
    approve() {
      this.dialogApprove = true;
    },
    close() {
      this.dialog = false;
      this.dialogApprove = false;
    },
  },
};
</script>
