<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <filter-component
      :filterList="filterList"
      v-on:showDataParent="showData"
      v-on:resetSearch="resetSearch"
      ref="filterComponentRef"
    ></filter-component>

    <table-log
      :expandedItem="true"
      class="mt-5"
      :headers="headers"
      :items="items"
      :options="options"
      :totalData="totalData"
      @update:options="updateOptions"
      :loading="loading"
      :footerProps="footerProps"
    >
      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
        />
      </template>

      <template #description="{data}">
        <div v-html="data.description"></div>
      </template>

      <template #brief_activitys="{data}">
        <div
          class="mb-0 no-wrap-text d-flex align-center"
          :class="data.brief_activity.is_success || 'response-failed'"
        >
          <div
            v-if="data.brief_activity.ip_address"
            class="d-flex align-center"
          >
            IP Address
            <v-chip class="bg-green-chip">
              {{ data.brief_activity.ip_address }}
            </v-chip>
          </div>
          <div class="d-flex align-center">
            Response Code
            <v-chip class="bg-green-chip">
              {{ data.brief_activity.response_code }}
            </v-chip>
          </div>
          <div class="d-flex align-center">
            Start Time
            <v-chip class="bg-green-chip">
              {{ data.brief_activity.start_time }}
            </v-chip>
          </div>
          <div class="d-flex align-center">
            End Time
            <v-chip class="bg-green-chip">
              {{ data.brief_activity.end_time }}
            </v-chip>
          </div>
          <div class="d-flex align-center">
            Elapsed Time
            <v-chip class="bg-green-chip">
              {{ data.brief_activity.elapsed_time }}
            </v-chip>
          </div>
        </div>
      </template>
      <template #no-data>
        <button-reset v-on:resetSearch="resetSearch"/>
      </template>

      <template #expanded-item="props">
        <expanded-items-log :data="props"/>
      </template>
    </table-log>
  </v-card>
</template>

<script>
import FilterComponent from '@/components/table/FilterComponent.vue';
import TableLog from '@/components/table/TableLog.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import ExpandedItemsLog from '@/components/utility/ExpandedItemsLog.vue';
import ButtonReset from '@/components/button/ButtonReset.vue';

const dateObj = new Date();
const date = `${dateObj.getUTCFullYear()
}-${
  (`0${dateObj.getMonth() + 1}`).slice(-2)
}-${
  (`0${dateObj.getDate()}`).slice(-2)}`;

export default {
  components: {
    FilterComponent,
    TableLog,
    ActionButton,
    ExpandedItemsLog,
    ButtonReset,
  },
  data() {
    return {
      listButton: {
        buttonRestart: true,
      },
      filterList: [
        {
          label: 'Date',
          type: 'date',
          icon: 'mdi-calendar',
          showDatePicker: false,
          max: true,
          value: '',
          rules: [(v) => !!v || 'This Field is required'],
        },
        {
          label: 'Phone Number',
          type: 'number',
          icon: 'mdi-magnify',
          value: '',
          rules: [(v) => !!v || 'This Field is required'],
        },
        {
          label: 'Jenis Description',
          type: 'select',
          item: [],
          value: '',
        },
      ],
      headers: [
        {
          text: 'No',
          align: '',
          width: '50px',
          value: 'no',
        },
        {
          text: 'Phone Number',
          align: 'center',
          width: '200px',
          value: 'phone_number',
        },
        {
          text: 'Description',
          align: 'center',
          width: '240px',
          value: 'description',
        },
        {
          text: 'Brief Activity',
          align: 'center',
          width: '400px',
          value: 'brief_activitys',
        },
      ],
      options: {},
      isFirstLoad: true,
      footerProps: {
        itemsPerPageOptions: [10, 20, 50, 100],
      },
    };
  },
  beforeMount() {
    this.jenisDescription();
    this.$store.commit('log/JakOneMobile/setClearJakOne');
  },
  computed: {
    allData() {
      return this.$store.state.log.JakOneMobile.jakOneMobileData;
    },
    items() {
      return this.allData?.list || [];
    },
    totalData() {
      return this.allData?.totalData || 0;
    },
    totalPage() {
      return this.$store.state.log.JakOneMobile.totalPage;
    },
    loading() {
      return this.$store.state.log.JakOneMobile.loading;
    },
  },
  methods: {
    jenisDescription() {
      const data = [
        {
          name: 'Onboarding',
          value: 'ONBOARDING',
        },
        {
          name: 'Transaction',
          value: 'TRX',
        },
        {
          name: 'OTP',
          value: 'OTP',
        },
      ];
      this.filterList[2].item = data;
    },
    resetSearch() {
      const value = {
        start_time: {
          HH: '00',
          mm: '00',
        },
        end_time: {
          HH: '23',
          mm: '59',
        },
      };
      this.filterList.map((obj) => {
        const container = obj;
        if (obj.type !== 'time to time') {
          container.value = '';
        } else {
          container.value = { ...value };
        }

        return container;
      });
      this.$refs.filterComponentRef.resetValidation();
      this.$store.commit('log/JakOneMobile/setClearJakOne');
    },
    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },
    showData() {
      this.getData();
    },
    getData() {
      const { page, itemsPerPage } = this.options;
      const search = {
        page,
        limit: itemsPerPage,
        search: {
          trx_date: this.filterList[0].value,
          phone_number: this.filterList[1].value,
          type: this.filterList[2].value,
        },
        sort_by: 'datetime',
        sort_order: 'DESC',
      };

      this.$store
        .dispatch('log/JakOneMobile/fetchData', {
          date,
          search,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },
  },
};
</script>
