<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <v-alert
      v-model="flashError"
      dense
      border="left"
      type="error"
      dismissible
      elevation="2"
    >
      {{ flashErrorMessage }}
    </v-alert>

    <table-list
      :headers="headers"
      :allData="allData"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :loading="loading"
      @update:options="updateOptions"
    >
      <template v-slot:filter>
        <filter-list
          :search="search"
          v-on:clearSearchParent="clearSearch"
          v-on:showDataParent="showData"
          :loading="loading"
        />
      </template>

      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:showDataParent="filterData"
          v-on:getDataParent="getData"
        />
      </template>

      <template v-slot:item="{ item, index }">
        <data-list
          :item="item"
          :index="index"
          :options="options"
          v-on:viewItemParent="viewItem"
          :selectedRow="selectedRow"
          v-on:selectRowParent="selectRow"
        />
      </template>

      <template v-slot:nodata>
        <v-btn outlined dark color="primaryred" @click="clearSearch">
          Reset
        </v-btn>
      </template>
    </table-list>
  </v-card>
</template>

<script>
import FilterList from '@/components/table/FilterTrackingList.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import TableList from '@/components/table/TableList.vue';
import DataList from '@/components/table/DataTrackingList.vue';

const dateObj = new Date();
const date = `${dateObj.getUTCFullYear()
}-${
  (`0${dateObj.getMonth() + 1}`).slice(-2)
}-${
  (`0${dateObj.getDate()}`).slice(-2)}`;

export default {
  components: {
    FilterList, ActionButton, TableList, DataList,
  },
  data: () => ({
    selectedRow: '',
    listButton: {
      buttonRestart: true,
      buttonDots: false,
      formDateRange: {
        label: 'Tanggal Pengajuan',
      },
    },
    filter: {
      created_at: [],
    },
    search: {
      inputer_name: '',
      merchant_name: '',
      mid: '',
      task_type: '',
      qr_type: '',
      status: '',
      reference_number: '',
      created_at: [],
      updated_at: null,
      account_classification: '',
    },
    searchDefault: {
      inputer_name: '',
      merchant_name: '',
      mid: '',
      task_type: '',
      qr_type: '',
      status: '',
      reference_number: '',
      created_at: [],
      updated_at: null,
      account_classification: '',
    },
    searchFix: {
      inputer_name: '',
      merchant_name: '',
      mid: '',
      task_type: '',
      qr_type: '',
      status: '',
      reference_number: '',
      created_at: [],
      updated_at: null,
    },
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'id',
        sortable: false,
      },
      {
        text: 'Inputer Name',
        value: 'inputer_name',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Nama Merchant',
        value: 'merchant_name',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Reference No',
        value: 'reference_number',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'MID',
        value: 'mid',
        width: '150',
        sortable: false,
        align: 'center',
      },
      {
        text: 'Jenis Pengajuan',
        value: 'task_type',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Jenis QR',
        value: 'qr_type',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Klasifikasi Merchant',
        value: 'account_classification',
        width: '13%',
        align: 'start',
      },
      {
        text: 'Tanggal Pengajuan',
        value: 'created_at',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Last Update',
        value: 'updated_at',
        width: '150',
        align: 'center',
        sortable: false,
      },
      {
        text: 'Status',
        value: 'status',
        width: '100',
        align: 'center',
        sortable: false,
      },
      { text: 'Action', value: 'actions', sortable: false },
    ],
    page: '1',
    options: {},
    dialog: false,
    isFirstLoad: true,
  }),
  beforeMount() {
    this.$store.commit('trackingPengajuan/clearMerchants');
  },
  computed: {
    flashErrorMessage() {
      return this.$store.state.trackingPengajuan.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.trackingPengajuan.flashError;
      },
      set() {
        this.$store.commit('trackingPengajuan/setFlashErrorMessage', '');
      },
    },
    loading() {
      return this.$store.state.trackingPengajuan.loading;
    },
    allData() {
      return this.$store.state.trackingPengajuan.allMerchants;
    },
    totalData() {
      return this.$store.state.trackingPengajuan.totalMerchants;
    },
    totalPage() {
      return this.$store.state.trackingPengajuan.totalPage;
    },
    filterAllow() {
      if (this.search.created_at.length < 1) {
        return true;
      }
      return false;
    },
  },
  methods: {
    selectRow(index) {
      if (this.selectedRow !== index) {
        this.selectedRow = index;
      } else {
        this.selectedRow = '';
      }
    },
    clearSearch() {
      this.search = { ...this.searchDefault };
      this.$nextTick(() => {
        this.$store.commit('trackingPengajuan/clearMerchants');
        this.showData();
      });
    },

    filterData(val) {
      val.sort();
      this.filter.created_at = val;
      this.showData();
    },

    showData() {
      this.search.created_at = this.filter.created_at;
      this.searchFix = { ...this.search };
      this.getData();
    },

    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },

    getData() {
      if (this.filterAllow) {
        this.$vuetify.goTo(0);
        this.showAlert('tanggal pengajuan is required !', 'mdi-alert', 'red');
        this.$store.commit('trackingPengajuan/clearMerchants');
      } else {
        this.$store.commit('trackingPengajuan/setFlashErrorMessage', '');
        const {
          page, itemsPerPage, sortBy, sortDesc,
        } = this.options;

        this.$store
          .dispatch('trackingPengajuan/fetchMerchants', {
            date,
            page,
            itemsPerPage,
            sortBy,
            sortDesc,
            search: this.searchFix,
          })
          .then(() => {
            if (this.totalPage < page) {
              if (this.totalPage === 0) {
                this.options.page = 1;
              } else {
                this.options.page = this.totalPage;
              }
            }
          });
      }
    },

    viewItem(item) {
      this.$router.push(`/tracking-pengajuan/list/view/${item.id}`);
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
