import CryptoJS from "crypto-js";
import { btoa, atob, dateHelpers } from "@/lib/helpers";
import axios from "axios";

export const nnsManagement = {
    namespaced: true,

    state() {
        return {
            allData: [],
            totalData: 0,
            loading: false,
            cacheData: {},
            errors: [],
            flashErrorMessage: "",
            flashError: false,
            item: null,
            insertProgress: 0,
            downloadFailedURL: "",
            countDone: 0,
            countSame: 0,
            intervalExport: null,
            exportId: null,
            countDataExport: 0,
            totalPage: 0,

            requestData: null,
        };
    },

    mutations: {
        setData(state, { payload }) {
            state.allData = payload.data.list;
        },

        setItem(state, { payload }) {
            state.item = payload.data.data;
        },

        setCategory(state, data) {
            state.allCategory = data;
        },

        setFlashErrorMessage(state, payload) {
            state.flashErrorMessage = payload;
            state.flashError = payload != "";
        },

        clearData(state) {
            state.allData = [];
            state.totalData = 0;
        },

        clearErrors(state) {
            state.errors = [];
        },

        clearErrorVariable(state, variable) {
            state.errors[variable] = [];
        },

        setErrors(state, payload) {
            state.errors = payload;
        },

        setTotalData(state, { payload }) {
            state.totalData = parseInt(payload.data.countdata);
            state.totalPage = parseInt(payload.data.totalPage);
        },

        setLoading(state, loading) {
            state.loading = loading;
        },

        setInsertProgress(state, insertProgress) {
            state.insertProgress = insertProgress;
        },

        setIntervalExport(state, payload) {
            state.intervalExport = payload;
        },

        setExportData(state, payload) {
            state.exportId = payload.export_id;
            state.countDataExport = payload.count_data;
        },

        setStateVariable(state, { payload, variable }) {
            state[variable] = payload;
        },

        setCancelation(state, payload) {
            state.requestData = payload
        },

        removeData(state, { index }) {
            state.totalData = state.totalData - 1;
            state.allData = state.allData.filter(function (value) {
                return value.id != index;
            });
        },

        pushData(state, { payload }) {
            state.totalData = state.totalData + 1;
            state.allData.push(payload);
        },

        editData(state, { payload, index }) {
            Object.assign(state.allData[index], payload);
        },
    },

    actions: {
        async getData(
            ctx,
            { date, page, itemsPerPage, sortBy, sortDesc, search }
        ) {
            if (ctx.state.requestData) ctx.state.requestData.cancel();

            const axiosSource = axios.CancelToken.source();
            ctx.commit("setCancelation", { cancel: axiosSource.cancel, msg: "Loading..." })

            ctx.commit("clearData");
            ctx.commit("setLoading", true);
            const sort = !sortDesc[0] ? "ASC" : "DESC";
            let sort_by = `${sortBy[0] ? sortBy[0] : "id"} ${sort}`;

            const data = JSON.stringify({
                page: page.toString(),
                limit: itemsPerPage.toString(),
                search,
                sort_by,
                version: btoa(
                    CryptoJS.SHA256(
                        sort_by +
                        itemsPerPage.toString() +
                        page.toString() +
                        date +
                        atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
                    ).toString()
                ),
            });
            const config = {
                method: "post",
                url:
                    process.env.VUE_APP_API_BASE_URL +
                    "/index.php?r=api/v1/nns/nns/index",
                headers: {
                    "X-CSRF-TOKEN": JSON.parse(
                        localStorage.getItem("logged_in_qram")
                    ).token,
                    "Content-Type": "application/json",
                },
                data,
                cancelToken: axiosSource.token
            };
            const doGetOut = () => ctx.dispatch("logout", null, { root: true });
            await axios(config)
                .then((response) => {
                    if (response.data.code !== 200) {
                        if (localStorage.getItem("language_qram") == null) {
                            ctx.commit(
                                "setFlashErrorMessage",
                                response.data.message_id
                            );
                        } else {
                            ctx.commit(
                                "setFlashErrorMessage",
                                response.data.message_en
                            );
                        }

                        if (response.data.code === "401") doGetOut();
                    } else {
                        ctx.commit("setTotalData", { payload: response.data });
                        ctx.commit("setData", { payload: response.data });
                    }
                })
                .catch((err) => {
                    if (err.response.status == "403") {
                        ctx.commit(
                            "setSnackbar",
                            {
                                status: true,
                                message: err.response.data.message,
                                icon: "mdi-alert",
                                color: "red",
                            },
                            { root: true }
                        );
                    }
                    else if (err.response.status == "401") {
                        ctx.dispatch("logout", null, { root: true });
                    }
                    // if (err.response.data.code !== null)
                    //     if (err.response.data.code === "401") doGetOut();
                }).finally(() => {
                    ctx.commit("setLoading", false);
                });
        },

        async addNns(ctx, { dataForm }) {
            ctx.commit("setLoading", true);
            const data = JSON.stringify({
                bank_code: dataForm.bank_code,
                bank_name: dataForm.bank_name,
                bank_nns: dataForm.bank_nns,
                brand_name: dataForm.brand_name,
                version: btoa(
                    CryptoJS.SHA256(
                        dataForm.bank_code +
                        dataForm.bank_nns +
                        dataForm.bank_name +
                        dataForm.brand_name +
                        dateHelpers() +
                        atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
                    ).toString()
                ),
            });
            const config = {
                method: "post",
                url:
                    process.env.VUE_APP_API_BASE_URL +
                    "/index.php?r=api/v1/nns/nns/create",
                headers: {
                    "X-CSRF-TOKEN": JSON.parse(
                        localStorage.getItem("logged_in_qram")
                    ).token,
                    "Content-Type": "application/json",
                },
                data: data,
            };
            return await axios(config)
                .then(function (response) {
                    if (response.data.code != "200") {
                        ctx.commit("setErrors", response.data.data);
                        if (localStorage.getItem("language_qram") == null) {
                            ctx.commit(
                                "setFlashErrorMessage",
                                response.data.message_id
                            );
                        } else {
                            ctx.commit(
                                "setFlashErrorMessage",
                                response.data.message_en
                            );
                        }
                        return false;
                    } else {
                        const newData = response.data.data;
                        ctx.commit("pushData", {
                            payload: newData,
                        });
                        return true;
                    }
                })
                .catch(function () {
                    return false;
                })
                .finally(() => {
                    ctx.commit("setLoading", false);
                });
        },

        async updateNns(ctx, { dataForm, index }) {
            ctx.commit("setLoading", true);
            const data = JSON.stringify({
                id: dataForm.id,
                bank_code: dataForm.bank_code,
                bank_name: dataForm.bank_name,
                bank_nns: dataForm.bank_nns,
                brand_name: dataForm.brand_name,
                version: btoa(
                    CryptoJS.SHA256(
                        dataForm.id +
                        dataForm.bank_code +
                        dataForm.bank_nns +
                        dataForm.bank_name +
                        dataForm.brand_name +
                        dateHelpers() +
                        atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
                    ).toString()
                ),
            });
            const config = {
                method: "post",
                url:
                    process.env.VUE_APP_API_BASE_URL +
                    "/index.php?r=api/v1/nns/nns/edit",
                headers: {
                    "X-CSRF-TOKEN": JSON.parse(
                        localStorage.getItem("logged_in_qram")
                    ).token,
                    "Content-Type": "application/json",
                },
                data: data,
            };
            return await axios(config)
                .then(function (response) {
                    if (response.data.code != "200") {
                        ctx.commit("setErrors", response.data.data);
                        if (localStorage.getItem("language_qram") == null) {
                            ctx.commit(
                                "setFlashErrorMessage",
                                response.data.message_id
                            );
                        } else {
                            ctx.commit(
                                "setFlashErrorMessage",
                                response.data.message_en
                            );
                        }
                        return false;
                    } else {
                        const updated = response.data.data;
                        ctx.commit("editData", {
                            payload: updated,
                            index,
                        });
                        return true;
                    }
                })
                .catch(function () {
                    return false;
                })
                .finally(() => {
                    ctx.commit("setLoading", false);
                });
        },
    },
};
