<template>
  <v-card class="d-flex pa-2 flex-column" outlined>
    <v-alert
      v-model="flashError"
      dense
      border="left"
      type="error"
      dismissible
      elevation="2"
    >
      {{ flashErrorMessage }}
    </v-alert>

    <table-list
      :headers="headers"
      :allData="allData"
      :options="options"
      :totalData="totalData"
      :tableHeight="'70vh'"
      :loading="loading"
      @update:options="updateOptions"
    >
      <template v-slot:filter>
        <filter-list
          :search="search"
          v-on:clearSearchParent="clearSearch"
          v-on:showDataParent="showData"
          :loading="loading"
        />
      </template>

      <template v-slot:top>
        <action-button
          :listButton="listButton"
          :loading="loading"
          v-on:getDataParent="getData"
          v-on:addItemParent="addItem"
        />
      </template>

      <template v-slot:item="{ item, index }">
        <data-list
          :item="item"
          :index="index"
          :options="options"
          v-on:editItemParent="editItem"
          v-on:actItemParent="actItem"
          :selectedRow="selectedRow"
          v-on:selectRowParent="selectRow"
        />
      </template>

      <template v-slot:nodata>
        <v-btn outlined dark color="primaryred" @click="clearSearch">
          Reset
        </v-btn>
      </template>
    </table-list>

    <form-mcc-mdr
      :loading="loading"
      :dialog="dialog"
      :editedItem="editedItem"
      :editedIndex="editedIndex"
      v-on:closeCancelParent="close"
    />

    <confirm-dialog
      :loading="loading"
      :dialog="dialogDelete"
      :title="
        'Are you sure you want ' +
        (editedItem.status == 1 ? 'Inactivate' : 'Activate') +
        ' this MCC & MDR'
      "
      v-on:closeCancelParent="close"
      v-on:confirmParent="confirm"
    />
  </v-card>
</template>

<script>
import FilterList from '@/components/table/FilterMccMdrList.vue';
import ActionButton from '@/components/table/ActionButton.vue';
import TableList from '@/components/table/TableList.vue';
import DataList from '@/components/table/DataMccMdrList.vue';
import FormMccMdr from '@/components/dialog/FormMccMdr.vue';
import ConfirmDialog from '@/components/dialog/ConfirmDialog.vue';

const dateObj = new Date();
const date = `${dateObj.getUTCFullYear()
}-${
  (`0${dateObj.getMonth() + 1}`).slice(-2)
}-${
  (`0${dateObj.getDate()}`).slice(-2)}`;

export default {
  components: {
    FilterList,
    ActionButton,
    TableList,
    DataList,
    FormMccMdr,
    ConfirmDialog,
  },
  data: () => ({
    selectedRow: '',
    listButton: {
      buttonRestart: true,
      buttonDots: false,
      buttonAdd: true,
    },
    search: {
      mcc: '',
      description: '',
      category_id: '',
      mcc_name: '',
      mdr_on_us: '',
      mdr_off_us: '',
      status: '',
      created_at: null,
      updated_at: null,
    },
    searchDefault: {
      mcc: '',
      description: '',
      category_id: '',
      mcc_name: '',
      mdr_on_us: '',
      mdr_off_us: '',
      status: '',
      created_at: null,
      updated_at: null,
    },
    searchFix: {
      mcc: '',
      description: '',
      category_id: '',
      mcc_name: '',
      mdr_on_us: '',
      mdr_off_us: '',
      status: '',
      created_at: null,
      updated_at: null,
    },
    headers: [
      {
        text: 'No',
        align: 'start',
        value: 'mcc',
        width: '1%',
        sortable: false,
      },
      {
        text: 'Kode MCC',
        value: 'mcc',
        width: '10%',
        align: 'center',
      },
      {
        text: 'Kategori MCC',
        value: 'category_name',
        width: '11%',
        align: 'center',
      },
      {
        text: 'Jenis Usaha',
        value: 'mcc_name',
        width: '15%',
        align: 'center',
      },
      {
        text: 'MDR On Us',
        value: 'mdr_on_us',
        width: '8%',
        align: 'center',
      },
      {
        text: 'MDR Off Us',
        value: 'mdr_off_us',
        width: '8%',
        align: 'center',
      },
      {
        text: 'Created Date',
        value: 'created_at',
        width: '13%',
        align: 'center',
      },
      {
        text: 'Last Updated Date',
        value: 'updated_at',
        width: '13%',
        align: 'center',
      },
      {
        text: 'Status',
        value: 'status',
        width: '11%',
        align: 'center',
      },
      {
        text: 'Action',
        value: 'actions',
        width: '10%',
        sortable: false,
      },
    ],
    editedItem: {
      mcc: '',
      description: '',
      category_id: '',
      mcc_category_name: '',
      mcc_name: '',
      mdr_on_us: '',
      mdr_off_us: '',
      status: '',
    },
    defaultItem: {
      mcc: '',
      description: '',
      category_id: '',
      mcc_category_name: '',
      mcc_name: '',
      mdr_on_us: '',
      mdr_off_us: '',
      status: '',
    },
    editedIndex: -1,
    page: '1',
    options: {},
    dialog: false,
    dialogDelete: false,
    isFirstLoad: false,
  }),
  beforeMount() {
    this.$store.commit('settingFee/mccmdr/clearData');
  },
  computed: {
    flashErrorMessage() {
      return this.$store.state.settingFee.mccmdr.flashErrorMessage;
    },
    flashError: {
      get() {
        return this.$store.state.settingFee.mccmdr.flashError;
      },
      set() {
        this.$store.commit('settingFee/mccmdr/setFlashErrorMessage', '');
      },
    },
    loading() {
      return this.$store.state.settingFee.mccmdr.loading;
    },
    allData() {
      return this.$store.state.settingFee.mccmdr.allData;
    },
    totalData() {
      return this.$store.state.settingFee.mccmdr.totalData;
    },
    totalPage() {
      return this.$store.state.settingFee.mccmdr.totalPage;
    },
  },
  methods: {
    selectRow(index) {
      if (this.selectedRow !== index) {
        this.selectedRow = index;
      } else {
        this.selectedRow = '';
      }
    },
    clearSearch() {
      this.search = { ...this.searchDefault };
      this.$nextTick(() => {
        this.$store.commit('settingFee/mccmdr/clearData');
        this.showData();
      });
    },

    showData() {
      this.searchFix = { ...this.search };
      this.getData();
    },

    updateOptions(val) {
      this.options = val;
      if (!this.isFirstLoad) {
        this.getData();
      }
      this.isFirstLoad = false;
    },

    getData() {
      this.$store.commit('settingFee/mccmdr/setFlashErrorMessage', '');
      const {
        page, itemsPerPage, sortBy, sortDesc,
      } = this.options;

      this.$store
        .dispatch('settingFee/mccmdr/getData', {
          date,
          page,
          itemsPerPage,
          sortBy,
          sortDesc,
          search: this.searchFix,
        })
        .then(() => {
          if (this.totalPage < page) {
            if (this.totalPage === 0) {
              this.options.page = 1;
            } else {
              this.options.page = this.totalPage;
            }
          }
        });
    },

    resetUniqueValidation() {
      this.$store.commit('settingFee/mccmdr/clearErrors');
    },

    close() {
      this.resetUniqueValidation();
      this.dialog = false;
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },

    addItem() {
      this.dialog = true;
    },

    editItem(item) {
      const newItem = item;
      newItem.data = { id: newItem.id };
      this.editedIndex = this.$store.state.settingFee.mccmdr.allData.indexOf(newItem);
      this.editedItem = { ...newItem };
      this.dialog = true;
    },

    actItem(item) {
      const newItem = item;
      newItem.data = { id: newItem.id };
      this.editedIndex = this.$store.state.rbac.role.allRoles.indexOf(newItem);
      this.editedItem = { ...newItem };
      this.dialogDelete = true;
    },

    async confirm() {
      await this.$store
        .dispatch('settingFee/mccmdr/deleteData', {
          date,
          body: this.editedItem,
          index: this.editedIndex,
        })
        .then((status) => {
          if (status) {
            this.close();
            this.showAlert(
              'Update status success !',
              'mdi-check-circle',
              'green',
            );
          } else {
            this.showAlert('Error on update status !', 'mdi-alert', 'red');
          }
        });
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
