import Parent from "../views/Parent.vue";

// import client merchant
import Merchant from "../views/client/merchant/Merchant.vue";
import MerchantOutlet from "../views/client/merchant/MerchantOutlet.vue";
import MerchantDetail from "../views/client/merchant/MerchantDetail.vue";
import AddMerchant from "../views/client/merchant/AddMerchant.vue";

import { setBreadcrumb } from "./index.js";

export const clientRoute = {
  path: "/client",
  name: "Client",
  meta: {
    title: "JAKONE - Merchant Management",
    appBarTitle: "Merchant Management",
  },
  component: Parent,
  children: [
    {
      path: "/client/merchant",
      meta: {
        title: "JAKONE - Merchant",
        appBarTitle: "Merchant",
        breadcrumbs: () => {
          return [
            setBreadcrumb("merchant management", "/client", true),
            setBreadcrumb("merchant", "/client/merchant"),
          ];
        },
      },
      component: Merchant,
      name: "Merchant",
    },
    {
      path: "/client/merchant/view/:id(\\d+)",
      meta: {
        title: "JAKONE - Merchant Detail",
        appBarTitle: "Merchant Detail",
        breadcrumbs: (params) => {
          const id = params.id;
          return [
            setBreadcrumb("merchant management", "/client", true),
            setBreadcrumb("merchant", "/client/merchant"),
            setBreadcrumb("detail", "/client/merchant/view", true),
            setBreadcrumb(`${id}`, `/client/merchant/view/${id}`),
          ];
        },
      },
      component: MerchantDetail,
      name: "MerchantDetail",
    },
    {
      path: "/client/merchant/:id(\\d+)/list-outlet",
      name: "ListOutletMerchant",
      meta: {
        title: "JAKONE - List Outlet Merchant",
        appBarTitle: "List Outlet Merchant",
        breadcrumbs: (params) => {
          const id = params.id;
          return [
            setBreadcrumb("merchant management", "/client", true),
            setBreadcrumb("merchant", "/client/merchant"),
            setBreadcrumb(`${id}`, `/client/merchant/${id}`, true),
            setBreadcrumb(`list outlet`, `/client/merchant/${id}/list-outlet`),
          ];
        },
      },
      component: MerchantOutlet,
    },
    {
      path: "/client/merchant/add",
      name: "Register Merchant",
      meta: {
        title: "JAKONE - Register Merchant",
        appBarTitle: "Register Merchant",
        breadcrumbs: () => {
          return [
            setBreadcrumb("merchant management", "/client", true),
            setBreadcrumb("merchant", "/client/merchant"),
            setBreadcrumb("Add New Merchant", "/client/merchant/add", true),
          ];
        },
      },
      component: AddMerchant,
    },
  ],
};
