<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div
    class="d-flex pa-2 flex-row flex-wrap"
    :style="{ height: 'fit-content' }"
  >
    <card-row
      :title="'Search'"
      class="red--text text--darken-4"
      :cardColor="'white'"
      :displayed="true"
    >
      <div style="width: 100%">
        <v-row>
          <v-col cols="12" sm="3" md="3">
            <v-text-field
              v-model="search.merchant_name"
              type="text"
              label="Nama Merchant"
              hide-details="true"
              dense
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="3" md="3">
            <v-text-field
              v-model="search.mid"
              type="text"
              label="MID"
              hide-details="true"
              dense
            ></v-text-field>
          </v-col>

          <v-col cols="12" sm="3" md="3">
            <v-text-field
              v-model="search.nmid"
              type="text"
              label="NMID"
              hide-details="true"
              dense
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="3" md="3">
            <v-select
              v-model="search.status"
              :items="listAccountStatus"
              label="Status"
              item-text="text"
              item-value="value"
              clearable
              hide-details="true"
              dense
            ></v-select>
          </v-col>
          <v-col cols="12" sm="6" md="6">
            <v-text-field
              v-model="search.address"
              type="text"
              label="Alamat Lengkap"
              hide-details="true"
              dense
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="3" md="3">
            <v-select
              v-model="search.merchant_criteria"
              :items="listMerchantCriteria"
              label="Kriteria Merchant"
              item-text="text"
              item-value="value"
              clearable
              hide-details="true"
              dense
            ></v-select>
          </v-col>
          <v-col cols="12" sm="3" md="3">
            <v-text-field
              v-model="search.mobile_phone"
              type="text"
              label="No. Handphone"
              hide-details="true"
              dense
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="3" md="3">
            <v-dialog
              ref="rangeDateDialog"
              v-model="rangeDateDialog"
              :return-value.sync="search.created_at"
              persistent
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  id="rangeDate"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                  v-model="dateRangeText"
                  label="Tanggal Active"
                  hide-details="true"
                  dense
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="search.created_at"
                scrollable
                range
                :max="getTommorowDate(0)"
              >
                <v-spacer></v-spacer>
                <v-btn text color="accent" @click="rangeDateDialog = false">
                  Cancel
                </v-btn>
                <v-btn
                  text
                  color="accent"
                  @click="
                    () => {
                      checkRangeDate();
                      $refs.rangeDateDialog.save(search.created_at);
                    }
                  "
                >
                  OK
                </v-btn>
              </v-date-picker>
            </v-dialog>
          </v-col>
          <v-col cols="12" sm="3" md="3">
            <v-dialog
              ref="rangeDateDialogInactive"
              v-model="rangeDateDialogInactive"
              :return-value.sync="search.deactivated_at"
              persistent
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  id="rangeDate"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                  v-model="dateRangeTextInactive"
                  label="Tanggal Inactive"
                  hide-details="true"
                  dense
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="search.deactivated_at"
                scrollable
                range
                :max="getTommorowDate(0)"
              >
                <v-spacer></v-spacer>
                <v-btn
                  text
                  color="accent"
                  @click="rangeDateDialogInactive = false"
                >
                  Cancel
                </v-btn>
                <v-btn
                  text
                  color="accent"
                  @click="
                    () => {
                      checkRangeDateInactive();
                      $refs.rangeDateDialogInactive.save(search.deactivated_at);
                    }
                  "
                >
                  OK
                </v-btn>
              </v-date-picker>
            </v-dialog>
          </v-col>

          <v-col cols="12" sm="3" md="3">
            <v-select
              v-model="search.qr_type"
              :items="listQrType"
              label="Jenis QR"
              item-text="text"
              item-value="value"
              clearable
              hide-details="true"
              dense
            ></v-select>
          </v-col>

          <v-col cols="12" sm="3" md="3">
            <v-autocomplete
              class="pt-0"
              clearable
              label="Klasifikasi Merchant"
              v-model="search.account_classification"
              :items="itemsKlasifikasi"
              item-text="nama_klasifikasi"
              item-value="id"
            />
          </v-col>
        </v-row>
        <v-row class="d-flex align-center justify-left">
          <v-col cols="12">
            <v-btn outlined dark color="primaryred" @click="clearSearch">
              Reset
            </v-btn>
            <v-btn
              color="primary"
              elevation="0"
              class="ml-2"
              @click.stop="showData"
              :loading="loading"
              >show</v-btn
            >
          </v-col>
        </v-row>
      </div>
    </card-row>
  </div>
</template>

<script>
import {
  listMerchantCriteria,
  listAccountStatus,
  qrType,
  settlementTypeItems,
} from '@/store/params';
import { dateHelpers } from '@/lib/helpers';
import CardRow from '@/components/CardRow.vue';

const dateObj = new Date();

export default {
  components: { CardRow },
  props: {
    search: {
      type: Object,
    },
    loading: {
      type: Boolean,
    },
  },
  data: () => ({
    listMerchantCriteria,
    listAccountStatus,
    listQrType: qrType,
    settlementTypeItems,
    rangeDateDialog: false,
    rangeDateDialogInactive: false,
  }),
  computed: {
    dateRangeText() {
      return this.search.created_at.join(' ~ ');
    },
    dateRangeTextInactive() {
      return this.search.deactivated_at.join(' ~ ');
    },
    allData() {
      return this.$store.state.klasifikasi.items;
    },
    itemsKlasifikasi() {
      return this.allData?.list || [];
    },
  },
  beforeMount() {
    const search = {
      page: 1,
      limit: 100,
      search: {
        created_date_start: '',
        created_date_end: '',
        name: '',
        status: 'ALL',
      },
    };
    this.$store
      .dispatch('klasifikasi/fetchData', {
        search,
      });
  },
  methods: {
    getTommorowDate(days) {
      const tomorrow = new Date();
      tomorrow.setDate(dateObj.getDate() + days);
      const dateTommorrow = this.convertDate(tomorrow);
      return dateTommorrow;
    },

    convertDate(date) {
      return dateHelpers(date);
    },

    checkRangeDate() {
      if (this.search.created_at.length === 1) {
        this.$emit('update-created', this.search.created_at[0]);
      }
    },

    checkRangeDateInactive() {
      if (this.search.deactivated_at.length === 1) {
        this.$emit('update-deactivated', this.search.deactivated_at[0]);
      }
    },
    replaceNullWithEmptyString(obj) {
      Object.keys(obj).forEach((key) => {
        if (obj[key] === null) {
          this.$set(obj, key, '');
        } else if (typeof obj[key] === 'object') {
          this.replaceNullWithEmptyString(obj[key]);
        }
      });
    },

    clearSearch() {
      this.$emit('clearSearchParent');
    },
    showData() {
      this.replaceNullWithEmptyString(this.search);
      this.$emit('showDataParent');
    },
  },
};
</script>
