<!-- eslint-disable vue/no-mutating-props -->
<template>
  <tr>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        label="Kode Bank"
        v-model="search.bank_code"
        type="text"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        label="Kode NNS"
        v-model="search.bank_nns"
        type="text"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        label="Nama PJSP"
        v-model="search.bank_name"
        type="text"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        label="Brand Name"
        v-model="search.brand_name"
        type="text"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-menu
        ref="dateMenu"
        v-model="dateMenu"
        :close-on-content-click="false"
        transition="scale-transition"
        offset-y
        max-width="290px"
        min-width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            label="Created Date"
            v-model="dateFormatted"
            v-bind="attrs"
            @blur="search.created_at = parseDate(dateFormatted)"
            v-on="on"
            hide-details="true"
            dense
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="search.created_at"
          no-title
          scrollable
          @input="dateMenu = false"
        >
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dateMenu = false"> Cancel </v-btn>
        </v-date-picker>
      </v-menu>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
  </tr>
</template>

<script>
export default {
  props: {
    search: {
      type: Object,
    },
    loading: {
      type: Boolean,
    },
  },
  data: () => ({
    dateFormatted: '',
    dateMenu: false,
  }),
  watch: {
    'search.created_at': {
      handler() {
        this.dateFormatted = this.formatDate(this.search.created_at);
        this.showData();
      },
    },
  },
  methods: {
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${day}/${month}/${year}`;
    },
    parseDate(date) {
      if (!date) return null;

      const [day, month, year] = date.split('/');
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`;
    },
    clearSearch() {
      this.$emit('clearSearchParent');
    },
    showData() {
      this.$emit('showDataParent');
    },
  },
};
</script>
