<template>
  <detail
    :title="'Merchant Detail'"
    :loading="loading"
    :subtitle="'Informasi detail mengenai merchant'"
    :caption="caption"
    v-on:pendingParent="pending"
    v-on:rejectParent="reject"
    v-on:approveParent="approve"
    :actionButton="actionButton"
  >
    <template v-slot:content>
      <content-data-merchant
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
        :menu="'approval'"
      />

      <content-data-doc
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
      />

      <content-data-endpoint
        v-if="data.qr_type === 'QR API'"
        :merchantType="'Merchant'"
        :data="data"
      />

      <content-data-remark v-if="data != null" :data="data" />

      <form-approval
        :loading="loading"
        :dialog="dialog"
        :reasonType="reasonType"
        :editedItem="editedItem"
        :editedIndex="editedIndex"
        v-on:closeCancelParent="close"
        :data="data"
        :message="
          !checkStatus
            ? messagePopUp.recheck
            : messagePopUp.new
        "
      />

      <confirm-dialog
        :loading="loadingAll"
        :dialog="dialogApprove"
        :title="
          !checkStatus
            ? messagePopUp.recheck
            : messagePopUp.new
        "
        v-on:closeCancelParent="close"
        v-on:confirmParent="confirm"
      />
    </template>
  </detail>
</template>

<script>
import Detail from '@/components/detail/Detail.vue';
import ContentDataMerchant from '@/components/detail/ContentDataMerchant.vue';
import ContentDataDoc from '@/components/detail/ContentDataDoc.vue';
import ContentDataRemark from '@/components/detail/ContentDataRemark.vue';
import FormApproval from '@/components/dialog/FormApproval.vue';
import ConfirmDialog from '@/components/dialog/ConfirmDialog.vue';
import ContentDataEndpoint from '@/components/detail/ContentDataEndpoint.vue';

export default {
  components: {
    Detail,
    ContentDataMerchant,
    ContentDataRemark,
    ContentDataDoc,
    FormApproval,
    ConfirmDialog,
    ContentDataEndpoint,
  },
  beforeMount() {
    this.getDetailData();
  },
  data() {
    return {
      messagePopUp: {
        new: 'Apakah klasifikasi sudah sesuai?',
        recheck: 'Apakah anda yakin ingin melanjutkan proses ?',
      },
      dialog: false,
      dialogApprove: false,
      actionButton: {
        buttonPending: true,
        buttonReject: true,
        buttonApprove: true,
      },
      loading: false,
      reasonType: null,
      editedItem: {
        id: this.$route.params.id,
        id_reason: null,
        note: '',
      },
      defaultItem: {
        id: this.$route.params.id,
        id_reason: null,
        note: '',
      },
      editedIndex: -1,
    };
  },
  computed: {
    checkStatus() {
      return this.data.status === 'New';
    },
    data() {
      return this.$store.state.daftarTugas.ap.merchant;
    },
    loadingAll() {
      return this.$store.state.daftarTugas.ap.loading;
    },
    caption() {
      let caption = null;
      if (this.data != null) {
        caption = {
          jenis_pengajuan: this.data.task_type,
          created_at: this.data.created_at,
          reference_number: this.data.reference_number,
        };
      }
      return caption;
    },
    merchantType() {
      if (this.data !== null) {
        if (this.data.merchant_type_id === 2) {
          return 'Aggregator';
        }
        return 'Merchant';
      }
      return '';
    },
  },
  methods: {
    async getDetailData() {
      this.loading = true;

      await this.$store
        .dispatch('daftarTugas/ap/viewMerchant', {
          id: this.$route.params.id,
        })
        .then(() => {
          if (this.data == null) {
            this.$router.push('/daftar-approval');
          }
        });

      this.loading = false;
    },
    pending() {
      this.reasonType = 1;
      this.dialog = true;
    },
    reject() {
      this.reasonType = 2;
      this.dialog = true;
    },
    approve() {
      this.dialogApprove = true;
    },

    async confirm() {
      await this.$store
        .dispatch('daftarTugas/ap/saveApprove', {
          accountClassification: this.data.account_classification,
          id: this.$route.params.id,
        })
        .then((status) => {
          if (status) {
            this.close();
            this.showAlert('Approve success !', 'mdi-check-circle', 'green');
            this.$router.push('/daftar-approval');
          } else {
            this.showAlert('Error on Approve !', 'mdi-alert', 'red');
          }
        });
    },

    resetUniqueValidation() {
      this.$store.commit('daftarTugas/ap/clearErrors');
    },

    close() {
      this.resetUniqueValidation();
      this.dialog = false;
      this.dialogApprove = false;
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem };
        this.editedIndex = -1;
      });
    },

    showAlert(message, icon, color) {
      this.$store.commit(
        'setSnackbar',
        {
          status: true,
          message,
          icon,
          color,
        },
        { root: true },
      );
    },
  },
};
</script>
