import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';
import { intervalExport } from '@/store/params';

const merchantTransaction = {
  namespaced: true,

  state() {
    return {
      allData: [],
      totalData: 0,
      loading: false,
      loadingAcquirer: false,
      loadingIssuer: false,
      cacheData: {},
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      item: null,
      insertProgress: 0,
      downloadFailedURL: '',
      countDone: 0,
      countSame: 0,
      intervalExport: null,
      exportId: null,
      countDataExport: 0,
      allAcquirer: null,
      allIssuer: null,
      totalPage: 0,

      requestData: null,
    };
  },

  mutations: {
    setData(state, { payload }) {
      state.allData = payload.data.list;
    },

    setItem(state, { payload }) {
      state.item = payload.data.data;
    },

    setCategory(state, data) {
      state.allCategory = data;
    },

    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },

    clearData(state) {
      state.allData = [];
      state.totalData = 0;
    },

    clearErrors(state) {
      state.errors = [];
    },

    setErrors(state, payload) {
      state.errors = payload;
    },

    setTotalData(state, { payload }) {
      state.totalData = parseInt(payload.data.countdata, 10);
      state.totalPage = parseInt(payload.data.totalPage, 10);
    },

    setLoading(state, loading) {
      state.loading = loading;
    },

    setLoadingAcquirer(state, loadingAcquirer) {
      state.loadingAcquirer = loadingAcquirer;
    },

    setLoadingIssuer(state, loadingIssuer) {
      state.loadingIssuer = loadingIssuer;
    },

    setInsertProgress(state, insertProgress) {
      state.insertProgress = insertProgress;
    },

    setIntervalExport(state, payload) {
      state.intervalExport = payload;
    },

    setExportData(state, payload) {
      state.exportId = payload.export_id;
      state.countDataExport = payload.count_data;
    },

    setAcquirer(state, payload) {
      state.allAcquirer = payload;
    },

    setIssuer(state, payload) {
      state.allIssuer = payload;
    },

    setStateVariable(state, { payload, variable }) {
      state[variable] = payload;
    },

    setCancelation(state, payload) {
      state.requestData = payload;
    },
  },

  actions: {
    async getData(
      ctx,
      {
        page, itemsPerPage, sortBy, sortDesc, search,
      },
    ) {
      if (ctx.state.requestData) ctx.state.requestData.cancel();

      const axiosSource = axios.CancelToken.source();
      ctx.commit('setCancelation', { cancel: axiosSource.cancel, msg: 'Loading...' });

      ctx.commit('clearData');
      ctx.commit('setLoading', true);
      const sort = !sortDesc[0] ? 'ASC' : 'DESC';
      const sortingBy = `${sortBy[0] ? sortBy[0] : 'rrn'} ${sort}`;

      if (search.trx_datetime.length === 2) {
        const d1 = Date.parse(search.trx_datetime[0]);
        const d2 = Date.parse(search.trx_datetime[1]);

        if (d1 > d2) {
          [search.trx_datetime[1],
            search.trx_datetime[0]] = [search.trx_datetime[0],
            search.trx_datetime[1]];
        }
      }

      const data = JSON.stringify({
        page: page.toString(),
        limit: itemsPerPage.toString(),
        search,
        sort_by: sortingBy,
        version: btoa(
          CryptoJS.SHA256(
            page.toString()
                        + itemsPerPage.toString()
                        + dateHelpers()
                        + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
                    `${process.env.VUE_APP_API_BASE_URL
                    }/index.php?r=api/v1/report/merchant-transaction/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
        cancelToken: axiosSource.token,
      };
      const doGetOut = () => ctx.dispatch('logout', null, { root: true });
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }

            if (response.data.code === 401) doGetOut();
          } else {
            ctx.commit('setTotalData', { payload: response.data });
            ctx.commit('setData', { payload: response.data });
          }
        })
        .catch((err) => {
          if (err.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: err.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (err.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        }).finally(() => {
          ctx.commit('setLoading', false);
        });
    },

    async exportList(
      ctx,
      {
        page, itemsPerPage, sortBy, sortDesc, search,
      },
    ) {
      if (ctx.rootState.isExport === true) {
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Export in progress !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        return;
      }

      ctx.commit('setLoading', true);
      ctx.commit('setIsExport', true, { root: true });
      ctx.commit('setStateVariable', {
        payload: 0,
        variable: 'countSameExport',
      });
      const sort = sortDesc[0] === false ? 'ASC' : 'DESC';
      const data = JSON.stringify({
        search,
        sort_order: sort,
        sort_by: sortBy[0],
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString()
                        + page.toString()
                        + dateHelpers()
                        + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
                    `${process.env.VUE_APP_API_BASE_URL
                    }/index.php?r=api/v1/export/merchant-transaction/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const failed = () => {
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Export Failed !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        ctx.commit(
          'setInsertProgressGlobal',
          {
            absoluteBar: false,
            absoluteBarText: '',
          },
          { root: true },
        );
        ctx.commit('setIsExport', false, { root: true });
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            failed();
          } else {
            if (response.data.data.message != null) {
              ctx.commit(
                'setSnackbar',
                {
                  status: true,
                  message: response.data.data.message,
                  icon: 'mdi-alert',
                  color: 'red',
                },
                { root: true },
              );
              ctx.commit('setIsExport', false, { root: true });
              return;
            }
            ctx.commit('setExportData', response.data.data);
            const interval = setInterval(() => {
              ctx.dispatch('exportStatus', {
                exportId: ctx.state.exportId,
                countDataExport: ctx.state.countDataExport,
                controller: 'merchant-transaction',
              }, { root: true });
            }, intervalExport);
            ctx.commit('setIntervalExport', interval, { root: true });
            ctx.commit(
              'setInsertProgressGlobal',
              {
                absoluteBar: true,
                absoluteBarText: 'Start to export data..',
              },
              { root: true },
            );
          }
        })
        .catch((error) => {
          failed();
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },

    async getAcquirer(ctx) {
      ctx.commit('setLoadingAcquirer', true);

      const data = JSON.stringify({
        version: btoa(
          CryptoJS.SHA256(
            dateHelpers() + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
                    `${process.env.VUE_APP_API_BASE_URL
                    }/index.php?r=api/v1/report/merchant-transaction/acquirer`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const doGetOut = () => ctx.dispatch('logout', null, { root: true });
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }

            if (response.data.code === 401) doGetOut();
          } else {
            ctx.commit('setAcquirer', response.data.data);
          }
        })
        .catch((err) => {
          if (err.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: err.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (err.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoadingAcquirer', false);
        });
    },

    async getIssuer(ctx) {
      ctx.commit('setLoadingIssuer', true);

      const data = JSON.stringify({
        version: btoa(
          CryptoJS.SHA256(
            dateHelpers() + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
                    `${process.env.VUE_APP_API_BASE_URL
                    }/index.php?r=api/v1/report/merchant-transaction/issuer`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(
            localStorage.getItem('logged_in_qram'),
          ).token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const doGetOut = () => ctx.dispatch('logout', null, { root: true });
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_id,
              );
            } else {
              ctx.commit(
                'setFlashErrorMessage',
                response.data.message_en,
              );
            }

            if (response.data.code === 401) doGetOut();
          } else {
            ctx.commit('setIssuer', response.data.data);
          }
        })
        .catch((err) => {
          if (err.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: err.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (err.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoadingIssuer', false);
        });
    },
  },
};

export default merchantTransaction;
