<!-- eslint-disable vue/no-mutating-props -->
<template>
  <tr>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-autocomplete
        v-model="search.merchant_name"
        type="text"
        label="Nama"
        :items="nameMerchant"
        v-on:keyup.enter="showDataAutocomplete"
        @change="showData"
        :search-input.sync="searchName"
        :freeSolo="true"
        :no-cache="false"
        :no-data-text="
          search.merchant_name.length < 3
            ? 'Type at least 3 characters'
            : 'No data available'
        "
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-autocomplete>
    </td>
    <td
      v-if="page == 'history'"
      class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"
    >
      <v-text-field
        v-if="!page"
        v-model="search.nmid"
        type="text"
        label="MID"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
      <v-text-field
        v-else
        v-model="search.mid"
        type="text"
        label="MID"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.account_type"
        :items="listMerchantType"
        label="Type"
        item-text="text"
        item-value="value"
        @change="showData"
        @input="search.account_type = $event ? $event : ''"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.qr_type"
        :items="listQr"
        label="Type"
        item-text="text"
        item-value="value"
        @change="showData"
        @input="search.qr_type = $event ? $event : ''"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.address"
        type="text"
        label="Alamat Lengkap"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-autocomplete
        clearable
        label="Pilih Klasifikasi"
        v-model="search.account_classification"
        :items="itemsKlasifikasi"
        item-text="nama_klasifikasi"
        item-value="id"
      />
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-text-field
        v-model="search.reference_number"
        type="text"
        label="Nomor Pengajuan"
        v-on:keyup.enter="showData"
        hide-details="true"
        dense
        prepend-inner-icon="mdi-magnify"
      ></v-text-field>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-menu
        ref="dateMenu"
        v-model="dateMenu"
        :close-on-content-click="false"
        transition="scale-transition"
        offset-y
        max-width="290px"
        min-width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="dateFormatted"
            label="Request Date"
            v-bind="attrs"
            @blur="search.created_at = parseDate(dateFormatted)"
            v-on="on"
            hide-details="true"
            dense
            clearable
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="search.created_at"
          no-title
          scrollable
          @input="dateMenu = false"
        >
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dateMenu = false"> Cancel </v-btn>
        </v-date-picker>
      </v-menu>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-menu
        ref="lastUpdateMenu"
        v-model="lastUpdateMenu"
        :close-on-content-click="false"
        transition="scale-transition"
        offset-y
        max-width="290px"
        min-width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="dateFormatLastUpdate"
            label="Last Update"
            v-bind="attrs"
            @blur="search.updated_at = parseDate(dateFormatLastUpdate)"
            v-on="on"
            hide-details="true"
            dense
            clearable
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="search.updated_at"
          no-title
          scrollable
          @input="lastUpdateMenu = false"
        >
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="lastUpdateMenu = false">
            Cancel
          </v-btn>
        </v-date-picker>
      </v-menu>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row">
      <v-select
        v-model="search.task_type"
        :items="listTaskType"
        label="Jenis Pengajuan"
        item-text="text"
        item-value="value"
        @change="showData"
        @input="search.task_type = $event ? $event : ''"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td
      v-if="page"
      class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"
    >
      <v-select
        v-model="search.status"
        :items="listStatus"
        label="Request Status"
        item-text="text"
        item-value="value"
        @change="showData"
        @input="search.status = $event ? $event : ''"
        clearable
        hide-details="true"
        dense
      ></v-select>
    </td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
    <td class="pa-3 d-block d-sm-table-cell d-print-table-cell fixed-row"></td>
  </tr>
</template>

<script>
import {
  listMerchantType,
  listTaskType,
  listHistoryInputerStatus,
  listQrType,
} from '@/store/params';

export default {
  props: {
    search: {
      type: Object,
    },
    loading: {
      type: Boolean,
    },
    page: {
      type: String,
    },
  },
  data: () => ({
    nameMerchant: [],
    searchName: '',
    dateFormatted: '',
    dateFormatLastUpdate: '',
    dateMenu: false,
    lastUpdateMenu: false,
    listMerchantType,
    listQr: listQrType,
    listTaskType,
    listStatus: listHistoryInputerStatus,
  }),
  computed: {
    allData() {
      return this.$store.state.klasifikasi.items;
    },
    itemsKlasifikasi() {
      return this.allData?.list || [];
    },
  },
  beforeMount() {
    const search = {
      page: 1,
      limit: 100,
      search: {
        created_date_start: '',
        created_date_end: '',
        name: '',
        status: 'ALL',
      },
    };
    this.$store
      .dispatch('klasifikasi/fetchData', {
        search,
      });
  },
  watch: {
    'search.account_classification': function (val) {
      if (!val) {
        this.search.account_classification = '';
      }
      this.showData();
    },
    searchName(value) {
      if (this.$route.path === '/history-inputer');
      const type = this.$route.path === '/history-inputer'
        ? 'historyInputer'
        : 'daftarInputer';
      if (value && value.length >= 3) {
        const dataUser = JSON.parse(localStorage.logged_in_qram);
        this.$store
          .dispatch(`${type}/ap/getSearchName`, {
            value,
            userId: dataUser.user.id,
          })
          .then(() => {
            const store = this.$route.path === '/history-inputer'
              ? this.$store.state.historyInputer.ap.nameMerchant
              : this.$store.state.daftarInputer.ap.nameMerchant;
            this.nameMerchant = store;
          });
      } else {
        this.nameMerchant = [];
      }
    },
    'search.created_at': {
      handler() {
        this.dateFormatted = this.formatDate(this.search.created_at);
        this.showData();
      },
    },
    'search.request_at': {
      handler() {
        this.dateFormatted = this.formatDate(this.search.request_at);
        this.showData();
      },
    },
    'search.updated_at': {
      handler() {
        this.dateFormatLastUpdate = this.formatDate(this.search.updated_at);
        this.showData();
      },
    },
    'search.submit_at': {
      handler() {
        this.dateFormatLastUpdate = this.formatDate(this.search.submit_at);
        this.showData();
      },
    },
    'search.last_update': {
      handler() {
        this.dateFormatLastUpdate = this.formatDate(this.search.last_update);
        this.showData();
      },
    },
  },
  methods: {
    formatDate(newDate) {
      if (!newDate) return null;

      const [year, month, day] = newDate.split('-');
      return `${day}/${month}/${year}`;
    },
    parseDate(newDate) {
      if (!newDate) return null;

      const [day, month, year] = newDate.split('/');
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`;
    },
    clearSearch() {
      this.$emit('clearSearchParent');
    },
    showDataAutocomplete(val) {
      if (!this.nameMerchant.includes(this.search.merchant_name)) {
        this.$emit('update-props', val.target.value);
      }
      this.showData();
    },
    showData() {
      this.$emit('showDataParent');
    },
  },
};
</script>
