/* eslint-disable camelcase */
import CryptoJS from 'crypto-js';
import { btoa, atob, dateHelpers } from '@/lib/helpers';
import axios from 'axios';
import { intervalExport, intervalUpload } from '@/store/params';

const merchant = {
  namespaced: true,

  state() {
    return {
      allMerchants: [],
      totalMerchants: 0,
      loading: false,
      errors: [],
      flashErrorMessage: '',
      flashError: false,
      merchant: null,
      merchantDocument: [],
      countData: 0,
      countSuccess: 0,
      countFailed: 0,
      uploadProgress: 0,
      onUpload: false,
      insertProgress: 0,
      onInsert: false,
      insertName: '',
      importId: null,
      downloadFailedURL: '',
      countDone: 0,
      countSame: 0,
      intervalImport: null,
      intervalExport: null,
      exportId: null,
      countDataExport: 0,
      totalPage: 0,
    };
  },

  mutations: {
    setMerchants(state, { payload }) {
      state.allMerchants = payload.data.list;
    },

    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload !== '';
    },

    clearMerchants(state) {
      state.allMerchants = [];
      state.totalMerchants = 0;
    },

    clearErrors(state) {
      state.errors = [];
    },

    setErrors(state, payload) {
      state.errors = payload;
    },

    setTotalMerchants(state, { payload }) {
      state.totalMerchants = parseInt(payload.data.countdata, 10);
      state.totalPage = parseInt(payload.data.totalPage, 10);
    },

    clearErrorVariable(state, variable) {
      state.errors[variable] = [];
    },

    setLoading(state, loading) {
      state.loading = loading;
    },

    setDetailMerchant(state, { payload }) {
      state.merchant = payload;
    },
    setMerchantVariable(state, { variable, payload }) {
      state.merchant[variable] = payload;
    },

    setMerchantDocument(state, document) {
      state.merchantDocument.push(document);
    },
    setUploadProgress(state, uploadProgress) {
      state.uploadProgress = uploadProgress;
    },
    setOnUpload(state, onUpload) {
      state.onUpload = onUpload;
    },
    setInsertProgress(state, insertProgress) {
      state.insertProgress = insertProgress;
    },
    setInsertName(state, insertName) {
      state.insertName = insertName;
    },
    setOnInsert(state, onInsert) {
      state.onInsert = onInsert;
    },
    setIntervalImport(state, payload) {
      state.intervalImport = payload;
    },
    setIntervalExport(state, payload) {
      state.intervalExport = payload;
    },
    setImportData(state, payload) {
      state.importId = payload.import_id;
      state.countData = payload.count_data;
    },
    setExportData(state, payload) {
      state.exportId = payload.export_id;
      state.countDataExport = payload.count_data;
    },
    setDownloadFailedURL(state, payload) {
      state.downloadFailedURL = payload;
    },
    setStateVariable(state, { payload, variable }) {
      state[variable] = payload;
    },
    clearLastResult(state) {
      state.countData = 0;
      state.countSuccess = 0;
      state.countFailed = 0;
      state.countDone = 0;
      state.countSame = 0;
      state.uploadProgress = 0;
      state.onUpload = false;
      state.insertProgress = 0;
      state.onInsert = false;
      state.insertName = '';
      state.intervalImport = null;
      state.importId = null;
      state.downloadFailedURL = '';
    },
  },

  actions: {
    async fetchMerchants(
      ctx,
      {
        page, itemsPerPage, sortBy, sortDesc, search, merchantId, action, searchDefault,
      },
    ) {
      ctx.commit('clearMerchants');
      ctx.commit('setLoading', true);
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        sortingBy = `${sortBy[0]} ${sort}`;
      } else {
        sortingBy = `id ${sort}`;
      }
      const controller = (ctrl) => {
        switch (ctrl) {
          case 'merchant':
            return 'merchant/merchant/index';
          case 'outlet':
            return 'outlet/outlet/index';
          default:
            return 'merchant/merchant/index';
        }
      };
      const updatedMerchantId = merchantId == null ? '' : merchantId;
      search = { ...searchDefault, ...search };
      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        merchant_id: updatedMerchantId.toString(),
        version:
          updatedMerchantId === ''
            ? btoa(
              CryptoJS.SHA256(
                itemsPerPage.toString()
                + page.toString()
                + dateHelpers()
                + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
              ).toString(),
            )
            : btoa(
              CryptoJS.SHA256(
                updatedMerchantId.toString()
                + sortingBy.toString()
                + itemsPerPage.toString()
                + page.toString()
                + dateHelpers()
                + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
              ).toString(),
            ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/${controller(action)}`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setTotalMerchants', {
              payload: response.data,
            });
            ctx.commit('setMerchants', { payload: response.data });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },

    async updateMerchant(ctx, {
      merchantId, daerah, tempUniqueData,
    }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        merchant_id: merchantId,
        merchant_name: ctx.state.merchant.merchant_name,
        ktp_no:
          tempUniqueData.ktp_no === ctx.state.merchant.ktp_no
            ? ''
            : ctx.state.merchant.ktp_no,
        mobile_phone: ctx.state.merchant.mobile_phone,
        company_name: ctx.state.merchant.company_name,
        owner_name: ctx.state.merchant.owner_name,
        merchant_criteria: ctx.state.merchant.merchant_criteria,
        npwp_no:
          tempUniqueData.npwp_no === ctx.state.merchant.npwp_no
            ? ''
            : ctx.state.merchant.npwp_no,
        address: ctx.state.merchant.address,
        email:
          tempUniqueData.email === ctx.state.merchant.email
            ? ''
            : ctx.state.merchant.email,
        phone: ctx.state.merchant.phone,
        phone_area_code: ctx.state.merchant.phone_area_code,
        province_name: daerah.province_name,
        city_name: daerah.city_name,
        sub_district_name: daerah.sub_district_name,
        urban_name: daerah.urban_name,
        postal_code: daerah.postal_code,
        province_id: '',
        city_id: '',
        sub_district_id: '',
        urban_id: '',
        no_rekening:
          tempUniqueData.account_number === ctx.state.merchant.account_number
            ? ''
            : ctx.state.merchant.account_number,
        approver_1: ctx.state.merchant.approver_1_id,
        approver_2: ctx.state.merchant.approver_2_id,
        mcc: ctx.state.merchant.mcc,
        mdr_on_us: ctx.state.merchant.mdr_on_us,
        mdr_off_us: ctx.state.merchant.mdr_off_us,
        version: btoa(
          CryptoJS.SHA256(
            merchantId.toString()
            + ctx.state.merchant.merchant_name.toString()
            + ctx.state.merchant.owner_name.toString()
            + ctx.state.merchant.merchant_criteria.toString()
            + daerah.postal_code.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/merchant/merchant/update`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            ctx.commit('setErrors', response.data.data);
            if (
              typeof response.data.data.message !== 'object'
              && response.data.data.message !== null
            ) {
              ctx.commit(
                'setSnackbar',
                {
                  status: true,
                  message: response.data.data.message,
                  icon: 'mdi-alert',
                  color: 'red',
                },
                { root: true },
              );
            }
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            return response.data;
          }
          return false;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return false;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async viewMerchant(ctx, { id }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/merchant/merchant/view`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
          } else {
            ctx.commit('setDetailMerchant', {
              payload: response.data.data,
            });
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },

    async viewDocument(ctx, {
      id, docType, type, download,
    }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        account_id: id,
        type,
        doc_type: docType,
        download,
        version: btoa(
          CryptoJS.SHA256(
            type.toString()
            + docType.toString()
            + id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/document/document/view`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return null;
          }
          return response.data.data.base64_image;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return null;
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
      return result;
    },

    async getQRList(
      ctx,
      {
        id, page, itemsPerPage, sortBy, sortDesc, search,
      },
    ) {
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        sortingBy = `${sortBy[0]} ${sort}`;
      } else {
        sortingBy = `${sort}`;
      }

      const data = JSON.stringify({
        id,
        page,
        limit: itemsPerPage,
        search,
        sort_by: sortingBy,
        version: btoa(
          CryptoJS.SHA256(
            id.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/merchant/merchant/qr-list`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            return null;
          }
          return response.data.data;
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return null;
        });
      return result;
    },

    async downloadTemplate(ctx, { action }) {
      const controller = (ctrl) => {
        switch (ctrl) {
          case 'add':
            return 'bulk-upload-merchant';
          case 'add-outlet':
            return 'bulk-add-outlet';
          case 'update':
            return 'bulk-update-merchant';
          case 'update-outlet':
            return 'bulk-update-merchant';
          default:
            return 'bulk-upload-merchant';
        }
      };
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/onboarding/${controller(action)
          }/download`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
      };
      const result = await axios(config)
        .then((response) => response.data)
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return null;
        });
      return result;
    },

    async bulkUpload(ctx, {
      file, action, merchant_id,
    }) {
      const data = new FormData();
      data.append('file', file);
      ctx.commit('setOnUpload', true);
      ctx.commit(
        'setInsertProgressGlobal',
        {
          absoluteBar: true,
          absoluteBarText: 'Uploading...',
        },
        { root: true },
      );
      if (merchant_id !== null) {
        data.append('merchant_id', merchant_id);
      }
      const controller = (ctrl) => {
        switch (ctrl) {
          case 'add':
            return 'bulk-upload-merchant';
          case 'update':
            return 'bulk-update-merchant';

          case 'add-outlet':
            return 'bulk-add-outlet';

          case 'update-outlet':
            return 'bulk-update-merchant';
          default:
            return ctrl;
        }
      };

      const failed = () => {
        ctx.commit('setOnUpload', false);
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Upload Failed !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        ctx.commit(
          'setInsertProgressGlobal',
          {
            absoluteBar: false,
            absoluteBarText: '',
          },
          { root: true },
        );
      };

      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/onboarding/${controller(action)
          }/import`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        onUploadProgress(progressEvent) {
          ctx.commit(
            'setUploadProgress',
            parseInt(
              Math.round((progressEvent.loaded / progressEvent.total) * 100),
              10,
            ),
          );
        },
        data,
      };
      const result = await axios(config)
        .then((response) => {
          if (response.data.code === 200) {
            ctx.commit('setImportData', response.data.data);
            const interval = setInterval(() => {
              ctx.dispatch('importStatus', {
                date: dateHelpers(),
                controller: controller(action),
              });
            }, intervalUpload);
            ctx.commit('setIntervalImport', interval);
            ctx.commit('setInsertName', 'Start to insert data..');
            ctx.commit('setOnInsert', true);
            ctx.commit(
              'setInsertProgressGlobal',
              {
                absoluteBar: true,
                absoluteBarText: 'Start to insert data..',
              },
              { root: true },
            );
          } else {
            failed();
            if (response.data.code === 302) {
              ctx.commit(
                'setSnackbar',
                {
                  status: true,
                  message: response.data.data.message,
                  icon: 'mdi-alert',
                  color: 'red',
                },
                { root: true },
              );
            }
          }
          return response.data;
        })
        .catch((error) => {
          failed();
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
          return null;
        })
        .finally(() => {
          ctx.commit('setOnUpload', false);
        });
      return result;
    },

    async importStatus(ctx, { controller }) {
      const data = JSON.stringify({
        import_id: ctx.state.importId,
        count_data: ctx.state.countData,
        version: btoa(
          CryptoJS.SHA256(
            ctx.state.importId.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/onboarding/${controller
          }/status-import`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const failed = () => {
        clearInterval(ctx.state.intervalImport);
        ctx.commit('setOnInsert', false);
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Upload Failed !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        ctx.commit(
          'setInsertProgressGlobal',
          {
            absoluteBar: false,
            absoluteBarText: '',
          },
          { root: true },
        );
      };

      await axios(config)
        .then((response) => {
          if (response.data.code === '00') {
            clearInterval(ctx.state.intervalImport);
            ctx.commit('setOnInsert', false);
            ctx.commit('setStateVariable', {
              variable: 'countSuccess',
              payload: response.data.data.count_success,
            });
            ctx.commit('setStateVariable', {
              variable: 'countFailed',
              payload: response.data.data.count_failed,
            });
            if (response.data.data.count_success !== ctx.state.countData) {
              ctx.dispatch('downloadFailedImport', {
                controller,
              });
            } else {
              ctx.commit(
                'setSnackbar',
                {
                  status: true,
                  message: 'Success, Bulk Upload Done !',
                  icon: 'mdi-alert',
                  color: 'green',
                },
                { root: true },
              );
            }
            ctx.commit(
              'setInsertProgressGlobal',
              {
                absoluteBar: false,
                absoluteBarText: '',
              },
              { root: true },
            );
          } else if (response.data.code === 302) {
            failed();
          } else {
            const totalDataDone = parseInt(response.data.data.count_success, 10)
              + parseInt(response.data.data.count_failed, 10);
            ctx.commit(
              'setInsertName',
              `Insert ${totalDataDone}/${ctx.state.countData} data..`,
            );
            ctx.commit(
              'setInsertProgress',
              (totalDataDone / ctx.state.countData) * 100,
            );
            ctx.commit(
              'setInsertProgressGlobal',
              {
                absoluteBar: true,
                absoluteBarText:
                  `Insert ${totalDataDone
                  }/${ctx.state.countData
                  } data..`,
              },
              { root: true },
            );
            if (ctx.state.countDone !== totalDataDone) {
              ctx.commit('setStateVariable', {
                payload: totalDataDone,
                variable: 'countDone',
              });
              ctx.commit('setStateVariable', {
                payload: 0,
                variable: 'countSame',
              });
            } else {
              ctx.commit('setStateVariable', {
                payload: ctx.state.countSame + 1,
                variable: 'countSame',
              });
            }
            if (ctx.state.countSame > 2) {
              failed();
            }
          }
        })
        .catch((error) => {
          failed();
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        });
    },

    async downloadFailedImport(ctx, { controller }) {
      ctx.commit('setLoading', true);
      const data = JSON.stringify({
        import_id: ctx.state.importId,
        version: btoa(
          CryptoJS.SHA256(
            ctx.state.importId.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        ),
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/onboarding/${controller
          }/download-failed-import`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };
      await axios(config)
        .then((response) => {
          if (response.data.code === 200) {
            ctx.commit('setDownloadFailedURL', response.data.data.download);
          }
        })
        .catch((error) => {
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
          ctx.commit(
            'setSnackbar',
            {
              status: true,
              message: 'Upload done with some failed record !',
              icon: 'mdi-alert',
              color: 'orange',
            },
            { root: true },
          );
        });
    },

    async exportList(
      ctx,
      {
        page, itemsPerPage, sortBy, sortDesc, search, view, IDMerchant,
      },
    ) {
      if (ctx.rootState.isExport === true) {
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Export in progress !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        return;
      }
      ctx.commit('setLoading', true);
      ctx.commit('setIsExport', true, { root: true });
      ctx.commit('setStateVariable', {
        payload: 0,
        variable: 'countSameExport',
      });
      const sort = sortDesc[0] === false ? 'DESC' : 'ASC';
      let sortingBy = '';
      if (sortBy[0] != null) {
        sortingBy = `${sortBy[0]} ${sort}`;
      } else {
        sortingBy = `${sort}`;
      }

      const controller = (ctrl) => {
        switch (ctrl) {
          case 'merchant':
            return 'export-merchant-list';
          case 'merchant-outlet':
            return 'export-outlet-list';
          default:
            return 'export-merchant-list';
        }
      };

      const version = view === 'merchant'
        ? btoa(
          CryptoJS.SHA256(
            page.toString()
            + itemsPerPage.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        )
        : btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString()
            + page.toString()
            + dateHelpers()
            + atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3)),
          ).toString(),
        );

      const data = JSON.stringify({
        search,
        sort_by: sortingBy,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        merchant_id: IDMerchant.toString(),
        version,
      });
      const config = {
        method: 'post',
        url:
          `${process.env.VUE_APP_API_BASE_URL
          }/index.php?r=api/v1/export/${controller(view)
          }/index`,
        headers: {
          'X-CSRF-TOKEN': JSON.parse(localStorage.getItem('logged_in_qram'))
            .token,
          'Content-Type': 'application/json',
        },
        data,
      };

      const failed = () => {
        ctx.commit(
          'setSnackbar',
          {
            status: true,
            message: 'Export Failed !',
            icon: 'mdi-alert',
            color: 'red',
          },
          { root: true },
        );
        ctx.commit(
          'setInsertProgressGlobal',
          {
            absoluteBar: false,
            absoluteBarText: '',
          },
          { root: true },
        );
        ctx.commit('setIsExport', false, { root: true });
      };

      await axios(config)
        .then((response) => {
          if (response.data.code !== 200) {
            if (localStorage.getItem('language_qram') == null) {
              ctx.commit('setFlashErrorMessage', response.data.message_id);
            } else {
              ctx.commit('setFlashErrorMessage', response.data.message_en);
            }

            if (response.data.code === 401) {
              ctx.dispatch('logout', null, { root: true });
            }
            failed();
          } else {
            if (response.data.data.message != null) {
              ctx.commit(
                'setSnackbar',
                {
                  status: true,
                  message: response.data.data.message,
                  icon: 'mdi-alert',
                  color: 'red',
                },
                { root: true },
              );
              ctx.commit('setIsExport', false, { root: true });
              return;
            }
            ctx.commit('setExportData', response.data.data);
            const interval = setInterval(() => {
              ctx.dispatch(
                'exportStatus',
                {
                  date: dateHelpers(),
                  exportId: ctx.state.exportId,
                  countDataExport: ctx.state.countDataExport,
                  controller: controller(view),
                },
                { root: true },
              );
            }, intervalExport);
            ctx.commit('setIntervalExport', interval, { root: true });
            ctx.commit(
              'setInsertProgressGlobal',
              {
                absoluteBar: true,
                absoluteBarText: 'Start to export data..',
              },
              { root: true },
            );
          }
        })
        .catch((error) => {
          failed();
          if (error.response.status === 403) {
            ctx.commit(
              'setSnackbar',
              {
                status: true,
                message: error.response.data.message,
                icon: 'mdi-alert',
                color: 'red',
              },
              { root: true },
            );
          } else if (error.response.status === 401) {
            ctx.dispatch('logout', null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit('setLoading', false);
        });
    },
  },
};

export default merchant;
