<template>
  <detail
    :title="'Merchant Detail'"
    :loading="loading"
    :subtitle="'Informasi detail mengenai merchant'"
    :actionButton="actionButton"
  >
    <template v-slot:content>
      <content-data-merchant
        v-if="merchantType === 'Merchant'"
        :merchantType="'Merchant'"
        :data="data"
        :menu="'approval'"
      />
    </template>
  </detail>
</template>

<script>
import Detail from '@/components/detail/Detail.vue';
import ContentDataMerchant from '@/components/detail/ContentDataMerchantEdit.vue';

export default {
  components: {
    Detail,
    ContentDataMerchant,
  },
  beforeMount() {
    this.getDetailData();
  },
  data() {
    return {
      dialog: false,
      dialogApprove: false,
      actionButton: {
        hideButton: true,
      },
      loading: false,
      reasonType: null,
    };
  },
  computed: {
    data() {
      return this.$store.state.daftarInputer.ap.merchant;
    },
    merchantType() {
      let result = '';
      if (this.data != null) {
        if (this.data.merchant_type_id === 2) {
          result = 'Aggregator';
        } else {
          result = 'Merchant';
        }
      }
      return result;
    },
  },
  methods: {
    async getDetailData() {
      this.loading = true;
      await this.$store
        .dispatch('daftarInputer/ap/viewMerchant', {
          id: this.$route.params.id,
        })
        .then(() => {
          if (this.data == null) {
            this.$router.push('/daftar-inputer');
          }
        });
      this.loading = false;
    },
  },
};
</script>
