<template>
<v-dialog v-model="dialog" width="1000" persistent>
  <v-card>
    <v-toolbar
    >
      <v-btn
        icon
        dark
        color="primaryred"
        @click="cancelDialog"
      >
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-toolbar>
    <v-card-title v-if="title" class="title">
      {{title}}
    </v-card-title>

    <v-img
      :src="caution"
      contain
    ></v-img>

  </v-card>
</v-dialog>
</template>

<script>
export default {
  props: ["dialog","title","caution","loading"],
  methods: {
    cancelDialog() {
      this.$emit('closeCancelParent')
    },
    confirm() {
      this.$emit('confirmParent')
    }
  }
}
</script>
