import CryptoJS from "crypto-js";
import { btoa, atob } from "@/lib/helpers";
import axios from "axios";
import { interval_export, interval_upload } from "@/store/params.js";

export const aggregator = {
  namespaced: true,

  state() {
    return {
      allAggregators: [],
      totalAggregators: 0,
      allMerchants: [],
      totalMerchants: 0,
      loading: false,
      errors: [],
      flashErrorMessage: "",
      flashError: false,
      merchant: null,
      aggregator: null,
      aggregatorDocument: [],
      countData: 0,
      countSuccess: 0,
      countFailed: 0,
      uploadProgress: 0,
      onUpload: false,
      insertProgress: 0,
      onInsert: false,
      insertName: "",
      importId: null,
      downloadFailedURL: "",
      countDone: 0,
      countSame: 0,
      intervalImport: null,
      intervalExport: null,
      exportId: null,
      countDataExport: 0,
      totalPage: 0,
    };
  },

  mutations: {
    setAggregators(state, { payload }) {
      state.allAggregators = payload.data.list;
    },

    setMerchants(state, { payload }) {
      state.allMerchants = payload.data.list;
    },

    clearMerchants(state) {
      state.allMerchants = [];
      state.totalMerchants = 0;
    },

    setTotalMerchants(state, { payload }) {
      state.totalMerchants = parseInt(payload.data.countdata);
      state.totalPage = parseInt(payload.data.totalPage);
    },

    setFlashErrorMessage(state, payload) {
      state.flashErrorMessage = payload;
      state.flashError = payload != "" ? true : false;
    },

    clearAggregators(state) {
      state.allAggregators = [];
      state.totalAggregators = 0;
    },

    clearErrors(state) {
      state.errors = [];
    },

    clearErrorVariable(state, variable) {
      state.errors[variable] = [];
    },

    setErrors(state, payload) {
      state.errors = payload;
    },

    setTotalAggregators(state, { payload }) {
      state.totalAggregators = parseInt(payload.data.countdata);
      state.totalPage = parseInt(payload.data.totalPage);
    },

    setLoading(state, loading) {
      state.loading = loading;
    },

    setDetailAggregator(state, { payload }) {
      state.aggregator = payload;
    },

    setAggregatorDocument(state, document) {
      state.aggregatorDocument.push(document);
    },

    setAggregatorVariable(state, { variable, payload }) {
      state.aggregator[variable] = payload;
    },

    setUploadProgress(state, uploadProgress) {
      state.uploadProgress = uploadProgress;
    },
    setOnUpload(state, onUpload) {
      state.onUpload = onUpload;
    },
    setInsertProgress(state, insertProgress) {
      state.insertProgress = insertProgress;
    },
    setInsertName(state, insertName) {
      state.insertName = insertName;
    },
    setOnInsert(state, onInsert) {
      state.onInsert = onInsert;
    },
    setIntervalImport(state, payload) {
      state.intervalImport = payload;
    },
    setImportData(state, payload) {
      state.importId = payload.import_id;
      state.countData = payload.count_data;
    },
    setIntervalExport(state, payload) {
      state.intervalExport = payload;
    },
    setExportData(state, payload) {
      state.exportId = payload.export_id;
      state.countDataExport = payload.count_data;
    },
    setDownloadFailedURL(state, payload) {
      state.downloadFailedURL = payload;
    },
    setStateVariable(state, { payload, variable }) {
      state[variable] = payload;
    },
    clearLastResult(state) {
      state.countData = 0;
      state.countSuccess = 0;
      state.countFailed = 0;
      state.countDone = 0;
      state.countSame = 0;
      state.uploadProgress = 0;
      state.onUpload = false;
      state.insertProgress = 0;
      state.onInsert = false;
      state.insertName = "";
      state.intervalImport = null;
      state.importId = null;
      state.downloadFailedURL = "";
    },
  },

  actions: {
    async fetchAggregators(
      ctx,
      { date, page, itemsPerPage, sortBy, sortDesc, search }
    ) {
      ctx.commit("clearAggregators");
      ctx.commit("setLoading", true);
      const sort = sortDesc[0] == false ? "ASC" : "DESC";
      let sort_by = "";
      if (sortBy[0] != null) {
        sort_by = sortBy[0] + " " + sort;
      } else {
        sort_by = "id " + sort;
      }

      const data = JSON.stringify({
        search,
        sort_by: sort_by,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        version: btoa(
          CryptoJS.SHA256(
            itemsPerPage.toString() +
              page.toString() +
              date +
              atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
          ).toString()
        ),
      });

      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/client/aggregator/index",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };
      await axios(config)
        .then(function(response) {
          if (response.data.code != "200") {
            if (localStorage.getItem("language_qram") == null) {
              ctx.commit("setFlashErrorMessage", response.data.message_id);
            } else {
              ctx.commit("setFlashErrorMessage", response.data.message_en);
            }

            if (response.data.code == "401") {
              ctx.dispatch("logout", null, { root: true });
            }
          } else {
            ctx.commit("setTotalAggregators", {
              payload: response.data,
            });
            ctx.commit("setAggregators", {
              payload: response.data,
            });
          }
        })
        .catch(function(error) {
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit("setLoading", false);
        });
    },

    async fetchMerchants(
      ctx,
      {
        date,
        page,
        itemsPerPage,
        sortBy,
        sortDesc,
        search,
        aggregatorId,
        merchantId,
        action,
      }
    ) {
      ctx.commit("clearMerchants");
      ctx.commit("setLoading", true);
      const sort = sortDesc[0] == false ? "ASC" : "DESC";
      let sort_by = "";
      if (sortBy[0] != null) {
        sort_by = sortBy[0] + " " + sort;
      } else {
        sort_by = "id " + sort;
      }

      const controller = (ctrl) => {
        switch (ctrl) {
          case "merchant":
            return "submerchant/submerchant/index";
          case "outlet":
            return "outlet/outlet/index";
          default:
            return "merchantsub/submerchant/index";
        }
      };
      aggregatorId = aggregatorId == null ? "" : aggregatorId;
      merchantId = merchantId == null ? "" : merchantId;

      const version = (act) => {
        switch (act) {
          case "merchant":
            return btoa(
              CryptoJS.SHA256(
                aggregatorId.toString() +
                  sort_by.toString() +
                  itemsPerPage.toString() +
                  page.toString() +
                  date +
                  atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
              ).toString()
            );
          case "outlet":
            return btoa(
              CryptoJS.SHA256(
                merchantId.toString() +
                  sort_by.toString() +
                  itemsPerPage.toString() +
                  page.toString() +
                  date +
                  atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
              ).toString()
            );
          default:
            return btoa(
              CryptoJS.SHA256(
                itemsPerPage.toString() +
                  page.toString() +
                  date +
                  atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
              ).toString()
            );
        }
      };

      const data = JSON.stringify({
        search,
        sort_by: sort_by,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        aggregator_id: aggregatorId.toString(),
        merchant_id: merchantId.toString(),
        version: version(action),
      });

      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/" +
          controller(action),
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };
      await axios(config)
        .then(function(response) {
          if (response.data.code != "200") {
            if (localStorage.getItem("language_qram") == null) {
              ctx.commit("setFlashErrorMessage", response.data.message_id);
            } else {
              ctx.commit("setFlashErrorMessage", response.data.message_en);
            }

            if (response.data.code == "401") {
              ctx.dispatch("logout", null, { root: true });
            }
          } else {
            ctx.commit("setTotalMerchants", {
              payload: response.data,
            });
            ctx.commit("setMerchants", {
              payload: response.data,
            });
          }
        })
        .catch(function(error) {
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit("setLoading", false);
        });
    },

    async createAggregator(ctx, { date, payload }) {
      ctx.commit("setLoading", true);
      const data = JSON.stringify({
        pic_name: payload.pic_name,
        pic_ktp_no: payload.pic_ktp_no,
        pic_jabatan: payload.pic_jabatan,
        pic_mobile_phone: payload.pic_mobile_phone,
        pic_email: payload.pic_email,
        aggre_company_name: payload.aggre_company_name,
        aggre_owner_name: payload.aggre_owner_name,
        aggre_ktp_no: payload.aggre_ktp_no,
        aggre_npwp_no: payload.aggre_npwp_no,
        aggre_address: payload.aggre_address,
        aggre_email: payload.aggre_email,
        aggre_phone: payload.aggre_phone,
        aggre_phone_area_code: payload.aggre_phone_area_code,
        aggre_mobile_phone: payload.aggre_mobile_phone,
        province_id: "",
        province_name: payload.province_name,
        city_id: "",
        city_name: payload.city_name,
        sub_district_id: "",
        sub_district_name: payload.sub_district_name,
        urban_id: "",
        urban_name: payload.urban_name,
        postal_code: payload.postal_code,
        no_rekening: payload.no_rekening,
        approver_1: payload.approver_1,
        approver_2: payload.approver_2,
        version: btoa(
          CryptoJS.SHA256(
            payload.no_rekening +
              date +
              atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
          ).toString()
        ),
      });
      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/client/aggregator/create",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };
      return await axios(config)
        .then(function(response) {
          if (response.data.code != "200") {
            ctx.commit("setErrors", response.data.data);
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: "Failed create data",
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
            return false;
          } else {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: "Register Aggregator Success !",
                icon: "mdi-check-circle",
                color: "green",
              },
              { root: true }
            );
            return true;
          }
        })
        .catch(function(error) {
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
          return false;
        })
        .finally(() => {
          ctx.commit("setLoading", false);
        });
    },

    async updateAggregator(
      ctx,
      { date, aggregator_id, daerah, tempUniqueData }
    ) {
      ctx.commit("setLoading", true);
      const data = JSON.stringify({
        aggregator_id,
        pic_name: ctx.state.aggregator.aggre_pic_name,
        pic_ktp_no:
          tempUniqueData.aggre_pic_ktp_no ==
          ctx.state.aggregator.aggre_pic_ktp_no
            ? ""
            : ctx.state.aggregator.aggre_pic_ktp_no,
        pic_jabatan: ctx.state.aggregator.aggre_pic_jabatan,
        pic_mobile_phone: ctx.state.aggregator.aggre_pic_mobile_phone,
        pic_email:
          tempUniqueData.aggre_pic_email == ctx.state.aggregator.aggre_pic_email
            ? ""
            : ctx.state.aggregator.aggre_pic_email,
        aggre_company_name: ctx.state.aggregator.company_name,
        aggre_owner_name: ctx.state.aggregator.owner_name,
        aggre_ktp_no:
          tempUniqueData.ktp_no == ctx.state.aggregator.ktp_no
            ? ""
            : ctx.state.aggregator.ktp_no,
        aggre_npwp_no:
          tempUniqueData.npwp_no == ctx.state.aggregator.npwp_no
            ? ""
            : ctx.state.aggregator.npwp_no,
        aggre_address: ctx.state.aggregator.address,
        aggre_email:
          tempUniqueData.email == ctx.state.aggregator.email
            ? ""
            : ctx.state.aggregator.email,
        aggre_phone: ctx.state.aggregator.phone,
        aggre_phone_area_code: ctx.state.aggregator.phone_area_code,
        province_name: daerah.province_name,
        city_name: daerah.city_name,
        sub_district_name: daerah.sub_district_name,
        urban_name: daerah.urban_name,
        postal_code: daerah.postal_code,
        province_id: "",
        city_id: "",
        sub_district_id: "",
        urban_id: "",
        no_rekening:
          tempUniqueData.account_number == ctx.state.aggregator.account_number
            ? ""
            : ctx.state.aggregator.account_number,
        approver_1: ctx.state.aggregator.approver_1_id,
        approver_2: ctx.state.aggregator.approver_2_id,
        version: btoa(
          CryptoJS.SHA256(
            aggregator_id.toString() +
              date +
              atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
          ).toString()
        ),
      });

      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/client/aggregator/update",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };
      return await axios(config)
        .then(function(response) {
          if (response.data.code != "200") {
            ctx.commit("setErrors", response.data.data);
            if (
              typeof response.data.data !== "object" &&
              response.data.data !== null
            ) {
              ctx.commit(
                "setSnackbar",
                {
                  status: true,
                  message: response.data.data,
                  icon: "mdi-alert",
                  color: "red",
                },
                { root: true }
              );
            }
            if (localStorage.getItem("language_qram") == null) {
              ctx.commit("setFlashErrorMessage", response.data.message_id);
            } else {
              ctx.commit("setFlashErrorMessage", response.data.message_en);
            }

            if (response.data.code == "401") {
              ctx.dispatch("logout", null, { root: true });
            }
          } else {
            return response.data;
          }
          return false;
        })
        .catch(function(error) {
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
          return false;
        })
        .finally(() => {
          ctx.commit("setLoading", false);
        });
    },

    async viewAggregator(ctx, { date, id }) {
      ctx.commit("setLoading", true);
      const data = JSON.stringify({
        id,
        version: btoa(
          CryptoJS.SHA256(
            id.toString() +
              date +
              atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
          ).toString()
        ),
      });
      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/client/aggregator/view",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };
      await axios(config)
        .then(function(response) {
          if (response.data.code != "200") {
            if (localStorage.getItem("language_qram") == null) {
              ctx.commit("setFlashErrorMessage", response.data.message_id);
            } else {
              ctx.commit("setFlashErrorMessage", response.data.message_en);
            }

            if (response.data.code == "401") {
              ctx.dispatch("logout", null, { root: true });
            }
          } else {
            ctx.commit("setDetailAggregator", {
              payload: response.data.data,
            });
          }
        })
        .catch(function(error) {
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit("setLoading", false);
        });
    },

    async viewDocument(ctx, { date, id, doc_type, type, download }) {
      ctx.commit("setLoading", true);
      const data = JSON.stringify({
        account_id: id,
        type,
        doc_type,
        download,
        version: btoa(
          CryptoJS.SHA256(
            type.toString() +
              doc_type.toString() +
              id.toString() +
              date +
              atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
          ).toString()
        ),
      });
      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/document/document/view",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };
      return await axios(config)
        .then(function(response) {
          if (response.data.code != "200") {
            if (localStorage.getItem("language_qram") == null) {
              ctx.commit("setFlashErrorMessage", response.data.message_id);
            } else {
              ctx.commit("setFlashErrorMessage", response.data.message_en);
            }

            if (response.data.code == "401") {
              ctx.dispatch("logout", null, { root: true });
            }
            return null;
          } else {
            return response.data.data.base64_image;
          }
        })
        .catch(function(error) {
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
          return null;
        })
        .finally(() => {
          ctx.commit("setLoading", false);
        });
    },

    async downloadTemplate(ctx, { action }) {
      const controller = (ctrl) => {
        switch (ctrl) {
          case "add-sub":
            return "bulk-upload-merchant";
          case "add-outlet":
            return "bulk-add-outlet";
          default:
            return "bulk-upload-merchant";
        }
      };

      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/onboarding/" +
          controller(action) +
          "/download",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
      };
      return await axios(config)
        .then(function(response) {
          return response.data;
        })
        .catch(function(error) {
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
          return null;
        });
    },

    async bulkUploadSub(
      ctx,
      { file, date, action, aggregator_id, merchant_id }
    ) {
      var data = new FormData();
      data.append("file", file);
      data.append("aggregator_id", aggregator_id);
      if (merchant_id !== null) {
        data.append("merchant_id", merchant_id);
      }
      ctx.commit("setOnUpload", true);
      ctx.commit(
        "setInsertProgressGlobal",
        {
          absoluteBar: true,
          absoluteBarText: "Uploading...",
        },
        { root: true }
      );

      const controller = (ctrl) => {
        switch (ctrl) {
          case "add-sub":
            return "bulk-sub-merchant";
          case "update-sub":
            return "bulk-update-merchant";

          case "add-outlet":
            return "bulk-add-outlet";

          case "update-outlet":
            return "bulk-update-merchant";
          default:
            return ctrl;
        }
      };

      const failed = () => {
        ctx.commit("setOnUpload", false);
        ctx.commit(
          "setSnackbar",
          {
            status: true,
            message: "Upload Failed !",
            icon: "mdi-alert",
            color: "red",
          },
          { root: true }
        );
        ctx.commit(
          "setInsertProgressGlobal",
          {
            absoluteBar: false,
            absoluteBarText: "",
          },
          { root: true }
        );
      };

      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/onboarding/" +
          controller(action) +
          "/import",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        onUploadProgress: function(progressEvent) {
          ctx.commit(
            "setUploadProgress",
            parseInt(
              Math.round((progressEvent.loaded / progressEvent.total) * 100)
            )
          );
        },
        data: data,
      };
      return await axios(config)
        .then(function(response) {
          if (response.data.code == 200) {
            ctx.commit("setImportData", response.data.data);
            const interval = setInterval(function() {
              ctx.dispatch("importStatus", {
                date,
                controller: controller(action),
              });
            }, interval_upload);
            ctx.commit("setIntervalImport", interval);
            ctx.commit("setInsertName", "Start to insert data..");
            ctx.commit("setOnInsert", true);
            ctx.commit(
              "setInsertProgressGlobal",
              {
                absoluteBar: true,
                absoluteBarText: "Start to insert data..",
              },
              { root: true }
            );
          } else {
            failed();
            if (response.data.code == "302") {
              ctx.commit(
                "setSnackbar",
                {
                  status: true,
                  message: response.data.data.message,
                  icon: "mdi-alert",
                  color: "red",
                },
                { root: true }
              );
            }
          }
          return response.data;
        })
        .catch(function(error) {
          failed();
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
          return null;
        })
        .finally(() => {
          ctx.commit("setOnUpload", false);
        });
    },

    async importStatus(ctx, { date, controller }) {
      const data = JSON.stringify({
        import_id: ctx.state.importId,
        count_data: ctx.state.countData,
        version: btoa(
          CryptoJS.SHA256(
            ctx.state.importId.toString() +
              date +
              atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
          ).toString()
        ),
      });
      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/onboarding/" +
          controller +
          "/status-import",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };

      const failed = () => {
        clearInterval(ctx.state.intervalImport);
        ctx.commit("setOnInsert", false);
        ctx.commit(
          "setSnackbar",
          {
            status: true,
            message: "Upload Failed !",
            icon: "mdi-alert",
            color: "red",
          },
          { root: true }
        );
        ctx.commit(
          "setInsertProgressGlobal",
          {
            absoluteBar: false,
            absoluteBarText: "",
          },
          { root: true }
        );
      };

      await axios(config)
        .then(function(response) {
          if (response.data.code == "00") {
            clearInterval(ctx.state.intervalImport);
            ctx.commit("setOnInsert", false);
            ctx.commit("setStateVariable", {
              variable: "countSuccess",
              payload: response.data.data.count_success,
            });
            ctx.commit("setStateVariable", {
              variable: "countFailed",
              payload: response.data.data.count_failed,
            });
            if (response.data.data.count_success != ctx.state.countData) {
              ctx.dispatch("downloadFailedImport", {
                date,
                controller,
              });
            } else {
              ctx.commit(
                "setSnackbar",
                {
                  status: true,
                  message: "Success, Bulk Upload Done !",
                  icon: "mdi-alert",
                  color: "green",
                },
                { root: true }
              );
            }
            ctx.commit(
              "setInsertProgressGlobal",
              {
                absoluteBar: false,
                absoluteBarText: "",
              },
              { root: true }
            );
          } else if (response.data.code == "302") {
            failed();
          } else {
            let totalDataDone =
              parseInt(response.data.data.count_success) +
              parseInt(response.data.data.count_failed);
            ctx.commit(
              "setInsertName",
              "Insert " + totalDataDone + "/" + ctx.state.countData + " data.."
            );
            ctx.commit(
              "setInsertProgress",
              (totalDataDone / ctx.state.countData) * 100
            );
            ctx.commit(
              "setInsertProgressGlobal",
              {
                absoluteBar: true,
                absoluteBarText:
                  "Insert " +
                  totalDataDone +
                  "/" +
                  ctx.state.countData +
                  " data..",
              },
              { root: true }
            );
            if (ctx.state.countDone != totalDataDone) {
              ctx.commit("setStateVariable", {
                payload: totalDataDone,
                variable: "countDone",
              });
              ctx.commit("setStateVariable", {
                payload: 0,
                variable: "countSame",
              });
            } else {
              ctx.commit("setStateVariable", {
                payload: ctx.state.countSame + 1,
                variable: "countSame",
              });
            }
            if (ctx.state.countSame > 2) {
              failed();
            }
          }
        })
        .catch(function(error) {
          failed();
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
        });
    },

    async downloadFailedImport(ctx, { date, controller }) {
      ctx.commit("setLoading", true);
      const data = JSON.stringify({
        import_id: ctx.state.importId,
        version: btoa(
          CryptoJS.SHA256(
            ctx.state.importId.toString() +
              date +
              atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
          ).toString()
        ),
      });
      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/onboarding/" +
          controller +
          "/download-failed-import",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };
      await axios(config)
        .then(function(response) {
          if (response.data.code == 200) {
            ctx.commit("setDownloadFailedURL", response.data.data.download);
          }
        })
        .catch(function(error) {
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit("setLoading", false);
          ctx.commit(
            "setSnackbar",
            {
              status: true,
              message: "Upload done with some failed record !",
              icon: "mdi-alert",
              color: "orange",
            },
            { root: true }
          );
        });
    },

    async exportList(
      ctx,
      {
        date,
        page,
        itemsPerPage,
        sortBy,
        sortDesc,
        search,
        view,
        IDAggregator,
        IDMerchant,
      }
    ) {
      if (ctx.rootState.isExport === true) {
        ctx.commit(
          "setSnackbar",
          {
            status: true,
            message: "Export in progress !",
            icon: "mdi-alert",
            color: "red",
          },
          { root: true }
        );
        return;
      }
      ctx.commit("setLoading", true);
      ctx.commit("setIsExport", true, { root: true });
      ctx.commit("setStateVariable", {
        payload: 0,
        variable: "countSameExport",
      });
      const sort = sortDesc[0] == false ? "ASC" : "DESC";
      let sort_by = "";
      if (sortBy[0] != null) {
        const q = sortBy[0] == "name" ? "merchant_name" : sortBy[0];
        sort_by = q + " " + sort;
      } else {
        sort_by = "id " + sort;
      }
      const controller = (ctrl) => {
        switch (ctrl) {
          case "aggregator":
            return "export-aggregator-list";
          case "sub-merchant":
            return "export-sub-merchant-list";
          case "sub-merchant-outlet":
            return "export-outlet-list";
          default:
            return "export-aggregator-list";
        }
      };

      const data = JSON.stringify({
        search,
        sort_by: sort_by,
        limit: itemsPerPage.toString(),
        page: page.toString(),
        aggregator_id: IDAggregator.toString(),
        merchant_id: IDMerchant.toString(),
        version: btoa(
          CryptoJS.SHA256(
            IDAggregator.toString() +
              itemsPerPage.toString() +
              page.toString() +
              date +
              atob(process.env.VUE_APP_STRING_EXTRA1.slice(0, -3))
          ).toString()
        ),
      });
      const config = {
        method: "post",
        url:
          process.env.VUE_APP_API_BASE_URL +
          "/index.php?r=api/v1/export/" +
          controller(view) +
          "/index",
        headers: {
          "X-CSRF-TOKEN": JSON.parse(localStorage.getItem("logged_in_qram"))
            .token,
          "Content-Type": "application/json",
        },
        data: data,
      };

      const failed = () => {
        ctx.commit(
          "setSnackbar",
          {
            status: true,
            message: "Export Failed !",
            icon: "mdi-alert",
            color: "red",
          },
          { root: true }
        );
        ctx.commit(
          "setInsertProgressGlobal",
          {
            absoluteBar: false,
            absoluteBarText: "",
          },
          { root: true }
        );
        ctx.commit("setIsExport", false, { root: true });
      };

      await axios(config)
        .then(function(response) {
          if (response.data.code != "200") {
            if (localStorage.getItem("language_qram") == null) {
              ctx.commit("setFlashErrorMessage", response.data.message_id);
            } else {
              ctx.commit("setFlashErrorMessage", response.data.message_en);
            }

            if (response.data.code == "401") {
              ctx.dispatch("logout", null, { root: true });
            }
            failed();
          } else {
            if (response.data.data.message != null) {
              ctx.commit(
                "setSnackbar",
                {
                  status: true,
                  message: response.data.data.message,
                  icon: "mdi-alert",
                  color: "red",
                },
                { root: true }
              );
              ctx.commit("setIsExport", false, { root: true });
              return;
            }
            ctx.commit("setExportData", response.data.data);
            const interval = setInterval(function() {
              ctx.dispatch(
                "exportStatus",
                {
                  date,
                  exportId: ctx.state.exportId,
                  countDataExport: ctx.state.countDataExport,
                  controller: controller(view),
                },
                { root: true }
              );
            }, interval_export);
            ctx.commit("setIntervalExport", interval, { root: true });
            ctx.commit(
              "setInsertProgressGlobal",
              {
                absoluteBar: true,
                absoluteBarText: "Start to export data..",
              },
              { root: true }
            );
          }
        })
        .catch(function(error) {
          failed();
          if (error.response.status == "403") {
            ctx.commit(
              "setSnackbar",
              {
                status: true,
                message: error.response.data.message,
                icon: "mdi-alert",
                color: "red",
              },
              { root: true }
            );
          } else if (error.response.status == "401") {
            ctx.dispatch("logout", null, { root: true });
          }
        })
        .finally(() => {
          ctx.commit("setLoading", false);
        });
    },
  },
};
