<template>
  <div>
    <v-row class="align-center">
      <v-col cols="10" sm="4"> {{ placeholder }} </v-col>
      <v-col cols="2" sm="1"> : </v-col>
      <v-col cols="12" sm="7">
        <v-select
          :label="label"
          :value="value"
          :options="searched"
          :searchable="true"
          :placeholder="placeholder"
          @search="onSearch"
          :allow-input="false"
          ref="mySelect"
          @input="onInput"
          :isRequired="true"
          :invalid-message="'This field is required'"
        />
        <slot name="content"></slot>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import vSelect from 'vue-select';
import 'vue-select/dist/vue-select.css'; // import CSS style

export default {
  components: {
    'v-select': vSelect,
  },

  props: {
    placeholder: {
      type: String,
    },
    items: {
      type: Array,
    },
    rule: {
      type: Array,
    },
    label: {
      type: String,
    },
    value: {
      type: [String, Number, Object],
      default: '',
    },
    required: {
      type: Boolean,
    },
  },
  computed: {
    searched() {
      return this.$store.state.utility.areaList;
    },
  },
  methods: {
    updateInput(val) {
      this.$emit('update:value', val);
    },
    onSearch(searchText) {
      this.$store.dispatch('utility/getArea', { qeyword: searchText });
    },
    onInput(selectedOption) {
      this.value = selectedOption;
      this.$emit('update:value', selectedOption);
    },
  },
};
</script>
