<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12">
        <transition name="fade">
          <v-card
            shaped
            class="elevation-0"
            :style="{ 'border-bottom-right-radius': '0px' }"
            v-if="show"
          >
            <v-toolbar flat>
              <v-btn-toggle
                class="w-100"
                v-model="loginOption"
                rounded="0"
                color="red"
                group
                mandatory
              >
                <v-btn class="w-50 bolder" value="1">
                  Login
                </v-btn>

                <v-btn class="w-50 bolder" value="2">
                  SSO
                </v-btn>
              </v-btn-toggle>
            </v-toolbar>
            <v-card-text>
              <v-form ref="form" lazy-validation>
                <v-text-field
                  label="Username"
                  name="username"
                  :rules="usernameRules"
                  prepend-icon="mdi-account"
                  type="text"
                  v-model="username"
                  required
                  v-on:keyup.enter="login"
                  autocomplete="username"
                ></v-text-field>

                <v-text-field
                  id="password"
                  label="Password"
                  name="password"
                  :rules="passwordRules"
                  prepend-icon="mdi-lock"
                  v-model="password"
                  required
                  autocomplete="current-password"
                  v-on:keyup.enter="login"
                  :append-icon="passwordFieldType ? 'mdi-eye-off' : 'mdi-eye'"
                  @click:append="() => (passwordFieldType = !passwordFieldType)"
                  :type="passwordFieldType ? 'password' : 'text'"
                ></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions style="padding: 2px 16px 2px 16px">
              <v-btn
                color="blue-grey darken-2"
                @click="login"
                style="color: white"
                width="100%"
                :loading="loading"
                >Login</v-btn
              >
            </v-card-actions>
            <div
              class="mt-5 ms-5"
              :style="loginOption !== '1' ? 'visibility: hidden;' : ''"
            >
              <h4>
                <i>
                  * Untuk Field Office dan Kantor Cabang, wajib login dengan
                  SSO!
                </i>
              </h4>
            </div>
          </v-card>
        </transition>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import CustomEncryption from '@/lib/customEncryption';
import { extraString3 } from '@/store/params';
import { keyTest } from '../lib/helpers';

export default {
  name: 'Login',
  props: {
    source: String,
  },
  beforeCreate() {
    if (this.$store.state.authenticated) {
      this.$router.push({ name: 'Dashboard' });
    }
  },
  data() {
    return {
      loginOption: '1', // 1 is login 2 is SSO login
      passwordFieldType: true,
      username: '',
      password: '',
      usernameRules: [(v) => !!v || 'Username is required'],
      passwordRules: [(v) => !!v || 'Password is required'],
      show: false,
    };
  },
  computed: {
    loading() {
      return this.$store.state.loading;
    },
  },

  mounted() {
    console.log(keyTest);
    setTimeout(() => {
      this.show = true;
    }, 300);
  },
  watch: {
    loginOption() {
      this.$refs.form.reset();
      this.username = '';
      this.password = '';
    },
  },

  methods: {
    async login() {
      this.$store.commit('setIsExpired', false);
      if (this.$refs.form.validate() && !this.loading) {
        const speEncrypt = new CustomEncryption();
        const extras = speEncrypt.decryption({
          enc: process.env.VUE_APP_STRING_EXTRA2,
          masterkey: extraString3,
        });
        await this.$store
          .dispatch('requestAuthKey', {
            encryptedRequest: speEncrypt.encrypt('', this.username, extras),
          })
          .then((response) => {
            if (response !== false && response != null) {
              speEncrypt.decrypt(response, true, false, extras);
              this.$store
                .dispatch('login', {
                  loginOption: this.loginOption,
                  username: this.username,
                  password: this.password,
                  encryptedUsername: speEncrypt.encrypt(
                    speEncrypt.encryption(this.username, false),
                    '',
                    extras,
                  ),
                  encryptedPassword: speEncrypt.encrypt(
                    speEncrypt.encryption(this.password, false),
                    '',
                    extras,
                  ),
                  accessKey: speEncrypt.encrypt(
                    localStorage.getItem('qram_key'),
                    '',
                    extras,
                  ),
                })
                .then((res) => {
                  if (res !== false && res != null) {
                    this.$store.dispatch('set_login', {
                      payload: res,
                    });
                  }
                })
                .finally(() => {
                  this.$store.commit('setLoading', false);
                });
            }
          });
      }
    },

    toForgotPassword() {
      this.$router.push('/forgot-password');
    },
  },
};
</script>

<style scoped>
.w-100 {
  width: 100%;
}
.w-50 {
  width: 50%;
}
.bolder {
  font-weight: bolder;
  font-size: 20px !important;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
